import * as React from 'react';
import { CustomModal } from 'Pages/CustomerCarePage/Components/CustomModal';
import { AssigneeNotesData } from 'Pages/CustomerCarePage/Models';

interface IProps {
    assigneeNotes: AssigneeNotesData;
    // isOpen: boolean;
    onClose: () => void;
}

export class AssigneeNotesModal extends React.Component<IProps> {

    render(): JSX.Element {
        return (
            <CustomModal
                className='modal'
                isOpen={this.props.assigneeNotes.isModalOpen}
                handleClose={this.props.onClose}
                header='Assignee Notes'
                content={
                    <>
                    <div className='sub-section-2'>
                        <div className='light-text'>PREVIOUS ASSIGNEE</div>
                        <div className='text'>{this.props.assigneeNotes.previousAssignee}</div>
                    </div>
                    {this.props.assigneeNotes.notes && this.props.assigneeNotes.notes !== '' ? (
                        <div className='sub-section-2'>
                            <div className='light-text'>NOTES</div>
                            <div className='text'>
                                {this.props.assigneeNotes.notes ? this.props.assigneeNotes.notes : 'None'}
                            </div>
                        </div>
                    ) : null}
                    </>
                } />
        );
    }
}