import * as React from 'react';
import { CustomModal } from 'Pages/CustomerCarePage/Components/CustomModal';
import { CustomButton } from 'Pages/CustomerCarePage/Components/CustomButton';
import { Processmaker } from 'Pages/CustomerCarePage/Processmaker';
import { API } from 'Pages/CustomerCarePage/API';
import { TicketData } from 'Pages/CustomerCarePage/Models';

interface IProps {
    caseId: string;
    ticket: TicketData;
    isOpen: boolean;
    onClose: () => void;
    onSuccess: (onAfterSuccess: (onSuccess: Function) => void) => void;
    onBeforeMainAction: () => void;
}

export class ReturnAsessmentModal extends React.Component<IProps> {

    onReturnAssessment = () => {
        this.props.onBeforeMainAction();
        const ticket = this.props.ticket;
        
        const body = {
            button_pressed: 'RETURN ASSESSMENT'
        };

        const patchBody = {
            assign_to: ticket.ticket_owner_name ? ticket.ticket_owner_name : ticket.createdBy.name,
            assigned_by: ticket.assignee.name,
            assign_remarks: ''
        };

        Processmaker.put(`cases/${this.props.caseId}/variable`, body, () => {
            Processmaker.put(`cases/${this.props.caseId}/route-case`, {}, () => {
                this.props.onSuccess((onSuccess) => {
                    API.patch(`customer-care/tickets/${ticket.id}`, patchBody, onSuccess);
                });
            });
        });
    }

    render(): JSX.Element {
        return (
            <CustomModal
                className='modal'
                isOpen={this.props.isOpen}
                handleClose={() => this.props.onClose()}
                header='Return Assessments'
                content={
                    'Returning your assessment will send the ' +
                    'ticket back to the ticket owner for resolving ' + 
                    'and closing. Are you sure you want to finish approvals?'}
                footer={
                    <>
                    <CustomButton
                        text='Cancel'
                        variant='outlined'
                        onClick={() => {
                            this.props.onClose();
                        }} />
                    <span style={{padding: '1rem'}} />
                    <CustomButton 
                        text='Confirm'
                        variant='contained'
                        onClick={() => {
                            this.setState({loading:true})
                            this.onReturnAssessment();
                        }} />
                    </>
            } />
        );
    }
}