//#region Global Imports
import * as React from 'react';
import { makeStyles } from '@material-ui/styles';
import Link from '@material-ui/core/Link';
import List from '@material-ui/core/List';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText'

interface SidebarListProps {
	children: React.ReactNode;
}
const listItemStyles = makeStyles({
	root: {
		justifyContent: 'space-between'
	},
});

interface SidebarListProps {
	children: React.ReactNode;
}

interface ListSectionProps {
	subheader: string | null;
	nameKey: string;
	valueKey: string;
	selectionKey: string;
	sidebarList: any[];
	onClick: (item: any) => void;
	itemAdornmentOnClick: (item: any) => void;
	itemAdornment: (item: any) => React.ReactNode | null;
	children: React.ReactNode | null;
}

interface HeaderLinkProps {
	name: string;
	value: string;
	selectionKey: string;
	sidebarList: any[];
	onClick: any;
}

export const HeaderLink: React.FC<HeaderLinkProps> = (
	props: HeaderLinkProps,
): JSX.Element | null => {
	const { selectionKey, sidebarList, name, value, onClick } = props;
	
	if (sidebarList.length > 0) {
		return (
			<ListItem key={value} style={{paddingTop: 4, paddingBottom: 4}}>
				<Link 
					onClick={onClick} 
					underline={'none'}
				>
					<ListItemText
						style={
							(selectionKey === value ? 
							{
								color: '#2C2E8E',
								fontWeight: 600,
								textDecoration: 'underline',
								cursor: 'default',
							} : 
							{
								color: '#272E4C',
								fontWeight: 400,
								cursor: 'pointer',
							})
						}
						disableTypography={true}
						primary={name}
					/>
				</Link>
			</ListItem>
		);
	} else {
		return null;
	}
};

export const ListSection: React.FC<ListSectionProps> = (
	props: ListSectionProps,
): JSX.Element | null => {
	let {
		children,
		itemAdornment,
		itemAdornmentOnClick,
		subheader,
		selectionKey,
		sidebarList,
		nameKey,
		valueKey,
		onClick,
	} = props;
	const listItemClasses = listItemStyles();
	return (
		<List>
			{subheader != null ? (
				<ListSubheader disableSticky={true}>{subheader}</ListSubheader>
			) : null}
			{sidebarList.map((item: any) =>
				item[nameKey] && item[valueKey] && item['included'] ? (
					<ListItem key={`${item[valueKey]}`} classes={listItemClasses} style={{paddingTop: 4, paddingBottom: 4}}>
						<Link
							onClick={() => onClick(item)}
							underline={'none'}
						>
							<ListItemText 
								style={
									(selectionKey === item[valueKey] ? 
									{
										color: '#2C2E8E',
										fontWeight: 600,
										textDecoration: 'underline',
										cursor: 'default',
									} : 
									{
										color: '#272E4C',
										fontWeight: 400,
										cursor: 'pointer',
									})
								}
								disableTypography={true} 
								primary={`${item[nameKey]}`} />
						</Link>
						{itemAdornment != null? (
							<div onClick={() => itemAdornmentOnClick(item)}>{itemAdornment(item)}</div>
						) : null}
					</ListItem>
				) : null,
			)}
			{children}
		</List>
	);
};

export const SidebarList: React.FC<SidebarListProps> = (props: SidebarListProps): JSX.Element => {
	const { children } = props;

	return (
		<div style={{ marginTop: '27.5px', marginRight: '40px', borderRight: '1px solid #D8D9DF' }}>
			{children}
		</div>
	);
};
