export default function App() {
  const [selectedMenu, setSelectedMenu] = useState('');

  const RenderComponent = (selected: string, Component) => {
    return class App extends React.Component {
      componentDidMount() {
        setSelectedMenu(selected);
      }

      render() {
        return <Component {...this.props} />
      }
    }
  }

  let routes = [
    {
      path: '/home',
      component: RenderComponent('dashboard', HomePage),
      // component: HomePage,
    },
    {
      path: '/dashboard',
      component: RenderComponent('dashboard', Dashboard),
    },
    {
      path: '/',
      component: RenderComponent('dashboard', Dashboard),
      exact: true
    },
    {
      path: '/login',
      component: RenderComponent('dashboard', TestLoginPage),
      // component: TestLoginPage,
    },
    {
      path: '/tickets',
      component: RenderComponent('tickets', TicketListPage),
      // component: TicketListPage,
    },
    {
      path: '/create-ticket',
      // component: RenderComponent('create new', CreateTicketPage),
      component: RenderComponent('create_ticket', CreateTicketPage),
      // component: CreateTicketPage
    },
    {
      path: '/update-ticket/:caseId/:ticketId',
      component: RenderComponent('create_ticket', UpdateTicketPage),
      // component: UpdateTicketPage
    },
    {
      path: '/resolve-ticket/:caseId/:ticketId',
      component: RenderComponent('create_ticket', ResolveTicketPage),
      // component: ResolveTicketPage
    }
  ];
  const prefix = '/customer-care';
  routes.map(route => (route.path = `${prefix}${route.path}`));

  return (
    <div>
      <Helmet titleTemplate="NEO - %s" defaultTitle="NEO">
        <meta name="description" content="NEO application" />
      </Helmet>
      <div className="side-bar">
        <Components.UI.PageLayout
          history={history}
          page={'customer care'}
          selectedMenu={selectedMenu}
          setSelectedMenu={setSelectedMenu}
        />
      </div>
      <div className="main">
        <Switch>
          {renderRoutes(routes)}
          <Route path="" component={NotFoundPage} />
        </Switch>
      </div>
    </div>
  );
}
