//#region Global Imports
import * as React from 'react';
import { BenefitsTable } from '../../TableComponent/BenefitsTable';
import Grid from '@material-ui/core/Grid';
// import Hidden from '@material-ui/core/Hidden';
import FormControl from '@material-ui/core/FormControl';
import Typography from '@material-ui/core/Typography';
import { WaitingPeriodField } from '../../ComboField';
import { ViewField } from '../../ViewField';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';

//import { Components } from '@hims/core';
//#endregion Global Imports

import { defaultProps, radioStyles } from './Constants';
import { ICategorySubForm } from './ICategorySubForm';

export const DentalForm: React.FC<ICategorySubForm.IProps> = (
	props: ICategorySubForm.IProps,
): JSX.Element | null => {
	const { formHeader, rows, viewOnly, setSubForm, setSubForm2, subFormNode } = props;
	const radioClasses = radioStyles();
	console.log(rows);
	console.log(props);

	let metadata:any = {};
	if (subFormNode) {
		if (subFormNode['custom_metadata']) {
			metadata = subFormNode['custom_metadata'];
		}
	}

	// const fakeRows: any = [
	// 	{
	// 		category: 'Dental Examination',
	// 		limit_type: ['max_session'],
	// 		limit: [3],
	// 	},
	// 	{
	// 		category: 'Annual oral prophylaxis',
	// 		limit_type: ['amt_limit'],
	// 		limit: [1000],
	// 	},
	// 	{
	// 		category: 'Orthodontic consultation',
	// 		limit_type: ['amt_limit'],
	// 		limit: [1000],
	// 	},
	// 	{
	// 		category: 'Prenatal check of cheeks and gums',
	// 		limit_type: ['amt_limit'],
	// 		limit: [1000],
	// 	},
	// 	{
	// 		category: 'Temporo Mandibular Joint (TMJ) consultation',
	// 		limit_type: ['max_session'],
	// 		limit: [2],
	// 	},
	// ];

	const columns: any = [
		{ name: 'name', title: 'Category' },
		{ 
			name: 'limit_type', 
			title: 'Limit Type',
			getCellValue: row => (row.limits
				? row.limits : [])
		},
		{ 
			name: 'limit', 
			title: 'Limit',
			getCellValue: row => (row.limits
				? row.limits : []) 
		},
	];

	const formattedColumns = {
		limit: ['limit'],
		limit_type: ['limit_type'],
	};

	const columnExtensions = [
		{ columnName: 'limit_type', width: 150, wordWrapEnabled: true },
		{ columnName: 'limit', width: 150, wordWrapEnabled: true },
	];

	let finalRows = rows;
	
	if (viewOnly) {
		finalRows = rows.filter((row) => row.included ? row.included : false);
	}

	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2} style={{ marginTop: '27.5px' }}>
					{!viewOnly ? formHeader : null}
				</Grid>
			</Grid>
			<Grid item sm={12}>
				<BenefitsTable
					viewOnly={viewOnly}
					columns={columns}
					formattedColumns={formattedColumns}
					columnExtensions={columnExtensions}
					{...props}
					rows={finalRows}
				/>
			</Grid>
				<Grid item sm={12}>
					{!viewOnly ? (
						<React.Fragment>
							<Typography variant="overline" display="block" gutterBottom>
								Activation
							</Typography>
							<FormControl component="fieldset">
								<RadioGroup name="waiting_period">
									<FormControlLabel
										classes={radioClasses}
										value="waiting_period_effectivity"
										control={
											<Radio
												checked={metadata['waiting_period_type'] === 'effectivity'} 
												name="waiting_period_type"
												onClick={(e) => {
													e.stopPropagation();
													setSubForm2("waiting_period_type", "effectivity");
												}} />
										}
										label={
											<React.Fragment>
												<Typography variant="body1">
													Waiting period after effectivity of member
												</Typography>
												<FormControl>
												<WaitingPeriodField 
														disabled={metadata['waiting_period_type'] !== 'effectivity'}
														id="waiting_period_effectivity" 
														fieldName="waiting_period"
														selectName="waiting_period_unit"
														fieldValue={
															metadata['waiting_period_type'] === 'effectivity' ? 
															metadata['waiting_period'] : 0
														}
														selectValue={
															metadata['waiting_period_type'] === 'effectivity' ?
															metadata['waiting_period_unit'] : ''
														}
														fieldOnChange={setSubForm}
														selectOnChange={setSubForm}
														inputProps={{ min: 0 }}
														noDefault
													/>
												</FormControl>
											</React.Fragment>
										} />
									<FormControlLabel
										classes={radioClasses}
										value="waiting_period_hire"
										control={
											<Radio 
												checked={metadata['waiting_period_type'] === 'date_of_hire'} 
												name="waiting_period_type"
												onClick={(e) => {
													e.stopPropagation();
													setSubForm2("waiting_period_type", "date_of_hire");
												}}
											/>
										}
										label={
											<React.Fragment>
												<Typography variant="body1">
													Waiting period after date of hire
												</Typography>
												<FormControl>
												<WaitingPeriodField 
														disabled={metadata['waiting_period_type'] !== 'date_of_hire'}
														id="waiting_period_hire" 
														fieldName="waiting_period"
														selectName="waiting_period_unit"
														fieldValue={
															metadata['waiting_period_type'] === 'date_of_hire' ? 
															metadata['waiting_period'] : 0
														}
														selectValue={
															metadata['waiting_period_type'] === 'date_of_hire' ?
															metadata['waiting_period_unit'] : ''
														}
														fieldOnChange={setSubForm}
														selectOnChange={setSubForm}
														inputProps={{ min: 0 }}
														noDefault
													/>
												</FormControl>
											</React.Fragment>
										} />
								</RadioGroup>
							</FormControl>
						</React.Fragment> 
					) : (
						metadata['waiting_period_type'] && metadata['waiting_period_unit'] ? (
							<ViewField 
								label="Activation"
								value={metadata['waiting_period_type'] + " " + metadata['waiting_period_unit']}/>
						) : null
					)}
				</Grid>
		</React.Fragment>
	);
};

DentalForm.defaultProps = defaultProps;