import * as React from 'react';
import { Grid } from '@material-ui/core';

interface IProps {
    data: any[];
    renderHeader: () => JSX.Element;
    renderRow: (rowData: any, index: number) => JSX.Element;
}

export class ComplexTable extends React.Component<IProps> {

    render(): JSX.Element {
        return (
            <div className='complex-table'>
                <Grid className='header' container spacing={1}>
                    {this.props.renderHeader()}
                </Grid>
                
                {this.props.data.length > 0 ? (
                    this.props.data.map((rowData, index) => {
                        const backColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
                        return (
                            <div className='item' style={{backgroundColor: backColor}}>
                                {this.props.renderRow(rowData, index)}
                            </div>
                        );
                    })
                ) : (
                    <div style={{
                        backgroundColor: '#FFFFFF',
                        textAlign: 'center', 
                        padding: '1rem'}}>
                        {'No data'}
                    </div>
                )}
            </div>
        );
    }
}