
import * as React from 'react';
import { Grid, TableCell, TableRow } from '@material-ui/core';
import { InquiryData, DropdownChoice, PermissionData } from 'Pages/CustomerCarePage/Models';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { faMinusCircle, faPlus, faPen, faCheckCircle, faTimesCircle } from '@fortawesome/free-solid-svg-icons';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { InputChip } from 'Pages/CustomerCarePage/Components/InputChip';
import { CustomChip } from 'Pages/CustomerCarePage/Components/CustomChip';
import { CustomDropzone } from 'Pages/CustomerCarePage/Components/CustomDropzone';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
import { API } from 'Pages/CustomerCarePage/API';

interface IProps {
    callSnackbar?: (message : string) => void;
    inquiries: InquiryData[];
    permission: PermissionData;
    buttonClicked?: '' | 'Submit' | 'Resolve and Close' | 'Save For Now';
    isResolveTicketAttempt: boolean;
    refreshInquiryState: () => void;
}

interface IStates {
    inquiry: DropdownChoice;

    itemToEditIndex: number;
    inquiryToEdit: DropdownChoice;
}

export class UpdateInquirySection extends React.Component<IProps, IStates> {

    inquiries: DropdownChoice[];

    constructor(props: IProps) {
        super(props);

        this.state = {
            inquiry: { value: '', label: '' },

            itemToEditIndex: -1,
            inquiryToEdit: { value: '', label: '' }
        };

        this.inquiries = [
            { value: '1', label: 'Member Status / Card Inquiry' },
            { value: '2', label: 'Member Network Access Inquiry' },
            { value: '3', label: 'Member Benefit Inquiry - General [Outpatient]' },
            { value: '4', label: 'Member Benefit Inquiry - General [Inpatient]' },
            { value: '5', label: 'Member Benefit Inquiry - General [Emergency]' },
            { value: '6', label: 'Member Benefit Inquiry - General [Dental]' },
            { value: '7', label: 'Member Benefit Inquiry - General [APE / ECU]' },
            { value: '8', label: 'Member Benefit Inquiry - General [Riders]' },
            { value: '9', label: 'Member Coverage Inquiry - Specific Benefit' },
            { value: '10', label: 'Member Availment Inquiry - CanAvail' },
            { value: '11', label: 'Member Eligibility Inquiry' },
            { value: '12', label: 'Reimbursement Status Inquiry' },
            { value: '13', label: 'General Affiliated Network Inquiry - Facility' },
            { value: '14', label: 'General Affiliated Network Inquiry - Doctor' },
            { value: '15', label: 'General Affiliated Network Inquiry - Dentist' },
            { value: '16', label: 'Procedural Inquiry - Outpatient' },
            { value: '17', label: 'Procedural Inquiry - Inpatient' },
            { value: '18', label: 'Procedural Inquiry - Emergency' },
            { value: '19', label: 'Procedural Inquiry - Dental' },
            { value: '20', label: 'Procedural Inquiry - APE / ECU' },
            { value: '21', label: 'Procedural Inquiry - Member Enrolment / Cancellation / Update' },
            { value: '22', label: 'Procedural Inquiry - Reimbursement' },
            { value: '23', label: 'Procedural Inquiry - Others' }
        ];
    }

    getFilteredInquiries(): DropdownChoice[] {
        let filteredInquiries: DropdownChoice[] = [];
        for (var i = 0; i < this.inquiries.length; i++) {
            let isSame = false;
            for (var j = 0; j < this.props.inquiries.length; j++) {
                isSame = (
                    this.props.inquiries[j].inquiry.value === 
                    this.inquiries[i].value ? true : isSame);
            }
            if (!isSame) {
                filteredInquiries = filteredInquiries.concat(this.inquiries[i]);
            }
        }
        return filteredInquiries;
    }

    onAddInquiry() {
        if (this.state.inquiry.value !== '') {
            this.props.inquiries.push({
                inquiry: this.state.inquiry,
                detail: '',
                specificDetail: [],
                answer: '',
                remarks: '',
                relatedTickets: [],
                supportDocs: []
            });
            
            this.props.refreshInquiryState();
            this.setState({inquiry: { value: '', label: '' }});
        }
    }

    onRemoveInquiry(index: number) {
        this.props.inquiries.splice(index, 1);
        this.props.refreshInquiryState();
    }


    render(): JSX.Element {
        return (
            <div className='section' style={{paddingTop: 64,}}>
                {
                    this.props.permission.canUpdate ?
                    (
                        <>
                            <div className='header-1'>Update Inquiry</div>
                            <div className='text'>
                                Add an inquiry type and then add the question and its details after
                            </div>
                        </>
                    ) :
                    (
                        <>
                            <div className='header-1'>Details</div>
                            <div className='text'>
                            </div>
                        </>
                    )
                }
                <div className='sub-section'>
                    {true ? (
                        <CustomTable 
                            className={!this.props.permission.canUpdate ? 'custom-table' : undefined}
                            rows={this.props.inquiries}
                            renderHeader={() => this.renderTableHeader()}
                            renderRow={(rowData: any, index: number) => {
                                if (this.props.permission.canUpdate) {
                                    return this.renderEditabletableRow(rowData, index);
                                } else {
                                    return this.renderViewTableRow(rowData, index);
                                }
                            }} />
                    ) : (null)}
                </div>
            </div>
        );
    }

    renderTableHeader(): JSX.Element {
        return (
            this.props.permission.canUpdate ? (
                <>
                <TableCell>
                    <Dropdown 
                        choices={this.getFilteredInquiries()}
                        value={this.state.inquiry}
                        onChange={(value: DropdownChoice) => {
                            this.setState({inquiry: value});
                        }} />
                </TableCell>
                <TableCell style={{width: '200px', textAlign: 'center'}}>
                    <TextButton 
                        icon={faPlus} 
                        label="Add inquiry"
                        onClick={() => {
                            this.onAddInquiry();
                        }} />
                </TableCell>
                </>
            ) : (
                <TableCell>Inquiries</TableCell>
            )
        );
    }


    renderEditabletableRow(inquiry: InquiryData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                <TableCell colSpan={2}>
                    <Grid container spacing={1}>
                        {this.state.itemToEditIndex === index ? (
                            <>
                            <Grid item xs={10}>
                                <Dropdown 
                                    choices={this.getFilteredInquiries()}
                                    value={this.state.inquiryToEdit}
                                    onChange={(value: DropdownChoice) => {
                                        this.setState({inquiryToEdit: value});
                                    }} />
                            </Grid>
                            <Grid item xs={2} style={{textAlign: 'right'}}>
                                <IconButton
                                    icon={faCheckCircle}
                                    onClick={() => {
                                        this.props.inquiries[index].inquiry = this.state.inquiryToEdit;
                                        this.props.refreshInquiryState();
                                        this.setState({itemToEditIndex: -1});
                                    }}/>
                                <IconButton
                                    icon={faTimesCircle}
                                    onClick={() => {
                                        this.setState({itemToEditIndex: -1});
                                    }}/>
                            </Grid>
                            </>
                        ) : (
                            <>
                            <Grid item xs={11} className='header-2'>
                                {inquiry.inquiry.label}
                            </Grid>
                            <Grid item xs={1} style={{textAlign: 'right'}}>
                                <IconButton
                                    icon={faPen}
                                    onClick={() => {
                                        this.setState({
                                            itemToEditIndex: index,
                                            inquiryToEdit: {
                                                value: inquiry.inquiry.value,
                                                label: inquiry.inquiry.label
                                            }
                                        });
                                    }}/>
                                <IconButton
                                    icon={faMinusCircle}
                                    onClick={() => {
                                        this.onRemoveInquiry(index);
                                    }}/>
                            </Grid>
                            </>
                        )}
                    </Grid>
                    <div style={{position: 'relative'}}>
                        <div style={{paddingTop: '0.5rem'}} />
                        <div className='line-border' />
                        <div style={{paddingTop: '0.5rem'}} />
                        <Grid container spacing={2}>
                            <Grid item xs={6}>
                                <InputField 
                                    placeholder='Details of inquiry (Question)*'
                                    error={this.props.isResolveTicketAttempt && inquiry.detail.length === 0}
                                    value={inquiry.detail}
                                    onChange={(value: string) => {
                                        inquiry.detail = value;
                                        this.props.refreshInquiryState();
                                    }} />
                            </Grid>
                            <Grid item xs={6}>
                                <InputChip 
                                    placeholder='Specific Details (Optional, press enter to add)'
                                    value={inquiry.specificDetail}
                                    onAdd={(value: string) => {
                                        inquiry.specificDetail.push(value);
                                        this.props.refreshInquiryState();
                                    }}
                                    onDelete={(index: number) => {
                                        inquiry.specificDetail.splice(index, 1);
                                        this.props.refreshInquiryState();
                                    }} />
                            </Grid>
                        </Grid>
                        <InputField 
                            placeholder='Answer*'
                            error={
                                this.props.isResolveTicketAttempt && 
                                this.props.buttonClicked === 'Resolve and Close' &&
                                inquiry.answer === ''}
                            value={inquiry.answer}
                            onChange={(value: string) => {
                                inquiry.answer = value;
                                this.props.refreshInquiryState();
                            }} />
                        <InputField 
                            placeholder='Remarks (Optional)'
                            value={inquiry.remarks}
                            onChange={(value: string) => {
                                inquiry.remarks = value;
                                this.props.refreshInquiryState();
                            }} />
                        <Grid container spacing={2}>
                            <Grid item xs={6}>
                                <InputChip 
                                    placeholder='Related Tickets (Optional, press enter to add)'
                                    value={inquiry.relatedTickets}
                                    onAdd={(value: string) => {
                                        inquiry.relatedTickets.push(value);
                                        this.props.refreshInquiryState();
                                    }}
                                    onDelete={(index: number) => {
                                        inquiry.relatedTickets.splice(index, 1);
                                        this.props.refreshInquiryState();
                                    }} />
                            </Grid>
                            <Grid item xs={4}>
                                <CustomDropzone 
                                    accept='image/*, .pdf, .doc, .docx, .csv, .odt, .ods, .xls, .xlsx, .txt'
                                    onDrop={(files: File[]) => {
                                        inquiry.supportDocs.push(files[0]);
                                        this.props.refreshInquiryState();
                                    }}/>
                            </Grid>
                            <Grid item xs={2}>
                                {inquiry.supportDocs.map((file, index) => {
                                    // @ts-ignore
                                    const downloadLink = file.url ? file.url.replace('public/', API.getUrl()) : undefined;

                                    if (!file) {
                                        if (this.props.callSnackbar) {
                                            inquiry.supportDocs.splice(index, 1);
                                            this.props.callSnackbar('Unsupported file type');
                                        }
                                        return null;
                                    }

                                    return (
                                        <CustomChip
                                            label={file.name}
                                            value={index.toString()}
                                            downloadLink={downloadLink}
                                            onDelete={(value: string) => {
                                                inquiry.supportDocs.splice(parseInt(value), 1);
                                                this.props.refreshInquiryState();
                                            }} />
                                    );
                                })}
                            </Grid>
                        </Grid>
                    </div>
                </TableCell>
            </TableRow>
        );
    }

    renderViewTableRow(inquiry: InquiryData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        const detail = inquiry.detail.length > 0 ? inquiry.detail : '---';
        const specificDetail = inquiry.specificDetail.length > 0 ? inquiry.specificDetail.join(', ') : '---';
        const answer = inquiry.answer.length > 0 ? inquiry.answer : '---';
        const remarks = inquiry.remarks.length > 0 ? inquiry.remarks : '---';
        const relatedTickets = inquiry.relatedTickets.length > 0 ? inquiry.relatedTickets.join(', ') : '---';

        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                <TableCell colSpan={2}>
                    <Grid container spacing={1}>
                        <Grid item xs={12} className='header-2'>
                            {inquiry.inquiry.label}
                        </Grid>
                    </Grid>
                    <div style={{position: 'relative'}}>
                        <div style={{paddingTop: '0.5rem'}} />
                        <div className='line-border' />
                        <div style={{paddingTop: '0.5rem'}} />
                        <Grid container spacing={2}>
                            <Grid item xs={6}>
                                <div className='light-text'>Details of inquiry (Question)</div>
                                <div className='small-text'>{detail}</div>
                            </Grid>
                            <Grid item xs={6}>
                                <div className='light-text'>Specific Details</div>
                                <div className='small-text'>{specificDetail}</div>
                            </Grid>
                            <Grid item xs={6}>
                                <div>
                                    <Grid container spacing={1}>
                                        <Grid item xs={12}>
                                            <div className='light-text'>Answer</div>
                                            <div className='small-text'>{answer}</div>
                                        </Grid>
                                        <Grid item xs={12}>
                                            <div className='light-text'>Remarks (Optional)</div>
                                            <div className='small-text'>{remarks}</div>
                                        </Grid>
                                        <Grid item />
                                        <Grid item xs={12}>
                                            <div className='light-text'>Related Tickets</div>
                                            <div className='small-text'>{relatedTickets}</div>
                                        </Grid>
                                    </Grid>
                                </div>
                            </Grid>
                            <Grid item xs={6}>
                                <div className='light-text'>Supporting Documents</div>
                                {inquiry.supportDocs.map(doc => {
                                    // @ts-ignore
                                    const downloadLink = doc.url ? doc.url.replace('public/', API.getUrl()) : '';
                                    return (
                                        <div className='small-text'>
                                            <TextButton label={doc.name} downloadLink={downloadLink} />
                                        </div>
                                    );
                                })}
                            </Grid>
                        </Grid>
                    </div>
                </TableCell>
            </TableRow>
        );
    }
}