import * as React from 'react';
import {  Grid } from '@material-ui/core';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';
import { TicketData, DropdownChoice, LoginData } from 'Pages/CustomerCarePage/Models';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { CustomDropzone } from 'Pages/CustomerCarePage/Components/CustomDropzone';
import { InputArea } from 'Pages/CustomerCarePage/Components/InputArea';
import { CustomChip } from 'Pages/CustomerCarePage/Components/CustomChip';
import { brokerList } from './BrokerList';

interface IProps {
    clientOrMember: string | null;
    
    login: LoginData;
    searchIDType?: string;
    ticket: TicketData;
    facilities: DropdownChoice[];
    assignees: DropdownChoice[];
    createTicketAttemp: boolean;
    refreshTicketState: () => void;
    loadAssignees: (ticketType: string, specificType?: string) => void;
    callSnackbar: (message : string) => void;
    isMember?: boolean;
}

export class TicketInfoSection extends React.Component<IProps> {

    ticketChannels: DropdownChoice[];
    ticketSources: DropdownChoice[];
    memberSourceChoices: DropdownChoice[];
    corporateSourceChoices: DropdownChoice[];
    ticketTypes: DropdownChoice[];
    specificTypes: DropdownChoice[];
    urgencies: DropdownChoice[];

    constructor(props: IProps) {
        super(props);

        this.ticketChannels = [
            { value: '1', label: 'Call' },
            { value: '2', label: 'SMS' },
            { value: '3', label: 'Text' },
            { value: '4', label: 'Email' },
            { value: '5', label: 'Walk-in' }
        ];

        this.ticketSources = [
            { value: '1', label: 'Member' },
            { value: '2', label: 'Corporate Client' },
            { value: '3', label: 'Broker' },
            { value: '4', label: 'Agent' },
            { value: '5', label: 'Clinic/Hospital' },
            { value: '6', label: 'PRO'}
        ];

        this.memberSourceChoices = [
            { value: 'Actual Member', label: 'Actual Member' },
            { value: 'Other', label: 'Other' },
        ];

        this.corporateSourceChoices = [
            { value: 'Company HR', label: 'Company HR' },
            { value: 'Company Nurse', label: 'Company Nurse' },
            { value: 'Other', label: 'Other' },
        ];

        this.ticketTypes = [
            { value: '1', label: 'Availment', params : ['Customer Service', 'Patient Relations', 'Member Only'] },
            { value: '2', label: 'Inquiry', params : ['Customer Service', 'Patient Relations', 'AMD'] },
            { value: '3', label: 'Request/Endorsement', params: ['Customer Service', 'Patient Relations', 'AMD']  },
            { value: '4', label: 'Complaint/Concern', params: ['Customer Service', 'Patient Relations', 'AMD'] },
            { value: '5', label: 'Request Requiring Approval', params: ['AMD'] }
        ];

        this.specificTypes = [
            { value: '1', label: 'Outpatient Procedure', params: ['Availment', 'Customer Service', 'Patient Relations']},
            { value: '2', label: 'Inpatient', params: ['Availment', 'Patient Relations', 'Customer Service']},
            { value: '3', label: 'Emergency Room', params: ['Availment', 'Customer Service']  },
            { value: '4', label: 'Consultation', params: ['Availment', 'Customer Service']  }
        ];

        this.urgencies = [
            { value: '1', label: 'Low' },
            { value: '2', label: 'Medium' },
            { value: '3', label: 'High' }
        ];
    }
    
    getSpecficTypes = () => {
        const myDepartment = this.props.login.group ? this.props.login.group.department : '';
        return this.specificTypes.filter(type => type.params && type.params.includes(myDepartment));
    }

    getTicketTypes = () => {
        return this.ticketTypes.filter(type => {
            const myDepartment = this.props.login.group ? this.props.login.group.department : '';
            const searchIDType = this.props.searchIDType;
            const params = type.params;
            if (((searchIDType === 'Client ID' && this.props.clientOrMember === null) || this.props.clientOrMember ==='client'   ) && params && params.includes('Member Only')) {
                return false;
            }
            if (params && params.includes(myDepartment)) {
                return true;
            }
            return false;
        });
    }

    render(): JSX.Element {
        const ticket = this.props.ticket;

        return (
            <div className='ticket-info'>
                <div style={{paddingTop: '5rem'}} />
                <div className='sub-section'>
                    <div className='header-2'>TICKET INFORMATION</div>
                    <Grid container spacing={1}>
                        <Grid item xs={5}>
                            <Dropdown 
                                placeholder='Received via'
                                choices={this.ticketChannels}
                                value={{ value: '', label: ticket.ticketChannel }}
                                error={
                                    this.props.createTicketAttemp && 
                                    ticket.ticketChannel === ''}
                                onChange={(value: DropdownChoice) => {
                                    this.props.ticket.ticketChannel = value.label;
                                    this.props.refreshTicketState();
                                }} />
                        </Grid>
                        <Grid item xs={7}>
                            <InputField 
                                placeholder='Transaction No.' 
                                value={ticket.transactionNo}
                                disabled={true}
                                onChange={(value: string) => {
                                    this.props.ticket.transactionNo = value;
                                    this.props.refreshTicketState();
                                }} />
                        </Grid>
                    </Grid>
                    <Dropdown 
                        placeholder='Received From*'
                        choices={this.ticketSources}
                        value={{ value: '', label: ticket.ticketSource}}
                        error={
                            this.props.createTicketAttemp && 
                            ticket.ticketSource === ''}
                        onChange={(value: DropdownChoice) => {
                            this.props.ticket.ticketSource = value.label;
                            this.props.refreshTicketState();
                        }} />

                    {ticket.ticketSource === 'Member' && this.props.isMember ? (
                        <>
                        <Dropdown 
                            placeholder='Caller*'
                            choices={this.memberSourceChoices}
                            value={ticket.memberSourceChoice}
                            error={
                                this.props.createTicketAttemp && 
                                ticket.memberSourceChoice.value === ''
                            }
                            onChange={(value: DropdownChoice) => {
                                this.props.ticket.memberSourceChoice = value;
                                this.props.refreshTicketState();
                            }} />
                        {ticket.memberSourceChoice && ticket.memberSourceChoice.value === 'Other' ? (
                            <>
                            <InputField 
                                placeholder='Name of Caller*' 
                                value={ticket.callerName}
                                error={
                                    this.props.createTicketAttemp && 
                                    ticket.callerName.trim() === ''
                                }
                                onChange={(value: string) => {
                                    this.props.ticket.callerName = value;
                                    this.props.refreshTicketState();
                                }} />
                            <InputField 
                                placeholder='Relationship to Member*' 
                                value={ticket.relationshipToMember}
                                error={
                                    this.props.createTicketAttemp && 
                                    ticket.relationshipToMember.trim() === ''
                                }
                                onChange={(value: string) => {
                                    this.props.ticket.relationshipToMember = value;
                                    this.props.refreshTicketState();
                                }} />
                            </> 
                        ) : null}
                        </>

                    ) : ticket.ticketSource === 'Member' ? (
                        <InputField 
                            placeholder='Name of Caller*' 
                            value={ticket.callerName}
                            error={
                                this.props.createTicketAttemp && 
                                ticket.callerName.trim() === ''
                            }
                            onChange={(value: string) => {
                                this.props.ticket.callerName = value;
                                this.props.refreshTicketState();
                            }} />

                    ): ticket.ticketSource === 'PRO' ? (
                        <InputField 
                            placeholder='Name of PRO*' 
                            value={ticket.callerName}
                            error={
                                this.props.createTicketAttemp && 
                                ticket.callerName.trim() === ''
                            }
                            onChange={(value: string) => {
                                this.props.ticket.callerName = value;
                                this.props.refreshTicketState();
                            }} />

                    ) : ticket.ticketSource === 'Corporate Client' ? (
                        <>
                        <Dropdown 
                            placeholder='Caller*'
                            choices={this.corporateSourceChoices}
                            value={ticket.corporateSourceChoice}
                            error={
                                this.props.createTicketAttemp && 
                                ticket.corporateSourceChoice.value === ''
                            }
                            onChange={(value: DropdownChoice) => {
                                this.props.ticket.corporateSourceChoice = value;
                                this.props.refreshTicketState();
                            }} />
                        {ticket.corporateSourceChoice && ticket.corporateSourceChoice.value === 'Other' ? (
                            <InputField 
                                placeholder='Company Designation/Position*' 
                                value={ticket.companyPosition}
                                error={
                                    this.props.createTicketAttemp && 
                                    ticket.companyPosition.trim() === ''
                                }
                                onChange={(value: string) => {
                                    this.props.ticket.companyPosition = value;
                                    this.props.refreshTicketState();
                                }} />
                        ) : null}
                        <InputField 
                            placeholder='Name of Caller*' 
                            value={ticket.callerName}
                            error={
                                this.props.createTicketAttemp && 
                                ticket.callerName.trim() === ''
                            }
                            onChange={(value: string) => {
                                this.props.ticket.callerName = value;
                                this.props.refreshTicketState();
                            }} />
                        </>
                    
                    ) : ticket.ticketSource === 'Broker' ? (
                        <>
                        <Dropdown 
                            placeholder='Broker Name*'
                            choices={brokerList}
                            value={ticket.brokerName}
                            error={
                                this.props.createTicketAttemp && 
                                ticket.brokerName.value === ''
                            }
                            onChange={(value: DropdownChoice) => {
                                this.props.ticket.brokerName = value;
                                this.props.refreshTicketState();
                            }} />
                        <InputField 
                            placeholder='Name of Caller*' 
                            value={ticket.callerName}
                            error={
                                this.props.createTicketAttemp && 
                                ticket.callerName.trim() === ''
                            }
                            onChange={(value: string) => {
                                this.props.ticket.callerName = value;
                                this.props.refreshTicketState();
                            }} />
                        </>

                    ) : ticket.ticketSource === 'Agent' ? (
                        <InputField 
                            placeholder='Name of Caller*' 
                            value={ticket.callerName}
                            error={
                                this.props.createTicketAttemp && 
                                ticket.callerName.trim() === ''
                            }
                            onChange={(value: string) => {
                                this.props.ticket.callerName = value;
                                this.props.refreshTicketState();
                            }} />

                    ) : ticket.ticketSource === 'Clinic/Hospital' ? (
                        <>
                        <Dropdown 
                            placeholder='Name of Hospital*'
                            choices={[...this.props.facilities, { value: '', label: 'NOT AFFILIATED'}]}
                            value={ticket.facility}
                            error={
                                this.props.createTicketAttemp && 
                                ticket.facility.value === ''}
                            onChange={(value: DropdownChoice) => {
                                this.props.ticket.facility = value;
                                this.props.refreshTicketState();
                            }} />
                        {ticket.facility && ticket.facility.label === 'NOT AFFILIATED' ? (
                            <InputField 
                                placeholder='Clinic/Hospital Name*' 
                                value={ticket.facility.value}
                                error={
                                    this.props.createTicketAttemp && 
                                    ticket.department.trim() === ''}
                                onChange={(value: string) => {
                                    this.props.ticket.facility.value = value;
                                    this.props.refreshTicketState();
                                }} />
                        ) : null}
                        <InputField 
                            placeholder='Department/Section*' 
                            value={ticket.department}
                            error={
                                this.props.createTicketAttemp && 
                                ticket.department.trim() === ''}
                            onChange={(value: string) => {
                                this.props.ticket.department = value;
                                this.props.refreshTicketState();
                            }} />
                        <InputField 
                            placeholder='Name of Caller*' 
                            value={ticket.callerName}
                            error={
                                this.props.createTicketAttemp && 
                                ticket.callerName.trim() === ''
                            }
                            onChange={(value: string) => {
                                this.props.ticket.callerName = value;
                                this.props.refreshTicketState();
                            }} />
                        </>
                    ) : null}

                    <InputField 
                        placeholder='Contact No.*' 
                        value={ticket.contactNo}
                        error={
                            this.props.createTicketAttemp && 
                            ticket.contactNo.trim() === ''}
                        onChange={(value: string) => {
                            if (value.match(/^[0-9\b]+$/) || value.length == 0) {
                                this.props.ticket.contactNo = value; 
                                this.props.refreshTicketState();
                            }
                        }} />
                </div>
                
                <div className='sub-section'>
                    <div className='header-2'>TICKET CATEGORY</div>
                    <Grid container spacing={1}>
                        <Grid item xs={5}>
                            <Dropdown 
                                placeholder='Ticket Type*'
                                choices={this.getTicketTypes()}
                                value={{ value: '', label: ticket.ticketType }}
                                error={
                                    this.props.createTicketAttemp && 
                                    ticket.ticketType === ''}
                                onChange={(value: DropdownChoice) => {
                                    this.props.ticket.ticketType = value.label;
                                    this.props.ticket.specificType = (
                                        value.label !== 'Availment' ? 'Not Applicable' : ''
                                    );
                                    this.props.ticket.assignee = { 
                                        id: '', 
                                        isGroup: false, 
                                        name: '', 
                                        customData: undefined 
                                    };
                                    this.props.refreshTicketState();
                                }} />
                        </Grid>
                        <Grid item xs={7}>
                            <Dropdown 
                                placeholder='Specific Type*'
                                disabled={(ticket.ticketType !== 'Availment' && ticket.ticketType !== 'Non-Availment')}
                                choices={this.getSpecficTypes()}
                                value={{ value: '', label: ticket.specificType}}
                                error={
                                    this.props.createTicketAttemp && 
                                    ticket.specificType === '' && 
                                    ticket.ticketType !== 'Inquiry'}
                                onChange={(value: DropdownChoice) => {
                                    this.props.ticket.specificType = value.label;
                                    this.props.ticket.assignee = { 
                                        id: '', 
                                        isGroup: false, 
                                        name: '', 
                                        customData: undefined 
                                    };
                                    this.props.refreshTicketState();
                                }} />
                        </Grid>
                        <Grid item xs={5}>
                            <Dropdown 
                                placeholder='Urgency'
                                choices={this.urgencies}
                                value={{ value: '', label: ticket.urgency}}
                                error={
                                    this.props.createTicketAttemp && 
                                    ticket.urgency === ''}
                                onChange={(value: DropdownChoice) => {
                                    this.props.ticket.urgency = value.label;
                                    this.props.refreshTicketState();
                                }} />
                        </Grid>
                        <Grid item xs={7}>
                            <Dropdown 
                                placeholder='Assign To*'
                                choices={this.props.assignees}
                                disabled={ticket.ticketType === ''}
                                showAllResult
                                value={{ 
                                    value: ticket.assignee && ticket.assignee.id ? ticket.assignee.id : '', 
                                    label: ticket.assignee && ticket.assignee.name ? ticket.assignee.name : ''}}
                                error={
                                    this.props.createTicketAttemp && 
                                    ticket.assignee.id === ''}
                                onFocus={() => {
                                    this.props.loadAssignees(this.props.ticket.ticketType,this.props.ticket.specificType)
                                }}
                                onChange={(value: DropdownChoice) => {
                                    this.props.ticket.assignee = {
                                        id: value.value,
                                        name: value.label,
                                        isGroup: value.params && value.params.length > 0 ? value.params[0] : false,
                                        customData: value.params ? value.params[1] : undefined
                                    };
                                    this.props.refreshTicketState();
                                }} />
                        </Grid>
                    </Grid>
                </div>

                <div className='sub-section'>
                    <div className='header-2'>ADDITIONAL NOTES AND FILES</div>
                    <InputArea
                        placeholder={`Additional Notes${
                            this.props.ticket.assignee.name !== 'Me' && 
                            this.props.ticket.assignee.name !== '' ? 
                            '*' : ''}`}
                        value={ticket.additionalNotes}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.ticket.assignee.name !== 'Me' &&
                            ticket.additionalNotes === ''}
                        onChange={(value: string) => {
                            this.props.ticket.additionalNotes = value;
                            this.props.refreshTicketState();
                        }} />
                    <CustomDropzone
                        accept='image/*, .pdf, .doc, .docx, .csv, .odt, .ods, .xls, .xlsx, .txt'
                        onDrop={(files: File[]) => {
                            ticket.additionalFiles.push(files[0]);
                            this.props.refreshTicketState();
                        }} />
                    {ticket.additionalFiles.map((file, index) => {
                        if (!file) {
                            ticket.additionalFiles.splice(index,1);
                            this.props.callSnackbar('Unsupported file type');
                            return null
                        }
                        return (
                            <CustomChip
                                label={file.name}
                                value={index.toString()}
                                onDelete={(value: string) => {
                                    ticket.additionalFiles.splice(parseInt(value), 1);
                                    this.props.refreshTicketState();
                                }} />
                        );
                    })}
                </div>
                <div className='section' />
            </div>
        );
    }
}
