    loadTicketData = (url: string) => {
        API.get(url, (response) => {
            const ticketCategory = response.ticket_category;
            const confinement = ticketCategory.confinement_details;
            const inquiry = ticketCategory.inquiry_details;
            const request = ticketCategory.request_details;
            const complaint = ticketCategory.complaint_details;

            this.setState({ticket: Payloads.getTicketData(response)});
            const ticketData = Payloads.getTicketData(response);
            if (response && response.assigned_by && response.assign_remarks) {
                this.setState({assigneeNotes: {
                    isModalOpen: false,
                    previousAssignee: response.assigned_by,
                    notes: response.assign_remarks
                }}) 
            }

            Processmaker.get('cases', (cases) => {
                const ticketNo = ticketData.ticketNo;
                const caseId = GlobalFunction.extractTicketNumber(ticketNo);
                const targetCase = cases.find(caze => caze.app_number === caseId);

                let isAssignedToMe = false;
                
                if (targetCase) { 
                    isAssignedToMe = true;
                    this.setState({currentCase: {
                        task: targetCase.app_tas_title,
                        appTitle: targetCase.app_title
                    }});

                    if (targetCase.app_tas_title === 'Inquiry - Comment' ||
                        targetCase.app_tas_title === 'Inquiry - Escalate Ticket' ||
                        targetCase.app_tas_title === 'Endorsement - Comment' ||
                        targetCase.app_tas_title === 'Endorsement - Escalate Ticket' ||
                        targetCase.app_tas_title === 'Complaint - Comment' ||
                        targetCase.app_tas_title === 'Complaint - Escalate Ticket') {
                        this.setState({permission: {
                            canAssign: true,
                            canEscalate: true,
                            canComment: true
                        }});
                    } else if (
                        targetCase.app_tas_title === 'Inquiry - View Details' ||
                        targetCase.app_tas_title === 'Inquiry - View Ticket Details 2') {
                        this.setState({permission: {
                            canUpdate: true,
                            canUploadFile: true,
                            canAssign: true,
                            canEscalate: true,
                            canResolve: true,
                            canClose: true,
                            canComment: true
                        }});
                    } else if (targetCase.app_tas_title === 'Endorsement - View Ticket Details 2') {
                        this.setState({permission: {
                            canUpdate: true,
                            canUploadFile: true,
                            canAssign: true,
                            canEscalate: true,
                            canResolve: true,
                            canClose: true,
                            canComment: true
                        }});
                    } else if (targetCase.app_tas_title === 'Inpatient - View Details') {
                        this.setState({permission: {
                            canUpdate: true,
                            canUploadFile: true,
                            canTransfer: true,
                            canProceedToAssessment: true
                        }});
                    } else if (targetCase.app_tas_title === 'Complaint - View Ticket Details 2') {
                        this.setState({permission: {
                            canComment: true,
                            canUpdate: true,
                            canUploadFile: true,
                            canAssign: true,
                            canEscalate: true,
                            canSubmitForResolution: true
                        }});
                    } else if (targetCase.app_tas_title === 'Approval - View Ticket Details 2') {
                        this.setState({permission: {
                            canComment: true,
                            canUpdate: true,
                            canAssign: true,
                            canEscalateApproval: true,
                            canEscalateComment: true,
                            canProceedToAssessment: true
                        }});
                    } else if (
                        targetCase.app_tas_title === 'Approval - Comment' ||
                        targetCase.app_tas_title === 'Approval - Escalate Ticket') {
                        this.setState({permission: {
                            canComment: true,
                            canAssign: true,
                            canEscalateComment: true
                        }});
                    }

                    /* Assignee Notes */
                    if (this.state.login) {
                        const isShownAlready = response.notes_showed;
                        if (targetCase.previous_usr_uid !== this.state.login.pmakerUid && !isShownAlready) {
                            if (this.state.assigneeNotes) {
                                this.state.assigneeNotes.isModalOpen = true;
                                this.setState({assigneeNotes: this.state.assigneeNotes});
                            }
                        }
                    }
                }

                /* Cancel Button Availability */
                if (this.state.login &&
                    this.state.login.group &&
                    response.ticket_owner_department === this.state.login.group.department &&
                    this.state.ticket && (
                        (this.state.ticket.status !== 'Closed' && isAssignedToMe) ||
                        (this.state.ticket.status === 'Closed')
                    )) {
                    this.state.permission.canCancel = true;
                    this.setState({permission: this.state.permission});
                }

                this.setState({isSaving: false});
            }, () => {
                this.setState({isSaving: false});
            });

            if (confinement) {
                this.setState({confinement: Payloads.getConfinementData(confinement, response)});
                const facilityName = this.state.confinement ? this.state.confinement.provider.label : '';
                API.pnGet(`p/f/partners/facilities/${facilityName}/doctor-procedures`, (response) => {
                    const doctors = response.data.doctors.map(doctor => {
                        return {
                            value: doctor.doctor_id,
                            label: doctor.doctor_name,
                            params: doctor.type ? [doctor.type] : ['']
                        };
                     });
                     this.setState({doctors: doctors});
                 }, () => {
                     this.setState({snackbarMessage: 'Failed to get doctors data.'});
                 });

            } else if (inquiry) {
                this.tabList[0].label = 'Inquiry Details';
                this.setState({inquiries: Payloads.getInquiryData(inquiry)});

            } else if (request && this.state.ticket) {
                this.tabList[0].label = 'Request Updates';
                if (this.state.ticket.ticketType === 'Request/Endorsement') {
                    this.setState({endorsement: Payloads.getEndorsementData(request)});
                } else {
                    this.setState({requestApproval: Payloads.getRequestApprovalData(request)});
                }

            } else if (complaint) {
                this.tabList[0].label = 'Complaint Details';
                this.setState({complaint: Payloads.getComplaintData(complaint)});
            }

            this.setState({comments: Payloads.getCommentData(response.comments)});

            if (response.updates) {
                this.setState({updates: Payloads.getUpdateData(response.updates)});
            }

            if (response.member_id.trim().length > 0) {
                API.get(`customer-care/members/${response.member_id}`, (response) => {
                    this.setState({member: Payloads.getMemberData(response)});
                    API.get(`customer-care/members/${response.member_id}/benefits`, () => {});
                });
            }

            if (response.client_id.trim().length > 0) {
                API.get(`customer-care/clients/${response.client_id}`,(response) => {
                    this.setState({client: Payloads.getClientData(response)});
                });
            }
        }, (error) => {
            this.setState({snackbarMessage: error.toString()});
        });
    }









    getValidationErrorList = () => {
        let errors: ErrorMessageData[] = [];

        const confinement = this.state.confinement;
        const inquiries = this.state.inquiries;
        // const complaints = this.state.complaint

        if (confinement) {
            // console.log('error',confinement)
            /* Check if no diagnosis */
            if (confinement.diagnoses.length === 0) {
                errors.push({
                    message: 'Please add atleast one diagnosis before proceeding:',
                    targets: []
                })
            }

            if(confinement.accommodations.length === 0){
                errors.push({
                    message:'Please add atleast one room accomodation before proceeding',
                    targets:[]
                })
            }

            let admittingcheck:boolean = false

            confinement.physicians.forEach(item=>{
                if(item.physician.params && item.physician.params.includes('Admitting')){
                    admittingcheck=true
                }
            })

            if(!admittingcheck){
                errors.push({
                    message:'Please add atleast one admitting physician before proceeding',
                    targets:[]
                })
            }

            /* Check if there's a physician assigned to each diagnosis */
            let errorDiagnosis = confinement.diagnoses.filter(diagnosis => {
                let matched = false;
                confinement.physicians.map(physician => {
                    matched = physician.diagnosis.value === diagnosis.diagnosis.value ? true : matched;
                });
                return !matched;
            });
            if (errorDiagnosis.length > 0) {
                errors.push({
                    message: 'Please assign a physician for the following diagnoses before proceding:',
                    targets: errorDiagnosis.map(diagnosis => {
                        return `${diagnosis.diagnosis.value} ${diagnosis.diagnosis.label}`
                    })
                });
                this.setState({tabSelected: 5});
            }

            /* Check discharge details */
            const requiredFields: string[] = [];
            if (!confinement.dischargeDate || (confinement.dischargeDate && !(moment(confinement.dischargeDate).isValid()))){
                requiredFields.push('Discharge Date');
            }else if(confinement.dischargeDate && 
               moment(confinement.dischargeDate).isValid() && 
               moment(confinement.dateAdmitted).isValid() &&
               moment(confinement.dischargeDate).startOf('day')
               .diff(moment(confinement.dateAdmitted).startOf('day')) < 0
               ){
                errors.push({
                    message: 'Discharge date cannot be before admitted date.',
                    targets: []
                });
            }
                
            if (!confinement.finalBill)
                requiredFields.push('Final Hospital Bill Amount');

            if (requiredFields.length > 0) {
                errors.push({
                    message: 'Please fill up the following fields before proceding:',
                    targets: requiredFields
                });
                this.setState({tabSelected: 7});
            }

            /* Check primary diagnosis */
            const diagnoses_primary = confinement.diagnoses.filter(diagnosis => diagnosis.isPrimary).length
            if (diagnoses_primary === 0) {
                errors.push({
                    message: 'Please choose atleast one primary diagnosis.',
                    targets: []
                });
                this.setState({tabSelected: 4});
            } else if (diagnoses_primary > 1) {
                errors.push({
                    message: 'There cannot be more than one primary diagnosis.',
                    targets: []
                });
                this.setState({tabSelected: 4});
            }
        }

        if (inquiries) {

            /* Check if no inquiry */
            if (inquiries.length === 0) {
                errors.push({
                    message: 'Please add atleast one inquiry before proceeding.',
                    targets: []
                });
            }
            let errorInquiry : any = [];
            inquiries.forEach(inquiry => {
                if (inquiry.detail.length === 0)
                    errorInquiry.push(`${inquiry.inquiry.label}: Details of inquiry`);
                if (inquiry.answer === '')
                    errorInquiry.push(`${inquiry.inquiry.label}: Answer`);
            });
            if (errorInquiry.length > 0) {
                errors.push({
                    message: 'Please fill up the required fields for the following inquiries before proceding:',
                    targets: errorInquiry.map(inquiry => {
                        return inquiry
                    })
                });
            }
        }



        return errors;
    }










    onUploadConfinementFiles = (files: FileData[], key: string, nextAction: Function) => {
        let hasFiles = false;

        const formData = new FormData();
        files.forEach(file => {
            if (file.file && file.file['path'] && !file.file['field']) {
                formData.append(key, file.file);
                hasFiles = true;
            }
        });

        this.setState({isSaving: true});

        if (hasFiles) {
            API.postFormData('tmp/upload?path=customer-care', formData,
                (uploadResponse) => {
                    nextAction(uploadResponse);
                },
                (error) => {
                    console.log(error);
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Failed to upload files.'
                    });
                });
        } else {
            nextAction([]);
        }
    }










    onUploadFiles = (files: File[], key: string, nextAction: Function) => {
        let hasFiles = false;

        const formData = new FormData();
        files.forEach(file => {
            if (!file['field']) {
                formData.append(key, file);
                hasFiles = true;
            }
        });

        this.setState({isSaving: true});

        if (hasFiles) {
            API.postFormData('tmp/upload?path=customer-care', formData,
                (uploadResponse) => {
                    nextAction(uploadResponse);
                },
                (error) => {
                    console.log(error);
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Failed to upload files.'
                    });
                });
        } else {
            nextAction([]);
        }
    }










    onUpdateTicket = (
        flags: OnUpdateFlagData[], 
        historyAction: HistoryAction,
        login: LoginData, 
        currentCase: CaseData,
        onSuccess: Function) => {

        const ticket = this.state.ticket;
        const confinement = this.state.confinement;
        const inquiries = this.state.inquiries;
        const endorsement = this.state.endorsement;
        const complaint = this.state.complaint;

        if (!ticket) return;

        const fileData = this.state.confinement ? (
            this.state.confinement.files.filter(file => file.file ? !file.file['field'] : false)
        ) : [];

        const putTicket = (
            supportingFiles: any, 
            inquiryFiles: File[][], 
            endorsementFiles: File[],
            complaintFiles: File[]) => {

            
            const payload = Payloads.getPutTicketPayload(
                ticket,
                !supportingFiles.error && supportingFiles.length > 0 ? (
                    confinement ? (
                        confinement.files
                            .map(file => {
                                return {
                                    ...file.file,
                                    docName: file.docName
                                }
                            })
                            .filter(file => file ? file['field'] : false)
                            .concat(
                                supportingFiles.map((response, index) => {
                                    return {
                                        ...response,
                                        docName: fileData[index].docName
                                    }
                                })
                            )
                    ) : []
                ) : (
                    confinement ? (
                        confinement.files.map(file => {
                            return {
                                ...file.file,
                                docName: file.docName
                            }
                        })
                    ) : []
                ),
                inquiryFiles,
                endorsementFiles,
                complaintFiles,
                historyAction,
                login,
                currentCase,
                flags,
                this.state.member,
                this.state.client,
                this.state.confinement,
                this.state.inquiries,
                this.state.endorsement,
                this.state.complaint,
                this.state.requestApproval
            );

            API.put(`customer-care/tickets/${this.props.match.params['ticketId']}`, payload, () => {
                if (flags.includes('Mark as Resolve')) {
                    onResolveProcessmaker();
                } else {
                    onSuccess();
                }
            }, () => {
                this.setState({
                    isSaving: false,
                    snackbarMessage: 'Failed to update ticket.'
                });
            });
        }

        this.setState({isSaving: true});

        this.onUploadConfinementFiles(fileData, 'supporting_document', (supportingFiles) => {

            if (inquiries && inquiries.length > 0) {
                const inquiryFiles: File[][] = [];
                inquiries.forEach(inquiry => {
                    this.onUploadFiles(inquiry.supportDocs, 'supporting_document',
                        (inquiryFilesResponse) => {
                            inquiryFiles.push(
                                !inquiryFilesResponse.error &&
                                inquiryFilesResponse.length > 0 ? (
                                    inquiry.supportDocs
                                        .filter(file => file['field'])
                                        .concat(inquiryFilesResponse)
                                ) : (
                                    inquiry.supportDocs
                                )
                            );
                            if (inquiries && inquiries.length === inquiryFiles.length) {
                                putTicket(supportingFiles, inquiryFiles, [], []);
                            }
                        }
                    );
                });

            } else if (endorsement) {
                this.onUploadFiles(endorsement.requestFiles, 'supporting_document', (response) => {
                    const endorsementFiles = !response.error && response.length > 0 ? (
                        endorsement.requestFiles.filter(file => file['field']).concat(response)
                    ) : (
                        endorsement.requestFiles
                    );
                    putTicket(supportingFiles, [], endorsementFiles, []);
                });

            } else if (complaint) {
                this.onUploadFiles(complaint.files, 'supporting_document', (response) => {
                    const complaintFiles = !response.error && response.length > 0 ? (
                        complaint.files.filter(file => file['field']).concat(response)
                    ) : (
                        complaint.files
                    );
                    putTicket(supportingFiles, [], [], complaintFiles);
                });

            } else {
                putTicket(supportingFiles, [], [], []);
            }
        });

        const pmCallShortcut = (method: 'POST' | 'PUT', url: string, body: any, onSuccess: Function) => {
            Processmaker.shortcutCall(method, url, body, onSuccess, () => {
                this.setState({
                    isSaving: false,
                    snackbarMessage: 'Failed to update ticket.'
                });
            })
        }

        const onResolveProcessmaker = () => {
            const caseId = this.props.match.params['caseId'];
            const body = {
                button_pressed: 'RESOLVE'
            };

            pmCallShortcut('PUT', `cases/${caseId}/variable`, body, () => {
                pmCallShortcut('PUT', `cases/${caseId}/route-case`, {}, () => {
                    onSuccess();
                });
            });
        }
    }










    onProceedWithFinalAssessment = (
        flags: OnUpdateFlagData[], 
        login: LoginData, 
        caseData: CaseData,
        onSuccess: Function, 
        confinement?: ConfinementDetails) => {

        const proceedWithFinalAssessment = () => {
            const pmCallShortcut = (method: 'POST' | 'PUT', url: string, body: any, onSuccess: Function) => {
                Processmaker.shortcutCall(method, url, body, onSuccess, () => {
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Failed to proceed with final assessment.'
                    });
                })
            }

            const caseId = this.props.match.params['caseId'];

            const body = {
                button_pressed: 'PROCEED WITH FINAL ASSESSMENT',
                within_limit: true
            };

            pmCallShortcut('PUT', `cases/${caseId}/variable`, body, () => {
                pmCallShortcut('PUT', `cases/${caseId}/route-case`, {}, () => {
                    this.onUpdateTicket(flags, 'Proceed with Final Assessment', login, caseData, onSuccess);
                });
            });
        }

        if (this.state.login && this.state.login.ccData) {
            if (confinement) {
                const approvalLimit = this.state.login.ccData.amount_limit;
                if (confinement.finalBill <= approvalLimit) {
                    this.setState({isSaving: true});
                    proceedWithFinalAssessment();
                } else {
                    this.setState({isFinalAssessmentModalOpen: true});
                }
            } else {
                this.setState({isSaving: true});
                proceedWithFinalAssessment();
            }
        }
    }










    onSubmitForResolution = (
        flags: OnUpdateFlagData[], 
        login: LoginData, 
        caseData: CaseData,
        onSuccess: Function) => {

        const submitForResolution = () => {
            const pmCallShortcut = (method: 'POST' | 'PUT', url: string, body: any, onSuccess: Function) => {
                Processmaker.shortcutCall(method, url, body, onSuccess, () => {
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Failed to proceed with submit for resolution.'
                    });
                })
            }

            const caseId = this.props.match.params['caseId'];

            const body = {
                button_pressed: 'PROCEED',
                within_limit: true
            };

            pmCallShortcut('PUT', `cases/${caseId}/variable`, body, () => {
                pmCallShortcut('PUT', `cases/${caseId}/route-case`, {}, () => {
                    this.onUpdateTicket(flags, 'Updated Ticket', login, caseData, onSuccess);
                });
            });
        }
        
        submitForResolution();
    }










    onSaveForNow = (callback: any = null) => {
        const login = this.state.login;
        const ticket = this.state.ticket;
        const confinement = this.state.confinement;
        const inquiries = this.state.inquiries;
        const endorsement = this.state.endorsement;
        const complaint = this.state.complaint;

        if (!ticket) 
            return;


        const putTicket = (
            supportingFiles: any, 
            inquiryFiles: File[][], 
            endorsementFiles: File[],
            complaintFiles: File[]) => {
            if (!login) return;

            const payload = Payloads.saveInUpdateTicketPayload(
                login,
                ticket,
                this.state.tabSelected,
                this.state.updateState,
                this.state.accommodationState,
                this.state.diagnosisState,
                this.state.physicianState,
                this.state.procedureState,
                this.state.otherDetailState,
                this.state.fileState,
                supportingFiles,
                inquiryFiles,
                endorsementFiles,
                complaintFiles,
                this.state.permission,
                this.state.comments,
                this.state.updates,
                this.state.member,
                this.state.client,
                confinement,
                inquiries,
                endorsement,
                complaint,
                this.state.requestApproval,
                this.state.currentCase);

            API.post('tmp', JSON.stringify(payload), (response) => {
                if (response._id) {
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Progress saved.',
                        lastSave: new Date()
                    }, () => {
                        if(callback){
                            callback(true);
                        }
                    });
                } else {
                    this.setState({
                        isSaving: false,
                        snackbarMessage: 'Failed to save progress.'
                    }, () => {
                        if(callback){
                            callback(false);
                        }
                    });
                }
            }, () => {
                this.setState({
                    isSaving: false,
                    snackbarMessage: 'Failed to save progress.'
                }, () => {
                    if(callback){
                        callback(false);
                    }
                });
            });
        }

        this.onUploadConfinementFiles(confinement ? confinement.files : [], 'supporting_document', (response) => {
            const supportingFiles = !response.error && response.length > 0 ?
                ticket.additionalFiles.filter(file => file['field']).concat(response) :
                ticket.additionalFiles;

            if (inquiries && inquiries.length > 0) {
                const inquiryFiles: File[][] = [];
                inquiries.forEach(inquiry => {
                    this.onUploadFiles(inquiry.supportDocs, 'supporting_document', (response) => {
                        inquiryFiles.push(!response.error && response.length > 0 ?
                            inquiry.supportDocs.filter(file => file['field']).concat(response) :
                            inquiry.supportDocs
                        );
                        if (inquiries && inquiries.length === inquiryFiles.length) {
                            putTicket(supportingFiles, inquiryFiles, [], []);
                        }
                    });
                });

            } else if (endorsement) {
                this.onUploadFiles(endorsement.requestFiles, 'supporting_document', (response) => {
                    const endorsementFiles = !response.error && response.length > 0 ?
                        endorsement.requestFiles.filter(file => file['field']).concat(response) :
                        endorsement.requestFiles;
                    putTicket(supportingFiles, [], endorsementFiles, []);
                });

            } else if (complaint) {
                this.onUploadFiles(complaint.files, 'supporting_document', (response) => {
                    const complaintFiles = !response.error && response.length > 0 ?
                        complaint.files.filter(file => file['field']).concat(response) :
                        complaint.files;
                    putTicket(supportingFiles, [], [], complaintFiles);
                });

            } else {
                putTicket(supportingFiles, [], [], []);
            }
        });
    }










    onPatchStatus = (status: string) => {
        if (!this.state.ticket) 
            return;

        this.setState({
            isSaving: true,
            ticket: {
                ...this.state.ticket,
                status: status
            }
        }, () => {
            this.onSaveForNow(() => {
                if (!this.state.ticket || !this.state.login || !this.state.currentCase)
                    return;

                const ticketId = this.props.match.params['ticketId'];
                const payload = { 
                    status: status,
                    ticket_history_info: {
                        assignee_id: this.state.login.userId,
                        assignee_name: `${this.state.login.firstName} ${this.state.login.lastName}`,
                        pmaker_task: this.state.currentCase.task,
                        action: 
                            status === 'Pending' ? 'Marked Ticket as Pending' :
                            status === 'Open' ? 'Resumed Ticket' : ''
                    }
                };
                API.patch(`customer-care/tickets/${ticketId}`, payload, () => {
                    window.location.reload();
                }, () => {
                    this.setState({snackbarMessage: 'An error occurred while updating ticket status.'});
                });
            });
        });
    }










