  renderAction(row: any): JSX.Element {
    return (
      <TableCell>
        { 
          row.is_view ?
          <Typography className={clsx('dashboard-claim-link')}>
            View
          </Typography> 
          : (row.task_title=== 'Complaint - Close Ticket'
          || row.task_title=== 'Complaint - Claim Close Task'
          || row.task_title === 'Complaint - Escalate Ticket 3'   
          || row.task_title === 'Inpatient - Review Ticket'
          || row.task_title === 'Approval - Claim Close Ticket' 
          || row.task_title === 'Approval - Escalate Ticket 4') ?
          <Typography className={clsx('dashboard-claim-link')}>
            Close
          </Typography> 
          : (row.task_title === 'Outpatient - Approvers Submit for Assessment' 
          || row.task_title === 'Outpatient - Approver Submit for Assessment'
          || row.task_title === 'Outpatient - Approver Summarize Assessment' 
          || row.task_title === 'Outpatient - Submit for Assessment' 
          || row.task_title === 'Outpatient - Summarize Assessment' 
          || row.task_title === 'Inpatient - Approvers Proceed with Final Assessment' 
          || row.task_title === 'Inpatient - Approver Proceed with Final Assessment' 
          || row.task_title === 'Inpatient - Approver Summarize Assessment' 
          || row.task_title === 'Inpatient - Proceed with Final Assessment' 
          || row.task_title === 'Inpatient - Summarize Assessment' 
          || row.task_title === 'Approval - Approvers Submit for Assessment'
          || row.task_title === 'Approval - Approver Submit for Assessment'
          || row.task_title === 'Approval - Approver Summarize Assessment') ?
          <Typography className={clsx('dashboard-claim-link')}>
            Approve
          </Typography> 
          : (row.task_title === 'Inquiry - Comment' 
          || row.task_title === 'Inquiry - Escalate Ticket' 
          || row.task_title === 'Outpatient - Comment' 
          || row.task_title === 'Complaint - Comment' 
          || row.task_title === 'Complaint - Comment 2' 
          || row.task_title === 'Complaint - Escalate Ticket'
          || row.task_title === 'Complaint - Escalate Ticket 2' 
          || row.task_title === 'Endorsement - Comment'
          || row.task_title === 'Endorsement - Escalate Ticket' 
          || row.task_title === 'Approval - Comment'
          || row.task_title === 'Approval - Comment 2'
          || row.task_title === 'Approval - Comment 3'
          || row.task_title === 'Approval - Comment 4') ?
          <Typography className={clsx('dashboard-claim-link')}>
            Comment
          </Typography>
          : 
          <Typography className={clsx('dashboard-claim-link')}>
            Update
          </Typography>
        }
      </TableCell>
    );
  }









  renderCard(title: React.ReactNode, count: number, content: React.ReactNode, ticket_type_field: string): JSX.Element {
    return (
      <>
        <Typography className={clsx('sub-title')}>
          {title}
        </Typography>
        <Paper className={clsx('content')} style={{height:'100%'}}>
          <Box
            className={clsx('card-header')}
            display="flex"
            flexDirection="row"
            justifyContent="space-between">
            <Box>
              <Typography
                className={clsx('dashboard-count')}
                style={{ color: '#3AB77D' }}>
                { count }
              </Typography>
              <Typography className={clsx('dashboard-count-description')} >
                Open and Pending Tickets
              </Typography>
            </Box>
            <Box>
              <Select 
                items={ticket_options} 
                value={this.state[ticket_type_field]} 
                onChange={(e) => {
                  this.handleTicketTypeSelect(e, ticket_type_field)
                }} />
            </Box>
          </Box>
          <Box className={clsx('card-content')}>
            {content}
          </Box>
        </Paper>
      </>
    );
  }







