import * as React from 'react';
import { DropdownChoice, EndorsementDetails } from 'Pages/CustomerCarePage/Models';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { Grid } from '@material-ui/core';
import { CustomChip } from 'Pages/CustomerCarePage/Components/CustomChip';
import { CustomDropzone } from 'Pages/CustomerCarePage/Components/CustomDropzone';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';

interface IProps {
    endorsement: EndorsementDetails;
    createTicketAttemp: boolean;
    refreshEndorsementState: () => void;
    callSnackbar?: (message : string) => void;
}

export class EndorsementSection extends React.Component<IProps> {

    requestTypes: DropdownChoice[] = [
        { value: '1', label: 'Enrollment Endorsement' },
        { value: '2', label: 'Member Information Update Endorsement' },
        { value: '3', label: 'Termination Endorsement' },
        { value: '4', label: 'Cancellation Endorsement' },
        { value: '5', label: 'Card Printing / Reprinting Endorsement' },
        { value: '6', label: 'Card Replacement Endorsement' },
        { value: '7', label: 'Activation of Membership' },
        { value: '8', label: 'Submission of Supporting Documents Endorsement' },
        { value: '9', label: 'Other APD Endorsements' },
        { value: '10', label: 'Request for Availment Details' },
        { value: '11', label: 'Wellness Session / Program' },
        { value: '12', label: 'Client Orientation' }
    ];

    render(): JSX.Element {
        return (
            <div className='availment-section'>
                <div style={{paddingTop: '5rem'}} />
                <div className='sub-section'>
                    <div className='header-2'>REQUEST DETAILS</div>
                    <Dropdown 
                        choices={this.requestTypes}
                        placeholder='Request Type*'
                        value={this.props.endorsement.requestType}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.endorsement.requestType.value === ''}
                        onChange={(value: DropdownChoice) => {
                            this.props.endorsement.requestType = value;
                            this.props.refreshEndorsementState();
                        }} />
                    <InputField
                        placeholder='Background / Details of Request*'
                        value={this.props.endorsement.background}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.endorsement.background === ''}
                        onChange={(value: string) => {
                            this.props.endorsement.background = value;
                            this.props.refreshEndorsementState();
                        }} />
                    <InputField
                        placeholder='Notes'
                        value={this.props.endorsement.notes}
                        onChange={(value: string) => {
                            this.props.endorsement.notes = value;
                            this.props.refreshEndorsementState();
                        }} />
                    <Grid container>
                        <Grid item xs={6}>
                            <CustomDropzone
                                accept='image/*, .pdf, .doc, .docx, .csv, .odt, .ods, .xls, .xlsx, .txt'
                                placeholder='Request Files'
                                onDrop={(files: File[]) => {
                                    this.props.endorsement.requestFiles.push(files[0]);
                                    this.props.refreshEndorsementState();
                                }} />
                            {this.props.endorsement.requestFiles.map((file, index) => {
                                if(file === undefined){
                                    if(this.props.callSnackbar){
                                        this.props.endorsement.requestFiles.splice(index,1)
                                        this.props.callSnackbar('Unsupported file type')
                                    }
                                    return null
                                }
                                return (
                                    <CustomChip
                                        label={file.name}
                                        value={index.toString()}
                                        onDelete={(value: string) => {
                                            this.props.endorsement.requestFiles.splice(parseInt(value), 1);
                                            this.props.refreshEndorsementState();
                                        }} />
                                );
                            })}
                    </Grid>
                    </Grid>
                </div>
                <div className='section' />
            </div>
        );
    }
}