import * as React from 'react';
import './component-style.scss';
import MomentUtils from '@date-io/moment';
import { MuiPickersUtilsProvider, MaterialUiPickersDate, KeyboardTimePicker } from '@material-ui/pickers';

interface IProps {
    placeholder?: string;
    value: Date | null;
    disabled?: boolean;
    onChange: (value: Date | null) => void;
}

export class TimePicker extends React.Component<IProps> {

    render(): JSX.Element {
        return (
            <div className='date-picker'>
                {this.props.placeholder ? (
                    <div className='text'>{this.props.placeholder}</div>
                ) : null}
                <MuiPickersUtilsProvider utils={MomentUtils}>
                    <KeyboardTimePicker
                        className={this.props.disabled ? 'text-field-disabled text-field' : 'text-field'}
                        disabled={this.props.disabled}
                        variant='dialog'
                        value={this.props.value}
                        onChange={(date: MaterialUiPickersDate | null) => {
                            this.props.onChange(date ? date.toDate() : null);
                        }} />
                </MuiPickersUtilsProvider>
            </div>
        );
    }
}