//#region Global Imports
import * as React from 'react';

import { BenefitsTable } from 'Components/UI/TableComponent/BenefitsTable';

import Grid from '@material-ui/core/Grid';
// import Hidden from '@material-ui/core/Hidden';
import FormControl from '@material-ui/core/FormControl';
import Typography from '@material-ui/core/Typography';

import {
	CurrencyField,
	WaitingPeriodField,
} from '../../ComboField';
import { ViewField } from '../../ViewField';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';

//import { Components } from '@hims/core';
//#endregion Global Imports

import { defaultProps, radioStyles, displayValue } from './Constants';
import { ICategorySubForm } from './ICategorySubForm';
import { GlobalFunction } from 'Components/UI/GlobalFunction';

export const OutpatientMedicineForm: React.FC<ICategorySubForm.IProps> = (
	props: ICategorySubForm.IProps,
): JSX.Element | null => {
	const { formHeader, rows, viewOnly, subFormNode, setSubForm, setSubForm2 } = props;
	const radioClasses = radioStyles();

	let metadata:any = {};
	if (subFormNode) {
		if (subFormNode['custom_metadata']) {
			metadata = subFormNode['custom_metadata'];
		}
	}

	// const fakeRows: any = [
	// 	{
	// 		medicine: 'Abacavir',
	// 		medicine_limit_type: ['benefit_limit'],
	// 		medicine_limit: [3],
	// 		availment_type: 'outright',
	// 	},
	// 	{
	// 		medicine: 'Acyclovir',
	// 		medicine_limit_type: ['amt_limit'],
	// 		medicine_limit: [1000],
	// 		availment_type: 'reimbursement',
	// 	},
	// 	{
	// 		medicine: 'Alendronate',
	// 		medicine_limit_type: ['amt_limit'],
	// 		medicine_limit: [1000],
	// 		availment_type: 'reimbursement',
	// 	},
	// 	{
	// 		medicine: 'Amifostine',
	// 		medicine_limit_type: ['benefit_limit'],
	// 		medicine_limit: [2],
	// 		availment_type: 'outright',
	// 	},
	// 	{
	// 		medicine: 'Paracetamol',
	// 		medicine_limit_type: ['amt_limit'],
	// 		medicine_limit: [2000],
	// 		availment_type: 'outright',
	// 	},
	// ];

	const columns: any = [
		{ name: 'name', title: 'Excluded Medicine' },
		{ 
			name: 'limit_type', 
			title: 'Limit Type',
			getCellValue: row => (row.limits
				? row.limits : []) },
		{ 
			name: 'limit', 
			title: 'Limit', 
			getCellValue: row => (row.limits
				? row.limits : []) },
		{ 
			name: 'availment', 
			title: 'Availment Type',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.availment 
				? row.custom_metadata.availment : []),
		},
	];

	const formattedColumns = {
		limit_type: ['limit_type'],
		limit: ['limit'],
		availment: ['availment'],
	};

	let columnExtensions = [
		{ columnName: 'name', wordWrapEnabled: true },
		{ columnName: 'limit_type', width: 150, wordWrapEnabled: true },
		{ columnName: 'limit', width: 150, wordWrapEnabled: true },
		{ columnName: 'availment', width: 175, wordWrapEnabled: true },
	];

	const finalRows = rows.filter((row) => row['custom_metadata']['is_excluded'] ? row['custom_metadata']['is_excluded'] : false);

	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2}>
					{!viewOnly ? formHeader : null}
				</Grid>
			</Grid>
			<Grid item sm={12}>
				<BenefitsTable
					viewOnly={viewOnly}
					disableSelect
					columns={columns}
					formattedColumns={formattedColumns}
					columnExtensions={columnExtensions}
					{...props}
					rows={finalRows}
				/>
			</Grid>
				<Grid item sm={12}>
					{!viewOnly ? (
						<React.Fragment>
							<FormControl style={{marginBottom: '20px'}}>
								<Typography variant="body1">
									Limit for All*
								</Typography>
								<CurrencyField 
									id="limit_for_all"
									fieldName="limit_for_all"
									selectName="limit_for_all_unit"
									fieldValue={
										metadata['limit_for_all'] !== undefined 
										&& metadata['limit_for_all'] !== '' ? 
										metadata['limit_for_all'] : ''
									}
									selectValue={
										metadata['limit_for_all_unit'] !== undefined ?
										metadata['limit_for_all_unit'] : ''
									}
									fieldOnChange={setSubForm}
									selectOnChange={setSubForm}
									inputProps={{ min: 0 }}
									allowNegative={false}
								/>
							</FormControl>
						</React.Fragment> 
					) : (
						metadata['limit_for_all'] && metadata['limit_for_all_unit'] ? (
							<ViewField 
								label="Limit for All"
								value={metadata['limit_for_all'] + ' ' + metadata['limit_for_all_unit']} />
						) : null
					)}
				</Grid>
				<Grid item sm={12}>
					{
						!viewOnly ? 
							<React.Fragment>
								<Typography variant="overline" display="block" gutterBottom>
									Activation
								</Typography>
								<FormControl component="fieldset">
									<RadioGroup name="waiting_period">
										<FormControlLabel
											classes={radioClasses}
											value="waiting_period_effectivity"
											control={
												<Radio
													checked={metadata['waiting_period_type'] === 'effectivity'} 
													name="waiting_period_type"
													onClick={(e) => {
														e.stopPropagation();
														if(metadata['waiting_period_type'] !== 'effectivity'){
															setSubForm2("waiting_period", "");
															setSubForm2("waiting_period_unit", "");
															setSubForm2("waiting_period_type", "effectivity");
														}
													}} 
												/>
											}
											label={
												<React.Fragment>
													<Typography variant="body1">
														Waiting period after effectivity of member
													</Typography>
													<FormControl>
													<WaitingPeriodField 
															disableSelect={metadata['waiting_period_type'] !== 'effectivity'}
															disableInput={metadata['waiting_period_type'] !== 'effectivity'}
															id="waiting_period_effectivity" 
															fieldName="waiting_period"
															selectName="waiting_period_unit"
															fieldValue={
																metadata['waiting_period_type'] === 'effectivity' ? 
																metadata['waiting_period'] : 0
															}
															selectValue={
																metadata['waiting_period_type'] === 'effectivity' ?
																metadata['waiting_period_unit'] : ''
															}
															fieldOnChange={setSubForm}
															selectOnChange={setSubForm}
															inputProps={{ min: 0 }}
															allowNegative={false}
															noDefault
														/>
													</FormControl>
												</React.Fragment>
											}
										/>
										<FormControlLabel
											classes={radioClasses}
											value="waiting_period_hire"
											control={
												<Radio 
													checked={metadata['waiting_period_type'] === 'date_of_hire'} 
													name="waiting_period_type"
													onClick={(e) => {
														e.stopPropagation();
														if(metadata['waiting_period_type'] !== 'date_of_hire'){
															setSubForm2("waiting_period", "");
															setSubForm2("waiting_period_unit", "");
															setSubForm2("waiting_period_type", "date_of_hire");
														}
													}}
												/>
											}
											label={
												<React.Fragment>
													<Typography variant="body1">
														Waiting period after date of hire
													</Typography>
													<FormControl>
														<WaitingPeriodField 
																disableSelect={metadata['waiting_period_type'] !== 'date_of_hire'}
																disableInput={metadata['waiting_period_type'] !== 'date_of_hire'}
																id="waiting_period_hire" 
																fieldName="waiting_period"
																selectName="waiting_period_unit"
																fieldValue={
																	metadata['waiting_period_type'] === 'date_of_hire' ? 
																	metadata['waiting_period'] : 0
																}
																selectValue={
																	metadata['waiting_period_type'] === 'date_of_hire' ?
																	metadata['waiting_period_unit'] : ''
																}
																fieldOnChange={setSubForm}
																selectOnChange={setSubForm}
																inputProps={{ min: 0 }}
																allowNegative={false}
																noDefault
															/>
													</FormControl>
												</React.Fragment>
											}
										/>
									</RadioGroup>
								</FormControl>
							</React.Fragment> : (
								metadata['waiting_period_type'] && metadata['waiting_period_type'] !== undefined ? (
									<ViewField 
										label="Activation"
										value={
											<div>
												<div>
													{ metadata['waiting_period_type'] === "effectivity" ?
														"Waiting period after effectivity of member": 
														metadata['waiting_period_type'] === "date_of_hire" ?
														"Waiting period after date of hire": null
													}
												</div>
												{metadata['waiting_period'] ? (
													<div>
														{ metadata['waiting_period_unit'] ? 
															displayValue(metadata['waiting_period'], metadata['waiting_period_unit']) :
															GlobalFunction.formatNumber(metadata['waiting_period'])
														}
													</div>
												) : null}
											</div>
										} />
								) : null
							)
					}
				</Grid>
		</React.Fragment>
	);
};

OutpatientMedicineForm.defaultProps = defaultProps;