import * as React from 'react';
import { Grid, TableRow, TableCell, Snackbar } from '@material-ui/core';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { CustomRadio } from 'Pages/CustomerCarePage/Components/CustomRadio';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { GlobalFunction, TimeOption } from 'Pages/CustomerCarePage/GlobalFunction';
import { CommentData, PermissionData, RequestApprovalDetails, TicketData, LoginData } from 'Pages/CustomerCarePage/Models';
import { Payloads } from 'Pages/CustomerCarePage/Payloads';
import { API } from 'Pages/CustomerCarePage/API';
import { CustomButton } from 'Pages/CustomerCarePage/Components/CustomButton';

interface IProps {
    ticketId: string;
    ticket: TicketData;
    permission: PermissionData;
    requestApproval: RequestApprovalDetails;
    updates: CommentData[];
    isShowRequiredField?: boolean;
    login: LoginData;
    refreshRequestApprovalState: () => void;
    refreshUpdateState: () => void;
    onBack?: () => void;
}

interface IStates {
    update: string;
    timeNow: number;

    snackbarMessage: string | null;
    isSending: boolean;
}

export class UpdateRequestApprovalSection extends React.Component<IProps, IStates> {
    
    interval?: NodeJS.Timeout;

    constructor(props: IProps) {
        super(props);

        this.state = {
            update: '',
            timeNow: 0,

            snackbarMessage: null,
            isSending: false
        };
    }

    componentDidMount() {
        this.interval = setInterval(() => this.setState({ timeNow: Date.now() }), 5000);
    }

    onAddUpdate = () => {
        if (this.props.updates) {
            this.props.updates.unshift({
                sender: `${this.props.login.firstName} ${this.props.login.lastName}`,
                senderId: this.props.login.userId,
                message: this.state.update, 
                date: new Date()
            });
            this.props.refreshUpdateState();
            this.setState({isSending: true});

            const payload = Payloads.getPatchUpdatePayload(this.props.updates);

            API.patch(`customer-care/tickets/${this.props.ticketId}`, payload,
                (patchTicketResponse) => {
                    if (patchTicketResponse.status < 400) {
                        this.setState({update: '', snackbarMessage: 'You added new update.'});
                    } else {
                        this.setState({update: '', snackbarMessage: 'Failed to add update.'});
                        if (this.props.updates) {
                            this.props.updates.shift();
                            this.props.refreshUpdateState();
                        }
                    }
                    this.setState({isSending: false});
                },
                () => {
                    this.setState({
                        update: '', 
                        isSending: false,
                        snackbarMessage: 'Failed to add update.'
                    });
                    if (this.props.updates) {
                        this.props.updates.shift();
                        this.props.refreshUpdateState();
                    }
                }
            );
        }
    }

    render(): JSX.Element {
        const requestApproval = this.props.requestApproval;
        return (
            <div className='section'>

                <Snackbar
			        anchorOrigin={{vertical: 'top', horizontal: 'right'}}
			        open={this.state.snackbarMessage ? true : false}
			        autoHideDuration={6000}
                    message={this.state.snackbarMessage}
                    onClose={() => {
                        this.setState({snackbarMessage: null});
                    }} />

                <div style={{ paddingTop: '2.5rem'}} />
                <div className='header-1' style={{display: 'inline-block'}}>
                    Resolve Request Requiring Approval
                </div>
                {this.props.onBack ? (
                    <div style={{float: 'right'}}>
                        <CustomButton 
                            className='button'
                            text='Edit Summary'
                            onClick={this.props.onBack} />
                    </div>
                ) : null}
                <Grid className='sub-section' container spacing={1}>
                    <Grid item xs={6}>
                        {this.renderInfoItem('SUMMARY OF REQUEST (TITLE)', requestApproval.title)}
                        {this.renderInfoItem('REQUEST TYPE', requestApproval.type.label)}
                        {this.renderInfoItem('RECOMMENDATION', requestApproval.recommendation)}
                    </Grid>
                    <Grid item xs={6}>
                        {this.renderInfoItem('BACKGROUND DETAILS', requestApproval.background ? requestApproval.background : 'N/A')}
                        {this.renderInfoItem('NOTES', requestApproval.notes ? requestApproval.notes : 'N/A')}
                        {this.renderInfoItem('FILES', 
                            <>
                            {this.props.ticket.additionalFiles.length > 0 ? 
                                this.props.ticket.additionalFiles.map(file => {
                                    // @ts-ignore
                                    const downloadLink = file.url ? file.url.replace('public/', API.getUrl()) : '';

                                    return (
                                        <div className='small-text'>
                                            <TextButton label={file.name} downloadLink={downloadLink} />
                                        </div>
                                    );
                                }) : 'N/A'
                            }
                            </>
                        )}
                        {this.renderInfoItem('REASON FOR RECOMMENDATION', requestApproval.basisRecommendation)}
                    </Grid>
                </Grid>

                {this.props.permission.canSummarize ? (  
                    <Grid className='sub-section' container>
                        <Grid item xs={6}>
                            <div className='header-2'>ASSESSMENT</div>
                            <div className='text'>Choose Recommendation</div>
                            <CustomRadio
                                label='Approve'
                                checkedCondition={requestApproval.assessmentType === 'Approved'}
                                onChange={() => {
                                    requestApproval.assessmentType = 'Approved';
                                    this.props.refreshRequestApprovalState();
                                }} />
                            <CustomRadio
                                label='Disapprove'
                                checkedCondition={requestApproval.assessmentType === 'Disapproved'}
                                onChange={() => {
                                    requestApproval.assessmentType = 'Disapproved';
                                    this.props.refreshRequestApprovalState();
                                }} />
                            <CustomRadio
                                label='Cancel'
                                checkedCondition={requestApproval.assessmentType === 'Canceled'}
                                onChange={() => {
                                    requestApproval.assessmentType = 'Canceled';
                                    this.props.refreshRequestApprovalState();
                                }} />
                            <InputField 
                                placeholder={`Reason${requestApproval.assessmentType === 'Canceled' ? '*' : ''}`}
                                value={requestApproval.assessmentRemarks}
                                error={
                                    this.props.isShowRequiredField &&
                                    requestApproval.assessmentType === 'Canceled' &&
                                    requestApproval.assessmentRemarks === ''}
                                onChange={(value: string) => {
                                    requestApproval.assessmentRemarks = value;
                                    this.props.refreshRequestApprovalState();
                                }} />
                        </Grid>
                    </Grid>
                ) : (
                    <Grid className='sub-section' container>
                        <Grid item xs={6}>
                            <div className='header-2'>ASSESSMENT</div>
                            <div className='sub-section-2'>
                                <div className='small-text'>
                                    {requestApproval.assessmentType !== '' ? 
                                    requestApproval.assessmentType : 'N/A'}
                                </div>
                            </div>
                            {requestApproval.assessmentType !== '' ? (
                                <div className='sub-section-2'>
                                    <div className='light-text'>REASON</div>
                                    <div className='small-text'>
                                        {requestApproval.assessmentRemarks !== '' ?
                                        requestApproval.assessmentRemarks : 'N/A'}
                                    </div>
                                </div>
                            ) : null}
                        </Grid>
                    </Grid>
                )}

                <div className='sub-section'>
                    <CustomTable 
                        className={!this.props.permission.canUpdate ? 'custom-table' : undefined}
                        rows={this.props.updates}
                        renderHeader={() => this.renderTableHeader()}
                        renderRow={(rowData: any, index: number) => 
                            this.renderTableRow(rowData, index)} />
                </div>
            </div>
        );
    }
    
    renderTableHeader(): JSX.Element {
        return (
            this.props.permission.canUpdate ? (
                <>
                <TableCell>
                    <InputField
                        value={this.state.update}
                        innerPlaceholder='Type in an update'
                        onChange={(value: string) => {
                            this.setState({update: value});
                        }}
                        onKeyDown={(key: string) => {
                            if (key === 'Enter' && !this.state.isSending && this.state.update !== '') {
                                this.onAddUpdate();
                            }
                        }} />
                </TableCell>
                {this.state.update && !this.state.isSending ? (
                    <TableCell style={{width: '150px', textAlign: 'center'}}>
                        <TextButton 
                            icon={faPlus} 
                            label='Add Update'
                            onClick={() => {
                                this.onAddUpdate();
                            }} />
                    </TableCell>
                ) : this.state.isSending ? (
                    <TableCell style={{width: '150px', textAlign: 'center'}}>
                        <Loading height='none' size={16} />
                    </TableCell>
                ) : null}
                </>
            ) : (
                <TableCell>Updates</TableCell>
            )
        );
    }

    renderTableRow(update: CommentData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        const timeOption: TimeOption = {
            dateTimeDivider: ' at ',
            showDayFromWeek: true
        };
        const timeDiff = GlobalFunction.getTimeDeffirence(update.date, new Date());

        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                <TableCell colSpan={2}>
                    <Grid container spacing={1}>
                        <Grid item xs={7} className='header-2'>
                            {update.sender}
                        </Grid>
                        <Grid item xs={5} className='light-text' style={{textAlign: 'right'}}>
                            {(timeDiff.length > 0 ? timeDiff + ' ago ' : ' ') + 
                            `(${GlobalFunction.toDateAndTime(update.date, timeOption)})`}
                        </Grid>
                        <Grid item xs={12} className='text'>
                            {update.message}
                        </Grid>
                    </Grid>
                </TableCell>
            </TableRow>
        );
    }

    renderInfoItem(label: string, content: React.ReactNode): JSX.Element {
        return (
            <div className='sub-section-2'>
                <div className='light-text'>{label}</div>
                <div className='small-text'>{content}</div>
            </div>
        );
    }
}

