type IComboFieldProps = TextFieldProps | OutlinedTextFieldProps;

export interface IComboContainerProps {
	fieldValue?: string;
	selectValue?: string;
	selectName?: string;
	fieldName?: string;
	disableSelect?: boolean;
	disableInput?: boolean;
	noDefault?: boolean;
	inputProps?: any;
	allowNegative?: boolean;
	fieldOnChange?: (e: any) => void;
	selectOnChange?: (e: any) => void;
}


export interface IComboMainFieldProps {
	items: any;
	defaultValue: string;
	type: string;
	currency: boolean;
	givenInputProps?: any;
}

export const EligibleMembersField: React.FC<IComboFieldProps & IComboContainerProps> = (props: IComboFieldProps & IComboContainerProps): JSX.Element => {
	const items = [{ label: 'Named', value: 'named' }, { label: 'Max', value: 'max' }];

	return (
		<ComboField
			{...props}
			items={items}
			type={'number'}
			currency={false}
			defaultValue={'named'}
		/>
	);
};

export const CurrencyField: React.FC<IComboFieldProps & IComboContainerProps> = (props: IComboFieldProps & IComboContainerProps): JSX.Element => {
	const items = [
		{ label: 'USD', value: 'USD' },
		{ label: 'PHP', value: 'PHP' },
		{ label: 'EUR', value: 'EUR' },
	];

	return (
		<ComboField
			{...props}
			items={items}
			type={'number'}
			currency={true}
			defaultValue={'PHP'}
		/>
	);
};

export const LimitField: React.FC<IComboFieldProps & IComboContainerProps> = (props: IComboFieldProps & IComboContainerProps): JSX.Element => {
	const items = LimitTypeItems;

	let disableInput = false;
	if (props.selectValue !== undefined && (props.selectValue === '' || ['ABL','MBL','PEC'].includes(props.selectValue))) {
		disableInput = true;
	}

	return (
		<ComboField
			{...props}
			items={items}
			type={'number'}
			currency={(props.selectValue === 'amt_limit' || props.selectValue === 'max_amount')}
			defaultValue={'percent_mbl'}
			disableInput={disableInput}
		/>
	);
};


