const ComboField: React.FC<IComboFieldProps & IComboMainFieldProps & IComboContainerProps> = (
	props: IComboFieldProps & IComboMainFieldProps & IComboContainerProps,
): JSX.Element => {
	const comboClasses = comboStyles();
	const comboSelectClasses = comboSelectStyles();

	let id: string | undefined;
	let label: React.ReactNode;
	let textfieldProps: any;
	let items: any;
	let fieldName: string | undefined;
	let selectName: string | undefined;
	let defaultValue: string;
	let fieldValue:  string | undefined;
	let selectValue:  string | undefined;
	//let type: string;
	let fieldOnChange: any;
	let selectOnChange: any;
	let disableSelect: boolean | undefined;
	let disableInput: boolean | undefined;
	let allowNegative: boolean | undefined;
	let inputProps: any;
	//let currency : boolean;
  	//const [blankValue, setBlankValue] = React.useState(false);
	const {noDefault} = props;
	const {} = props;
	({ id, label, items, fieldName, selectName, fieldOnChange, selectOnChange, fieldValue, selectValue, defaultValue, disableSelect, disableInput, inputProps, allowNegative, ...textfieldProps } = props);
	//const numallowedpattern = new RegExp(/\D/);
	//const givmin = {min:'0'};

	if (inputProps === undefined) {
		inputProps = {};
	}

	if (noDefault) {
		defaultValue = '';
	}


	//let value: any = '';

	/*if (fieldValue) {
		if (currency) {
			value = fieldValue;
		} else {
			value = fieldValue;
		}
	} else {
		if (type==='tel') {
			value = '0'
		} else if (type ==='number') {
			value = '';
		} else {
			value = ''
		}
	}*/

	let endadornment:any = null;
	if(items){
		if(items.length === 1){
			endadornment = (
				/*<OutlinedInput
					classes={comboSelectClasses}
					style={{minWidth:'16px'}}
					name={selectName}
					disabled={true}
					value={items[0].name}
					labelWidth={0}
					id={id + '_adornment'} />*/

					<Select
					value={items[0].label}
					disabled={disableSelect ? disableSelect : false}
					input={
						<OutlinedInput
							classes={comboSelectClasses}
							style={{minWidth:'62.44px'}}
							name={selectName}
							labelWidth={0}
							id={id + '_adornment'}
						/>
					}
					IconComponent={()=>{return(<Icon style={{display:'none'}}></Icon>)}}
				>
					<MenuItem key={items[0].label} value={items[0].label}>
						{items[0].label}
					</MenuItem>
				</Select>
			);
		}
		else{
			endadornment = (
				<Select
					value={selectValue !== undefined ? selectValue : defaultValue}
					onChange={
						(e) => { 
									selectOnChange(e);
								}
						}
					disabled={disableSelect ? disableSelect : false}
					input={
						<OutlinedInput
							classes={comboSelectClasses}
							name={selectName}
							labelWidth={0}
							id={id + '_adornment'}
						/>
					}
				>
					<MenuItem key={-1}  value="">
						
					</MenuItem>
					{items.map((item: any) => (
						<MenuItem key={item.value} value={item.value}>
							{item.label}
						</MenuItem>
					))}
				</Select>
			);
		}
	}
	else{
		endadornment = ''; 
	}

	/*const numformat = () => {
		return (
			<NumberFormat
			thousandSeparator={true}
			value={fieldValue}
			customInput={OutlinedInput}
			InputProps={{classes:comboClasses,
				style:[{borderTopRightRadius:0,width:'350px',borderBottomRightRadius:0}]}} 
			/>
		)
	}*/
 
	/*const limitInput = () => {
		return (<OutlinedInput 
			id={id}
			classes={comboClasses}
			value={fieldValue}
			endAdornment = {(
				<InputAdornment position="end">
					{ endadornment }
				</InputAdornment>
			)}
			{...textfieldProps}
		/>)
	}*/

	/*const endAdornment = () => {
		return (
					<InputAdornment position="end">
						{ endadornment }
					</InputAdornment>
				)
	}*/

	/*const handleChange = () => event => {
		console.log('event',event)
		if (fieldOnChange) {
			fieldOnChange(event);
		}
	}*/
	function NumberFormatCustom(props) {
		const { inputRef, onChange, ...other } = props;

		const [editMode, setEditMode] = React.useState(false);
		const [tempValue, setTempValue] = React.useState('');

		let newValue: any = fieldValue !== undefined ? fieldValue : '';

		if (editMode) {
			newValue = tempValue;
		}

		return (
		  <NumberFormat
			{...other}
			getInputRef={inputRef}
			classes={comboClasses}
			value={newValue}
			name={fieldName}
			isAllowed={(values) => {
				const {value, floatValue} = values;

				if (typeof floatValue==='undefined' || typeof value==='undefined') {
				  return true;
				}
				if (String(floatValue).charAt(0)==='0' && String(floatValue).length>1) {
					if (value.charAt(1) && value.charAt(1)!='.') {
						return false
					}
						return true
				}
				else {
				  return true;
				}
			}}
			onValueChange={(e: any)=>{
				if (editMode && e) {
					setTempValue(e.floatValue);
				}
			}}
			onBlur={(e)=>{
				setEditMode(false);

				e.target.value = e.target.value.replace(/,/g,'')
				if (fieldOnChange) {
					fieldOnChange(e);
				}
				// if(!focused) {
				// 	// setTempValue(fieldValue ? e.value : null);
				// 	setFocused(true)
				// }
				console.log('onChange', e);
			}}
			onFocus={(e)=>{
				setEditMode(true);

				if (newValue === 0 || newValue === '0') {
					setTempValue('');
				} else {
					setTempValue(newValue);
				}

				e.target.click();
			}}
			allowNegative={allowNegative}
			thousandSeparator
			isNumericString
		  />
		);
	  }
	  
	  NumberFormatCustom.propTypes = {
		inputRef: PropTypes.func.isRequired,
		onChange: PropTypes.func.isRequired,
	  };
	  
	
	return (
		<React.Fragment>
			<InputLabel htmlFor={id + '_label'}>{label}</InputLabel>
			<OutlinedInput
				disabled={disableInput ? disableInput : false}
				labelWidth={0}
				classes={comboClasses}
				style={{outlineOffset:'-2px',
						outlineColor:'#3AB77D'}}
				value={fieldValue}
				
				id="formatted-text-mask-input"
				inputComponent={NumberFormatCustom}
				endAdornment = {(
					<InputAdornment position="end">
						{ endadornment }
					</InputAdornment>
				)}
			/>
			{/*<OutlinedInput
				id={id}
				classes={comboClasses}
				type={currency ? 'tel' : type === 'number' ? 'tel' : type}
				name={fieldName}
				value={fieldValue}
				inputComponent={NumberFormatCustom}
				onChange={(e) => {
					if (fieldOnChange) {
						console.log('check')
						fieldOnChange(e);
					}
				}}
				endAdornment = {(
					<InputAdornment position="end">
						{ endadornment }
					</InputAdornment>
				)}
				{...textfieldProps}
			/>*/}
		</React.Fragment>
	);
};
