    renderHeader(): JSX.Element {
        const headerNameStyle = {
            margin: 0,
            padding: '12px 0 12px 0'
        };

        return (
            <>
                <Grid item xs={4}>
                    <div className='header-2' style={headerNameStyle}>Diagnosis</div>
                </Grid>
                <Grid item xs={3}>
                    <div className='header-2' style={headerNameStyle}>Doctor</div>
                </Grid>
                <Grid item xs={2}>
                    <div className='header-2' style={headerNameStyle}>Professional Fee</div>
                </Grid>
                <Grid item xs={3}>
                    <div className='header-2' style={{...headerNameStyle, textAlign: 'right'}}>
                        Total Amt. of Procedures
                    </div>
                </Grid>
            </>
        );
    }










    renderRow(diagnosis: DiagnosisData, diagnosisIndex: number): JSX.Element {
        return (
            <>
                <Grid className='small-section' container>
                    <Grid item xs={4}>
                        {diagnosis.icd10Code.value + ' - ' + diagnosis.icd10Code.label}
                    </Grid>
                    <Grid item xs={3}>
                        {diagnosis.doctors[0].name}
                    </Grid>
                    <Grid item xs={2}>
                        {'PHP ' + GlobalFunction.formatNumber(
                            diagnosis.procedures && 
                            diagnosis.procedures.length === 0 &&
                            diagnosis.assessmentType === 'Approved' && 
                            diagnosis.approvedAmount > 0 ?
                            diagnosis.approvedAmount :
                            diagnosis.assessmentType === 'Disapproved' || 
                            diagnosis.assessmentType === 'Pending' ?
                            0 :
                            diagnosis.doctors[0].fee
                            , true)}
                    </Grid>
                    <Grid item xs={3} style={{fontWeight: 'bold', textAlign: 'right'}}>
                        {'PHP ' + GlobalFunction.formatNumber(this.getProceduresCost(diagnosis), true)}
                    </Grid>
                </Grid>
                {diagnosis.procedures.length === 0 ? (
                    <>
                    <div className='line-border' />
                    {this.renderDiagnosisAssessment(diagnosis)}
                    </>
                ) : (
                    <>
                    <div className='line-border' />
                    {diagnosis.procedures.map((procedure, index) => {
                        return this.renderProcedureAssessment(procedure, index, diagnosis, diagnosisIndex);
                    })}
                    </>
                )}
            </>
        );
    }










    renderDiagnosisAssessment(diagnosis: DiagnosisData): JSX.Element {
        const canCoverCanAvailResult = this.state.canCoverCanAvailResults.filter(
            (item)=>item.diagnosisname === diagnosis.icd10Code.label &&
                item.procedurename === 'PsuedoProcedure'
        ) 
        return (
            <Grid style={{padding: '0.5rem 1.5rem'}} container spacing={1}>
                <Grid item xs={4}>
                    {/* {this.renderSystemAssessment(procedure, index, diagnosis, diagindex)} */}
                    {this.renderInfoItem('DATE OF AVAILMENT', GlobalFunction.toCommonDateString(diagnosis.dateAdded))}
                    {this.renderInfoItem('SYSTEM ASSESSMENT', 
                        <>
                        {canCoverCanAvailResult.length > 0 ? 
                            `${canCoverCanAvailResult[0]['status']}*` : 
                            'Unavailable'}
                            <Link onClick={() => {
                                if (canCoverCanAvailResult.length > 0) {
                                    this.setState({
                                        isModalOpen: true,
                                        renderAdjudication: true
                                    });
                                    this.setState({
                                        procedure: undefined,
                                        diagnosis: diagnosis,
                                        renderAdjudication: true,
                                        adjudicationSteps: canCoverCanAvailResult.length > 0 ? 
                                            canCoverCanAvailResult[0]['canCoverCanAvailstep'] : 
                                            [],
                                        systemassess: canCoverCanAvailResult[0]['status'],
                                        diagIndex: 0,
                                        procIndex: 0,
                                    });
                                } else {
                                    this.setState({
                                        isErrorModalOpen:true,
                                        ErrorMessage:'Please contact your administrator for support',
                                        ErrorHeader:'Unavailable Assessment',
                                    });
                                }}}>
                            <FontAwesomeIcon style={{color:'#C3C3C3'}} icon={faInfoCircle} /></Link>
                        </>
                    )}                
                </Grid>
                <Grid item xs={3}>
                    {this.renderInfoItem('REMAINING BALANCE', `PHP ${GlobalFunction.formatNumber(0, true)}*`)}
                    {this.renderInfoItem('CLAUSE', 'Laboratory tests, x-rays, and other diagnostic examinations prescribed by the INTELLICARE physician*')}
                </Grid>
                <Grid item xs={2}>
                    {this.renderInfoItem('Choose Assessment',
                        <>
                        <CustomRadio 
                            label='Approve'
                            checkedCondition={diagnosis.assessmentType === 'Approved'}
                            onChange={() => {
                                diagnosis.assessmentType = 'Approved';
                                this.props.refreshAvailmentState();
                            }} />
                        {diagnosis.assessmentType === 'Approved' ? (
                            <NumberField
                                value={diagnosis.approvedAmount}
                                placeholder='Partial (optional)'
                                isFloat
                                onChange={(value: number) => {
                                    diagnosis.approvedAmount = value;
                                    this.props.refreshAvailmentState();
                                }} />
                        ) : null}
                        <CustomRadio 
                            label='Disapprove'
                            checkedCondition={diagnosis.assessmentType === 'Disapproved'}
                            onChange={() => {
                                diagnosis.assessmentType = 'Disapproved';
                                this.props.refreshAvailmentState();
                            }} />
                        <CustomRadio 
                            label='Pending'
                            checkedCondition={diagnosis.assessmentType === 'Pending'}
                            onChange={() => {
                                diagnosis.assessmentType = 'Pending';
                                this.props.refreshAvailmentState();
                            }} />
                            </>
                    )}
                </Grid>
                <Grid item xs={3}>
                    {this.renderInfoItem('Remarks',
                        <InputArea
                            value={diagnosis.assessmentRemarks}
                            defaultRows={5}
                            onChange={(value: string) => {
                                diagnosis.assessmentRemarks = value;
                                this.props.refreshAvailmentState();
                            }} />
                    )}
                </Grid>
            </Grid>
        );
    }

    








