//#region Global Imports
import * as React from 'react';
import { useState } from 'react';

import { BenefitsTable } from 'Components/UI/TableComponent/BenefitsTable';
import { PHPPercField } from '../../PHPPercField';
import Grid from '@material-ui/core/Grid';
// import Hidden from '@material-ui/core/Hidden';
import FormControl from '@material-ui/core/FormControl';
// import InputLabel from '@material-ui/core/InputLabel';
import Typography from '@material-ui/core/Typography';

import { CurrencyField, WaitingPeriodField } from '../../ComboField';
import { ViewField } from '../../ViewField';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';

//import { Components } from '@hims/core';
//#endregion Global Imports

import { defaultProps, radioStyles, displayValue } from './Constants';
import { ICategorySubForm } from './ICategorySubForm';
import { GlobalFunction } from 'Components/UI/GlobalFunction';

export const ExecutiveCheckupForm: React.FC<ICategorySubForm.IProps> = (
	props: ICategorySubForm.IProps,
): JSX.Element | null => {
	const { formHeader, viewOnly, rows, subFormNode, setSubForm, setSubForm2 } = props;
	const radioClasses = radioStyles();
	const [ table, setTable ] = useState<string>('inpatient');
	// console.log(radioClasses, 'radioClasses')
	let metadata:any = {};
	// console.log(props, 'propssss');
	if (subFormNode) {
		if (subFormNode['custom_metadata']) {
			metadata = subFormNode['custom_metadata'];
		}
	}


	const columns: any = [
		{ 
			name: 'benefit',
			title: 'Benefit',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.title ? row.custom_metadata.title : '-'),
		},
		{
			 name: 'eligibility',
			 title: 'Eligibility',
			 getCellValue: row => (row.custom_metadata && row.custom_metadata.eligibility ? row.custom_metadata.eligibility : [])
		},
		{ 
			name: 'no_of_members',
			title: 'No. of Members', 
			getCellValue: row => (row.custom_metadata && row.custom_metadata.no_of_members ? row.custom_metadata.no_of_members : 0)
		},
		{ 
			name: 'number',
			title: 'Number',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.number ? row.custom_metadata.number : 0)
		},
		{ 
			name: 'peso_limit',
		 	title: 'Peso Limit',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.peso_limit ? row.custom_metadata.peso_limit : '')
		},
	];

	const formattedColumns = {
		number: ['number'],
		php: ['peso_limit'],
		eligibility: ['eligibility'],
		no_of_members: ['no_of_members'],
	};

	const columnExtensions = [
		{ columnName: 'benefit', width: 200, wordWrapEnabled: true },
		{ columnName: 'eligibility', width: 150 },
		{ columnName: 'no_of_members', width: 200 },
		{ columnName: 'number', width: 150 },
		{ columnName: 'peso_limit', width: 150 },
	];


	const billingArrangement = {
		imputed_in_premiums: "Imputed in premiums",
		billback: "Bill back with admin fee",
		billback_net: "Bill back, net of APE cost with admin fee",
		rider_fee: "1-time rider fee to be upon enrollment"
	}
	
	let finalRows = rows;
	


	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2}>
					{!viewOnly ? formHeader : null}
				</Grid>
			</Grid>
			<Grid item sm={12}>
				<Typography variant="overline" display="block" gutterBottom>
					Type
				</Typography>
				<RadioGroup name="type" value={table} row>
					<FormControlLabel 
						onClick={() => setTable('inpatient')} 
						value="inpatient" 
						control={
							<Radio 
								checked={metadata['benefit_type'] === 'inpatient'} 
								name="inpatient"
								onClick={(e) => {
									e.stopPropagation();
									setSubForm2("benefit_type", "inpatient");
								}}
							/>
						}
						label="Inpatient"
					/>
					<FormControlLabel 
						onClick={() => setTable('outpatient')} 
						value="outpatient" 
						control={
							<Radio 
								checked={metadata['benefit_type'] === 'outpatient'} 
								name="outpatient"
								onClick={(e) => {
									e.stopPropagation();
									setSubForm2("benefit_type", "outpatient");
								}}
							/>
						} 
						label="Outpatient" 
					/>
				</RadioGroup>
			</Grid>
			<Grid item sm={12}>
				<BenefitsTable
					viewOnly={viewOnly}
					columnExtensions={columnExtensions}
					formattedColumns={formattedColumns}
					columns={columns}
					{...props}
					rows={finalRows}
				/>
			</Grid>
			<Grid item sm={12}>
				{/* <Typography variant="overline" display="block" gutterBottom>
					Activation
				</Typography> */}

				<Grid item sm={12}>
				{!viewOnly ? (
					<React.Fragment>
						<Typography variant="overline" display="block" gutterBottom>
							Activation
						</Typography>
						<FormControl component="fieldset">
							<RadioGroup name="waiting_period">
								<FormControlLabel
									classes={radioClasses}
									value="waiting_period_effectivity"
									control={
										<Radio 
											checked={metadata['waiting_period_type'] === 'effectivity'} 
											name="waiting_period_type"
											onClick={(e) => {
												e.stopPropagation();
												if(metadata['waiting_period_type'] !== 'effectivity'){
													setSubForm2("waiting_period", "");
													setSubForm2("waiting_period_unit", "");
													setSubForm2("waiting_period_type", "effectivity");
												}
											}} />
									}
									label={
										<React.Fragment>
											<Typography variant="body1">
												Waiting period after effectivity of member
											</Typography>
											<FormControl>
												<WaitingPeriodField 
													disableSelect={metadata['waiting_period_type'] !== 'effectivity'}
													disableInput={metadata['waiting_period_type'] !== 'effectivity'}
													id="waiting_period_effectivity" 
													fieldName="waiting_period"
													selectName="waiting_period_unit"
													fieldValue={
														metadata['waiting_period_type'] === 'effectivity' ? 
														metadata['waiting_period'] : 0
													}
													selectValue={
														metadata['waiting_period_type'] === 'effectivity' ?
														metadata['waiting_period_unit'] : ''
													}
													fieldOnChange={setSubForm}
													selectOnChange={setSubForm}
													inputProps={{ min: 0 }}
													allowNegative={false}
													noDefault
												/>
											</FormControl>
										</React.Fragment>
									} />
								<FormControlLabel
									classes={radioClasses}
									value="waiting_period_hire"
									control={
										<Radio 
											checked={metadata['waiting_period_type'] === 'date_of_hire'} 
											name="waiting_period_type"
											onClick={(e) => {
												e.stopPropagation();
												if(metadata['waiting_period_type'] !== 'date_of_hire'){
													setSubForm2("waiting_period", "");
													setSubForm2("waiting_period_unit", "");
													setSubForm2("waiting_period_type", "date_of_hire");
												}
											}}
										/>
									}
									label={
										<React.Fragment>
											<Typography variant="body1">
												Waiting period after date of hire
											</Typography>
											<FormControl>
												<WaitingPeriodField 
													disableSelect={metadata['waiting_period_type'] !== 'date_of_hire'}
													disableInput={metadata['waiting_period_type'] !== 'date_of_hire'}
													id="waiting_period_hire" 
													fieldName="waiting_period"
													selectName="waiting_period_unit"
													fieldValue={
														metadata['waiting_period_type'] === 'date_of_hire' ? 
														metadata['waiting_period'] : 0
													}
													selectValue={
														metadata['waiting_period_type'] === 'date_of_hire' ?
														metadata['waiting_period_unit'] : ''
													}
													fieldOnChange={setSubForm}
													selectOnChange={setSubForm} 
													inputProps={{ min: 0 }}
													allowNegative={false}
													noDefault
												/>
											</FormControl>
										</React.Fragment>
									} />
							</RadioGroup>
						</FormControl>
					</React.Fragment> 
				) : (
					metadata['waiting_period_type'] && metadata['waiting_period_type'] !== undefined ? (
						<ViewField 
							label="Activation"
							value={
								<div>
									<div>
										{ metadata['waiting_period_type'] === "effectivity" ?
											"Waiting period after effectivity of member": 
											metadata['waiting_period_type'] === "date_of_hire" ?
											"Waiting period after date of hire": null
										}
									</div>
									{ metadata['waiting_period'] ? (
										<div>
											{ metadata['waiting_period_unit'] ? 
												displayValue(metadata['waiting_period'], metadata['waiting_period_unit']) :
												GlobalFunction.formatNumber(metadata['waiting_period'])
											}
										</div>
									) : null}
								</div>
							} />
					) : null
				)}
				</Grid>

				{!viewOnly ? (
					<React.Fragment>
						<Typography variant="body1" style={{ marginTop: '20px' }} gutterBottom>
							Billing arrangement
						</Typography>
						<RadioGroup name="billing_arangement">
								<FormControl component="fieldset">
								<FormControlLabel
									value="imputed_in_premiums"
									control={
										<Radio 
											checked={metadata['billing_arangement'] === 'imputed_in_premiums'} 
											name="imputed_in_premiums"
											onClick={(e) => {
												e.stopPropagation();
												if (metadata['billing_arangement'] !== 'imputed_in_premiums') {
													setSubForm2("billback_fee", null);
													setSubForm2("billback_type", null);
													setSubForm2("rider_fee", null);
													setSubForm2("rider_fee_unit", null);
													setSubForm2("billing_arangement", "imputed_in_premiums");
												}
											}} />
									}
									label={billingArrangement['imputed_in_premiums']} />
									</FormControl>
								<FormControl component="fieldset">
								<FormControlLabel
									value="billback"
									control={
										<Radio 
											checked={metadata['billing_arangement'] === 'billback'} 
											name="billback"
											onClick={(e) => {
												e.stopPropagation();
												if (metadata['billing_arangement'] !== 'billback') {
													setSubForm2("billback_fee", 15);
													setSubForm2("billback_type", 'percent');
													setSubForm2("rider_fee", null);
													setSubForm2("rider_fee_unit", null);
													setSubForm2("billback_net_fee", null);
													setSubForm2("billback_net_type", null);
													setSubForm2("billing_arangement", "billback");
												}
											}} />
									}
									label={
										<React.Fragment>
													<Typography variant="body1" gutterBottom>
														{billingArrangement['billback']}
													</Typography>
													<div style={{display:metadata['billing_arangement'] === 'billback'?'block':'none'}}>
														<PHPPercField
															id="billback_fee"
															variant="outlined"
															fieldName="billback_fee"
															selectName="billback_type"
															fieldValue={
																metadata['billing_arangement'] === 'billback' ? 
																metadata['billback_fee'] : 0
															}
															selectValue={
																metadata['billing_arangement'] === 'billback' ?
																metadata['billback_type'] : 'percent'
															}
															fieldOnChange={setSubForm}
															selectOnChange={setSubForm}
															allowNegative={false}
														/>
													</div>
												</React.Fragment>
										
										} />
								</FormControl>
								<FormControl component="fieldset">
								<FormControlLabel
									value="billback_net"
									control={
										<Radio 
											checked={metadata['billing_arangement'] === 'billback_net'} 
											name="billback_net"
											onClick={(e) => {
												e.stopPropagation();
												if (metadata['billing_arangement'] !== 'billback_net') {
													setSubForm2("billback_fee", null);
													setSubForm2("billback_type", null);
													setSubForm2("rider_fee", null);
													setSubForm2("rider_fee_unit", null);
													setSubForm2("billback_net_fee", '');
													setSubForm2("billback_net_type", 'percent');
													setSubForm2("billing_arangement", "billback_net");
												}
											}} />
									}
									label={<React.Fragment>
									<Typography variant="body1" gutterBottom>
										{billingArrangement['billback_net']}
									</Typography>
									<div style={{display:metadata['billing_arangement'] === 'billback_net'?'block':'none'}}>
										<PHPPercField
											id="billback_fee"
											variant="outlined"
											fieldName="billback_net_fee"
											selectName="billback_net_type"
											fieldValue={
												metadata['billing_arangement'] === 'billback_net' ? 
												metadata['billback_net_fee'] : 0
											}
											selectValue={
												metadata['billing_arangement'] === 'billback_net' ?
												metadata['billback_net_type'] : 'percent'
											}
											fieldOnChange={setSubForm}
											selectOnChange={setSubForm}
											allowNegative={false}
										/>
									</div>
								</React.Fragment>
								}
											
									/>
								</FormControl>
								<FormControl component="fieldset">
								<FormControlLabel
									classes={radioClasses}
									value="rider_fee"
									control={
										<Radio 
										checked={metadata['billing_arangement'] === 'rider_fee'} 
										name="rider_fee"
										onClick={(e) => {
											e.stopPropagation();
											if (metadata['billing_arangement'] !== 'rider_fee') {
												setSubForm2("billback_fee", null);
												setSubForm2("billback_type", null);
												setSubForm2("rider_fee", '');
												setSubForm2("rider_fee_unit", 'PHP');
												setSubForm2("billback_net_fee", null);
												setSubForm2("billback_net_type", null);
												setSubForm2("billing_arangement", "rider_fee");
											}
										}} />
									}
									label={
										<React.Fragment>
												<Typography variant="body1" gutterBottom>
													{billingArrangement['rider_fee']}
												</Typography>
												<div style={{display:metadata['billing_arangement'] === 'rider_fee'?'block':'none'}}>
														<Typography variant="body1">Fee</Typography>
												<CurrencyField 
													id="fee_field" 
													disabled={metadata['billing_arangement'] !== 'rider_fee'}
													fieldName="rider_fee"
													selectName="rider_fee_unit"
													fieldValue={
														metadata['billing_arangement'] === 'rider_fee' ? 
														metadata['rider_fee'] : 0
													}
													selectValue={
														metadata['billing_arangement'] === 'rider_fee' ?
														metadata['rider_fee_unit'] : 'PHP'
													}
													fieldOnChange={setSubForm}
													selectOnChange={setSubForm}
													inputProps={{ min: 0 }}
													allowNegative={false}
												/>
												</div>
											</React.Fragment>
									} />
							
						</FormControl>
						</RadioGroup>
					</React.Fragment> 
				) : (
					billingArrangement[metadata['billing_arangement']] ? (
						<ViewField 
							label="Billing arrangement"
							value={
								<div>
									<div>{billingArrangement[metadata['billing_arangement']]}</div>
									{metadata['billback_fee'] ? (
										<div>{`${metadata['billback_fee']} ${metadata['billback_type'] === 'percent' ? '%' : metadata['billback_type']}`}</div>
									) : null}
									{metadata['billback_net_fee'] ? (
										<div>{`${metadata['billback_net_fee']} ${metadata['billback_net_type'] === 'percent' ? '%' : metadata['billback_net_type']}`}</div>
									) : null}
									{metadata['rider_fee'] ? (
										<div>{`${metadata['rider_fee']} ${metadata['rider_fee_unit']}`}</div>
									) : null}
								</div>
							} />
					) : null
				)}
			</Grid>
		</React.Fragment>
	);
};

ExecutiveCheckupForm.defaultProps = defaultProps;
