//#region Global Imports
import * as React from 'react';

import { BenefitsTable } from 'Components/UI/TableComponent/BenefitsTable';

import Grid from '@material-ui/core/Grid';
// import Hidden from '@material-ui/core/Hidden';
//import { Components } from '@hims/core';
//#endregion Global Imports


import { defaultProps } from './Constants';
import { ICategorySubForm } from './ICategorySubForm';

export const SurehealthForm: React.FC<ICategorySubForm.IProps> = (
	props: ICategorySubForm.IProps,
): JSX.Element | null => {
	const { formHeader, rows, viewOnly } = props;

	console.log(rows);

	// const fakeRows: any = [
	// 	{
	// 		plan: 'Classic',
	// 		member_type: ['employees', 'employee_dependents'],
	// 	},
	// 	{
	// 		plan: 'Elite',
	// 		member_type: ['company_dependents', 'employees', 'employee_dependents'],
	// 	},
	// 	{
	// 		plan: 'Premium',
	// 		member_type: ['employees', 'employee_dependents'],
	// 	},
	// 	{
	// 		plan: 'Prestige',
	// 		member_type: ['company_dependents', 'employees', 'employee_dependents'],
	// 	},
	// ];

	const columns: any = [
		{ 
			name: 'plan',
			title: 'Plan',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.title ? row.custom_metadata.title : '-')
		},
		{ 
			name: 'member_type', 
			title: 'Member Type',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.member_type ? row.custom_metadata.member_type : [])
		},
	];

	const formattedColumns = {
		member_type: ['member_type'],
	};

	const columnExtensions = [
		{ columnName: 'member_type', width: 250, wordWrapEnabled: true },
	];

	let finalRows = rows;
	
	if (viewOnly) {
		let first = true;
		finalRows = rows.filter(() => {
			if (first) {
				first = false;
				return true;
			} 
			return false;
		});
	}

	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2}>
					{!viewOnly ? formHeader : null}
				</Grid>
			</Grid>
			<Grid item sm={12}>
				<BenefitsTable
					viewOnly={viewOnly}
					selectionType="radio"
					disableContractType
					formattedColumns={formattedColumns}
					columnExtensions={columnExtensions}
					columns={columns}
					{...props}
					rows={finalRows}
				/>
			</Grid>
		</React.Fragment>
	);
};

SurehealthForm.defaultProps = defaultProps;