import * as React from 'react';
import { CustomModal } from 'Pages/CustomerCarePage/Components/CustomModal';
import { CustomTable, HeaderData } from '../CustomTable';
import { TableRow, TableCell, Grid } from '@material-ui/core';
import { InputField } from '../InputField';
import { CCLogData } from 'Pages/CustomerCarePage/Models';
import { Loading } from '../Loading';
import { API } from 'Pages/CustomerCarePage/API';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';

interface IProps {
    ticketId: string;
    isOpen: boolean;
    onClose: () => void;
}

interface IStates {
    searchText: string;
    isLoading: boolean;
    logs: CCLogData[];
}

export class AuditLogsModal extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            searchText: '',
            isLoading: true,
            logs: []
        };
    }

    componentDidMount() {
        const filter = {
            where: {
                'ticket_info._id': this.props.ticketId
            }
        };

        API.get(`customer-care/tickets/history?filter=${JSON.stringify(filter)}`, (response) => {
            this.setState({
                isLoading: false,
                logs: response.history.map(log => {
                    return {
                        activityId: log.activity_id,
                        user: log.assignee_name,
                        activity: log.action,
                        date: new Date(log.date_created),
                        time: new Date(log.date_created)
                    }
                })
            });
        });
    }

    getFilteredLogs = (logs: CCLogData[]) => {
        if (this.state.searchText !== '') {
            return logs.filter(log => {
                return (
                    log.activityId.toLowerCase().includes(this.state.searchText.toLowerCase()) ||
                    log.activity.toLowerCase().includes(this.state.searchText.toLowerCase()) ||
                    log.user.toLowerCase().includes(this.state.searchText.toLowerCase()) ||
                    GlobalFunction.toShortDateString(log.date).toLowerCase().includes(this.state.searchText.toLowerCase()) ||
                    GlobalFunction.toTime(log.time).toLowerCase().includes(this.state.searchText.toLowerCase())
                );
            });
        }
        return logs;
    }

    render(): JSX.Element {

        const columns: HeaderData[] = [
            { name: 'Activity ID',  key: 'activityId',  filterType: 'string',   sortType: 'string', style: { width: '100px' } },
            { name: 'Activity',     key: 'activity',    filterType: 'string',   sortType: 'string', /*style: { minWidth: '100px' }*/ },
            { name: 'User',         key: 'user',        filterType: 'string',   sortType: 'string', style: { width: '150px' } },
            { name: 'Date',         key: 'date',        filterType: 'date',     sortType: 'date', style: { width: '125px' } },
            { name: 'Time',         key: 'time',        filterType: 'string',   sortType: 'string', style: { width: '125px' } }
        ];

        const renderRow = (log: CCLogData, index: number): JSX.Element => {
            
            const style: React.CSSProperties = {
                backgroundColor: (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA')
            };

            return (
                <TableRow style={style}>
                    <TableCell>{log.activityId}</TableCell>
                    <TableCell>{log.activity}</TableCell>
                    <TableCell>{log.user}</TableCell>
                    <TableCell>{GlobalFunction.toShortDateString(log.date)}</TableCell>
                    <TableCell>{GlobalFunction.toTime(log.time)}</TableCell>
                </TableRow>
            );
        }

        return (
            <CustomModal
                className='large-modal'
                isOpen={this.props.isOpen}
                handleClose={this.props.onClose}
                header=''
                content={
                    <>
                    {!this.state.isLoading ? (
                        <Grid container spacing={1}>
                            <Grid item xs={12}>
                                <InputField
                                    innerPlaceholder='Search log'
                                    value={this.state.searchText}
                                    onChange={(value: string) => {
                                        this.setState({searchText: value});
                                    }} />
                            </Grid>
                            <Grid item xs={12}>
                                <CustomTable 
                                    canOverflow
                                    columns={columns}
                                    rows={this.getFilteredLogs(this.state.logs)}
                                    rowsPerPageOptions={[5]}
                                    renderRow={renderRow} />
                            </Grid>
                        </Grid>
                    ) : (
                        <Loading />
                    )}
                    </>
                } />
        );
    }
}