import React from 'react';
import Input from '@material-ui/core/Input';
import NumberFormat from 'react-number-format';

import { DropdownChoice } from '../Models';
import Select from 'react-select';

import './component-style.scss';
interface IProps {
    placeholder: string;
    choices: DropdownChoice[];
    type: DropdownChoice;
    value: string;
    onChangeType: (type: DropdownChoice) => void;
    onChangeValue: (
        value: string,
        event: React.ChangeEvent<HTMLInputElement>,
    ) => void;
    mask?: string;
}

function NumberFormatCustom(props) {
    const { inputRef, onChange, mask, ...other } = props;

    return (
        <NumberFormat
            {...other}
            getInputRef={inputRef}
            onValueChange={values => {
                onChange({
                    target: {
                        value: values.value,
                    },
                });
            }}
            format={mask}
            isNumericString
        />
    );
}

export class TypeValueField extends React.Component<IProps> {
    render(): JSX.Element {
        return (
            <div className="detailed-input-field">
                <div className="text">{this.props.placeholder}</div>
                <div className="input-field">
                    <Select
                        style={{ width : 'auto'}}
                        className="actual-dropdown"
                        classNamePrefix="actual-dropdown"
                        value={this.props.type}
                        options={this.props.choices}
                        onChange={(choice: DropdownChoice) => {
                            this.props.onChangeType(choice);
                        }}
                    />
                    {this.props.mask ? (
                        <Input
                            style={{ width: '100%'}}
                            value={
                                this.props.value
                                    ? this.props.value.replace(/-/g, '')
                                    : ''
                            }
                            onChange={(
                                event: React.ChangeEvent<HTMLInputElement>,
                            ) => {
                                this.props.onChangeValue(
                                    event.target.value,
                                    event,
                                );
                            }}
                            id="formatted-text-mask-input"
                            inputComponent={NumberFormatCustom}
                            inputProps={{
                                mask: this.props.mask,
                                className: 'text-field right',
                            }}
                        />
                    ) : (
                        <input
                            className="text-field right"
                            value={this.props.value}
                            onChange={(
                                event: React.ChangeEvent<HTMLInputElement>,
                            ) => {
                                this.props.onChangeValue(
                                    event.target.value,
                                    event,
                                );
                            }}
                        />
                    )}
                </div>
            </div>
        );
    }
}
