//#region Global Imports
import * as React from 'react';
import {
	InpatientForm,
	RoomAndBoardForm,
	DefaultForm,
	APEForm,
	PPEForm,
	OutpatientForm,
	EmergencyForm,
	DentalForm,
	OutpatientMedicineForm,
	SpecialDiagnosticForm,
	MaternityForm,
	SurehealthForm,
	PreventiveCareForm,
	ADDForm,
	CorporateClinicForm,
	ExecutiveCheckupForm,
} from './CategorySubForm';
//import { Components } from '@hims/core';
//#endregion Global Imports

interface CategoryFormProps {
	subFormNode: any;
	rows: any[];
	viewOnly?: boolean;
	filters?: any[] | null;
	setFilters?: any;
}

const defaultProps: CategoryFormProps = {
	subFormNode: {},
	rows: [],
	viewOnly: false,
	filters: null,
	setFilters: null,
};

export const CategoryForm: React.FC<CategoryFormProps> = (
	props: CategoryFormProps,
): JSX.Element | null => {
	const { rows, subFormNode } = props;

	let selectedBenefitCategory = '';

	if (subFormNode && subFormNode['code']) {
		selectedBenefitCategory = subFormNode.code;
	}

	console.log("rows", rows);

	let formHeader: any = null;

	if (selectedBenefitCategory === 'RB') {
		return <RoomAndBoardForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'APE') {
		return <APEForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'PPE') {
		return <PPEForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'IP') {
		return <InpatientForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'OP') {
		return <OutpatientForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'DNT') {
		return <DentalForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'EM') {
		return <EmergencyForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'OM') {
		return <OutpatientMedicineForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'SD') {
		return <SpecialDiagnosticForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'MT') {
		return <MaternityForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'SH') {
		return <SurehealthForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'PV') {
		return <PreventiveCareForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'ADD') {
		return <ADDForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'CC') {
		return <CorporateClinicForm formHeader={formHeader} {...props} />;
	}
	if (selectedBenefitCategory === 'EC') {
		return <ExecutiveCheckupForm formHeader={formHeader} {...props} />;
	}

	if (rows.length > 0) {
		return <DefaultForm formHeader={formHeader} {...props} />;
	} else {
		return null;
	}
};

CategoryForm.defaultProps = defaultProps;
