import * as React from 'react';
import './component-style.scss';
import Select from 'react-select';
import { DropdownChoice } from '../Models';
import { Option } from 'react-select/src/filters';

interface IProps {
    placeholder?: string;
    disabled?: boolean;
    value: DropdownChoice;
    choices: DropdownChoice[];
    error?: boolean;
    onChange: (value: DropdownChoice) => void;
    onFocus?: () => void;
    isClearable?: boolean;
    handleBlur?: boolean;
    showAllResult?: boolean;
}

interface IStates {
    value: string;
}

export class Dropdown extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            value: ''
        }
    }

    shouldComponentUpdate(props: IProps, _state: any) {
        return (
            (this.props.placeholder !== props.placeholder) ||
            (this.props.disabled !== props.disabled) ||
            (this.props.value !== props.value) ||
            (this.props.choices !== props.choices) ||
            (this.props.error !== props.error)
        );
    }

    handleOnBlur() {
        const e = null
        throw e
    }

    render(): JSX.Element {

        return (
            <div className='dropdown'>
                {this.props.placeholder ? (
                    <div className={this.props.disabled ? 'text-disabled text' : 'text'}>
                        {this.props.placeholder}
                    </div>
                ) : null}
                
                <Select
                    className='actual-dropdown'
                    classNamePrefix={this.props.error ? 'actual-dropdown-error' : 'actual-dropdown'}
                    isDisabled={this.props.disabled ? this.props.disabled : false}
                    value={this.props.value}
                    options={this.props.choices} 
                    isClearable={this.props.isClearable}
                    onInputChange={(value: string, _a) => {
                        this.setState({value: value});
                    }}
                    onBlur={this.props.handleBlur ? this.handleOnBlur : undefined}
                    onFocus={() => {
                        console.log('Dropdown - onFocus()', this.props.placeholder);
                        console.log('Dropdown -',
                            'options:', { options: this.props.choices }, 
                            'count:', this.props.choices.length);
                        if (this.props.onFocus) {
                            this.props.onFocus();
                        }
                    }}
                    noOptionsMessage={() => {
                        return (
                            this.props.choices.length === 0 ? (
                                'No options'
                            ) : (
                                this.state.value.length < 3 ? (
                                    'Enter at least 3 characters to search'
                                ) : (
                                    'No options'
                                )
                            )
                        )
                    }}
                    filterOption={!this.props.showAllResult ? (option: Option, rawInput: string) => {
                        const hasNoInput = (rawInput.length === 0);
                        const choicesAreShort = (this.props.choices.length <= 20);
                        const inputIsShort = (rawInput.length < 3);
                        const isInputMatch = (option.label.toLowerCase().includes(rawInput.toLowerCase()));
                        if (choicesAreShort) {
                            if (hasNoInput) {
                                return true;
                            }
                            return isInputMatch;
                        } else {
                            if (!inputIsShort) {
                                return isInputMatch;
                            }
                            return false;
                        }
                    } : undefined}
                    onChange={(choice: DropdownChoice) => {
                        this.props.onChange(choice);
                    }} />
                    
            </div>
        );
    }
}