import * as React from 'react';
import { TableRow, TableCell } from '@material-ui/core';
import { ClientData, MemberData } from 'Pages/CustomerCarePage/Models';
import { CustomTable, HeaderData } from 'Pages/CustomerCarePage/Components/CustomTable';
// import { API } from 'Pages/CustomerCarePage/API';
import { Payloads } from 'Pages/CustomerCarePage/Payloads';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';
// import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
// import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
// import { faSort } from '@fortawesome/free-solid-svg-icons';

interface IProps {
    client: ClientData;
}

interface IStates {
    members: MemberData[];
}

export class MembersSection extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            members: []
        };
    }

    getFilter = (offset: number, limit: number, filter: any) => {
        const filterLayer1 = {
            where: {},
            limit: 0,
            skip: 0
        };

        const millisInDay = 1000 * 60 * 60 * 24;

        filterLayer1.where = {
            and: [
                { client_id: this.props.client.id },
                filter.memberId ? { 
                    member_id: { regexp: `/${filter.memberId}/i` }
                } : undefined,
                filter.name ? { 
                    or: [
                        { last_name: { regexp: `/${filter.name}/i` } },
                        { middle_name: { regexp: `/${filter.name}/i` } },
                        { first_name: { regexp: `/${filter.name}/i` } }
                    ]
                } : undefined,
                filter.corporateName ? {
                    registered_name: { regexp: `/${filter.corporateName}/i` }
                } : undefined,
                filter.status ? {
                    member_status: { regexp: `/${filter.status}/i` }
                } : undefined,
                filter.effectivityDate ? {
                    effectivity_date: {
                        between: [
                            GlobalFunction.getDate(new Date(filter.effectivityDate.getTime() - millisInDay)),
                            GlobalFunction.getDate(new Date(filter.effectivityDate.getTime() + millisInDay))
                        ]
                    }
                } : undefined,
            ].filter(item => item)
        }

        filterLayer1.limit = limit;
        filterLayer1.skip = offset;

        if (filterLayer1.where['and'].length === 0)
            filterLayer1.where = {};

        return filterLayer1;
    }

    render() : JSX.Element {

        const columns: HeaderData[] = [
            { name: 'Member ID',        key: 'memberId',        filterType: 'string', sortType: 'string', style: { minWidth: '200px' } },
            { name: 'Name',             key: 'name',            filterType: 'string', sortType: 'string', style: { minWidth: '150px' } },
            { name: 'Corporate/s',      key: 'corporateName',   filterType: 'string', sortType: 'string', style: { minWidth: '150px' } },
            { name: 'Effectivity Date', key: 'effectivityDate', filterType: 'date',   sortType: 'date',   style: { minWidth: '150px' } },
            { name: 'Termination Date', key: 'terminationDate', filterType: 'date',   style: { minWidth: '150px' }, filterDisabled: true },
            { name: 'Status',           key: 'status',          filterType: 'string', sortType: 'string', style: { minWidth: '150px' } }
        ];

        const renderTableRow = (member: MemberData, index: number): JSX.Element  =>{
            const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
            const effectivityDate = member.effectivityDate ? GlobalFunction.toShortDateString(member.effectivityDate) : '---';
            const terminationDate = member.terminationDate ? GlobalFunction.toShortDateString(member.terminationDate) : '---';
            const name = GlobalFunction.toNameFormat(
                member.firstName,
                member.middleName,
                member.lastName
            );
    
            return (
                <TableRow style={{backgroundColor: backgroundColor}}>
                    <TableCell>{member.memberId}</TableCell>
                    <TableCell>{name}</TableCell>
                    <TableCell>{member.corporateName || '---'}</TableCell>
                    <TableCell>{effectivityDate || '---'}</TableCell>
                    <TableCell>{terminationDate}</TableCell>
                    <TableCell>{member.status || '---'}</TableCell>
                </TableRow>
            );
        }

        return (
            <div className='sub-section' style={{overflowX: 'auto'}}>
                {this.state.members ? (
                    <CustomTable
                        rows={this.state.members}
                        columns={columns}
                        rowsPerPageOptions={[10, 25, 50]}
                        renderRow={renderTableRow}
                        serverSideOption={{
                            getUrl: (offset: number, limit: number, filter: any) => {
                                const filters = this.getFilter(offset, limit, filter);
                                return `members?filter=${JSON.stringify(filters)}&count=true`;
                            },
                            onSuccess: (response: any) => {
                                this.setState({members: Payloads.getMembers(response.members)});
                            },
                            getCount: (response: any) => {
                                return response.total.count;
                            }
                        }} />
                ) : (
                    <Loading height='10rem' />
                )}
            </div>
        );
    }

}