import * as React from 'react';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';
import Link from '@material-ui/core/Link';

import { faSearch } from '@fortawesome/free-solid-svg-icons';
import { GlobalFunction } from '../GlobalFunction';
import { MemberData, TicketData, PartialMemberData, DropdownChoice, BenefitData, SimpleTicketData, ClientData, PartialClientData } from 'Pages/CustomerCarePage/Models';
import { TextButton } from '../Components/TextButton';
import { SearchedMembersList } from './SearchedMembersList';
import { API } from '../API';
import { Loading } from '../Components/Loading';
import { TypeValueField } from '../Components/TypeValueField';
import { MemberDataSection } from './MemberDataSection';
import { ClientDataSection } from './ClientDataSection';
import { SearchedClientsList } from './SearchedClientsList';
import { Payloads } from '../Payloads';
import { AuditLogsModal } from '../Components/Modals/AuditLogsModal';

interface IProps {
    member?: MemberData;
    client?: ClientData;
    ticket?: TicketData;
    task: 'Create' | 'View' | 'Update' | 'Resolve';
    searchBarData?: SearchData;

    onOpenAdvanceSearch?: () => void;
    onUpdateMemberStateChange?: (member?: MemberData) => void;
    onUpdateClientStateChange?: (client?: ClientData) => void;
    resetCategoryDropdown?: () => void;
    openAsigneeNotesModal?: () => void;
    setSearchIDType?: (type: string) => void;
    onReassign?: () => void;
    onSetSearchBarData?: (searchBarData: SearchData) => void;

    promptChooseMember?: boolean;
    disableLinkedTickets?: boolean;
}

interface IStates {
    clientrecentTickets?: SimpleTicketData[];
    memberrecentTickets?: SimpleTicketData[];
    linkedTickets?: SimpleTicketData[];
    tmpGroup: string;
    isAuditLogsOpen: boolean;
    isMembersLoaded: boolean;
    loadingMembers: boolean;
    loadingMember: boolean;
    loadingBenefits: boolean;

    isClientsLoaded: boolean;
    loadingClients: boolean;
    loadingClient: boolean;

    loadingRecentTickets: boolean;
    loadingLinkedTickets: boolean; 
}

export interface SearchData {
    type: DropdownChoice;
    value: string;
}

export class LeftPanelSection extends React.Component<IProps, IStates> {

    clients: PartialClientData[];
    members: PartialMemberData[];
    benefits: BenefitData[];
    idTypes: DropdownChoice[];

    constructor(props: IProps) {
        super(props);

        this.state = {
            tmpGroup:'',
            isAuditLogsOpen: false,
            isMembersLoaded: false,
            loadingMembers: false,
            loadingMember: false,
            loadingBenefits: false,
            isClientsLoaded: false,
            loadingClients: false,
            loadingClient: false,
            loadingRecentTickets: false,
            loadingLinkedTickets: false
        };

        this.clients = [];
        this.members = [];
        this.benefits = [];

        this.idTypes = [
            { value: '1', label: 'Client ID' },
            { value: '2', label: 'Member ID' }
        ]
    }










    componentWillUpdate(props: IProps, _state: IStates) {
        if (!this.props.client && !this.props.member) {
            if (props.client || props.member) {
                const targetId =
                    props.member ? props.member.memberId :
                    props.client ? props.client.corporateAccountNo : '';
                this.getRecentTicketsData(targetId, props.member !== undefined);
            }
        }
        if(this.props.client && !this.state.clientrecentTickets && !this.props.member){
            const targetId = props.client ? props.client.corporateAccountNo : ''
            this.getRecentTicketsData(targetId, false);
        }
        if(this.props.member && !this.state.memberrecentTickets){
            const targetId = props.member ? props.member.memberId : ''
            this.getRecentTicketsData(targetId, true);
        }
    }










    getMemberData = (memberId: string) => {
        API.get(`customer-care/members/${memberId}`, (response) => {
            console.log('get member:',response);
            if (this.props.onUpdateMemberStateChange && this.props.onUpdateClientStateChange) {
                this.props.onUpdateClientStateChange(undefined);
                this.props.onUpdateMemberStateChange(Payloads.getMemberData(response));
                getMemberBenefit();
                this.getRecentTicketsData(memberId, true);
            }
            API.get(`customer-care/members/${response._id}/contracts/${response.latest_contract._id}`, (response) => {
                localStorage.setItem('foradjudicationmember',JSON.stringify(response.member));
                localStorage.setItem('foradjudicationbenefits',JSON.stringify(response.benefits));
            }, (error) => {
                console.log(error);
            });
            this.setState({loadingMember: false});
        }, (error) => {
            console.log(error);
        });

        const getMemberBenefit = () => {
            this.setState({loadingBenefits: true});
            API.get(`customer-care/members/${memberId}/benefits`, (response) => {
                this.benefits =
                    combineNodes(
                        response.filter(benefit => benefit.level == 6),
                        response.filter(benefit => benefit.level == 7)
                    )
                    .map(benefit => {
                        return {
                            category: benefit.parent_name,
                            name: benefit.name,
                            ...(benefit.limits.length > 0 ?
                                {
                                    limitType: benefit.limits[0].type,
                                    limit: benefit.limits[0].limit,
                                    limitUnit: benefit.limits[0].limit_unit
                                } : undefined)
                        }
                    });

                this.setState({loadingBenefits: false});
            }, (error) => {
                console.log(error);
                this.setState({loadingBenefits: false});
            });
        }

        const combineNodes = (parentNodes: any, childNodes: any) => {
            const nodes = parentNodes.reduce ((result, parent) => {
                parent.children.map ((child) => {
                let foundChild = childNodes.find ((childNode) => child === childNode._id);

                if (foundChild) {
                    let child = Object.assign({}, foundChild);

                    child['parent_name'] = parent.name;
                    child['parent_code'] = parent.code;
                    child['disabled'] = false;
                    result.push(child);
                }
                });

                return result;
            }, new Array());

            return nodes;
        };
    }










    getClientData = (clientId: string) => {
        API.get(`customer-care/clients/${clientId}`, (response) => {
            console.log('get client',response);
            if (this.props.onUpdateClientStateChange && this.props.onUpdateMemberStateChange) {
                this.props.onUpdateMemberStateChange(undefined);
                this.props.onUpdateClientStateChange(Payloads.getClientData(response));
                this.getRecentTicketsData(clientId, false);
                this.getLinkedTicketsData(clientId);
                console.log('client Data: ',response)
                localStorage.setItem('clientData',JSON.stringify(response))
            }
            this.setState({loadingClient: false});
        }, (error) => {
            console.log(error);
        });
    }










    getRecentTicketsData = (targetId: string, isMemberOnly: boolean) => {
        if (!this.state.loadingRecentTickets) {
            this.setState({
                loadingRecentTickets: true,
            });
        }
        
        const filter = {
            where: {
                '$and': [
                    isMemberOnly ? 
                        { member_id: targetId } :
                        { '$or': [{ member_id: targetId }, { client_id: targetId }] },
                    { ticket_id: { '$ne': null }}
                ]
            }
        };

        if (this.props.ticket) {
            filter.where.$and.push({
                // @ts-ignore
                ticket_id: { '$ne': this.props.ticket.ticketNo }
            });
        }
        API.get(`customer-care/tickets?filter=${JSON.stringify(filter)}`,
            (tickets) => {
                if (tickets) {
                    const ticketlist:SimpleTicketData[] = []
                    tickets.map(ticket => {
                        ticketlist.unshift({
                            ticketNo: ticket.ticket_id,
                            specificType: ticket.ticket_category.specific_type !== 'Not Applicable' ? (
                                ticket.ticket_category.specific_type
                            ) : (
                                ticket.ticket_category.ticket_type
                            ),
                            status: ticket.status,
                            dateCreated: new Date(ticket.date_created),
                            dateUpdated: new Date(ticket.date_updated)
                        });
                    });
                    this.setState({loadingRecentTickets: false});
                    if(isMemberOnly){
                        this.setState({memberrecentTickets: ticketlist});
                    } else {
                        this.setState({clientrecentTickets: ticketlist});
                    }
                }
            },
            (error) => {
                console.log(error);
                this.setState({loadingRecentTickets: false});
            }
        );
    }










    getLinkedTicketsData = (_targetId: string) => {
        if (!this.state.loadingLinkedTickets) {
            this.setState({loadingLinkedTickets: true});
        }

        if (this.state.linkedTickets) {
            this.state.linkedTickets.push({
                ticketNo: '999-999-9999*',
                specificType: 'None*',
                status: 'None*',
                dateCreated: new Date(),
                dateUpdated: new Date()
            });
            this.setState({linkedTickets: this.state.linkedTickets});
            console.log('linkedTickets', this.state.linkedTickets);
        }
        this.setState({loadingLinkedTickets: false});
    }










    onSearchChanged = (searchData: SearchData, onSetSearchBarData: Function) => {
        const sliceMemberID = (text: string) => {
            const slicedText: string[] = [];
            slicedText.push(text.slice(0, 2));
            slicedText.push(text.slice(2, 4));
            slicedText.push(text.slice(4, 9));
            slicedText.push(text.slice(9, 14));
            slicedText.push(text.slice(14, 16));
            return slicedText.filter(text => text !== '');
        }

        const sliceClientID = (text: string) => {
            const slicedText: string[] = [];
            slicedText.push(text.slice(0, 2));
            slicedText.push(text.slice(2, 4));
            slicedText.push(text.slice(4, 9));
            return slicedText.filter(text => text !== '');
        }

        const onSearchMember = () => {
            if (trimmedValue.length >= 13) {
                this.setState({loadingMembers: true});
                API.get(`customer-care/members?` +
                    `filter[where][member_id][like]=${newValue}&` +
                    `filter[where][member_status][inq]=Active&` +
                    `filter[where][member_status][inq]=Cancelled&` +
                    `filter[where][member_status][inq]=Expired&` +
                    `filter[where][member_status][inq]=Suspended&` +
                    `filter[where][member_status][inq]=Terminated&` +
                    `filter[where][member_status][inq]=Awaiting Activation`,
                    (response) => {
                        console.log(response);
                        this.members = response.map(member => {
                            return {
                                memberId: member.member_id,
                                firstName: member.first_name,
                                middleName: member.middle_name,
                                lastName: member.last_name,
                                clientName: member.client.registered_name,
                                isVip: member.is_vip
                            }
                        });
                        this.setState({
                            isMembersLoaded: true,
                            loadingMembers: false
                        });
                    },
                    (error) => {
                        console.log(error);
                        this.setState({
                            isMembersLoaded: true,
                            loadingMembers: false
                        });
                    });
            }
        }

        const onSearchClient = () => {
            if (trimmedValue.length >= 8) {
                this.setState({loadingClients: true});
                API.get(`customer-care/client?filter[where][corporate_account_no][like]=${newValue}`,
                    (response) => {
                        console.log(response);
                        this.clients = response.map(client => {
                            return {
                                id: client._id,
                                clientId: client.corporate_account_no,
                                clientName: client.registered_name
                            }
                        });
                        this.setState({
                            isClientsLoaded: true,
                            loadingClients: false
                        });
                    },
                    (error) => {
                        console.log(error);
                        this.setState({
                            isClientsLoaded: true,
                            loadingClients: false
                        });
                    });
            }
        }

        const trimmedValue = searchData.value.replace(/-/g, '');
        const splitValue = searchData.type.label === 'Client ID' ? (
            sliceClientID(trimmedValue)
        ) : (
            sliceMemberID(trimmedValue)
        );
        const newValue = splitValue ? splitValue.join('-') : '';
        searchData.value = newValue;
        onSetSearchBarData(searchData);

        if (searchData.type.label === 'Client ID') {
            onSearchClient();
        }

        if (searchData.type.label === 'Member ID') {
            onSearchMember();
        }
    }










    render(): JSX.Element {
        return (
            <div className='member-info'>

                {this.state.isAuditLogsOpen && this.props.ticket ? (
                    <AuditLogsModal
                        ticketId={this.props.ticket.id || ''}
                        isOpen={this.state.isAuditLogsOpen}
                        onClose={() => {
                            this.setState({isAuditLogsOpen: false});
                        }} />
                ) : null}

                {this.props.task === 'Create' && this.props.searchBarData && this.props.onSetSearchBarData ? (
                    <div>
                        {this.renderSearchArea(this.props.searchBarData, this.props.onSetSearchBarData)}
                        <div className='line-border' />
                    </div>
                ) : (
                    this.renderTopArea()
                )}

                {this.props.member ? (
                    <MemberDataSection
                        member={this.props.member}
                        benefits={this.benefits}
                        recentTickets={this.state.memberrecentTickets}
                        loadingBenefits={this.state.loadingBenefits}
                        loadingRecentTickets={this.state.loadingRecentTickets} />
                ) : this.props.client ? (
                    <ClientDataSection
                        client={this.props.client}
                        recentTickets={this.state.clientrecentTickets}
                        linkedTickets={this.state.linkedTickets}
                        disableLinkedTickets={this.props.disableLinkedTickets}
                        loadingRecentTickets={this.state.loadingRecentTickets}
                        loadingLinkedTickets={this.state.loadingLinkedTickets}
                        getLinkedTicketsData={this.getLinkedTicketsData}  />
                ) : (
                    <>
                    {this.state.loadingMember || this.state.loadingClient ? (
                        <Loading height='3rem' />
                    ) : null}
                    {this.props.promptChooseMember ? (
                        <div className='section' style={{color: '#E53935'}}>
                            Choose a member/client first before submitting the ticket.
                        </div>
                    ) : null}
                    </>
                )}

                {this.props.ticket ? (
                    this.renderTicketData(this.props.ticket)
                ) : (null)}


                {/* <div className='section'>
                    <TextButton 
                        label='View Audit Logs' 
                        onClick={() => {
                            this.setState({isAuditLogsOpen: true});
                        }} />
                </div> */}
                
                <div className='section' />
            </div>
        );
    }










    renderSearchArea(searchData: SearchData, onSetSearchBarData: Function): JSX.Element {
        const typeFieldMask = 
            searchData.type.label === 'Member ID' ? 
                '##-##-#####-#####-##' : 
                '##-##-#####';

        return (
            <div className='section'>
                <Breadcrumbs className='bread-crumbs'>
                    <Link href='#/customer-care'>TICKETS</Link>
                    <div className='current-text'>CREATE A TICKET</div>
                </Breadcrumbs>

                <div className='header-1'>Create Ticket</div>

                <div style={{position: 'relative'}}>
                    <TypeValueField
                        placeholder='Search ID'
                        type={searchData.type}
                        value={searchData.value}
                        choices={this.idTypes}
                        mask={typeFieldMask}
                        onChangeType={(type: DropdownChoice) => {
                            searchData.type = type;
                            onSetSearchBarData(searchData);
                        }}
                        onChangeValue={(value: string) => {
                            searchData.value = value;
                            this.onSearchChanged(searchData, onSetSearchBarData);
                        }} />

                    {this.state.loadingMembers ||  this.state.loadingClients ? (
                        <div style={{position: 'absolute', right: '8px', bottom: '13px'}}>
                            <Loading height='none' size={16}/>
                        </div>
                    ) : null}
                </div>

                {searchData.type.label === 'Member ID' ? (
                    <SearchedMembersList
                        searchText={searchData.value}
                        members={this.members}
                        onSelectMember={(memberId: string) => {
                            this.members = [];
                            this.clients = [];
                            this.getMemberData(memberId);

                            if (this.props.onUpdateMemberStateChange) {
                                this.props.onUpdateMemberStateChange(undefined);
                            }

                            this.setState({
                                loadingMember: true,
                                memberrecentTickets: []
                            });
                            
                            if (this.props.resetCategoryDropdown) {
                                this.props.resetCategoryDropdown();
                            }

                            searchData.value = '';
                            onSetSearchBarData(searchData);
                        }} />

                ) : searchData.type.label === 'Client ID' ? (
                    <SearchedClientsList
                        searchText={searchData.value}
                        clients={this.clients}
                        onSelectClient={(clientId: string) => {
                            this.members = [];
                            this.clients = [];
                            this.getClientData(clientId);

                            if (this.props.onUpdateClientStateChange) {
                                this.props.onUpdateClientStateChange(undefined);
                            }

                            this.setState({
                                loadingClient: true,
                                clientrecentTickets: [],
                                linkedTickets: []
                            });
                            
                            if (this.props.resetCategoryDropdown) {
                                this.props.resetCategoryDropdown();
                            }

                            searchData.value = '';
                            onSetSearchBarData(searchData);
                        }} />

                ) : null}

                <TextButton
                    icon={faSearch}
                    label='Advanced Search'
                    onClick={() => {
                        if (this.props.onOpenAdvanceSearch) {
                            this.props.onOpenAdvanceSearch();
                        }
                    }}
                    />
            </div>
        );
    }










    renderTopArea(): JSX.Element {
        return (
            <div className='section'>
                <Breadcrumbs className='bread-crumbs'>
                    <Link href='#/customer-care'>TICKETS</Link>
                    <Link className='current-text'>{this.props.task.toUpperCase() + ' TICKET'}</Link>
                </Breadcrumbs>

                {this.props.ticket ? (
                    <>
                    <div className='header-1'>{`${this.props.task} Ticket ${this.props.ticket.ticketNo}`}</div>
                    <div className='header-2'><span className="light-text">STATUS:</span> {`${this.props.ticket.status.toUpperCase()} TICKET`}</div>
                    </>
                ) : null}


            </div>
        );
    }










    renderTicketData(ticket: TicketData): JSX.Element {
        return (
            <div className='section'>
                <div className='sub-section'>
                    <div className='light-text'>TICKET TYPE</div>
                    <div className='small-text'>{ticket.ticketType}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>SPECIFIC TYPE</div>
                    <div className='small-text'>{ticket.specificType}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>URGENCY</div>
                    <div className='small-text'>{ticket.urgency}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>ASSIGNED TO</div>
                    <div className='small-text'>{ticket.assignee.name}</div>

                    {this.props.onReassign ? (
                        <div className='small-text'>
                            <TextButton
                                label='Re-assign Ticket'
                                onClick={() => {
                                    if (this.props.onReassign) {
                                        this.props.onReassign();
                                    }
                                }} />
                        </div>
                    ) : null}
                </div>
                {ticket.additionalNotes ? (
                    <div className='sub-section'>
                        <div className='light-text'>PREVIOUS ASSIGNEE NOTES</div>
                        <div>
                            {/* <Link 
                                style={{ 
                                    fontWeight :'bold', 
                                    textDecoration : 'underline',
                                    fontSize: '13px'}}
                                onClick={() => {
                                    if(this.props.openAsigneeNotesModal){
                                        console.log('CLICK')
                                        this.props.openAsigneeNotesModal()
                                    }
                                }}>
                                View Notes
                            </Link> */}
                            {/* For Reusability */}
                            <TextButton
                                label='View Notes'
                                onClick={() => {
                                    if(this.props.openAsigneeNotesModal) {
                                        this.props.openAsigneeNotesModal()
                                    }
                                }} />
                            {/* Simple isn't it? */}
                        </div>
                    </div>
                ) : null}
                <div className='sub-section'>
                    <div className='light-text'>RECEIVED VIA</div>
                    <div className='small-text'>{ticket.ticketSource}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>RECEIVED FROM</div>
                    <div className='small-text'>{ticket.callerName}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>NUMBER/EMAIL</div>
                    <div className='small-text'>{ticket.contactNo}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>CALL TRANSACTION NO.</div>
                    <div className='small-text'>{ticket.transactionNo}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>CREATED BY</div>
                    <div className='small-text'>{ticket.createdBy.name}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>DATE AND TIME CREATED</div>
                    <div className='small-text'>{GlobalFunction.toDateAndTime(ticket.dateCreated)}</div>
                </div>
                <div className='sub-section'>
                    <div className='light-text'>DATE AND TIME LAST UPDATED</div>
                    <div className='small-text'>{GlobalFunction.toDateAndTime(ticket.dateUpdated)}</div>
                </div>
                <div className='sub-section'>
                    <TextButton 
                        label='View Audit Logs' 
                        onClick={() => {
                            this.setState({isAuditLogsOpen: true});
                        }} />
                </div>
                
            </div>
        );
    }
}
