import * as React from 'react';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { DropdownChoice, RequestApprovalDetails } from 'Pages/CustomerCarePage/Models';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';
import { CustomRadio } from 'Pages/CustomerCarePage/Components/CustomRadio';

interface IProps {
    requestApproval: RequestApprovalDetails;
    refreshRequestApprovalState: () => void;
    createTicketAttemp: boolean;
}

export class RequestApprovalSection extends React.Component<IProps> {

    requestTypes: DropdownChoice[] = [
        { value: '1', label: 'Contract Revision' },
        { value: '2', label: 'Corporate Guarantee and BillBack Arrangements' },
        { value: '3', label: 'Sponsorship'},
        { value: '4', label: 'Special Accomodation - Enrollment'},
        { value: '5', label: 'Special Accomodation - Termination / Cancellation'},
        { value: '6', label: 'Special Accomodation - Availment'},
        { value: '7', label: 'Others'},
    ];

    render() {
        return (
            <div className='availment-section'>
                <div style={{paddingTop: '5rem'}} />
                <div className='sub-section'>
                    <div className='header-2'>REQUEST DETAILS</div>
                    <InputField 
                        placeholder='Summary of Request (Title)*' 
                        value={this.props.requestApproval.title}
                        charLimit={50}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.requestApproval.title === ''
                        }
                        onChange={(value: string) => {
                            this.props.requestApproval.title = value;
                            this.props.refreshRequestApprovalState();
                        }} />
                    <Dropdown 
                        placeholder='Request Type*' 
                        choices={this.requestTypes}
                        value={this.props.requestApproval.type}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.requestApproval.type.value === ''
                        }
                        onChange={(value: DropdownChoice) => {
                            this.props.requestApproval.type = value;
                            this.props.refreshRequestApprovalState();
                        }} />
                    <InputField 
                        placeholder='Background / Details of Request' 
                        value={this.props.requestApproval.background}
                        onChange={(value: string) => {
                            this.props.requestApproval.background = value;  
                            this.props.refreshRequestApprovalState();
                        }} />
                    <InputField 
                        placeholder='Notes' 
                        value={this.props.requestApproval.notes}
                        onChange={(value: string) => {
                            this.props.requestApproval.notes = value
                            this.props.refreshRequestApprovalState();
                        }} />
                </div>
                <div className='sub-section'>
                    <div className='header-2' style={{paddingBottom: '1em'}}>RECOMMENDATION</div>
                    <div className="text">Choose Recommendation*</div>
                    <CustomRadio
                        label='Approve'
                        checkedCondition={this.props.requestApproval.recommendation === 'Approved'}
                        onChange={() => {
                            this.props.requestApproval.recommendation = 'Approved';
                            this.props.refreshRequestApprovalState();
                        }} />
                    <CustomRadio
                        label='Disapprove'
                        checkedCondition={this.props.requestApproval.recommendation === 'Disapproved'}
                        onChange={() => {
                            this.props.requestApproval.recommendation = 'Disapproved';
                            this.props.refreshRequestApprovalState();
                        }} />
                    <InputField 
                        placeholder='Basis for Recommendation*' 
                        value={this.props.requestApproval.basisRecommendation}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.requestApproval.basisRecommendation === ''}
                        onChange={(value: string) => {
                            this.props.requestApproval.basisRecommendation = value;
                            this.props.refreshRequestApprovalState(); 
                        }} />
                </div>
            </div>
        );
    }
}