import * as React from 'react';
import {
  Typography,
  Box,
} from '@material-ui/core/';
import { makeStyles } from '@material-ui/core/styles';
import { GlobalFunction } from '../../../../GlobalFunction';
import { StatusComponent } from './StatusComponent';

const mphStyles = makeStyles({
    headerMemberName: {
        color: '#272e4c',
        fontWeight: 600,
        fontSize: '1rem',
        textTransform: 'capitalize',
    },
    headerMemberStatusText: {
        fontSize: '11px',
        color: 'rgba(39, 46, 76, 0.54)',
        fontWeight: 600,
        textTransform: 'uppercase',
    }
});

interface MemberProfileHeaderProps {
    memberData?: any;
}

export const MemberProfileHeader: React.FC<MemberProfileHeaderProps> = (props: MemberProfileHeaderProps): JSX.Element => {
    const classes = mphStyles(props);
    const { memberData } = props;

    if(memberData){
        let memberFullName = GlobalFunction.toNameFormat(
            memberData['first_name'],
            memberData['middle_name'],
            memberData['last_name']
        );

        return (
            <Box display="flex" flexDirection="row" alignItems="center">
                <Typography className={classes.headerMemberName}>
                    {memberFullName}
                </Typography>
                <Typography className={classes.headerMemberStatusText} style={{paddingLeft: 12}}>
                    <StatusComponent memberData={memberData} />
                    &nbsp;
                    {memberData.member_status}
                </Typography>
            </Box>
        );
    }
    else{
        return <div></div>;
    }

};