//#region Global Imports
import * as React from 'react';
import { makeStyles } from '@material-ui/styles';
import Typography from '@material-ui/core/Typography';
//import { Components } from '@hims/core';
//#endregion Global Imports

const labelStyles = makeStyles({
	root: {
		lineHeight: '1',
	},
	adornedStart: {
		paddingLeft: 0,
	}
});

const valueStyles = makeStyles({
	root: {
		fontWeight: 'bold'
	}
});

interface IViewFieldProps {
	label: string;
	value: React.ReactNode;
	menuItems?: any[];
}

export const ViewField: React.FC<IViewFieldProps> = (props: IViewFieldProps): JSX.Element => {
	const labelClasses = labelStyles();
	const valueClasses = valueStyles();

	const { label, value, menuItems } = props;

	let finalValue = value;

	if (menuItems !== undefined && menuItems.length > 0) {
		const found = menuItems.find ((item) => value === item.value);

		if (found && found['name']) {
			finalValue = found['name'];
		}
	}

	return (
		<React.Fragment>
			<Typography classes={labelClasses} style={{lineHeight: '1'}} variant="overline">{label}</Typography>
			<Typography classes={valueClasses} style={{fontWeight: 'bold', marginBottom: '25px'}} variant="body1">{finalValue ? finalValue : ''}</Typography>
		</React.Fragment>
	);
};
