import * as React from 'react';
import { TableCell, TableRow } from '@material-ui/core';
import { CustomTable, HeaderData } from 'Pages/CustomerCarePage/Components/CustomTable';

import { DropdownChoice, PartialTicketData } from 'Pages/CustomerCarePage/Models';
import { API } from 'Pages/CustomerCarePage/API';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { TicketInfoModal } from './TicketInfoModal';
import { LabelDisplayedRowsArgs } from '@material-ui/core/TablePagination';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';

interface IProps {
    memberId?: string;
    clientId?: string;
}

interface IStates {
    ticketListUpdated: boolean;
    selectedTicketId?: string;
}

export class ReviewTicketsSection extends React.Component<IProps, IStates> {

    ticketChannels: DropdownChoice[];
    ticketTypes: DropdownChoice[];
    specificTypes: DropdownChoice[];
    tickets: PartialTicketData[];

    constructor(props: any) {
        super(props);

        this.ticketChannels = [
            { value: '0', label: '' },
            { value: '1', label: 'Call' },
            { value: '2', label: 'SMS' },
            { value: '3', label: 'Text' },
            { value: '4', label: 'Email' }
        ];

        this.ticketTypes = [
            { value: '0', label: '' },
            { value: '1', label: 'Availment' },
            { value: '2', label: 'Inquiry' }
        ];

        this.specificTypes = [
            { value: '0', label: '' },
            { value: '1', label: 'Outpatient Procedure' },
            { value: '2', label: 'Inpatient' },
            { value: '3', label: 'Emergency Room' },
            { value: '4', label: 'Consultation' }
        ];

        this.state = {
            ticketListUpdated: false,
            selectedTicketId: undefined
        }

        this.tickets = [];
    }

    componentDidMount() {
        const targetId = this.props.clientId || this.props.memberId;

        const filter = {
            where: {
                '$and': [
                    { '$or': [{ member_id: targetId }, { client_id: targetId }] },
                    { ticket_id: { '$ne': null }}
                ]
            }
        };
        let filter_str = `?filter=${JSON.stringify(filter)}`
        if(this.props.clientId && !this.props.memberId){
            filter_str = `?filter[where][client_id]=${this.props.clientId}`
        }else if(this.props.memberId){
            filter_str = `?filter[where][member_id]=${this.props.memberId}`
        }

        API.get(`customer-care/tickets`+filter_str,
            (tickets) => {
                if (tickets) {
                    tickets.map(ticket => {
                        this.tickets.unshift({
                            id: ticket._id,
                            ticketNo: ticket.ticket_id,
                            urgency: (
                                ticket.ticket_category.urgency === 'Low' ? 3 :
                                ticket.ticket_category.urgency === 'Medium' ? 2 : 
                                ticket.ticket_category.urgency === 'High' ? 1 : 0),
                            status: ticket.status,
                            channel: ticket.received_via,
                            type: ticket.ticket_category.ticket_type,
                            specificType: ticket.ticket_category.specific_type,
                            source: {
                                value: ticket.received_from.toUpperCase(), 
                                label: (
                                    ticket.member_name ? ticket.member_name :
                                    ticket.corporate_client ? ticket.corporate_client :
                                    ticket.broker ? ticket.broker :
                                    ticket.hospital_name ? ticket.hospital_name : ''
                                )
                            },
                            assignee: ticket.assign_to,
                            lastUpdate: new Date(ticket.date_updated),
                        });
                    });

                    // TODO: PLEASE REMOVE THIS BLOCK IF OLD TICKETS ARE DELETED
                    this.tickets = this.tickets.filter(ticket => typeof ticket.source.label === 'string');
                    // =========================================================

                    this.setState({ticketListUpdated: true});
                }
            },
            (error) => {
                console.log(error);;
            });
    }

    render(): JSX.Element {

        const ticketDataColumn: HeaderData[] = [
            { name: 'Urgency',       key: 'urgency',      style: { minWidth: '75px' },  filterType: 'string', sortType: 'string' },
            { name: 'Status',        key: 'status',       style: { minWidth: '125px' }, filterType: 'string', sortType: 'string' },
            { name: 'Ticket ID',     key: 'ticketNo',     style: { minWidth: '150px' }, filterType: 'string', sortType: 'string' },
            { name: 'Channel',       key: 'channel',      style: { minWidth: '100px' }, filterType: 'string', sortType: 'string' },
            { name: 'Type',          key: 'type',         style: { minWidth: '100px' }, filterType: 'string', sortType: 'string' },
            { name: 'Specific Type', key: 'specificType', style: { minWidth: '200px' }, filterType: 'string', sortType: 'string' },
            { name: 'Source',        key: 'source',       style: { minWidth: '150px' }, filterType: 'string', sortType: 'string' },
            { name: 'Assigned',      key: 'assignee',     style: { minWidth: '150px' }, filterType: 'string', sortType: 'string' },
            { name: 'Last Update',   key: 'lastUpdate',   style: { minWidth: '150px' }, filterType: 'date',   sortType: 'date' },
            
        ];

        const renderTableRow = (ticket: PartialTicketData, index: number): JSX.Element => {
        const backgroundColor = (index % 2 === 0 ? (
                ticket.urgency === 1 ? '#FFF2F2' : '#FFFFFF'
            ) : (
                ticket.urgency === 1 ? '#F7ECEC' : '#F6F7FA'
            ));

        const urgencyColor = (
            ticket.urgency === 3 ? '#3AB77D' :
            ticket.urgency === 2 ? '#FAB435' :
            ticket.urgency === 1 ? '#FD5451' : '')
        
        return (
            <TableRow 
                style={{backgroundColor: backgroundColor}} 
                onClick={() => {
                    this.setState({selectedTicketId: ticket.id});
                }}>
                <TableCell style={{textAlign: 'center', paddingLeft: 0}}>
                    <div 
                        className='number-item' 
                        style={{display: 'inline-block', backgroundColor: urgencyColor}}>
                        {ticket.urgency}
                    </div>
                </TableCell>
                <TableCell>{ticket.status}</TableCell>
                <TableCell>{ticket.ticketNo}</TableCell>
                <TableCell>{ticket.channel}</TableCell>
                <TableCell>{ticket.type}</TableCell>
                <TableCell>{ticket.specificType}</TableCell>
                <TableCell>
                    <div className='text'>{ticket.source.label}</div>
                    <div className='light-text' style={{fontSize: '9px'}}>
                        {ticket.source.value}
                    </div>
                </TableCell>
                <TableCell>{ticket.assignee}</TableCell>
                <TableCell>{GlobalFunction.toDateAndTime(ticket.lastUpdate)}</TableCell>
            </TableRow>
        );
    }

        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Review Tickets</div>
                <div className='sub-section' style={{overflowX: 'auto'}}>
                    {this.state.ticketListUpdated ? (
                        <CustomTable
                            className='custom-table-selectable'
                            // rows={this.getFilteredTicketData()}
                            // renderHeader={() => this.renderTableHeader()}
                            rows={this.tickets}
                            columns={ticketDataColumn}
                            renderRow={(rowData: any, index: number) => {
                                return renderTableRow(rowData, index);
                            }}
                            onChangeInfo={(info: LabelDisplayedRowsArgs) => 
                                `${info.from}-${info.to} out of ${info.count} tickets`}
                            labelRowsPerPage='Show'
                            rowsPerPageOptions={[5, 10, 15]} />
                    ) : (
                        <Loading height='10rem'/>
                    )}
                </div>

                {this.state.selectedTicketId ? (
                    <TicketInfoModal 
                        ticketId={this.state.selectedTicketId}
                        onCloseButtonClicked={() => {
                            this.setState({selectedTicketId: undefined});
                        }} />
                ) : null}
                <div className='section' />
            </div>
        );
    }
}


