export class CBUtils {
    static getNodeTree = (item: any, nodes: any[]) => {
        let current: any = item;
        let treePath: any[] = [];
        let levelLimit = 2;
    
        if (Array.isArray(current['tree_path'])) {
            treePath = [...current['tree_path']].reverse();
        } else {
            treePath = Object.values(current['tree_path']).reverse();
        }
        treePath.shift();
    
        if (current.level === 3) {
            levelLimit = 1;
        }
    
        const nodeTree = treePath.reduce((result, path) => {
            if (current !== undefined && current.level > levelLimit) {
    
                const input = {
                    id: current.id,
                    name: current.name,
                    code: current.code
                };
    
                if (current['original_id']) {
                    input['original_id'] = current['original_id'];
                }
    
                result.push(input);
    
                current = nodes.find((node: any) => {
                    return node.level === (current.level - 1) &&
                        node.code === path &&
                        node.children.includes(current.id);
                });
            }
            return result;
        }, new Array()).reverse();
    
        return nodeTree;
    } 

    static convertNodeData = (nodesParam: any[], loadExisting = false) => {
        let nodeIDMap: any[] = [];
    
        let nodes = [...nodesParam];
    
        if (!loadExisting) {
            nodes = nodes.filter ((item: any) => {
                if (item.level === 1 || 
                    item.level === 2 || 
                    (item.level > 2 && item.tree_path[2] === 'BASE')
                ) {
                    return true;
                } 
                return false;
            });
        }
    
        nodes = nodes.sort ((node1: any, node2: any) => {
            if (node1.level === 3 && node1.code === 'BASE') return -1;
            if (node2.level === 3 && node2.code === 'BASE') return 1;
    
            if (node1.sort_index > node2.sort_index) return -1;
            if (node1.sort_index < node2.sort_index) return 1;
    
            if (node1.name > node2.name) return 1;
            if (node1.name < node2.name) return -1;
    
            return 0;
        });
    
        nodes = nodes.map ((item: any, i: number) => {
            const oldID = item['_id'];
            let itemPlanType = 'BASE';
    
            if (item.level > 2 && item.tree_path[2] !== 'BASE') {
                itemPlanType = item.tree_path[2];
            }
    
            const newID = "NODE-" + itemPlanType + "-"+i.toString().padStart(3, '0');
    
            item['included'] = true;
    
            const IDSet = {
                id: oldID,
                newID: newID
            };
    
            let metadata = Object.assign({}, item['custom_metadata']);
    
            if (metadata['included_facilities'] && metadata['included_facilities'].length > 0) {
                const newIncludedFacilities = [...metadata['included_facilities']];
    
                metadata['partner_network_list'] = newIncludedFacilities.map((item: any, i: number) => {
                    let newPartner = Object.assign({}, item);
    
                    newPartner['parent'] = newID;
                    newPartner['included'] = true;
                    newPartner['index'] = i;
    
                    return newPartner;
                });
    
                delete metadata.included_facilities;
            }
    
            if (metadata['excluded_facilties'] && metadata['excluded_facilties'].length > 0) {
                let newExcludedFacilities = [...metadata['excluded_facilties']];
    
                let partnerNetworkList: any[] = [];
    
                if (metadata['partner_network_list'] !== undefined) {
                    partnerNetworkList = [...metadata['partner_network_list']];
                }
    
                newExcludedFacilities = newExcludedFacilities.map((item: any, i: number) => {
                    let newPartner = Object.assign({}, item);
    
                    newPartner['parent'] = newID;
                    newPartner['included'] = false;
                    newPartner['index'] = i;
    
                    return newPartner;
                });
    
                metadata['partner_network_list'] = [...partnerNetworkList, ...newExcludedFacilities];
    
                delete metadata.excluded_facilties;
            }
    
            item['custom_metadata'] = metadata;
    
            nodeIDMap.push (IDSet);
    
            if (loadExisting) {
                item['original_id'] = item['_id'];
            }
    
            item['id'] = newID;
    
            delete item['_id'];
    
            return Object.assign({}, item);
        });
    
        nodes = nodes.map ((item: any) => {
            if (loadExisting) {
                item['original_children'] = [...item['children']];
            }
    
            if (item['children'].length > 0) {
                item['children'] = item['children'].reduce ((result, id) => {
                    const found = nodeIDMap.find ((set) => { return set.id === id; });
    
                    if (found) {
                        result.push(found.newID)
                    }
    
                    return result;
                }, []);
            }
            return item;
        });
    
        return nodes;
    }
    
    static createExclusionList = (nodes: any[]) => {
        const eList = nodes.reduce((exResult, item: any) => {
            // if (item['custom_metadata']['is_excluded'] && item['custom_metadata']['is_excluded'] === true) {
            if (item['level'] === 6 && item['tree_path'][3] === 'EX') {
                const exTree = CBUtils.getNodeTree(item, nodes);
                exResult.push(exTree);
            }
            return exResult;
        }, []);
    
        return eList;
    }

    static combineNodes = (parentNodes, childNodes) => {
        const nodes = parentNodes.reduce ((result, parent) => {
          parent.children.map ((child) => {
            let foundChild = childNodes.find ((childNode) => child === childNode.id);
      
            if (foundChild) {
              let child = Object.assign({}, foundChild);
      
              child['parent_name'] = parent.name;
              child['parent_code'] = parent.code;
              child['disabled'] = false;
              result.push(child);
            }
          });
      
          return result;
        }, []);
      
        return nodes;
    }
      
    static combineNodes2 = (parentNodes, childNodes) => {
        const nodes = parentNodes.reduce ((result, parent) => {
      
          let newParent = Object.assign({}, parent);
          let newChildNodes: any[] = []; 
      
          newParent.children.map ((child) => {
            let foundChild = childNodes.find ((childNode) => child === childNode.id);
      
            if (foundChild) {
              let child = Object.assign({}, foundChild);
      
              child['disabled'] = false;
              newChildNodes.push(child);
            }
          });
      
          newParent['child_nodes'] = newChildNodes;
          newParent['disabled'] = false;
      
          result.push(newParent);
      
          return result;
        }, []);
      
        return nodes;
    }
      
    static getFilteredNodes = (nodes, level) => {
        return nodes.filter((item: any) => {
          return (
            item.level === level
          );
        });
    }
      
    static getSidebarList = (benefitCategoryNodes, planType, planPackage) => {
        return benefitCategoryNodes.reduce((result, item: any) => {
      
          if (item.tree_path[2] === planType && item.tree_path[3] === planPackage) {
            let newItem = Object.assign({}, item);
      
            if (planType !== 'BASE') {
              const baseNode = benefitCategoryNodes.find ((baseItem) => {
                return (
                  baseItem.name === newItem.name &&
                  baseItem.code === newItem.code &&
                  baseItem.tree_path[2] === 'BASE' &&
                  baseItem.tree_path[3] === newItem.tree_path[3] &&
                  baseItem.tree_path[4] === newItem.tree_path[4] 
                );
              });
      
              if (baseNode && baseNode.included === false) {
                newItem['base_disabled'] = true;
              } else {
                newItem['base_disabled'] = false;
              }
            };
      
            result.push(newItem);
          }
            
          return result;
        }, new Array());
    }
      
    static getRows = (exclusionList, benefitCategoryNodes, benefitTypeNodes, mainBenefitNodes, subBenefitNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory) => {
        let rows: any[] = [];
        if (['EX'].includes(selectedBenefitCategory)) {
          if (exclusionList.length > 0) {
            rows = exclusionList.reduce ((result, exclusionArray) => {
              const code = exclusionArray[0].code;
              
              if (code === selectedPlanType) {
                let exclusion = Object.assign({}, exclusionArray[exclusionArray.length - 1]);
      
                const parent = Object.assign({}, exclusionArray[exclusionArray.length - 2]);
      
                exclusion['parent_name'] = parent.name;
                exclusion['parent_code'] = parent.code;
                exclusion['disabled'] = false;
                exclusion['included'] = false;
      
                const found = benefitTypeNodes.find((item: any) => {
                  return item.id === exclusion.id;
                });
      
                if (found && found['custom_metadata'] && found['custom_metadata']['is_excluded'] !== undefined) {
                  const metadata = Object.assign({}, found['custom_metadata']);
                  exclusion['included'] = metadata['is_excluded'];
                }
      
                result.push(exclusion);
              }
      
              return result;
            }, []);
          }
        } else if (['ID','NT'].includes(selectedBenefitCategory)) {
          const categoryNodes = benefitCategoryNodes.filter ((item: any) => {
            return item.tree_path[2] === selectedPlanType &&
            item.tree_path[3] === selectedBenefitCategory;
          });
          const typeNodes = benefitTypeNodes.filter((item: any) => {
            return (
              item.tree_path[2] === selectedPlanType  &&
              item.tree_path[3] === selectedBenefitCategory
            );
          });
      
          rows = CBUtils.combineNodes2 (categoryNodes, typeNodes);
      
        } else if (['EL'].includes(selectedBenefitCategory)) {
          const benefitNodes = mainBenefitNodes.filter((item: any) => {
            return (
              item.tree_path[2] === selectedPlanType  &&
              item.tree_path[3] === selectedPlanPackage &&
              item.tree_path[4] === selectedBenefitCategory &&
              item.tree_path[5] === 'DP'
            );
          });
      
          const subNodes = subBenefitNodes.filter((item: any) => {
            return (
              item.tree_path[2] === selectedPlanType  &&
              item.tree_path[3] === selectedPlanPackage &&
              item.tree_path[4] === selectedBenefitCategory &&
              item.tree_path[5] === 'DP'
            );
          });
      
          rows = CBUtils.combineNodes2 (benefitNodes, subNodes);
      
          if (selectedPlanType !== 'BASE') {
              rows = rows.map ((item) => {
                item.child_nodes.map((child) => {
                  const baseNode = subBenefitNodes.find ((baseItem) => {
                    return (
                      baseItem.name === child.name &&
                      baseItem.code === child.code &&
                      baseItem.tree_path[2] === 'BASE' &&
                      baseItem.tree_path[3] === child.tree_path[3] &&
                      baseItem.tree_path[4] === child.tree_path[4] &&
                      baseItem.tree_path[5] === child.tree_path[5] &&
                      baseItem.tree_path[6] === child.tree_path[6] &&
                      baseItem.tree_path[7] === child.tree_path[7]
                    )
                  });
      
                  if (baseNode && baseNode.included === true) {
                    child['disabled'] = true;
                  } else {
                    child['disabled'] = false;
                  }
      
                  return child;
                })
                return item;
            });
          }
      
        } else if (['RBC'].includes(selectedBenefitCategory))  {
          rows = [...benefitCategoryNodes];
      
          rows = rows.filter((item: any) => {
            return (
              item.tree_path[2] === selectedPlanType  &&
              item.tree_path[3] === selectedPlanPackage 
            );
          });
      
          rows = rows.map ((item) => {
            item['disabled'] = false;
            return item;
          });
        } else if (['EM','DNT','CC','EC','PV','SH','ADD','SD'].includes(selectedBenefitCategory)) {
          rows = [...benefitTypeNodes];
      
          rows = rows.filter((item: any) => {
            return (
              item.tree_path[2] === selectedPlanType  &&
              item.tree_path[3] === selectedPlanPackage &&
              item.tree_path[4] === selectedBenefitCategory
            );
          });
      
          if (selectedPlanType !== 'BASE' && selectedBenefitCategory !== 'SH') {
            rows = rows.map ((item) => {
              const baseNode = benefitTypeNodes.find ((baseItem) => {
                return (
                  baseItem.name === item.name &&
                  baseItem.code === item.code &&
                  baseItem.tree_path[2] === 'BASE' &&
                  baseItem.tree_path[3] === item.tree_path[3] &&
                  baseItem.tree_path[4] === item.tree_path[4] &&
                  baseItem.tree_path[5] === item.tree_path[5] 
                )
              });
      
              if (baseNode && baseNode.included === true) {
                item['disabled'] = true;
              } else {
                item['disabled'] = false;
              }
      
              return item;
            });
          } else {
            rows = rows.map ((item) => {
              item['disabled'] = false;
              return item;
            });
          }
          return rows;
        } else if (['MT','APE', 'IP', 'OP', 'OM','PPE'].includes(selectedBenefitCategory)) {
          const typeNodes = benefitTypeNodes.filter ((item: any) => {
            return item.tree_path[2] === selectedPlanType &&
            item.tree_path[4] === selectedBenefitCategory;
          });
          const benefitNodes = mainBenefitNodes.filter((item: any) => {
            return (
              item.tree_path[2] === selectedPlanType &&
              item.tree_path[3] === selectedPlanPackage &&
              item.tree_path[4] === selectedBenefitCategory
            );
          });
      
          rows = CBUtils.combineNodes (typeNodes, benefitNodes);
      
          if (selectedPlanType !== 'BASE') {
            rows = rows.map ((item) => {
              const baseNode = mainBenefitNodes.find ((baseItem) => {
                return (
                  baseItem.name === item.name &&
                  baseItem.code === item.code &&
                  baseItem.tree_path[2] === 'BASE' &&
                  baseItem.tree_path[3] === item.tree_path[3] &&
                  baseItem.tree_path[4] === item.tree_path[4] &&
                  baseItem.tree_path[5] === item.tree_path[5] &&
                  baseItem.tree_path[6] === item.tree_path[6]
                )
              });
      
              if (baseNode && baseNode.included === true) {
                item['disabled'] = true;
              } else {
                item['disabled'] = false;
              }
      
              return item;
            });
          }
      
        } else {
          rows = mainBenefitNodes.filter((item: any) => {
              return (
                item.tree_path[2] === selectedPlanType &&
                item.tree_path[3] === selectedPlanPackage &&
                item.tree_path[4] === selectedBenefitCategory
              );
            }
          );
        }
      
        if (selectedBenefitCategory !== 'EX') {
          rows = rows.filter((item: any) => {
            if (item['custom_metadata'] && item['custom_metadata']['standard_exclusion'] !== undefined) {
              return item['custom_metadata']['standard_exclusion'] ? false : true;
            }
            return true;
          });
      
        } 
      
        return rows;
    }
      
    static getContractNode = (contractNodes) => {
        return contractNodes.find(item => 
          item.code === 'CT'
        );
    }
      
    static getCategoryNode = (planPackageNodes, benefitCategoryNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory) => {
        if (['RBC', 'EX'].includes(selectedBenefitCategory)) {
          return planPackageNodes.find(item => 
            item.code === selectedPlanPackage &&
            item.tree_path[2] === selectedPlanType && 
            item.tree_path[3] === selectedPlanPackage 
          );
        } else if (selectedBenefitCategory === 'NT') {
            return planPackageNodes.find(item => 
              item.code === selectedBenefitCategory &&
                item.tree_path[2] === selectedPlanType && 
                item.tree_path[3] === selectedBenefitCategory 
              );
        } else {
          return benefitCategoryNodes.find(item => 
            item.code === selectedBenefitCategory &&
            item.tree_path[2] === selectedPlanType && 
            item.tree_path[3] === selectedPlanPackage && 
            item.tree_path[4] === selectedBenefitCategory
          );
        }
    }    
    

    static getPlanTypeList = (planTypeNodes, mainBenefitNodes, benefitTypeNodes, subBenefitNodes) => {
      const coverageCategories = ['EM','SD','PV'];
      const riderCategories = ['DNT','EC','CC','SH','ADD'];
      // const combinedCategories = [...coverageCategories, ...riderCategories];
      const combinedNodes = [...mainBenefitNodes, ...benefitTypeNodes, ...subBenefitNodes];

      return planTypeNodes.map((item: any) => {
        item['count'] = combinedNodes.reduce ((result, benefit) => {
            if (  benefit.level === 6 &&
                benefit.tree_path[2] === item.code &&
                benefit.included && 
                ['CV'].includes(benefit.tree_path[3]) &&  
                coverageCategories.includes(benefit.tree_path[4])
            ) {
            if (  benefit.tree_path[2] !== 'BASE' ) {
              const baseNode = benefitTypeNodes.find ((baseItem) => {
                return (
                  baseItem.name === benefit.name &&
                  baseItem.code === benefit.code &&
                  baseItem.tree_path[2] === 'BASE' &&
                  baseItem.tree_path[3] === benefit.tree_path[3] &&
                  baseItem.tree_path[4] === benefit.tree_path[4] &&
                  baseItem.tree_path[5] === benefit.tree_path[5] 
                )
              });

              if (baseNode && !baseNode.included) {
                result++;
              } 
            } else {
              result++;
            }
          } else if (  benefit.level === 6 &&
                benefit.tree_path[2] === item.code &&
                benefit.included && 
                ['RI'].includes(benefit.tree_path[3]) &&  
                riderCategories.includes(benefit.tree_path[4]) 
            ) {
            if (  benefit.tree_path[2] !== 'BASE' ) {
              const baseNode = benefitTypeNodes.find ((baseItem) => {
                return (
                  baseItem.name === benefit.name &&
                  baseItem.code === benefit.code &&
                  baseItem.tree_path[2] === 'BASE' &&
                  baseItem.tree_path[3] === benefit.tree_path[3] &&
                  baseItem.tree_path[4] === benefit.tree_path[4] &&
                  baseItem.tree_path[5] === benefit.tree_path[5] 
                )
              });

              if (baseNode && !baseNode.included && benefit.tree_path[4] !== 'SH') {
                result++;
              } 
            } else {
              result++;
            }
          } else if ( benefit.level === 7 &&
                    benefit.tree_path[2] === item.code && 
                    benefit.included &&
                    !['EM', 'EC', 'EL'].includes(benefit.tree_path[4])
              ) {
            if (benefit.tree_path[2] !== 'BASE') {
              const baseNode = mainBenefitNodes.find ((baseItem) => {
                return (
                  baseItem.name === benefit.name &&
                  baseItem.code === benefit.code &&
                  baseItem.tree_path[2] === 'BASE' &&
                  baseItem.tree_path[3] === benefit.tree_path[3] &&
                  baseItem.tree_path[4] === benefit.tree_path[4] &&
                  baseItem.tree_path[5] === benefit.tree_path[5] &&
                  baseItem.tree_path[6] === benefit.tree_path[6]
                )
              });

              if (baseNode && !baseNode.included) {
                result++;
              } 
            } else {
              result++;
            }
          } else if ( benefit.level === 8 &&
                    benefit.tree_path[2] === item.code && 
                    benefit.included  &&
                    !['OP'].includes(benefit.tree_path[4])
              ) {
            if (benefit.tree_path[2] !== 'BASE') {
              const baseNode = mainBenefitNodes.find ((baseItem) => {
                return (
                  baseItem.name === benefit.name &&
                  baseItem.code === benefit.code &&
                  baseItem.tree_path[2] === 'BASE' &&
                  baseItem.tree_path[3] === benefit.tree_path[3] &&
                  baseItem.tree_path[4] === benefit.tree_path[4] &&
                  baseItem.tree_path[5] === benefit.tree_path[5] &&
                  baseItem.tree_path[6] === benefit.tree_path[6]
                )
              });

              if (baseNode && !baseNode.included) {
                result++;
              } 
            } else {
              result++;
            }
          }
          return result;
        }, 0);

        return item;
      });
    };

    static generatePlanNodes = (baseNodes: any[], name: string, code: string, tmp_data: any, basePlanType: string = 'BASE') => {
      let planNodes = [...baseNodes];
      let planTypeID = '';

      planNodes = planNodes.filter((item: any) => {
        return item.level > 2 && item.tree_path[2] === basePlanType
      });

      let nodeIDMap: any[] = [];

      planNodes = planNodes.map ((item: any, i: number) => {
        let newItem = Object.assign({}, item);

        const oldID = newItem['id'];
        const newID = "NODE-" + code + "-"+i.toString().padStart(3, '0')

        if ((newItem.level > 2 && newItem.tree_path[2] === basePlanType)
        ) {
          newItem.tree_path = Object.assign({}, newItem.tree_path);
          newItem.tree_path[2] = code;
        } 

        if ((newItem.level === 3 && newItem.code === basePlanType)) {
          newItem['name'] = name;
          newItem['code'] = code;
          if (!newItem['custom_metadata']) {
            newItem['custom_metadata'] = {};
          }
          if (!newItem['custom_metadata_schema']) {
            newItem['custom_metadata_schema'] = {};
          }
          newItem['custom_metadata'] = Object.assign({}, newItem.custom_metadata);
          newItem['description'] = 'Specific Plan Type Template';
          newItem['sort_index'] = 2;
          newItem['custom_metadata']['title'] = "Benefits for " + name;
          newItem["custom_metadata"]["contract_plan_type"] = tmp_data['contractPlanType'] ? tmp_data['contractPlanType'] : (tmp_data['requestedRate'] ? 'HMO' : '');
          newItem["custom_metadata"]["requested_rate"] = tmp_data['requestedRate'] ? tmp_data['requestedRate'] : 0;
          newItem["custom_metadata"]["admin_fee"] = tmp_data['adminFee'] ? tmp_data['adminFee'] : 0;
          newItem["custom_metadata"]["naf"] = tmp_data['naf'] ? tmp_data['naf'] : 0;
          newItem["custom_metadata"]["rsd"] = tmp_data['rsdFee'] ? tmp_data['rsdFee'] : 0;

          newItem["custom_metadata"]["indicative_principals"] = tmp_data['indicativeNumOfPrincipals'] ? tmp_data['indicativeNumOfPrincipals'] : 0;
          newItem["custom_metadata"]["indicative_dependents"] = tmp_data['indicativeNumOfDependents'] ? tmp_data['indicativeNumOfDependents'] : 0;
          newItem['custom_metadata_schema'] = {
            "properties":{
              "title": {
                "type": "string"
              },
              "contract_plan_type": {
                "type": "string"
              },
              "requested_rate": {
                "type": "number"
              },
              "admin_fee": {
                "type": "number"
              },
              "naf": {
                "type": "number"
              },
              "rsd": {
                "type": "number"
              },
              "indicative_principals": {
                "type": "number"
              },
              "indicative_dependents": {
                "type": "number"
              }
            }
          };
          
          planTypeID = newID;
        }

        const IDSet = {
          id: oldID,
          newID: newID
        };

        nodeIDMap.push (IDSet);
        newItem['id'] = newID;
        newItem['edited'] = false;

        delete newItem.original_children;
        delete newItem.original_id;

        return newItem;
      });

      planNodes = planNodes.map ((item: any) => {
        if (item['children'].length > 0) {
          item['children'] = item['children'].reduce ((result, id) => {
            const found = nodeIDMap.find ((set) => { return set.id === id; });

            if (found) {
              result.push (found.newID);
            }
            return result;
          }, []);
        }

        if (item['limits']) {
          item['limits'] = [...item['limits']]
        }

        if (item['custom_metadata'] && item['custom_metadata']['partner_network_list']) {
          let metadata = Object.assign({}, item['custom_metadata']);

          let newPartnerNetworkList = [...item['custom_metadata']['partner_network_list']];

          newPartnerNetworkList = newPartnerNetworkList.reduce((result, partner: any) => {
            let newPartner = Object.assign({}, partner);

            newPartner['parent'] = item['id'];

            result.push(newPartner);
            return result;
          }, new Array());

          metadata['partner_network_list'] = newPartnerNetworkList;

          item['custom_metadata'] = metadata;
        }

        if (item['original_node']) {
          const found = nodeIDMap.find ((set) => { return set.id === item['original_node']; });
            
          if (found) {
            item['original_node'] = found.newID;
          }
        }
        return item;
      });

      return {planNodes, planTypeID};
    }

    static combineBaseBenefitNodes = (baseNodes: any[], benefitNodes: any[], addFlag = false) => {
      // Get all the plan types
      const planTypeNodes = benefitNodes.reduce((result, node) => {
        if (node.level === 3 && node.code !== "BASE") {
          result.push(node);
        }
        return result;
      }, []);

      let nodes = [...baseNodes];
      
      // remove the base eligiblity nodes
      nodes = nodes.filter ((item: any) => {
        return !([7,8].includes(item.level) && item.tree_path[3] === 'TC' && item.tree_path[4] === 'EL');
      });

      nodes = nodes.map ((item: any) => {
        if (item.level === 6 &&  item.tree_path[3] === 'TC' && item.tree_path[4] === 'EL') {
          let newItem = Object.assign({}, item);

          newItem.children = newItem.children.filter(child =>
                nodes.find((node) => node.id === child) ? true : false
              );
              return newItem;
          } else {
            return item;
          }
      });

      // Duplicate the base nodes for every non-base plan type
      nodes = planTypeNodes.reduce ((result, planTypeNode) => {
        const {planNodes, planTypeID} = CBUtils.generatePlanNodes(result, planTypeNode.name, planTypeNode.code, planTypeNode);

        const index = result.findIndex(item => 
          item.level === 2 && 
          item.code === 'CT'
        );

        result[index]['children'].push(planTypeID);

        return [...result, ...planNodes];
      }, nodes);

      // Overlay the benefit nodes array to the base nodes array and return
      nodes = nodes.map ((node: any) => {
        let newNode = Object.assign({}, node);

        const found = benefitNodes.find((benefit: any) => {
          if (newNode.level === 8) {
            return  newNode.name === benefit.name &&
                      newNode.code === benefit.code &&
                      newNode.level === benefit.level && 
                      newNode.tree_path[2] === benefit.tree_path[2] &&
                      newNode.tree_path[3] === benefit.tree_path[3] &&
                      newNode.tree_path[4] === benefit.tree_path[4] &&
                      newNode.tree_path[5] === benefit.tree_path[5] &&
                      newNode.tree_path[6] === benefit.tree_path[6] &&
                      newNode.tree_path[7] === benefit.tree_path[7];
          } else if (newNode.level === 7) {
            return  newNode.name === benefit.name &&
                      newNode.code === benefit.code &&
                      newNode.level === benefit.level && 
                      newNode.tree_path[2] === benefit.tree_path[2] &&
                      newNode.tree_path[3] === benefit.tree_path[3] &&
                      newNode.tree_path[4] === benefit.tree_path[4] &&
                      newNode.tree_path[5] === benefit.tree_path[5] &&
                      newNode.tree_path[6] === benefit.tree_path[6];
          } else if (newNode.level === 6) {
            return  newNode.name === benefit.name &&
                      newNode.code === benefit.code &&
                      newNode.level === benefit.level && 
                      newNode.tree_path[2] === benefit.tree_path[2] &&
                      newNode.tree_path[3] === benefit.tree_path[3] &&
                      newNode.tree_path[4] === benefit.tree_path[4] &&
                      newNode.tree_path[5] === benefit.tree_path[5];
          } else if (newNode.level === 5) {
            return  newNode.name === benefit.name &&
                      newNode.code === benefit.code &&
                      newNode.level === benefit.level && 
                      newNode.tree_path[2] === benefit.tree_path[2] &&
                      newNode.tree_path[3] === benefit.tree_path[3] &&
                      newNode.tree_path[4] === benefit.tree_path[4];
          } else if (newNode.level === 4) {
            return  newNode.name === benefit.name &&
                      newNode.code === benefit.code &&
                      newNode.level === benefit.level && 
                      newNode.tree_path[2] === benefit.tree_path[2] &&
                      newNode.tree_path[3] === benefit.tree_path[3];
          } else if (newNode.level === 3) {
            return  newNode.name === benefit.name &&
                      newNode.code === benefit.code &&
                      newNode.level === benefit.level && 
                      newNode.tree_path[2] === benefit.tree_path[2];
          } else if (newNode.code === 'CT') {
            return  benefit.code === 'CT';
          } else if (newNode.code === 'MCT') {
            return  benefit.code === 'MCT';
          } else {
            return  newNode.name === benefit.name &&
                      newNode.code === benefit.code &&
                      newNode.level === benefit.level;
          } 
        });

        if (found) {
          const foundNode = Object.assign({}, found);
          delete foundNode.id;
          delete foundNode.children;
          delete foundNode.node_path;
          delete foundNode.tree_id;
          delete foundNode.tree_path;

          newNode = Object.assign({}, newNode, foundNode);

          if (addFlag && newNode.level > 3 && newNode.tree_path[2] !== 'BASE') {
            newNode['edited'] = true;
          }
        } else {
          if (newNode.level > 6 || 
            (newNode.level === 5 && ['RI','CV','RB','ID'].includes(newNode.tree_path[3])) ||
            (newNode.level === 6 && newNode.tree_path[3] === 'EX') ||
            (newNode.level === 6 && newNode.tree_path[3] === 'RI' && newNode.tree_path[4] === 'OM') ||
            (newNode.level === 6 && ['EM','CC','EC','DNT','ADD','SH','SD','PV'].includes(newNode.tree_path[4]))
            ) {

            newNode['included'] = false;
          } 
        } 

        return newNode;
      });

      nodes.map ((node: any, i: number) => {
        if (node.level > 3 && node.tree_path[2] !== 'BASE' && node.tree_path[3] !== 'RB') {
          const baseIndex = CBUtils.getMatchByPlanCode(nodes, nodes[i], 'BASE');

          if (addFlag && (node.tree_path[3] === 'EX' || (node.tree_path[3] === 'RI' && node.tree_path[4] === 'OM'))) {
            if (baseIndex > -1 && 
              nodes[baseIndex]['custom_metadata']['is_excluded'] && 
              nodes[baseIndex]['custom_metadata']['is_excluded'] === true
            ) {
              let newNode = Object.assign({}, nodes[i]);

              if (addFlag) {
                newNode['is_base'] = true;
              }

              nodes[i] = newNode;
            }
          } else {
            if (baseIndex > -1 && nodes[baseIndex]['included'] && nodes[baseIndex]['included'] === true) {
              let newNode = Object.assign({}, nodes[i]);

              if (newNode['included'] === false) {
                const foundNode = Object.assign({}, nodes[baseIndex]);
                delete foundNode.id;
                delete foundNode.children;
                delete foundNode.original_id;
                delete foundNode.original_children;
                delete foundNode.node_path;
                delete foundNode.tree_id;
                delete foundNode.tree_path;

                newNode = Object.assign({}, newNode, foundNode);
                
                newNode['included'] = true;
              }
              
              if (addFlag && !newNode['edited']) {
                newNode['is_base'] = true;
              }

              nodes[i] = newNode;
            }

          }
        }
      });
      

      let count = 0;

      let eligiblityNodes = benefitNodes.filter ((benefit: any) => {
        return [7,8].includes(benefit.level) && benefit.tree_path[3] === 'TC' && benefit.tree_path[4] === 'EL';
      });

      eligiblityNodes =  eligiblityNodes.sort ((node1: any, node2: any) => {
        if (node1.level < node2.level) return -1;
        if (node1.level > node2.level) return 1;

        return 0;
      });

      eligiblityNodes.map ((benefit: any) => {
            const newID = "NODE-LOAD-"+(count).toString().padStart(3, '0');
            const parentLevel = benefit.level - 1;
            const parentCode = benefit.tree_path[benefit.level-2];
            const parentIndex = nodes.findIndex ((node) => {
              if (benefit.level === 5) {
                return  parentCode === node.code &&
                      parentLevel === node.level && 
                      benefit.tree_path[2] === node.tree_path[2] &&
                      benefit.tree_path[3] === node.tree_path[3];
              } else if (benefit.level === 6) {
                return  parentCode === node.code &&
                      parentLevel === node.level && 
                      benefit.tree_path[2] === node.tree_path[2] &&
                      benefit.tree_path[3] === node.tree_path[3] &&
                      benefit.tree_path[4] === node.tree_path[4];
              } else if (benefit.level === 7) {
                return  parentCode === node.code &&
                      parentLevel === node.level && 
                      benefit.tree_path[2] === node.tree_path[2] &&
                      benefit.tree_path[3] === node.tree_path[3] &&
                      benefit.tree_path[4] === node.tree_path[4] &&
                      benefit.tree_path[5] === node.tree_path[5];
              } else if (benefit.level === 8) {
                return  parentCode === node.code &&
                      parentLevel === node.level && 
                      benefit.tree_path[2] === node.tree_path[2] &&
                      benefit.tree_path[3] === node.tree_path[3] &&
                      benefit.tree_path[4] === node.tree_path[4] &&
                      benefit.tree_path[5] === node.tree_path[5] &&
                      benefit.tree_path[6] === node.tree_path[6];
              }
              return false;
            });

          let newNode = Object.assign({}, benefit);
            newNode['children'] = [];
          if(nodes[parentIndex] && nodes[parentIndex] !== undefined){
            let newChildren = [...nodes[parentIndex].children];
            newChildren.push(newID);

            nodes[parentIndex]['children'] = newChildren;
          }

          newNode['included'] = true;
          newNode['id'] = newID;

          nodes.push(newNode);
          count++;
      
      });

      return nodes;
    }

    static getMatchByPlanCode (nodes: any[], baseNode: any, planCode: string) {
        const planIndex = nodes.findIndex((item: any) => {
            if (baseNode.level === 8) {
                return  baseNode.name === item.name &&
                        baseNode.code === item.code &&
                        baseNode.level === item.level && 
                        planCode === item.tree_path[2] &&
                        baseNode.tree_path[3] === item.tree_path[3] &&
                        baseNode.tree_path[4] === item.tree_path[4] &&
                        baseNode.tree_path[5] === item.tree_path[5] &&
                        baseNode.tree_path[6] === item.tree_path[6] &&
                        baseNode.tree_path[7] === item.tree_path[7];
            } else if (baseNode.level === 7) {
                return  baseNode.name === item.name &&
                        baseNode.code === item.code &&
                        baseNode.level === item.level && 
                        planCode === item.tree_path[2] &&
                        baseNode.tree_path[3] === item.tree_path[3] &&
                        baseNode.tree_path[4] === item.tree_path[4] &&
                        baseNode.tree_path[5] === item.tree_path[5] &&
                        baseNode.tree_path[6] === item.tree_path[6];
            } else if (baseNode.level === 6) {
                return  baseNode.name === item.name &&
                        baseNode.code === item.code &&
                        baseNode.level === item.level && 
                        planCode === item.tree_path[2] &&
                        baseNode.tree_path[3] === item.tree_path[3] &&
                        baseNode.tree_path[4] === item.tree_path[4] &&
                        baseNode.tree_path[5] === item.tree_path[5];
            } else if (baseNode.level === 5) {
                return  baseNode.name === item.name &&
                        baseNode.code === item.code &&
                        baseNode.level === item.level && 
                        planCode === item.tree_path[2] &&
                        baseNode.tree_path[3] === item.tree_path[3] &&
                        baseNode.tree_path[4] === item.tree_path[4];
            } else if (baseNode.level === 4) {
                return  baseNode.name === item.name &&
                        baseNode.code === item.code &&
                        baseNode.level === item.level && 
                        planCode === item.tree_path[2] &&
                        baseNode.tree_path[3] === item.tree_path[3];
            } else if (baseNode.level === 3) {
                return  baseNode.name === item.name &&
                        baseNode.code === item.code &&
                        baseNode.level === item.level && 
                        planCode === item.tree_path[2];
            } else {
                return  baseNode.name === item.name &&
                        baseNode.code === item.code &&
                        baseNode.level === item.level;
            } 
        });
        return planIndex;
    }

}