import * as React from 'react';
import './component-style.scss';
import { Loading } from './Loading';

interface IProps {
    height?: string;
    color?: string;
    size?: number;
    position?: 'fixed' | 'absolute';
}

export class LoadingBlock extends React.Component<IProps> {

    render(): JSX.Element {
        return (
            <div
                style={{
                    backgroundColor: '#00000055',
                    zIndex: 1000,
                    position: this.props.position || 'fixed',
                    width: '100%',
                    height: '100%'}}>
                <Loading 
                    height={this.props.height || '15rem'}
                    size={this.props.size || 100}
                    color={this.props.color} />
            </div>

        );
    }
}