//#region Global Imports
import * as React from 'react';
import { useState } from 'react';
import { BenefitsTable } from 'Components/UI/TableComponent/BenefitsTable';
import Grid from '@material-ui/core/Grid';
// import Hidden from '@material-ui/core/Hidden';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import Typography from '@material-ui/core/Typography';

import { ViewField } from '../../ViewField';

//import { Components } from '@hims/core';
//#endregion Global Imports


import { defaultProps } from './Constants';
import { ICategorySubForm } from './ICategorySubForm';

export const APEForm: React.FC<ICategorySubForm.IProps> = (
	props: ICategorySubForm.IProps,
): JSX.Element | null => {
	const { formHeader, viewOnly, setSubForm, rows, subFormNode } = props;
  	const [blankValue, setBlankValue] = useState(false);

	let metadata:any = {};
	if (subFormNode) {
		if (subFormNode['custom_metadata']) {
			metadata = subFormNode['custom_metadata'];
		}
	}

	const columns: any = [
		{ name: 'parent_name', title: 'Category' },
		{ name: 'name', title: 'Description' },
		{ 
			name: 'eligible_availees', 
			title: 'Eligible Availees',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.eligible_availees 
								 	? row.custom_metadata.eligible_availees : []),
		},
		{ 
			name: 'age_limit', 
			title: 'Minimum Age', 
			getCellValue: row => (row.custom_metadata && row.custom_metadata.age_limit 
								 	? row.custom_metadata.age_limit : 0),
		},
		{ 
			name: 'max_availee', 
			title: 'Max. Availee', 
			getCellValue: row => (row.custom_metadata && row.custom_metadata.max_availee 
								 	? row.custom_metadata.max_availee : 0),
		},
		{ 
			name: 'payment', 
			title: 'Payment',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.payment 
								 	? row.custom_metadata.payment : ''),
		},
		{ 
			name: 'required', 
			title: 'Required',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.required 
								 	? row.custom_metadata.required : false),
		},
	];

	const formattedColumns = {
		eligible_availees: ['eligible_availees'],
		age_limit: ['age_limit'],
		required: ['required'],
		number: ['max_availee'],
		payment: ['payment'],
	};

	const columnExtensions = [
		{ columnName: 'parent_name', width: 150, wordWrapEnabled: true },
		{ columnName: 'name', width: 250, wordWrapEnabled: true },
		{ columnName: 'eligible_availees', width: 150, wordWrapEnabled: true },
		{ columnName: 'age_limit', width: 150, wordWrapEnabled: true },
		{ columnName: 'max_availee', width: 150, wordWrapEnabled: true },
		{ columnName: 'payment', width: 150, wordWrapEnabled: true },
		{ columnName: 'required', width: 150, wordWrapEnabled: true },
	];

	let finalRows = rows;
	


	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2}>
					{!viewOnly ? formHeader : null}
				</Grid>
			</Grid>
			<Grid item sm={12} >
				<BenefitsTable
					viewOnly={viewOnly}
					columns={columns}
					formattedColumns={formattedColumns}
					columnExtensions={columnExtensions}
					{...props}
					rows={finalRows}
				/>
			</Grid>
			<Grid item sm={12}>
				{!viewOnly ? (
					<>
					<Typography variant="overline" display="block" gutterBottom>
						Mobile APE
					</Typography>
					<React.Fragment>
						<Typography variant="body1" gutterBottom>
							Min no. of availees in a shift (8 hrs)
						</Typography>
						<OutlinedInput 
							onChange={(e) => {
					        	setBlankValue(false);
								setSubForm(e);
							}}
							value={blankValue ? '' : (metadata['mobile_ape_availees'] ? metadata['mobile_ape_availees'] : 0)}
							type="number" 
							onKeyPress={(e)=>{
								if(e.key==='-'||e.key==='.'||e.key==='e')
								{e.preventDefault();return false;}
								else
								{
									return e.key;
								}
							}}
					        onFocus={() => {
					          if (!metadata['mobile_ape_availees'] || metadata['mobile_ape_availees'] === 0 || metadata['mobile_ape_availees'] === '0') {
					            setBlankValue(true);
					          }
					        }}
					        onBlur={() => {
					        	setBlankValue(false);
					        }}
							labelWidth={0} 
							name="mobile_ape_availees" 
							id="mobileAPEs"
							inputProps={{min:'0'}} />
					</React.Fragment>
					</>
				) : (
					metadata['mobile_ape_availees'] ? (
						<>
						<Typography variant="overline" display="block" gutterBottom>
							Mobile APE
						</Typography>
						<ViewField 
							label="Min no. of availees in a shift (8 hrs)"
							value={metadata['mobile_ape_availees']} />
						</>
					) : null
				)}
			</Grid>
		</React.Fragment>
	);
};

APEForm.defaultProps = defaultProps;