import * as React from 'react';
import {  Grid, TableCell, TableRow } from '@material-ui/core';
import { faMinusCircle, faPlus, faPen, faCheckCircle, faTimesCircle } from '@fortawesome/free-solid-svg-icons';
import { DetailedDropdown } from 'Pages/CustomerCarePage/Components/DetailedDropdown';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { ProcedureData, DropdownChoice, AvailmentDetails, DiagnosisData } from 'Pages/CustomerCarePage/Models';
import { NumberField } from 'Pages/CustomerCarePage/Components/NumberField';
import { CustomCheckbox } from 'Pages/CustomerCarePage/Components/CustomCheckbox';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
import { Global } from 'Pages/CustomerCarePage/Global';

interface IProps {
    availment: AvailmentDetails;
    facilities: DropdownChoice[];
    doctors: DropdownChoice[];
    createTicketAttemp: boolean;
    refreshAvailmentState: () => void;
    loadDoctors: (facilityCode: string) => void;
    loadFacilities: () => void;
}

interface IStates {
    icd10Code: DropdownChoice;
    doctor: DropdownChoice;
    doctorFeeHeader: number;

    itemToEditIndex: number;
    icd10CodeToEdit: DropdownChoice;
    doctorToEdit: DropdownChoice;
    doctorFeeToEdit: number;
}

export class AvailmentSection extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            icd10Code: { value: '', label: '' },
            doctor: { value: '', label: '' },
            doctorFeeHeader : 0,

            itemToEditIndex: -1,
            icd10CodeToEdit: { value: '', label: '' },
            doctorToEdit: { value: '', label: '' },
            doctorFeeToEdit: 0
        };
    }

    getFilteredICD10 = () => {
        if (Global.ICD10_CODES) {
            return Global.ICD10_CODES.filter(icd10Code => {
                return this.props.availment.diagnoses.find(diagnosis => {
                    return diagnosis.icd10Code.value === icd10Code.value;
                }) === undefined
            });
        }
        return [];
    }

    getFilteredCptCodes = (diagnosis:DiagnosisData) => {
        console.log(diagnosis);
        if(Global.CPT_CODES){
            return Global.CPT_CODES.filter(cptCodes =>{
                return diagnosis.procedures.find(procedures =>{
                    return procedures.cptCode.value === cptCodes.value
                }) === undefined
            })
        }
        return [];
    }

    getProcedureCosts = (index : number) => {
        if(this.props.availment !== undefined &&
           this.props.availment.diagnoses !== undefined &&
           this.props.availment.diagnoses[index] !== undefined &&
           this.props.availment.diagnoses[index].doctors !== undefined &&
           this.props.availment.diagnoses[index].doctors[0] !== undefined &&
           this.props.availment.diagnoses[index].doctors[0].fee !== undefined){
                return this.props.availment.diagnoses[index].doctors[0].fee
        }
        return 0
    }
    
    pushBlankDiagnosis = (icd10Code: DropdownChoice, doctor: DropdownChoice, doctorTotalValue : number) => {
        this.props.availment.diagnoses.push({
            icd10Code: icd10Code,
            remarks: '',
            isPrimary: false,
            isFinal: false,
            dateAdded: new Date(),
            doctors: [{
                id: doctor.value,
                name: doctor.label,
                specialization: doctor.params ? doctor.params[0] : '',
                type: 'Admitting',
                fee: doctorTotalValue,
                noOfVisits: 0,
                remarks: ''
            }],
            procedures: [{
                cptCode: { value: '', label: '' },
                isPHIC: false,
                amount: 0,
                notes: '',
                doctor: '',
                dateAdded: new Date(),
                assessmentType: '',
                assessmentRemarks: '',
                approvedAmount: 0,
                loaNo: 0
            }],
            assessmentType: '',
            assessmentRemarks: '',
            approvedAmount: 0,
            loaNo: 0
        });
    }

    pushBlankProcedures = (diagnosisIndex: number) => {
        this.props.availment.diagnoses[diagnosisIndex].procedures.push({
            cptCode: { value: '', label: '' },
            isPHIC: false,
            amount: 0,
            notes: '',
            doctor: '',
            dateAdded: new Date(),
            assessmentType: '',
            assessmentRemarks: '',
            approvedAmount: 0,
            loaNo: 0
        });
    }

    render(): JSX.Element {
        return (
            <div className='availment-section'>
                <div style={{paddingTop: '5rem'}} />
                <div className='sub-section'>
                    <div className='header-2'>AVAILMENT DETAILS</div>
                    <DetailedDropdown 
                        choices={this.props.facilities}
                        placeholder='Provider*'
                        value={this.props.availment.provider}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.availment.provider.label === ''}
                        onFocus={() => {
                            this.props.loadFacilities();
                        }}
                        onChange={(value: DropdownChoice) => {
                            this.props.loadDoctors(value.label);
                            this.props.availment.provider = value;
                            this.props.refreshAvailmentState();
                        }} />
                </div>
                <div className='sub-section'>
                    <div className='header-3'>LIST OF AVAILMENTS</div>
                    <div className='text'>
                        Add a diagnosis first then add procedures per diagnosis after
                    </div>
                    <div className='sub-section'>
                        <CustomTable
                            rows={this.props.availment.diagnoses}
                            renderHeader={() => this.renderTableHeader()}
                            renderRow={(rowData: any, index: number) => 
                                this.renderTableRow(rowData, index)} />
                    </div>
                </div>
                <div className='section' />
            </div>
        );
    }

    renderTableHeader(): JSX.Element {
        return (
            <>
            <TableCell style={{minWidth: '200px', maxWidth: '200px'}}>
                <DetailedDropdown 
                    placeholder={'Add a diagnosis'}
                    choices={this.getFilteredICD10()}
                    value={this.state.icd10Code}
                    onChange={(value: DropdownChoice) => {
                        this.setState({icd10Code: value});
                    }} />
            </TableCell>
            <TableCell style={{minWidth: '150px', maxWidth: '150px'}}>
                <Dropdown 
                    placeholder={'Add doctors'}
                    choices={this.props.doctors}
                    value={this.state.doctor}
                    onChange={(value: DropdownChoice) => {
                        this.setState({doctor: value});
                    }} />
            </TableCell>
            <TableCell style={{minWidth: '70px', maxWidth: '70px'}}>
                <NumberField
                    placeholder={'Fee'}
                    value={this.state.doctorFeeHeader ? this.state.doctorFeeHeader : 0}
                    onChange={(value: number) => {
                    this.setState({
                        doctorFeeHeader : value
                        })
                }} />
            </TableCell>
            <TableCell style={{textAlign: 'center'}}>
                <TextButton 
                    icon={faPlus} 
                    label='Add diagnosis'
                    onClick={() => {
                        console.log(this.state.doctor)
                        if (this.state.doctor.value !== '' && this.state.icd10Code.value !== '') {
                            this.pushBlankDiagnosis(this.state.icd10Code, this.state.doctor, this.state.doctorFeeHeader);
                            this.props.refreshAvailmentState();
                            this.setState({
                                icd10Code: { value: '', label: '' },
                                doctor: { value: '', label: '' },
                                doctorFeeHeader: 0,
                            });
                        }
                    }} />
            </TableCell>
            </>
        );
    }

    renderTableRow(diagnosis: DiagnosisData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        return (
            <TableRow style={{backgroundColor: backgroundColor}}>
                <TableCell colSpan={4}>
                    <Grid container spacing={1}>
                        {this.state.itemToEditIndex === index ? (
                            <>
                            <Grid item xs={6}>
                                <DetailedDropdown 
                                    choices={this.getFilteredICD10()}
                                    value={this.state.icd10CodeToEdit}
                                    onChange={(value: DropdownChoice) => {
                                        this.setState({icd10CodeToEdit: value});
                                    }} />
                            </Grid>
                            <Grid item xs={3}>
                                <Dropdown 
                                    choices={this.props.doctors}
                                    value={this.state.doctorToEdit}
                                    onChange={(value: DropdownChoice) => {
                                        this.setState({doctorToEdit: value});
                                    }} />
                            </Grid>
                            <Grid item xs={2}>
                                <NumberField
                                    value={this.state.doctorFeeToEdit ? this.state.doctorFeeToEdit : 0}
                                    onChange={(value: number) => {
                                    this.setState({
                                        doctorFeeToEdit : value
                                    })
                                }} />
                            </Grid>
                            <Grid item xs={1}>
                                <IconButton
                                    icon={faCheckCircle}
                                    onClick={() => {
                                        this.props.availment.diagnoses[index].icd10Code = this.state.icd10CodeToEdit;
                                        this.props.availment.diagnoses[index].doctors[0].name = this.state.doctorToEdit.label;
                                        this.props.availment.diagnoses[index].doctors[0].fee = this.state.doctorFeeToEdit;
                                        this.props.refreshAvailmentState();
                                        this.setState({itemToEditIndex: -1});
                                    }}/>
                                <IconButton
                                    icon={faTimesCircle}
                                    onClick={() => {
                                        this.setState({itemToEditIndex: -1});
                                    }}/>
                            </Grid>
                            </>
                        ) : (
                            <>
                            <Grid item xs={6} className='header-2'>
                                {diagnosis.icd10Code.value + ' - ' + diagnosis.icd10Code.label}
                            </Grid>
                            <Grid item xs={3} className='header-2'>
                                {diagnosis.doctors[0].name}
                            </Grid>
                            <Grid item xs={2} className='header-2'>
                                {GlobalFunction.formatNumber(this.getProcedureCosts(index), true) + ' PHP'}
                            </Grid>
                            <Grid item xs={1}>
                                <IconButton
                                    icon={faPen}
                                    onClick={() => {
                                        this.setState({
                                            itemToEditIndex: index,
                                            icd10CodeToEdit: diagnosis.icd10Code,
                                            doctorToEdit: {
                                                value: diagnosis.doctors[0].id,
                                                label: diagnosis.doctors[0].name
                                            },
                                            doctorFeeToEdit : diagnosis.doctors[0].fee,
                                        });
                                    }}/>
                                <IconButton
                                    icon={faMinusCircle}
                                    onClick={() => {
                                        this.props.availment.diagnoses.splice(index, 1);
                                        this.props.refreshAvailmentState();
                                    }}/>
                            </Grid>
                            </>
                        )}
                    </Grid>
                    {diagnosis.procedures.map((procedure, procedureIndex) => {
                        return this.renderProcedureDetails(diagnosis,procedure, index, procedureIndex);
                    })}
                </TableCell>
            </TableRow>
        );
    }
    
    renderProcedureDetails(diagnosis: DiagnosisData, procedure: ProcedureData, diagnosisIndex: number, index: number): JSX.Element {
        const stateProcedure = this.props.availment.diagnoses[diagnosisIndex].procedures[index];
        const stateProcedures = this.props.availment.diagnoses[diagnosisIndex].procedures;
        return (
            <div style={{position: 'relative'}}>
                <div style={{paddingTop: '0.5rem'}} />
                <div className='line-border' />
                <div style={{paddingTop: '0.5rem'}} />
                <Grid container spacing={2}>
                    <Grid item xs={7}>
                        <DetailedDropdown 
                            placeholder='Procedure'
                            choices={Global.CPT_CODES ? this.getFilteredCptCodes(diagnosis) : []}
                            value={procedure.cptCode}
                            onChange={(value: DropdownChoice) => {
                                stateProcedure.cptCode = value;
                                if (index === (stateProcedures.length - 1)) {
                                    this.pushBlankProcedures(diagnosisIndex);
                                }
                                this.props.refreshAvailmentState();
                            }} />
                    </Grid>
                    <Grid item xs={3}>
                        <NumberField
                            placeholder='Amount'
                            error={
                                this.props.createTicketAttemp &&
                                procedure.cptCode.value !== '' &&
                                procedure.amount === 0}
                            value={procedure.amount}
                            onChange={(value: number) => {
                                stateProcedure.amount = value;
                                this.props.refreshAvailmentState();
                            }} />
                    </Grid>
                    <Grid item xs={2} style={{textAlign: 'right'}}>
                        <div style={{display: 'inline-block', paddingRight: '0.5rem'}}>
                            <CustomCheckbox
                                placeholder='PHIC'
                                value={procedure.isPHIC}
                                onChange={(value: boolean) => {
                                    stateProcedure.isPHIC = value;
                                    this.props.refreshAvailmentState();
                                }} />
                        </div>
                        <div style={{display: 'inline-block'}}>
                            <IconButton
                                icon={faMinusCircle}
                                onClick={() => {
                                    diagnosis.procedures.splice(index, 1);
                                    this.props.refreshAvailmentState();
                                    if (!diagnosis.procedures.find(procedure => procedure.cptCode.value === '')) {
                                        this.pushBlankProcedures(diagnosisIndex);
                                    }
                                }}/>
                        </div>
                    </Grid>
                </Grid>
                <div style={{position: 'absolute'}}>
                    <TextButton 
                        label='Add procedure'
                        icon={faPlus}
                        onClick={() => {
                            this.pushBlankProcedures(diagnosisIndex);
                            this.props.refreshAvailmentState();
                        }}/>
                </div>
                <div style={{paddingTop: '1rem'}} />
            </div>
        );
    }
}