//#region Global Imports
import * as React from 'react';

import { BenefitsTable } from '../../TableComponent/BenefitsTable';

import Grid from '@material-ui/core/Grid';
// import Hidden from '@material-ui/core/Hidden';
import Typography from '@material-ui/core/Typography';

import {
	LimitField,
} from '../../ComboField';
import { ViewField } from '../../ViewField';
//import { Components } from '@hims/core';
//#endregion Global Imports


import { defaultProps } from './Constants';
import { ICategorySubForm } from './ICategorySubForm';

export const InpatientForm: React.FC<ICategorySubForm.IProps> = (
	props: ICategorySubForm.IProps,
): JSX.Element | null => {
	const { formHeader, viewOnly, rows, setSubForm, subFormNode } = props;

	let metadata:any = {};
	if (subFormNode) {
		if (subFormNode['custom_metadata']) {
			metadata = subFormNode['custom_metadata'];
		}
	}

	// console.log(rows);

	// const fakeRows: any = [
	// 	{
	// 		category: 'Standard Admission Kit',
	// 		benefit: 'Cotton Balls',
	// 		inner_limit_type: ['amt_limit'],
	// 		inner_limit: [1000],
	// 		daily_inner_limit: [200],
	// 	},
	// 	{
	// 		category: 'Standard Admission Kit',
	// 		benefit: 'Alcohol',
	// 		inner_limit_type: ['amt_limit'],
	// 		inner_limit: [1000],
	// 		daily_inner_limit: [200],
	// 	},
	// 	{
	// 		category: 'Wound Care',
	// 		benefit: 'Povidone Iodine',
	// 		inner_limit_type: ['percent_mbl'],
	// 		inner_limit: [2],
	// 		daily_inner_limit: [0.5],
	// 	},
	// 	{
	// 		category: 'Transfusions & Infusions',
	// 		benefit: 'Blood Transfusion',
	// 		inner_limit_type: ['percent_mbl'],
	// 		inner_limit: [2],
	// 		daily_inner_limit: [0.5],
	// 	},
	// 	{
	// 		category: 'Professional Services',
	// 		benefit: 'Periodic medical check-up',
	// 		inner_limit_type: ['percent_abl', 'no_visits'],
	// 		inner_limit: [2, 3],
	// 		daily_inner_limit: [0.5, 1],
	// 	},
	// ];

	const columns: any = [
		{ name: 'parent_name', title: 'Category' },
		{ name: 'name', title: 'Description' },
		{ 
			name: 'limit_type', 
			title: 'Inner Limit Type',
			getCellValue: row => (row.limits
									? row.limits : [])
		},
		{ 
			name: 'limit', 
			title: 'Inner Limit',
			getCellValue: row => (row.limits
									? row.limits : [])
		},
		{ 
			name: 'daily_limit', 
			title: 'Daily Inner Limit',
			getCellValue: row => (row.limits
									? row.limits : [])
		},
	];

	const formattedColumns = {
		limit: ['limit', 'daily_limit'],
		limit_type: ['limit_type'],
	};

	const columnExtensions = [
		{ columnName: 'name', wordWrapEnabled: true },
		{ columnName: 'limit_type', width: 150, wordWrapEnabled: true },
		{ columnName: 'limit', width: 150, wordWrapEnabled: true },
		{ columnName: 'daily_limit', width: 150, wordWrapEnabled: true },
	];

	const amountLimit = {
		amt_limit: 'Amount Limit',
		max_session: 'Max Session',
		max_amount: 'Max Amount',
		percent_mbl: 'Percent of MBL',
		percent_abl: 'Percent of ABL',
		no_visits: 'No. of Visits'
	}
	
	let finalRows = rows;
	
	if (viewOnly) {
		finalRows = rows.filter((row) => row.included ? row.included : false);
	}

	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2} style={{ marginTop: '27.5px' }}>
					{!viewOnly ? formHeader : null}
				</Grid>
			</Grid>
			<Grid item sm={12}>
				<BenefitsTable
					viewOnly={viewOnly}
					columns={columns}
					formattedColumns={formattedColumns}
					columnExtensions={columnExtensions}
					{...props}
					rows={finalRows}
				/>
			</Grid>
			<Grid item sm={12}>
				{!viewOnly ? (
					<>
					<Typography variant="overline" display="block" gutterBottom>
						Limit
					</Typography>
					<React.Fragment>
						<Typography variant="body1" gutterBottom>
							Amount Limit for all procedures
						</Typography>
						<Typography variant="caption" gutterBottom>
							The amount specified for the particular procedure will override this amount
						</Typography>
						<LimitField id="amt_limit" 
							fieldName="amt_limit"
							selectName="amt_limit_unit"
							fieldValue={metadata['amt_limit']}
							selectValue={metadata['amt_limit_unit']}
							fieldOnChange={setSubForm}
							selectOnChange={setSubForm} 
							inputProps={{ min: 0 }}
							noDefault
						/>
					</React.Fragment> 
					</>
				) : (
					metadata['amt_limit'] && metadata['amt_limit_unit'] ? (
						<>
						<Typography variant="overline" display="block" gutterBottom>
							Limit
						</Typography>
						<ViewField 
							label="Amount Limit for all procedures"
							value={metadata['amt_limit'] + ' ' + amountLimit[metadata['amt_limit_unit']]} />
						</>
					) : null
				)}
			</Grid>
		</React.Fragment>
	);
};

InpatientForm.defaultProps = defaultProps;