//#region Global Imports
import * as React from 'react';

import { BenefitsTable } from '../../TableComponent/BenefitsTable';
import { useState, useEffect } from 'react';
import Grid from '@material-ui/core/Grid';
// import Hidden from '@material-ui/core/Hidden';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormGroup from '@material-ui/core/FormGroup';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Snackbar from  '@material-ui/core/Snackbar';
import SnackbarContent from  '@material-ui/core/SnackbarContent';
import { API } from '../../../../../../API';
import IconButton from '@material-ui/core/IconButton';
import { ViewField } from '../../ViewField';
//import { Components } from '@hims/core';
//#endregion Global Imports


import { defaultProps } from './Constants';
import { ICategorySubForm } from './ICategorySubForm';

export interface DropdownChoice {
    value: string;
    label: string;
    params?: string[];
}

export const PartnerNetworkForm: React.FC<ICategorySubForm.IProps> = (
	props: ICategorySubForm.IProps,
): JSX.Element | null => {
	const { formHeader, rows, viewOnly, subFormNode, setRowMetadata2, setSubForm2 } = props;
	// const [type, setType] = useState();
	const [dropdownData, setDropdownData] = useState<any[]>([]);
	const [selectedOption, setSelectedOption] = useState<DropdownChoice | null>(null);
	const [adderror, setAddError]=useState<boolean>(false)
	let typeRows: any[] = [];

	let groups: any[] = [];
	let type: string = '';
	if (subFormNode) {
		if (subFormNode['custom_metadata'] && subFormNode['custom_metadata']['groups']) {
			groups = subFormNode['custom_metadata']['groups'];
		}
		if (subFormNode['custom_metadata'] && subFormNode['custom_metadata']['type']) {
			type = subFormNode['custom_metadata']['type'];
		}
	}

	const handleLoadOptions = () => {
		API.get('facilities' + ('CB' ? '?filter={"where":{"type":"Clinic-based"}}' : ''), 
			(response) => {
				if (response && response.error === undefined){
					setDropdownData(response);
				} else{
					console.log('getfacilities api error: ');
				}
			}, 
			error => {
				console.log('getfacilities api error: ', error);
			}
		);
	};

	const handleToggleGroup = (value) => {
		const index = groups.findIndex((group) => value === group);

		let newGroups = [...groups];

		if (index > -1) {
			newGroups.splice(index, 1);
		} else {
			newGroups.push(value);
		}

		setSubForm2("groups", newGroups);
	}

	const handleSetNetworkType = (value) => {
		setSubForm2("type", value);
	}

	const show = (att:string) => {
			console.log(att);
		setAddError(false);
	}

	const handleAddDropdown = (selectedOption) => {
		if (type !== '' && dropdownData && dropdownData.length > 0 && selectedOption) {
			const found = dropdownData.find (
				(data) => 	data.name === selectedOption.label &&
				 			data.facility_id === selectedOption.value
				 		);

			const selectedType = rows.find((row) => row.code === type);

			let newPartnerNetwork: object[] = [];

			if (selectedType && selectedType['custom_metadata']['partner_network_list']) {
				newPartnerNetwork = [...selectedType['custom_metadata']['partner_network_list']];
			}
			
			const fnd = newPartnerNetwork.filter(function(element){
				return (element['_id']===found._id);})
			
			if (found && fnd.length===0) {
				let newPartner = Object.assign({}, found);
				newPartner['included'] = true;
				newPartner['parent'] = selectedType.id;
				newPartner['index'] = newPartnerNetwork.length;

				newPartnerNetwork.push(newPartner);
				setRowMetadata2('partner_network_list', newPartnerNetwork, selectedType.id);
				setSelectedOption(null);
			}
			else
			{
				setAddError(true);
			}
		}
	}
	
	// const fakeRows: any = [
	// 	{
	// 		status: "check",
	// 		name: "General Santos Doctor's Hospital",
	// 		category: 'Hospital',
	// 		address: 'St. Bartholomew Street',
	// 		city: 'General Santos City',
	// 		province: 'South Cotabato',
	// 	},
	// 	{
	// 		status: "check",
	// 		name: 'Philippine Medical Center',
	// 		category: 'Hospital',
	// 		address: '34 East Avenue',
	// 		city: 'Quezon City',
	// 		province: 'Metro Manila',
	// 	},
	// 	{
	// 		status: "check",
	// 		name: "ICC Medical City",
	// 		category: 'Mall-based',
	// 		address: 'Brgy. Felipe',
	// 		city: 'Cagayan de Oro City',
	// 		province: 'Misamis Oriental',
	// 	},
	// 	{
	// 		status: "cross",
	// 		name: "Gensan Women's Center",
	// 		category: 'Hospital',
	// 		address: 'Trese Martires St.',
	// 		city: 'General Santos City',
	// 		province: 'South Cotabato',
	// 	},
	// 	{
	// 		status: "none",
	// 		name: "Megamall Medical City",
	// 		category: 'Clinic',
	// 		address: 'EDSA',
	// 		city: 'Mandaluyong City',
	// 		province: 'Metro Manila',
	// 	},
	// 	{
	// 		status: "none",
	// 		name: 'SM North Medical City',
	// 		category: 'Mall-based',
	// 		address: 'EDSA',
	// 		city: 'Quezon City',
	// 		province: 'Metro Manila',
	// 	},
	// 	{
	// 		status: "none",
	// 		name: "St. Luke's Hospital - Quezon City",
	// 		category: 'Major Hospital',
	// 		address: '279 E Rodriguez Sr.',
	// 		city: 'Quezon City',
	// 		province: 'Metro Manila',
	// 	},
	// 	{
	// 		status: "none",
	// 		name: "St. Luke's Hospital - BGC",
	// 		category: 'Hospital',
	// 		address: '32nd St. cor 2nd Ave',
	// 		city: 'BGC',
	// 		province: 'Metro Manila',
	// 	},
	// ];

	const columns: any = [
		{ name: 'included', title: 'Status',
			getCellValue: row => row.included !== undefined ? row.included : null
		},
		{ name: 'name', title: 'Name' },
		{ name: 'type', title: 'Category'},
		{ name: 'street', title: 'Address' },
		{ name: 'city', title: 'City' },
		{ name: 'province', title: 'Province' },
	];

	const formattedColumns = {
		partner_checkbox: ['included'],
	};

	const columnExtensions = [
		{ columnName: 'included', width: 125, wordWrapEnabled: true },
		{ columnName: 'name', width: 350, wordWrapEnabled: true },
	];

	const selectedType = rows.find((row) => row.code === type);

	if (selectedType && selectedType['custom_metadata']['partner_network_list']) {
		typeRows = selectedType['custom_metadata']['partner_network_list'];
	}

	useEffect(() => {
		setSelectedOption(null);
		handleLoadOptions();
	}, [type]);

	if (viewOnly) {
		groups = groups.reduce((result, group) => {
			if (group === 'top_5_hospitals') {
				result.push("Top 5 Hospitals");
			}
			if (group === 'top_6_hospitals') {
				result.push("Top 6 Hospitals");
			}
			if (group === 'healthway_clinics') {
				result.push("Healthway Clinics");
			}
			if (group === 'medical_city_network') {
				result.push("Medical City Network");
			}
			return result;
		}, new Array());
	}

	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2} style={{ marginTop: '27.5px' }}>
					{!viewOnly ? formHeader : null}
				</Grid>
			</Grid>
			{ 
				viewOnly ? (
					type !== '' ? 
					<Grid item sm={12}>
						<ViewField 
							label="Type"
							value={type === 'OD' ? "Open Door" : "Clinic-based"} 
						/>
					</Grid> : null
				) : <Grid item sm={12}>
					<Typography variant="overline" display="block" gutterBottom>
						Type*
					</Typography>
					<RadioGroup name="type">
						<FormControlLabel 
							onClick={()=>handleSetNetworkType('OD')}
							value="open_door"
							control={<Radio checked={type === 'OD'} />} 
							label="Open Door"
						/>
						<FormControlLabel 
							onClick={()=>handleSetNetworkType('CB')}
							value="clinic_based"
							control={<Radio checked={type === 'CB'} />}
							label="Clinic-based" 
						/>
					</RadioGroup>
				</Grid>
			}
			
			{ 
				viewOnly ? (
					groups.length > 0 ? 
					<Grid item sm={12}>
						<ViewField 
							label="Group"
							value={groups.join(', ')} 
						/>
					</Grid> : null
				) : 
				<Grid item sm={12}>
					<Typography variant="overline" display="block" gutterBottom>
						Group
					</Typography>
					<FormGroup>
						<FormControlLabel 
							onClick={()=>handleToggleGroup('top_5_hospitals')}
							value="top_5_hospitals" 
							control={<Checkbox checked={groups.includes('top_5_hospitals')} />} 
							label="Top 5 Hospitals" 
						/>
						<FormControlLabel 
							onClick={()=>handleToggleGroup('top_6_hospitals')}
							value="top_6_hospitals" 
							control={<Checkbox checked={groups.includes('top_6_hospitals')} />} 
							label="Top 6 Hospitals" 
						/>
						{/*<FormControlLabel value="without_top_5_hospitals" control={<Checkbox />} label="Without the Top 5 Hospitals" />*/}
						<FormControlLabel 
							onClick={()=>handleToggleGroup('healthway_clinics')}
							value="healthway_clinics"
							control={<Checkbox checked={groups.includes('healthway_clinics')} />} 
							label="Healthway Clinics"
						/>
						<FormControlLabel 
							onClick={()=>handleToggleGroup('medical_city_network')}
							value="medical_city_network"
							control={<Checkbox checked={groups.includes('medical_city_network')} />} 
							label="Medical City Network"
						/>
					</FormGroup>
				</Grid>
			}
				
			<React.Fragment>
				<div id='snkbrcont' style={{display:'block',zIndex:1000}}> 
				<Snackbar
					id="rejmsg"
					key="close"
					anchorOrigin={{
						horizontal: 'center',
						vertical: 'top',
					}}
					open={adderror}
					autoHideDuration={5000}
					onClose={()=>{show('none');}}
				>
					<SnackbarContent
						style={{ backgroundColor: 'rgba(0, 0, 0, 0.75)' }}
						message={"Hospital/Clinic Has Already Been Added"}
						action={[
							<IconButton key="close" aria-label="close" color="inherit" onClick={()=>{show('none');}}>
							  <CloseIcon />
							</IconButton>,
						  ]}
						
					/>
				</Snackbar>
				</div>
			</React.Fragment>

			<Grid item sm={12}>
				<BenefitsTable 
					setRowMetadata2={setRowMetadata2}
					viewOnly={viewOnly}
					dropdownHeader={!viewOnly}
					disablePaging
					disableSearch
					disableFilter
					disableSelect
					formattedColumns={formattedColumns}
					columnExtensions={columnExtensions}
					rows={typeRows}
					columns={columns}
					dropdownData={dropdownData}
					selectedOption={selectedOption}
					setSelectedOption={setSelectedOption}
					setAddDropdown={handleAddDropdown}
				/>
			</Grid>
		</React.Fragment>
	);
};

PartnerNetworkForm.defaultProps = defaultProps;