import * as React from 'react';
import { Grid, TableRow, TableCell,Typography,Link } from '@material-ui/core';
import { CustomTable, HeaderData } from 'Pages/CustomerCarePage/Components/CustomTable';
import { faCheck, faTimes } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { API } from 'Pages/CustomerCarePage/API';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';
import { TextTab } from 'Pages/CustomerCarePage/Components/TextTab';

interface IProps {
    clientId: string;
    planType: string | undefined;
}

interface IStates {
    selectedTab: number;
    code: string;
    name: string;
    location: string;

    includedHospitals: FacilityData[];
    excludedHospitals: FacilityData[];
    groups: PartnerNetworkGroup[];

    isLoaded: boolean;



    tabList:JSX.Element;
    benefits:any[];
    type?: any;
}

interface PartnerNetworkGroup {
    checked: boolean;
    name: string;
    label: string;
}

interface FacilityData {
    code: string;
    name: string;
    location: string;
}

export class PartnerNetworkSection extends React.Component<IProps, IStates> {

    groupChoices: PartnerNetworkGroup[] = [
        { checked: false, name: 'Top 5 Hospitals', label: 'top_5_hospitals' },
        { checked: false, name: 'Top 6 Hospitals', label: 'top_6_hospitals' },
        { checked: false, name: 'Healthway Clinics', label: 'healthway_clinics' },
        { checked: false, name: 'Medical City Network', label: 'medical_city_network' }
    ]

    tabList = [
        { value: 1, label: 'Excluded Hospitals' },
        { value: 2, label: 'Included Hospitals' }
    ];

    selectedPType = 'BASE';
    
    constructor(props: IProps) {
        super(props);

        this.state = {
            selectedTab: 1,
            code: '',
            name: '',
            benefits:[],
            location: '',
            tabList:<div></div>,
            groups: [],
            isLoaded: false,
            includedHospitals: [],
            excludedHospitals: [],
        }
    }

    renderTabs = (tabs:string[]) =>{
        const tabelem:any = tabs.map((col)=>{
            return (<Typography style={{display:'inline-block',marginRight:'10px'}}>
                    <Link onClick={()=>{
                        this.selectedPType = col
                        this.constructPartnerNetworkData(this.state.benefits)
                    }}>
                        <span>{col}</span>
                    </Link>
                </Typography>)
        })
        return tabelem;
    }

    constructPartnerNetworkData = (response: any[]) => {

        const lastElement = (array: any[]) => {
            return array[array.length - 1];
        }

        const parentNode = response.find(node => node.level == 4 
            && lastElement(node.tree_path) === 'NT'
            && node.tree_path.includes(this.selectedPType));

        let pnType = response.find(node => node.level === 5 
            && node.tree_path[4] === parentNode.custom_metadata.type
            && node.tree_path.includes(this.selectedPType));
        
        const pnGroups = parentNode.custom_metadata.groups;

        // let pnIncludedFacilities = undefined;
        let pnExcludedFacilities = undefined;
        if (pnType) {
            // pnIncludedFacilities = pnType.custom_metadata.included_facilities;
            if (pnType.custom_metadata.excluded_facilities) {
                pnExcludedFacilities = pnType.custom_metadata.excluded_facilities;
            } else {
                pnExcludedFacilities = pnType.custom_metadata.excluded_facilties;
            }
        } 
        
        // console.log('pnType', pnType);
        // console.log('pnGroups', pnGroups);
        // console.log('pnFacilities', pnIncludedFacilities,pnExcludedFacilities);
        
        /* Assign Type */
        // @ts-ignore
        this.setState({type: pnType,
        });

        /* Assign Groups */
        if (pnGroups) {
            this.setState({groups: this.groupChoices.map(i => i)});
            this.state.groups.forEach(group => {
                group.checked = pnGroups.find(pnGroup => group.label === pnGroup)
            })
            this.setState({groups: this.state.groups});
        }

        /* Assign Facilities */
        // if (pnIncludedFacilities) {
        //     // @ts-ignore
        //     const includedfacilities = pnIncludedFacilities.map(facility => {
        //         return {
        //             code: facility.facility_id,
        //             name: facility.name,
        //             location: '---',
        //         }
        //     })
        //     this.setState({includedHospitals:includedfacilities ,
        //     selectedHospitals:includedfacilities });
        // } else {
        //     this.setState({includedHospitals:[] ,
        //         selectedHospitals:[] });
        // }

        if (pnExcludedFacilities) {
            // @ts-ignore
            this.setState({excludedHospitals: pnExcludedFacilities.map(facility => {
                return {
                    code: facility.facility_id,
                    name: facility.name,
                    location: '---',
                }
            })});
        } else {
            this.setState({excludedHospitals:[]});
        }
    }

    componentDidMount() {
        API.get(`clients/account/hmo-info/${this.props.clientId}`,
            (response) => {
                console.log('partner network',response)
                const pnetwork = response.benefit_plan_tree.filter(col=>
                    {return col.tree_path[3]==='NT'}
                )
                if(!this.props.planType){
                    const tabs = response.benefit_plan_tree.filter((element)=>{
                        return element.level===3})
                    const tabarray:string[] = []
                    tabs.map((col)=>{
                        tabarray.push(col.code)
                    })
                    this.setState({
                        tabList:this.renderTabs(tabarray),
                    });
                } else {
                    const ptype = response.benefit_plan_tree.find(node => 
                        node.name===this.props.planType    
                    )
                    this.selectedPType = ptype.code
                }
                this.setState({
                    
                    benefits:response.benefit_plan_tree,
                });
                console.log('pnetwork',pnetwork)
                this.constructPartnerNetworkData(response.benefit_plan_tree);
                this.setState({isLoaded: true});
            },
            (error) => { 
                console.log(error);
            });

        API.pnGet('p/f/partners/bare-facilities', (response) => {
            this.setState({includedHospitals: response.data.map(facility => {
                return {
                    code: facility.facility_id,
                    name: facility.facility_name,
                    location: '---',
                }
            })});
        });
    }

    render(): JSX.Element {
        return (
            <div className='section'>
                {!this.state.isLoaded ? (
                    <Loading height='10rem'/>
                ) : (
                    this.renderMainContent()
                )}
            </div>
        );
    }

    renderMainContent(): JSX.Element {
        return (
            <>
            <div style={{paddingTop: '2.5rem'}} />
            <div className='header-1'>Check Partner Network</div>
            <div className='sub-section'>
                <Grid container spacing={2} style={{ marginTop: '27.5px' }}>
                    {!this.props.planType ?
                    <Grid item sm={12} 
                        >
                        <div style={{
                            borderBottom: '1px solid #C3C3C3',
                            width: '100%'}}>
                            {this.state.tabList}
                        </div>
                        
                    </Grid>:null} 
                    <Grid item sm={12}>
                        <div className='header-2'>TYPE</div>
                        <div className='small-text'>{this.state.type ? this.state.type['name'] : '---'}</div>
                    </Grid>
                </Grid>
            </div>
            <div className='sub-section'>
                <div className='header-2'>GROUP</div>
                <Grid className='sub-section' container spacing={2} style={{width: '50%'}}>
                    {this.groupChoices.map(group => {
                        return (
                            <Grid item xs={6}>
                                <span>
                                    {group.checked ? (
                                        <FontAwesomeIcon icon={faCheck} style={{color: '#3AB77D'}} />
                                    ) : (
                                        <FontAwesomeIcon icon={faTimes} style={{color: '#FD5451'}} />
                                    )}
                                </span>
                                <span style={{paddingLeft: '1rem'}}>{group.name}</span>
                            </Grid>
                        );
                    })}
                </Grid>
            </div>
            <div className='sub-section'>
                <TextTab
                    tabList={this.tabList}
                    selectedTab={this.state.selectedTab}
                    tabStyle={() => {
                        return { padding: '0 1rem' };
                    }}
                    tabLabel={(index: number) => {
                        return (
                            <div style={{width: '100%'}}>
                                <div className='header-3' style={{margin: '6px 0'}}>
                                    {this.tabList[index].label}
                                </div>
                            </div>
                        );
                    }}
                    setSelectedTab={(value: number) => {
                        this.setState({selectedTab: value});
                    }} />

                {this.state.selectedTab === 1 ? (
                    <HospitalTable 
                        facilities={this.state.excludedHospitals}
                        onUpdateProps={() => {
                            this.setState({excludedHospitals: this.state.excludedHospitals});
                        }} />
                ) : this.state.selectedTab === 2 ? (
                    <HospitalTable 
                        facilities={this.state.includedHospitals}
                        onUpdateProps={() => {
                            this.setState({includedHospitals: this.state.includedHospitals});
                        }} />
                ) : null}
            </div>
            <div className='section' />
            </>
        );
    }
}






















