interface ClientBenefitProps {
    contractId: string;
}

export const ClientBenefitSection: React.FC<ClientBenefitProps> = (props: ClientBenefitProps): JSX.Element => {

    const classes = viewClientStyles();

    const [isLoading, SetIsLoading] = useState<boolean>(false);
    const [snackbarMessage, SetSnackbarMessage] = useState<string>('');
    const [selectedPlanType, SetSelectedPlanType] = useState<string>('BASE');
    const [selectedPlanPackage, SetSelectedPlanPackage] = useState<string>('RB');
    const [selectedBenefitCategory, SetSelectedBenefitCategory] = useState<string>('RBC');
    const [exclusionList, SetExclusionList] = useState<any[]>([]);
    const [contractNodes, SetContractNodes] = useState<any[]>([]);
    const [planTypeNodes, SetPlanTypeNodes] = useState<any[]>([]);
    const [planPackageNodes, SetPlanPackageNodes] = useState<any[]>([]);
    const [benefitCategoryNodes, SetBenefitCategoryNodes] = useState<any[]>([]);
    const [benefitTypeNodes, SetBenefitTypeNodes] = useState<any[]>([]);
    const [mainBenefitNodes, SetMainBenefitNodes] = useState<any[]>([]);
    const [subBenefitNodes, SetSubBenefitNodes] = useState<any[]>([]);
    
    const loadBenefitsData = () : void => {
        SetIsLoading(true);
        API.get(`base-benefit-plans/`, 
            (response) => {
                if(response && response.error === undefined && response.length > 0){
                    const data: any[] = [...response];
                    let baseNodes : any[] = [];

                    baseNodes = CBUtils.convertNodeData(data);

                    API.get(`benefit-plans/tree/${props.contractId}`, 
                        (response) => {
                            if(response && response.error === undefined && response.length > 0){
                                localStorage.setItem('client_benefits', JSON.stringify(response));
                                const data: any[] = [...response];
                                let benefitNodes : any[] = [];
                                let eList : any[] = [...exclusionList];

                                benefitNodes = CBUtils.convertNodeData(data, true);

                                let nodes: any[] = CBUtils.combineBaseBenefitNodes(baseNodes, benefitNodes);
                                
                                eList = CBUtils.createExclusionList(nodes);
                                
                                SetExclusionList(eList);
                                SetSelectedPlanPackage('RB');
                                SetSelectedBenefitCategory('RBC');
                                
                                SetContractNodes(CBUtils.getFilteredNodes(nodes, 2));
                                SetPlanTypeNodes(CBUtils.getFilteredNodes(nodes, 3));
                                SetPlanPackageNodes(CBUtils.getFilteredNodes(nodes, 4));
                                SetBenefitCategoryNodes(CBUtils.getFilteredNodes(nodes, 5));
                                SetBenefitTypeNodes(CBUtils.getFilteredNodes(nodes, 6));
                                SetMainBenefitNodes(CBUtils.getFilteredNodes(nodes, 7));
                                SetSubBenefitNodes(CBUtils.getFilteredNodes(nodes, 8));
                                SetIsLoading(false);
                            }
                            else{
                                console.log('get client benefits error', response.error);
                                localStorage.setItem('client_benefits', '');
                                SetIsLoading(false);
                                SetSnackbarMessage('An error occurred while loading client benefits.');
                            }
                        },
                        (error) => {
                            console.log('get client benefits error', error);
                            localStorage.setItem('client_benefits', '');
                            SetIsLoading(false);
                            SetSnackbarMessage('An error occurred while loading client benefits.');
                        });
                }
                else{
                    console.log('get client benefits error', response.error);
                    localStorage.setItem('client_benefits', '');
                    SetIsLoading(false);
                    SetSnackbarMessage('An error occurred while loading client benefits.');
                }
            },
            (error) => {
                console.log('get client benefits error', error);
                localStorage.setItem('client_benefits', '');
                SetIsLoading(false);
                SetSnackbarMessage('An error occurred while loading client benefits.');
            });
    }

    const selectContractNode = () => {
        return CBUtils.getContractNode(contractNodes);
    };

    const selectCategoryNode = () => {
        return CBUtils.getCategoryNode(planPackageNodes, benefitCategoryNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory);
    };

    const selectPlanTypeList = () => {
        return CBUtils.getPlanTypeList(planTypeNodes, mainBenefitNodes, benefitTypeNodes, subBenefitNodes);
    };

    const selectRows = () => {
        return CBUtils.getRows(exclusionList, benefitCategoryNodes, benefitTypeNodes, mainBenefitNodes, subBenefitNodes, selectedPlanType, selectedPlanPackage, selectedBenefitCategory);
    };

    const selectRoomAndBoardList = () => {
    return CBUtils.getSidebarList(benefitCategoryNodes, selectedPlanType, 'RB');
    };

    const selectCoverageList = () => {
    return CBUtils.getSidebarList(benefitCategoryNodes, selectedPlanType, 'CV');
    };

    const selectRiderList = () => {
    return CBUtils.getSidebarList(benefitCategoryNodes, selectedPlanType, 'RI');
    };

    useEffect(() => {
        loadBenefitsData();
    }, []);
    
    return (
        <div className='section'>
            {
                isLoading ? 
                <Loading height='10rem'/> 
                : (
                    <div>
                        <div style={{paddingTop: '2.5rem'}} />
                        <div className='header-1'>Client Benefits</div>
                        <Grid container className='sub-section' spacing={5} style={{paddingRight: 24}}>
                        <CBPlanTypes 
                            classes={classes} 
                            planTypeList={selectPlanTypeList()}
                            contractNode={selectContractNode()}
                            categoryNode={selectCategoryNode()}
                            roomAndBoardList={selectRoomAndBoardList()}
                            coverageList={selectCoverageList()}
                            riderList={selectRiderList()}
                            rows={selectRows()}
                            selectedPlanType={selectedPlanType}
                            selectedBenefitCategory={selectedBenefitCategory}
                            SetSelectedPlanType={SetSelectedPlanType}
                            SetSelectedPlanPackage={SetSelectedPlanPackage}
                            SetSelectedBenefitCategory={SetSelectedBenefitCategory} />
                        </Grid>
                        <div className='section' />
                    </div>
                )
            }
            <Snackbar
                anchorOrigin={{vertical: 'top', horizontal: 'right'}}
                open={snackbarMessage ? true : false}
                autoHideDuration={6000}
                onClose={() => {
                    SetSnackbarMessage('');
                }}
                message={snackbarMessage} />
        </div>
    );
}