import * as React from 'react';
import './component-style.scss';
import { List, Popper, Fade, ListItem } from '@material-ui/core';
import { TextButton } from './TextButton';
import { IconProp } from '@fortawesome/fontawesome-svg-core';

interface IProps {
    isOpen: boolean;
    anchor: HTMLButtonElement | null;
    items: {
        content: React.ReactNode;
        onClick: () => void;
        disabled?: boolean;
    }[];
}

export class ItemListPopper extends React.Component<IProps> {

    render(): JSX.Element {
        return (
            <Popper 
                open={this.props.isOpen}
                anchorEl={this.props.anchor}
                transition>
                {({TransitionProps}) => (
                    <Fade {...TransitionProps} timeout={350}>
                        <div style={{height: `${16 + 16 + (28 * this.props.items.length)}px`}}>
                            <div className='item-list'>
                                <List>
                                    {this.props.items.map(item => {
                                        return (
                                            <ListItem 
                                                className='small-text' 
                                                button 
                                                disabled={item.disabled}
                                                onClick={() => {
                                                    item.onClick();
                                                }}>
                                                {item.content}
                                            </ListItem>
                                        );
                                    })}
                                </List>
                            </div>
                        `</div>
                    </Fade>
                )}
            </Popper>
        );
    }
}

interface ILPButtonProps {
    icon: IconProp;
    label: string;
    color?: 'initial' | 'inherit' | 'primary' | 'secondary' | 'textPrimary' | 'textSecondary' | 'error';
}
export class ILPButton extends React.Component<ILPButtonProps> {

    render(): JSX.Element {
        return (
            <TextButton 
                icon={this.props.icon} 
                label={this.props.label}
                underline='none' 
                color={this.props.color}
                onClick={() => {}} />
        );
    }
}