
import * as React from 'react';
import './component-style.scss';
import { Chip } from '@material-ui/core';

interface IProps {
    label: string;
    value: string;
    onDelete: (value: string) => void;
    downloadLink?: string;
}

export class CustomChip extends React.Component<IProps> {

    render(): JSX.Element {
        return (
            this.props.downloadLink ? (
                <a href={this.props.downloadLink} download={this.props.downloadLink}>
                    <Chip 
                        className='chip'
                        label={this.props.label} 
                        onDelete={(e) => {
                            e.preventDefault();
                            this.props.onDelete(this.props.value);
                        }} />
                </a>
            ) : (
                <Chip 
                    className='chip'
                    label={this.props.label} 
                    onDelete={() => {
                        this.props.onDelete(this.props.value);
                    }} />
            )
        );
    }
}