import { Button, Grid } from '@material-ui/core';
// import {
//     faDownload
// } from '@fortawesome/free-solid-svg-icons';
import '../RendererComponent/styles.scss'

export interface RenderProps  {
    renderType: any,
    objId: any,
    label?: string,
    hideButton?: boolean,
    download?: boolean,
    setDownload?: any,
    fileType?: string, // DOCX or PDF
    clientID: string,
    clientName: string,
    apiContext?: string,
    renderAction: string,
    viewOnly?: (params) => any,
    onStartDownload: () => void,
    onError?: () => void
}

export interface RenderState {
    reset?: any,
    treeObj: Array<any>,
    htmlData: Object,
    nodeToExecute: number
}

export class RendererClass extends React.Component<RenderProps, RenderState, any>  {

    constructor (props: RenderProps) {
        super(props)
        this.state = {
            reset: false,
            treeObj: [],
            htmlData: '',
            nodeToExecute: 0
        }
    }

    componentDidMount() {
        this.setState({
            reset: false
        });
    }

    convertStringToFunction = (objId: string, stringFn: string) => {
        try {
           return new Function(`return ${stringFn}`)();
        } catch {
            console.log(`Error in converting fucntion in node _id: ${objId}`);
        }
	}

	traverseTree = (linearNodes) => {
		linearNodes.forEach((parentNodes, index) => {
            if (parentNodes.hasOwnProperty('valid_functions') && parentNodes.valid_functions.length > 0) {
            if (parentNodes._id === this.props.objId) {
                this.setState({
                    nodeToExecute: index
                });
            }
            let childNodes = parentNodes['children'];
            let extractedChildNodes: any = [];
            
            linearNodes[this.state.nodeToExecute].fileType = this.props.fileType && this.props.fileType.toLowerCase()
            
            // VALIDATE NODE IF IT HAS PROPERTY OF valid_functions
            linearNodes[this.state.nodeToExecute].valid_functions.forEach(fnName => {
                // CONVERT STRING FUNCTIONS TO VALID JAVASCRIPT FUNCTION
                if (parentNodes.hasOwnProperty(fnName)) {
                    if (parentNodes[fnName] !== '' && parentNodes[fnName] !== undefined) {
                        parentNodes[fnName] = this.convertStringToFunction(parentNodes['_id'], parentNodes[fnName]);
                    }
                }
            });
            
            if (parentNodes.children.length > 0) {
                childNodes.forEach(childNodeId => {
                    linearNodes.find(node => {
                        if (node._id === childNodeId) {
                            extractedChildNodes.push(node); 
                        }
                    });
                });
            }

            parentNodes.children = extractedChildNodes;
        }
    });
		return linearNodes[this.state.nodeToExecute];
	}

    ElToString(renderType) {
        // console.log(renderType);
        console.log(renderType);
        // console.log(this.state.htmlData);
        return this.state.htmlData
    }

    handleOnClick = () => {
        // console.log("true")
        // this.fetchTreeObj();
        let apiContext = '';

        console.log(this.props.apiContext)

        if (this.props.apiContext && this.props.apiContext !== undefined) {
            apiContext = this.props.apiContext;
        }

        API.getTreeObj(this.props.objId, apiContext).then((linearNodes: any) => {
            linearNodes.sort((a, b) => {
                return a.level - b.level;
            });

            let newNodes = this.traverseTree(linearNodes);

            this.setState({
                reset: true,
                htmlData: newNodes[this.props.renderType]()
            });

            console.log(newNodes);
        })
    }

    setReset = () => {
        this.setState({
            reset: false
        })
    }

    render (): JSX.Element {
        if (this.props.download && this.props.download === true && this.state.reset === false) {
            this.props.setDownload(false);
            this.handleOnClick();
        }

        return (
            <>
                {
                    !this.props.hideButton ? 
                    <Grid item xs={12} style={{textAlign: 'center', marginTop: '10px'}}>
                        <Button className={`makeStyles-downloadBtn-311`} variant="contained" color="primary" onClick={()=> this.handleOnClick()}>
                            {/* <FontAwesomeIcon
                                icon={faDownload}
                            ></FontAwesomeIcon> */}
                            {this.props.label ? this.props.label : 'Download file'}
                        </Button>
                    </Grid> : null
                }     
                {
                    this.state.reset === true && 
                    <HtmlToPDFRenderer 
                        clientID={this.props.clientID}
                        clientName={this.props.clientName} 
                        fileType={this.props.fileType ? this.props.fileType : 'pdf'} 
                        html={ this.ElToString(this.props.renderType ? this.props.renderType : 'renderProposal') } 
                        renderType={this.props.renderType ? this.props.renderType : 'renderProposal'} 
                        resetFlag={this.setReset}
                        onStartDownload={() => this.props.onStartDownload()}
                        onError={() => this.props.onError !== undefined && this.props.onError()}
                        viewOnly={this.props.viewOnly} 
                        renderAction={this.props.renderAction}
                        >   
                    </HtmlToPDFRenderer>
                }   
            </>
        )   
    }
}


