import * as React from 'react';
import { CustomModal } from 'Pages/CustomerCarePage/Components/CustomModal';
import { CustomButton } from 'Pages/CustomerCarePage/Components/CustomButton';
import { Processmaker } from 'Pages/CustomerCarePage/Processmaker';
import { API } from 'Pages/CustomerCarePage/API';
import { TicketData, LoginData, CaseData } from 'Pages/CustomerCarePage/Models';
import {
    TextField
} from '@material-ui/core';

interface IProps {
    caseId: string;
    caseData?: CaseData;
    ticket: TicketData;
    login: LoginData;
    isOpen: boolean;
    onClose: () => void;
    onSuccess: () => void;
}

export class CancelTicketModal extends React.Component<IProps> {

    state = {
        errorModal: false,
        errorModalMessage: '',
        reason: '',
        successModal: false
    }

    onCancel = () => {
        const ticket = this.props.ticket;
        const login = this.props.login;
        const caseData = this.props.caseData;
        
        const variableBody = {
            button_pressed: 'CANCEL'
        };

        const patchBody = {
            status: 'Cancelled',
            ticket_history_info: {
                assignee_id: login.userId,
                assignee_name: `${login.firstName} ${login.lastName}`,
                pmaker_task: caseData ? caseData.task : '',
                action: 'Cancelled Ticket'
            },
            cancellation_reason: this.state.reason
        };

        if (caseData) {
            Processmaker.put(`cases/${this.props.caseId}/variable`, variableBody, () => {
                Processmaker.put(`case/${this.props.caseId}/route-case`, {}, () => {
                    API.patch(`customer-care/tickets/${ticket.id}`, patchBody, async (res: any) => {
                        // this.props.onSuccess();
                        console.log(res)
                        this.props.onClose();

                        if (res.status === 204) {
                            this.setState({successModal: true})
                            return;
                        }

                        let respjson = await res.json();

                       

                        if (respjson) {
                           
                            if (respjson.error) {
                                this.setState({
                                    errorModalMessage: respjson.error.message
                                }, () => {
                                    this.setState({errorModal: true})
                                });
                            }
                        }
                        
                        this.setState({successModal: true})
                    });
                });
            });
        } else {
            API.patch(`customer-care/tickets/${ticket.id}`, patchBody, async (res: any) => {
                // this.props.onSuccess();
                console.log(res);
                this.props.onClose();

                if (res.status === 204) {
                    this.setState({successModal: true})
                    return;
                }

                let respjson = await res.json();

                if (respjson) {
                    if (respjson.error) {
                        
                        this.setState({
                            errorModalMessage: respjson.error.message
                        }, () => {
                            this.setState({errorModal: true})
                        });
                    }
                }    
            });
        }
    }

    handleFieldChanges = (e: any) => {
        let mes = e.target.value;

        this.setState({reason: mes});
    }

    render(): JSX.Element {
        return (
            <>

            {/* Error modal */}
            <CustomModal
                className='modal'
                isOpen={this.state.errorModal}
                handleClose={() => this.setState({errorModal: false})}
                header='Unable to Cancel Ticket'
                content={
                    <div style={{padding: '0.5rem 0'}}> 
                       {this.state.errorModalMessage}
                    </div>
                }
                footer={
                    <>
                    <div className='sub-section'>
                        <CustomButton
                            style={{width: 310}}
                            text='Okay'
                            onClick={() => {
                                this.setState({errorModal: false})
                            }} />
                    </div>
                    </>
                } />

            {/* Success modal */}
            <CustomModal
                className='modal'
                isOpen={this.state.successModal}
                handleClose={() => this.props.onSuccess()}
                header={`Ticket ${this.props.ticket.ticketNo} has been cancelled successfully.`}
                content=""
                footer={
                    <>
                    <div className='sub-section'>
                        <CustomButton
                            style={{width: 310}}
                            text='Okay'
                            onClick={() => {
                                this.props.onSuccess()
                            }} />
                    </div>
                    </>
                } />

            {/* Confirmation modal */}
            <CustomModal
                className='modal'
                isOpen={this.props.isOpen}
                handleClose={() => this.props.onClose()}
                header='Are you sure you want to cancel this ticket?'
                content={
                    <>
                        <div style={{padding: '0.5rem 0'}}> 
                            Cancelling this ticket will invalidate any assessment or approval for this confinement.
                        </div>
                        <div style={{padding: '0.5rem 0'}}> 
                            You will no longer be able to edit or update this ticket after cancellation. You can still view this ticket in the Tickets List. 
                        </div>
                        <div style={{padding: '0.5rem 0'}}> 
                            Are you sure you want to cancel this ticket?
                        </div>

                        <div style={{marginTop: 15}}>
                            <div style={{fontSize: 11, color: '#272E4C'}}>*Reason for Cancellation</div>
                            <TextField
                                multiline
                                fullWidth
                                rows={3}
                                value={this.state.reason}
                                onChange={this.handleFieldChanges}
                                variant="outlined" />
                        </div>
                    </>
                }
                footer={
                    <>
                    <div className='sub-section'>
                        <CustomButton
                            style={
                                {
                                    minWidth: 311,
                                    color: `${this.state.reason === '' ? '' : '#FD5451'}`,
                                    borderColor: `${this.state.reason === '' ? '' : '#FD5451'}`
                                }
                            }
                            text='Yes, cancel this ticket'
                            variant='outlined'
                            disabled={this.state.reason === '' ? true : false}
                            onClick={() => {
                                this.onCancel();
                            }} />
                    </div>
                    <div>
                        <CustomButton
                            style={{minWidth: 311}}
                            text='No, do not cancel this ticket'
                            onClick={() => {
                                this.props.onClose();
                            }} />
                    </div>
                    </>
                } />

            </>
        );
    }
}
