import * as React from 'react';
import IconButton from '@material-ui/core/IconButton';
import Tab, { TabProps } from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPencilAlt } from '@fortawesome/free-solid-svg-icons';

type PlanTypeTabProps = TabProps & {
	isSelected?: boolean;
	viewOnly?: boolean;
	subtitle?: string;
	data?: any;
	benefit_id: string;
	name: string;
	onEdit: (id:string, name:string) => void;
};

export const PlanTypeTab: React.FC<PlanTypeTabProps> = (props: PlanTypeTabProps): JSX.Element => {
	const { label, subtitle, ...tabProps } = props;
	
	const handleEdit= () =>{
		props.onEdit(props.benefit_id, props.name);
	}
	return (
		<Tab
			label={
				<div>
					<Typography 
						className="tab_text" 
						variant="h6" 
						style={{
							fontSize: '14px', 
							fontWeight: (props.isSelected ? 'bold' : 'normal'), 
							color: (props.isSelected ? '#3AB77D' : '#3C394A'),
						}}
					>
						{label}
						<IconButton size="small"></IconButton>
						{ !props.viewOnly && props.value !== "BASE" ?
							<span className="edit_btn" style={{position: "relative", top: 0, right: 0}} onClick={handleEdit}>
								<FontAwesomeIcon
									className="fa-sm"
									icon={faPencilAlt}
									style={{ color: '#3AB77D' }}
								/>
							</span>: null
						}
					</Typography>
					{subtitle ? (
						<Typography className="tab_caption" variant="caption" style={{fontSize: '11px'}}>
							{subtitle}
						</Typography>
					) : null}
					<br/>
					{ props.data !== undefined && props.data["requested_rate"] !== undefined 
						&& props.data["requested_rate"] !== 0 ?
						<Typography className="tab_caption" variant="caption" style={{fontSize: '11px'}}>
							{props.data["requested_rate"] + " PHP"}
						</Typography>: null

					}
				</div>
			}
			{...tabProps}
		/>
	);
};
