import * as React from 'react';
import { TextFieldProps, OutlinedTextFieldProps } from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import { makeStyles } from '@material-ui/styles';
//import { formatNumber } from 'Components/UI/TableComponent/BenefitsTable';
import PropTypes from 'prop-types';
import NumberFormat from 'react-number-format';

const comboStyles = makeStyles({
	adornedEnd: {
		paddingRight: 0,
	}
});

const comboSelectStyles = makeStyles({
	notchedOutline: {
		borderTopLeftRadius: 0,
		borderBottomLeftRadius: 0,
	},
	input: {
		textTransform: 'uppercase',
		padding: '12.5px 16px',
		fontSize:12,
	},
	root: {
		height: '2.90em'
	}
});
 
type IComboFieldProps = TextFieldProps | OutlinedTextFieldProps;

interface IComboContainerProps {
	fieldValue?: string;
	selectValue?: string;
	selectName?: string;
	fieldName?: string;
    disableSelect?: boolean;
	noDefault?: boolean;
	inputProps?: any;
	blankField?: boolean;
	allowNegative?: boolean;
	fieldOnChange?: (e: any) => void;
	selectOnChange?: (e: any) => void;
}



export const PHPPercField: React.FC<IComboFieldProps & IComboContainerProps> = (
	props: IComboFieldProps & IComboContainerProps,
): JSX.Element => {
	const comboClasses = comboStyles();
    const comboSelectClasses = comboSelectStyles();
    

	let textfieldProps: any;
	let fieldName: string | undefined;
	let selectName: string | undefined;
	let fieldValue:  string | undefined;
	let selectValue:  string | undefined;
	let fieldOnChange: any;
    let selectOnChange: any;
	let inputProps: any;
	let blankField: boolean | undefined;
	let allowNegative: boolean | undefined;
	const {} = props;
	({ fieldName, selectName, fieldOnChange, selectOnChange,
		fieldValue, selectValue, inputProps, blankField, allowNegative, ...textfieldProps } = props);
	//const numallowedpattern = new RegExp(/\D/);
	//const givmin = {min:'0'};
    function NumberFormatCustom(props) {
		const { inputRef, onChange, ...other } = props;
		const [editMode, setEditMode] = React.useState(false);
		const [tempValue, setTempValue] = React.useState('');

		let newValue: any = fieldValue ? fieldValue : blankField ? '' : '0';

		if (editMode) {
			newValue = tempValue;
		}

		return (
		  <NumberFormat
			{...other}
			getInputRef={inputRef}
			classes={comboClasses}
			type='tel'
			value={newValue}
            name={fieldName}
            min={0}
			isAllowed={(values) => {
				const {value, floatValue} = values;
                if(selectValue==='percent'){
                    if(value.indexOf('.')>=0)
                    { return false}
                }
				if (typeof floatValue==='undefined' || typeof value==='undefined') {
				  return true;
				}
				if (value.charAt(0)==='0' && value.length>1) {
					if (value.charAt(1) && value.charAt(1)!='.') {
					return false
					}
					return true
				}
				else {
				  return true;
				}

			  }}
			onValueChange={(e: any)=>{
				if (editMode && e) {
					setTempValue(e.floatValue);
				}
			}}
			onBlur={(e)=>{
				// focused = false

				setEditMode(false);
				e.target.value = e.target.value.replace(/,/g,'')
				if (fieldOnChange) {
					fieldOnChange(e);
				}
				// if(!focused) {
				// 	// setTempValue(fieldValue ? e.value : null);
				// 	setFocused(true)
				// }
				console.log('onChange', e);
			}}
			inputProps={{min:0}}
			onFocus={(e)=>{
				setEditMode(true);

				if (newValue === 0 || newValue === '0') {
					setTempValue('');
				} else {
					setTempValue(newValue);
				}

				e.target.click();
            }}
			thousandSeparator
			isNumericString
			allowNegative={allowNegative}
		  />
		);
	  }
	  
	  NumberFormatCustom.propTypes = {
		inputRef: PropTypes.func.isRequired,
		onChange: PropTypes.func.isRequired,
	  };
	if (inputProps === undefined) {
		inputProps = {};
	}
    return (
        <OutlinedInput
            classes={comboClasses}
            name={fieldName}
            labelWidth={0}
            value={fieldValue ? fieldValue : blankField ? '' : 0}
            inputComponent={NumberFormatCustom}
            //onChange={fieldOnChange ? fieldOnChange : null}
            //type={'tel'}
            inputProps={{min:'0'}}
            /*onKeyPress={(e)=>{ 
                let chk:any;
                if(selectValue==='PHP')
                {
                    chk = new RegExp(/[0-9.]/)
                }
                else {
                    chk = new RegExp(/[0-9]/)
                }
                if(!chk.test(e.key))
                {
                    e.preventDefault();
                } 
            }}*/
            endAdornment = {( 
                <InputAdornment position="end"> 
                    <Select
                        value={selectValue ? selectValue : 'percent'}
                        name={selectName}
                        onChange={(e) => { 
                            selectOnChange(e);
                        }}
                        input={
                            <OutlinedInput
                                classes={comboSelectClasses}
                                labelWidth={0}
                                id={selectName}
                            />
                        }
                    >
                        <MenuItem selected value="percent">
                            %
                        </MenuItem>
                        <MenuItem value="PHP">
                            PHP
                        </MenuItem>
                    </Select>
                </InputAdornment>
            )}
        />
    )
    
}