import * as React from 'react';
import { Table, TableBody, TableHead, TableRow, TablePagination, TableCell, Grid } from '@material-ui/core';
import './component-style.scss';
import { LabelDisplayedRowsArgs } from '@material-ui/core/TablePagination';
import { API } from '../API';
import { TextButton } from './TextButton';
import { InputField } from './InputField';
import { faSortUp, faSortDown, faAngleDoubleLeft, faAngleLeft, faAngleRight, faAngleDoubleRight } from '@fortawesome/free-solid-svg-icons';
import { IconButton } from './IconButton';
import { NumberField } from './NumberField';
import { DatePicker } from './DatePicker';
import { LoadingBlock } from './LoadingBlock';
import { DropdownChoice } from '../Models';
import { Dropdown } from './Dropdown';

interface IProps {
    className?: string;
    paginationClass?: string;

    /* Normal Mode Feature */
    rows: any[];
    renderHeader?: () => JSX.Element;
    renderRow: (rowData: any, index: number, filter?: any) => JSX.Element;
    noRowsMessage?: string | JSX.Element;

    /* Simple Mode Feature */
    columns?: HeaderData[];

    /* Additional Features */
    stickyHeader?: boolean;
    canOverflow?: boolean;

    /* Basic Pagination Features */ 
    rowsPerPageOptions?: number[];
    labelRowsPerPage?: string;
    onChangeInfo?: (info: LabelDisplayedRowsArgs) => string;

    /* Server Side Paginatio Feature */
    serverSideOption?: ServerSideOption;

    style?: React.CSSProperties;
}

export interface HeaderData {
    style?: React.CSSProperties;
    name?: string;
    key?: string;

    filterType?: 'string' | 'number' | 'date' | 'dropdown';
    filterOption?: DropdownChoice[]; // Needed in 'dropdown' filterType
    filterFunc?: (filterValue: any, cellValue: any) => boolean; // Recommended to use if not using string data type
    filterDisabled?: boolean;

    sortType?: 'string' | 'number' | 'date' | 'dropdown';
    sortFunc?: (rows: any[], key: string, isAscending: boolean) => void; // Recommended to use if not using string data type

    renderHeader?: JSX.Element;
}

interface ServerSideOption {
    disabled?: boolean;
    getUrl: (offset: number, limit: number, filter: any) => string;
    onSuccess: (response: any) => void;
    onFail?: (error: any) => void;
    getCount: (response: any) => number;
}

interface IStates {
    page: number;
    rowsPerPage: number;
    count: number;
    
    filterData: any;
    sortedColumn: string;
    isSearching: boolean;
}

export class CustomTable extends React.Component<IProps, IStates> {

    timeOut?: any;

    constructor(props: IProps) {
        super(props);

        this.state = {
            page: 0,
            rowsPerPage: 0,
            count: 0,

            filterData: {},
            sortedColumn: '',
            isSearching: false
        }
    }

    componentDidMount() {
        if (this.props.rowsPerPageOptions) {
            this.setState({rowsPerPage: this.props.rowsPerPageOptions[0]}, () => {
                if (this.props.serverSideOption) {
                    this.apiCall();
                }
            });
        }
    }

    resetPage = () => {
        this.setState({page: 0});
    }

    getSlicedData = (rows: any[]) => {
        if (this.props.rowsPerPageOptions && !this.props.serverSideOption) {
            const startingItem = this.state.page * this.state.rowsPerPage;
            return rows.slice(startingItem, startingItem + this.state.rowsPerPage);
        } else {
            return rows;
        }
    }

    getFilteredRows = (columns: HeaderData[]) => {;
        const filteredFilter = {};
        Object.keys(this.state.filterData).map(key => {
            if (this.state.filterData[key] && (
                (this.state.filterData[key].length && this.state.filterData[key].length > 0) ||
                (!this.state.filterData[key].length))) {
                filteredFilter[key] = this.state.filterData[key];
            }
        });
        const filteredRows = this.props.rows.filter(row => {
            var isFound = true;
            Object.keys(row).map(rowKey => {
                const column = columns.find(column => column.key === rowKey);
                const condition = column ?
                    column.filterFunc ? !column.filterFunc(filteredFilter[rowKey], row[rowKey]) :
                    column.filterType === 'string' ? 
                        filteredFilter[rowKey] ? 
                            !row[rowKey].toString().toLowerCase().includes(filteredFilter[rowKey].toLowerCase()) : 
                            false :
                    column.filterType === 'number' ? 
                        filteredFilter[rowKey] ? 
                            !row[rowKey].toString().includes(filteredFilter[rowKey]) : 
                            false : 
                    column.filterType === 'date' ? 
                        filteredFilter[rowKey] ? 
                            row[rowKey].toDateString() !== filteredFilter[rowKey].toDateString() : 
                            false :
                    column.filterType === 'dropdown' ? 
                        filteredFilter[rowKey] ? 
                            row[rowKey].toDateString().includes(filteredFilter[rowKey].value) :  
                            false :
                    false : 
                false;
                if (filteredFilter[rowKey] && condition) {
                    isFound = false;
                }
            });
            return isFound;
        });
        return filteredRows;
    }

    apiCall = (resetPage?: boolean) => {
        
        if (this.props.serverSideOption && !this.props.serverSideOption.disabled) {
            this.setState({isSearching: true});
            if (resetPage) {
                this.resetPage();
            }
            const serverSideOption = this.props.serverSideOption;
            const offset = (resetPage ? 0 : this.state.page) * this.state.rowsPerPage;
            const limit = this.state.rowsPerPage;
            API.get(serverSideOption.getUrl(offset, limit, this.state.filterData), (response) => {
                serverSideOption.onSuccess(response);
                this.setState({count: serverSideOption.getCount(response)});
                this.setState({isSearching: false});
            }, (error) => {
                if (serverSideOption.onFail) {
                    serverSideOption.onFail(error);
                }
                this.setState({isSearching: false});
            });
        }
    }

    render(): JSX.Element {
        const filteredRows = this.props.serverSideOption ? (
            this.getSlicedData(this.props.rows)
        ) : this.props.columns ? (
            this.getFilteredRows(this.props.columns)
        ) : this.getSlicedData(this.props.rows);

        return (
            <>
            <div style={{
                overflow: this.props.canOverflow ? 'auto' : 'none', 
                position: 'relative',
                ...this.props.style ? this.props.style : undefined}}>
                {this.state.isSearching ? (
                    <LoadingBlock height='3rem' size={80} position='absolute' />
                ) : null}
                <Table 
                    className={`${(this.props.className ? this.props.className : '')} table`} 
                    stickyHeader={this.props.stickyHeader ? this.props.stickyHeader : false}>

                    <TableHead>
                        {this.props.columns ? (
                            <TableRow>
                                {this.props.columns.map(column => {
                                    // @ts-ignore
                                    return this.renderHeaderColumn(column, this.props.serverSideOption !== undefined);
                                })}
                            </TableRow>
                        ) : this.props.renderHeader ? (
                            <TableRow>
                                {this.props.renderHeader()}
                            </TableRow>
                        ) : null}
                    </TableHead>

                    <TableBody>
                        {this.props.renderRow ? (
                            filteredRows.length > 0 ? (
                                this.props.serverSideOption ? (
                                    filteredRows.map((row, j) => {
                                        return this.props.renderRow(row, j, this.state.filterData)
                                    })
                                ) : (
                                    this.getSlicedData(filteredRows).map((row, j) => {
                                        return this.props.renderRow(row, j, this.state.filterData)
                                    })
                                )
                            ) : (
                                <TableRow>
                                    <TableCell colSpan={999} style={{textAlign: 'center'}}>
                                        {this.props.noRowsMessage ? this.props.noRowsMessage : 'No data found'}
                                    </TableCell>
                                </TableRow>
                            )
                        ) : null}
                    </TableBody>

                </Table>
            </div>

            {this.props.rowsPerPageOptions && !this.props.serverSideOption ? (
                <TablePagination
                    className={`${(this.props.paginationClass ? this.props.paginationClass : '')} pagination MuiTablePagination-root`}
                    rowsPerPageOptions={this.props.rowsPerPageOptions}
                    count={this.props.rows.length}
                    rowsPerPage={this.state.rowsPerPage}
                    page={this.state.page}
                    labelDisplayedRows={(info: LabelDisplayedRowsArgs) => {
                        return (
                            this.props.onChangeInfo ? (
                                this.props.onChangeInfo(info)
                            ) : (
                                `${info.from}-${info.to} of ${info.count}`
                            ));
                    }}
                    labelRowsPerPage={
                        this.props.labelRowsPerPage ?
                            this.props.labelRowsPerPage :
                            'Rows per page:'
                    }
                    onChangePage={(_event: unknown, page: number) => {
                        this.setState({page: page});
                    }}
                    onChangeRowsPerPage={(event: React.ChangeEvent<HTMLInputElement>) => {
                        this.setState({
                            rowsPerPage: parseInt(event.target.value, 10),
                            page: 0
                        });
                    }} />
            ) : null}

            {this.props.serverSideOption && this.props.rowsPerPageOptions ? (
                <ServerPagination
                    rowsPerPageOptions={this.props.rowsPerPageOptions}
                    rowsPerPage={this.state.rowsPerPage}
                    page={this.state.page}
                    count={this.state.count}
                    onChangePage={(page: number) => {
                        this.setState({page: page}, () => {
                            this.apiCall();
                        });
                    }}
                    onChangeRowsPerPage={(rowsPerPage: number) => {
                        this.setState({
                            rowsPerPage: rowsPerPage,
                            page: 0
                        }, () => {
                            this.apiCall();
                        });
                    }} />
            ) : null}
            </>
        );
    }

    renderHeaderColumn(header: HeaderData, isServerSideFiltering: boolean): JSX.Element {

        const onSortClick = () => {
            if (header.key && !header.sortFunc) {
                if (this.state.sortedColumn !== header.key) { // Ascending
                    this.props.rows.sort((a: any, b: any) => {
                        // @ts-ignore
                        const aValue = a[header.key];
                        // @ts-ignore
                        const bValue = b[header.key];

                        const aParsed = 
                            header.sortType === 'string' ? aValue.toString().toLowerCase() :
                            header.sortType === 'date' ? aValue.getTime() : 
                            header.sortType === 'number' ? aValue : 
                            header.sortType === 'dropdown' ? aValue.value.toString().toLowerCase() :
                            undefined;
                        const bParsed = 
                            header.sortType === 'string' ? bValue.toString().toLowerCase() :
                            header.sortType === 'date' ? bValue.getTime() : 
                            header.sortType === 'number' ? bValue : 
                            header.sortType === 'dropdown' ? bValue.value.toString().toLowerCase() :
                            undefined;

                        if (aParsed > bParsed) return 1;
                        if (aParsed < bParsed) return -1;
                        return 0;
                    });
                    this.setState({sortedColumn: header.key});

                } else { // Descending
                    this.props.rows.sort((a: any, b: any) => {
                        // @ts-ignore
                        const aValue = a[header.key];
                        // @ts-ignore
                        const bValue = b[header.key];

                        const aParsed = 
                            header.sortType === 'string' ? aValue.toString().toLowerCase() :
                            header.sortType === 'date' ? aValue.getTime() : 
                            header.sortType === 'number' ? aValue : 
                            header.sortType === 'dropdown' ? aValue.value.toString().toLowerCase() :
                            undefined;
                        const bParsed = 
                            header.sortType === 'string' ? bValue.toString().toLowerCase() :
                            header.sortType === 'date' ? bValue.getTime() : 
                            header.sortType === 'number' ? bValue : 
                            header.sortType === 'dropdown' ? bValue.value.toString().toLowerCase() :
                            undefined;

                        if (aParsed > bParsed) return -1;
                        if (aParsed < bParsed) return 1;
                        return 0;
                    });
                    this.setState({sortedColumn: ''});
                }
            } else if (header.key && header.sortFunc) {
                header.sortFunc(this.props.rows, header.key, this.state.sortedColumn !== header.key);
            }
        }

        const onFilterChanged = (value: any) => {
            if (!this.state.isSearching) {
                // @ts-ignore
                this.state.filterData[header.key] = value;
                this.setState({
                    filterData: this.state.filterData,
                    page: 0
                });
                if (isServerSideFiltering) {
                    clearTimeout(this.timeOut);
                    this.timeOut = setTimeout(() => {
                        this.apiCall(true);
                    }, 1000);
                }
            }
        }

        return (
            <TableCell style={header.style}>
                {header.name ? (
                    <span className='header-2'>{header.name}</span>
                ) : null}
                {header.sortType && header.key ? (
                    <div style={{float: 'right'}}>
                        <IconButton 
                            icon={this.state.sortedColumn !== header.key ? faSortDown : faSortUp} 
                            onClick={onSortClick} />
                    </div>
                ) : null}
                {header.filterType && header.key ? (
                    header.filterType === 'string' ? (
                        <InputField
                            value={this.state.filterData[header.key]}
                            disabled={header.filterDisabled}
                            onChange={onFilterChanged} />
                    ) : header.filterType === 'number' ? (
                        <NumberField
                            value={this.state.filterData[header.key]}
                            disabled={header.filterDisabled}
                            onChange={onFilterChanged} />
                    ) : header.filterType === 'date' ? (
                        <DatePicker
                            value={this.state.filterData[header.key] || null}
                            disabled={header.filterDisabled}
                            onChange={onFilterChanged} />
                    ) : header.filterType === 'dropdown' ? (
                        <Dropdown
                            value={this.state.filterData[header.key] || null}
                            choices={header.filterOption || []}
                            disabled={header.filterDisabled}
                            onChange={onFilterChanged} />
                    ) : null
                ) : null}
            </TableCell>
        );
    }
}










interface ServerPaginationProps {
    rowsPerPageOptions: number[];
    rowsPerPage: number;
    page: number;
    count: number;
    onChangePage: (page: number) => void;
    onChangeRowsPerPage: (rowsPerPage: number) => void;
}

export class ServerPagination extends React.PureComponent<ServerPaginationProps> {

    getPagesList = () => {
        const list: string[] = [];
        const maxPage = Math.ceil(this.props.count / this.props.rowsPerPage);
        let alreadyHidden = false;

        for (var i = Math.max(0, this.props.page - 4); i < maxPage; i++) {
            if (maxPage - i > 2 && i - this.props.page >= 2) { 
                if (!alreadyHidden) {
                    list.push('...');
                    alreadyHidden = true;
                }
            } else {
                list.push('' + (i + 1));
            }
        }
        return list.slice(Math.max(list.length - 5, 0));
    }


    render(): JSX.Element {
        const pages = this.getPagesList();

        const dropdownStyle: React.CSSProperties = {
            textAlign: 'center'
        };

        const style: React.CSSProperties = {
            textAlign: 'center',
            lineHeight: '2.5rem'
        };
        
        return (
            <Grid container spacing={1} className='custom-pagination'>
                <Grid item xs={5}>
                    {`${this.props.page * this.props.rowsPerPage + 1}-` +
                     `${Math.min((this.props.page + 1) * this.props.rowsPerPage, this.props.count)} out of ` +
                     `${this.props.count} items`}
                </Grid>
                <Grid item xs={7}>
                    <Grid container spacing={1}>
                        <Grid item xs />
                        <Grid item xs={1} style={dropdownStyle}>
                            <Dropdown
                                choices={this.props.rowsPerPageOptions.map(item => {
                                    return { value: item + '', label: item + '' }
                                })}
                                value={{
                                    value: this.props.rowsPerPage + '', 
                                    label: this.props.rowsPerPage + ''
                                }}
                                onChange={(value: DropdownChoice) => {
                                    this.props.onChangeRowsPerPage(parseInt(value.value));
                                }} />
                        </Grid>
                        <Grid item xs={1} style={style}>
                            <TextButton 
                                icon={faAngleDoubleLeft}
                                onClick={() => {
                                    const prevPage = this.props.page - 1;
                                    if (prevPage >= 0)
                                        this.props.onChangePage(0);
                                }} />
                        </Grid>
                        <Grid item xs={1} style={style}>
                            <TextButton 
                                icon={faAngleLeft}
                                onClick={() => {
                                    const prevPage = this.props.page - 1;
                                    if (prevPage >= 0)
                                        this.props.onChangePage(prevPage);
                                }} />
                        </Grid>
                        {pages.map(page => {
                            const pageNumber = parseInt(page) - 1;
                            const style: React.CSSProperties = {
                                fontWeight: this.props.page == pageNumber ? 'bold' : undefined,
                                cursor: !isNaN(pageNumber) ? 'pointer' : undefined,
                                textAlign: 'center',
                                lineHeight: '2.5rem'
                            };
                            return (
                                <Grid item xs={1}>
                                    <div 
                                        style={style}
                                        onClick={() => {
                                            if (!isNaN(pageNumber))
                                                this.props.onChangePage(pageNumber);
                                        }}>
                                        {page}
                                    </div>
                                </Grid>
                            );
                        })}
                        <Grid item xs={1} style={style}>
                            <TextButton 
                                icon={faAngleRight}
                                onClick={() => {
                                    const nextPage = this.props.page + 1;
                                    if (nextPage * this.props.rowsPerPage < this.props.count)
                                        this.props.onChangePage(nextPage);
                                }} />
                        </Grid>
                        <Grid item xs={1} style={style}>
                            <TextButton 
                                icon={faAngleDoubleRight}
                                onClick={() => {
                                    const nextPage = this.props.page + 1;
                                    if (nextPage * this.props.rowsPerPage < this.props.count)
                                        this.props.onChangePage(Math.floor(this.props.count / this.props.rowsPerPage));
                                }} />
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        );
    }
}

