import * as React from 'react';
import { Grid, TableCell, TableRow } from '@material-ui/core';
import { Loading } from '../Components/Loading';
import { GlobalFunction } from '../GlobalFunction';
import { CustomTable } from '../Components/CustomTable';
import { MemberData, SimpleTicketData, BenefitData } from '../Models';
import { API } from '../API';

interface IProps {
    member: MemberData;
    benefits: BenefitData[];
    recentTickets?: SimpleTicketData[];

    loadingBenefits: boolean;
    loadingRecentTickets: boolean;
}

interface IStates {
    memberType: string;
    memberPlanType: string;

    effectivityDate?: Date;
    renewalDate?: Date;
    extensionDate?: Date;
    roomAndBoardPlan?: any;
    roomAndBoard: any[];

    loadingBenefits: boolean;
}

export class MemberDataSection extends React.Component <IProps, IStates> {

    constructor(props: IProps){
        super(props);
        
        this.state = {
            memberType: '---',
            memberPlanType: '---',

            roomAndBoard: [],

            loadingBenefits: true
        }
    }










    componentDidMount() {
        const member = this.props.member;
        const contract = member.contract;
        API.get(`members/${this.props.member.id}`, (response) => { // TODO: Optimize later
            this.props.member.memberType = response.member_type;
            this.setState({
                memberType: response.member_type,
                memberPlanType: response.plan_type,
            });
        });

        if (contract) {
            API.get(`members/${member.id}/contracts/${contract.id}/benefits`, (response) => {
                this.setState({loadingBenefits: false});

                /* Just get the data that is needed */
                let rnbNode: any = null;
                let planTypeNode: any = null;
                const benefitPlans = response.filter(benefit => {
                    if (benefit.name.toLowerCase() === 'room and board') {
                        rnbNode = benefit;
                    }
                    if (benefit.level === 3 && benefit.type === 'PlanType') {
                        planTypeNode = benefit;
                    }
                    if (benefit.level === 1 && benefit.name.toLowerCase() === 'base mother contract') {
                        this.setState({
                            effectivityDate: new Date(benefit.custom_metadata.coverage_start_date),
                            renewalDate: new Date(benefit.custom_metadata.renewal_date),
                            extensionDate: new Date(benefit.custom_metadata.extension_date)
                        });
                    }
                    return benefit.level === 4;
                });

                const roomAndBoard = response.filter(benefit => {
                    let benefitTreeId = benefit.tree_id;
                    let roomAndBoardTreeId = rnbNode.tree_id;
                    if (planTypeNode) {
                        benefitTreeId = benefit.tree_id.replace('BASE', planTypeNode.code);
                        roomAndBoardTreeId = rnbNode.tree_id.replace('BASE', planTypeNode.code);
                    }
                    return benefit.level === 5 && benefitTreeId.startsWith(roomAndBoardTreeId);
                });

                this.setState({roomAndBoard: roomAndBoard.map(benefit => {
                    return {
                        name: benefit.name,
                        custom_metadata: benefit.custom_metadata,
                        limits: benefit.limits
                    };
                })});

                this.setState({roomAndBoardPlan: benefitPlans.find(plan => {
                    return plan.name.toLowerCase() === 'room and board';
                })});
            });
        }
    }










    getBenefitsLimit = () => {
        let limits: BenefitData[] = [];
        this.props.benefits.forEach(benefit => {
            let targetLimitIndex = -1;
            limits.forEach((limit, index) => {
                if (benefit.limitType && limit.limitType === benefit.limitType) {
                    targetLimitIndex = index;
                }
            })
            if (targetLimitIndex > -1) {
                limits[targetLimitIndex].limit += benefit.limit;
            } else {
                limits.push({...benefit});
            }
        });
        return limits.filter(benefit => benefit.limitType);
    }










    render(): JSX.Element {
        const member = this.props.member;
        const name = GlobalFunction.toNameFormat(
            member.firstName,
            member.middleName,
            member.lastName
        );
        const clientName = member.client ? member.client.registeredName : '---';
        const birthDate = GlobalFunction.toShortDateString(new Date(member.birthDate));
        const age = GlobalFunction.getAge(new Date(member.birthDate));
        const contractStatus = member.contract ? `${member.contract.status} Contract` : 'No Contract Found';
        const roomAndBoard = this.state.roomAndBoard;
        const roomAndBoardPlan = this.state.roomAndBoardPlan;
        const mblLimit = roomAndBoardPlan ? roomAndBoardPlan.limits.find(limit => limit.type === 'MBL') : undefined;
        const pecLimit = roomAndBoardPlan ? roomAndBoardPlan.limits.find(limit => limit.type === 'PEC') : undefined;

        return (
            <div className='section'>
                <div className={member.isVip ? 'tag' : ''}>
                    {member.isVip ? (
                        <div className='tag-label'>VIP</div>
                    ) : null}
                    <div className='circle' style={{backgroundColor: '#3AB77D'}} />
                    <div style={{display: 'inline-block'}}>
                        <div className='header-2'>{member.memberId}</div>
                    </div>
                    <div className='text'>{name}</div>
                    <div className='text'>{clientName}</div>
                    <div className='text'>{'---'}</div>
                    <div className='text'>{contractStatus}</div>
                </div>
                <Grid container>
                    {this.props.loadingBenefits ? (
                        <Grid item className='sub-section' xs={12}>
                            <Loading height='2rem' />
                        </Grid>
                    ) : (
                        this.getBenefitsLimit().map(benefit => {
                            return (
                                this.renderInfoItem(
                                    `${benefit.limitType.toUpperCase()} LIMIT`,
                                    `${GlobalFunction.formatNumber(benefit.limit)} ${benefit.limitUnit}`
                                )
                            );
                        })
                    )}
                </Grid>
                
                <Grid container>
                {this.state.loadingBenefits ? (
                        <Grid item className='sub-section' xs={12}>
                            <Loading height='2rem' />
                        </Grid>
                    ) : (
                        <>
                        {this.renderInfoItem('R&B', roomAndBoard.length > 0 ? (
                            roomAndBoard[roomAndBoard.length - 1].name
                        ) : '---')}
                        {this.renderInfoItem('EFFECTIVITY DATE', this.state.effectivityDate ? (
                            GlobalFunction.toShortDateString(this.state.effectivityDate)
                        ) : '---')}
                        {this.renderInfoItem('RENEWAL DATE', this.state.renewalDate ? (
                            GlobalFunction.toShortDateString(this.state.renewalDate)
                        ) : '---')}
                        {this.renderInfoItem('EXTENSION DATE', this.state.extensionDate ? (
                            GlobalFunction.toShortDateString(this.state.extensionDate)
                        ) : '---')}
                        {this.renderInfoItem('MBL', roomAndBoardPlan && mblLimit && typeof mblLimit.limit === 'number' ? (
                            `${mblLimit.limit_unit} ${GlobalFunction.formatNumber(mblLimit.limit, true)}`
                        ) : '---')}
                        {this.renderInfoItem('PEC LIMIT', roomAndBoardPlan && pecLimit && typeof pecLimit.limit === 'number' ? (
                            `${pecLimit.limit_unit} ${GlobalFunction.formatNumber(pecLimit.limit, true)}`
                        ) : '---')}
                        </>
                    )}
                    {this.renderInfoItem('BIRTHDATE (AGE)', `${birthDate} (${age})`)}
                    {this.renderInfoItem('GENDER / STATUS', `${member.gender} / ${member.civilStatus}`)}
                    {this.renderInfoItem('MEMBER TYPE', this.state.memberType && this.state.memberType !== '' ? this.state.memberType : '---')}
                    {this.renderInfoItem('MEMBER PLAN TYPE', this.state.memberPlanType)}
                </Grid>
                <div className='sub-section'>
                    {this.props.loadingRecentTickets ? (
                        <Loading height='2rem' />
                    ) : (
                        this.props.recentTickets ? (
                            <>
                            <div className='header-2'>RECENT TICKETS</div>
                            <CustomTable
                                paginationClass='custom-pagination-small'
                                className='custom-table-small'
                                labelRowsPerPage='Rows:'
                                rows={this.props.recentTickets}
                                rowsPerPageOptions={[5, 10, 25]}
                                renderHeader={() => this.renderTableHeader()}
                                renderRow={(rowData: SimpleTicketData, index: number) =>
                                    this.renderTableRow(rowData, index)} />
                            </>
                        ) : null
                    )}
                </div>
            </div>
        );;
    }










    renderTableHeader(): JSX.Element {
        return (
            <>
            <TableCell style={{minWidth: '70px', fontSize: '10px'}}>Ticket ID</TableCell>
            <TableCell style={{fontSize: '10px'}}>Status</TableCell>
            <TableCell style={{fontSize: '10px'}}>Created</TableCell>
            <TableCell style={{fontSize: '10px'}}>Updated</TableCell>
            
            </>
        );
    }










    renderTableRow(ticket: SimpleTicketData, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
        return (
            <TableRow style={{backgroundColor: backgroundColor, fontSize: '10px'}}>
                <TableCell>{ticket.ticketNo}</TableCell>
                <TableCell>{ticket.status}</TableCell>
                <TableCell>{GlobalFunction.toShortDateString(ticket.dateCreated)}</TableCell>
                <TableCell>{GlobalFunction.toShortDateString(ticket.dateUpdated)}</TableCell>
            </TableRow>
        );
    }










    renderInfoItem(title: string, content: React.ReactNode): JSX.Element {
        return (
            <Grid item className='sub-section' xs={6}>
                <div className='light-text'>{title}</div>
                <div className='small-text'>{content}</div>
            </Grid>
        );
    }
}