import * as React from 'react';
import { CustomModal } from 'Pages/CustomerCarePage/Components/CustomModal';
import { InputArea } from 'Pages/CustomerCarePage/Components/InputArea';
import { CustomButton } from 'Pages/CustomerCarePage/Components/CustomButton';
import { Processmaker } from 'Pages/CustomerCarePage/Processmaker';
import { DropdownChoice, TicketData, CaseData, LoginData } from 'Pages/CustomerCarePage/Models';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';
import { API } from 'Pages/CustomerCarePage/API';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { Global } from 'Pages/CustomerCarePage/Global';

interface IProps {
    caseId: string;
    login: LoginData;
    ticket: TicketData;
    currentCase: CaseData;
    isOpen: boolean;
    totalAmount: number;
    onClose: () => void;
    onSuccess: (withinLimit: boolean, onAfterSuccess?: (onSuccess: Function) => void) => void;
    onBeforeMainAction: () => void;
}

interface IStates {
    assignees: DropdownChoice[];
    assignee: DropdownChoice;
    notes: string;
    isError: boolean;
}

export class ReassignTicketModal extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            assignees: [],
            assignee: { value: '', label: '' },
            notes: '',
            isError: false
        };
    }

    componentDidMount() {
        const departmentArray: string[] = [];
        if (this.props.ticket.ticketType !== 'Request Requiring Approval' &&
            this.props.ticket.specificType !== 'Inpatient')
            departmentArray.push('Customer Service');
        if (this.props.ticket.ticketType !== 'Availment' && this.props.ticket.ticketType !== 'Request Requiring Approval'  || (this.props.ticket.ticketType === 'Availment'&& 
            this.props.ticket.specificType === 'Inpatient') || (this.props.ticket.ticketType === 'Availment'&& 
            this.props.ticket.specificType === 'Outpatient Procedure'))
            departmentArray.push('Patient Relations');
        if (this.props.ticket.ticketType !== 'Availment')
            departmentArray.push('AMD');
        const department = encodeURIComponent(departmentArray.join());
        
        API.get(`user-groups/customer-care?department=${department}`, (response) => {
            const groups = response.groups.map(group => {
                return {
                    value: group.pmaker_uid,
                    label: group.name,
                    params: [true, group]
                }
            });
            const users = response.users.map(user => {
                return {
                    value: user.pmaker_uid,
                    label: `${user.first_name} ${user.last_name}`,
                    params: [false, user]
                }
            });
            const combined = groups.concat(users);
            GlobalFunction.setMeInAssignees(combined, this.props.login.pmakerUid);
            this.setState({assignees: combined.filter(user => user.label !== 'Me')});
        });
    }

    isValid = () => {
        if (this.state.assignee.label !== 'Me' && this.state.notes === '') {
            this.setState({isError: true});
            return false;
        }
        return true;
    }

    onReassign = () => {
        if (!this.isValid()) return;
        if (!this.state.assignee.params)return;

        const assignee = this.state.assignee;
        const ticket = this.props.ticket;
        const currentCase = this.props.currentCase;
        const login = this.props.login;
        const approvalLimit = this.state.assignee.params[1].amount_limit;

        if (this.props.totalAmount <= approvalLimit) {
            this.props.onBeforeMainAction();
            
            const body = {
                button_pressed: 'ASSIGN',
                next_user: assignee.value,
                assign_to_group: assignee.params && assignee.params.length > 0 ? assignee.params[0] : false,
                within_limit: true
            };
    
            const patchBody = {
                assign_to: assignee.label,
                assigned_by: `${this.props.login.firstName} ${this.props.login.lastName}`,
                assign_remarks: this.state.notes,
                ticket_history_info: {
                    assignee_id: login.userId,
                    assignee_name: `${login.firstName} ${login.lastName}`,
                    pmaker_task: currentCase.task,
                    action: 'Reassigned Ticket'
                }
            };
            Global.NEW_ASSIGNEE = patchBody.assign_to;

            Processmaker.put(`cases/${this.props.caseId}/variable`, body, () => {
                Processmaker.put(`cases/${this.props.caseId}/route-case`, {}, () => {
                    if (currentCase.task === 'Inquiry - Comment' ||
                        currentCase.task === 'Outpatient - Comment' || 
                        currentCase.task === 'Complaint - Resolve Ticket') {
                        this.props.onSuccess(true, (onSuccess) => {
                            API.patch(`customer-care/tickets/${ticket.id}`, patchBody, onSuccess);
                        });
                    } else {
                        Processmaker.put(`cases/${this.props.caseId}/route-case`, {}, () => {
                            this.props.onSuccess(true, (onSuccess) => {
                                API.patch(`customer-care/tickets/${ticket.id}`, patchBody, onSuccess);
                            });
                        });
                    }
                });
            });
        } else {
            this.props.onSuccess(false);
        }
    }

    render(): JSX.Element {
        
        return (
            <CustomModal
                className='modal'
                isOpen={this.props.isOpen}
                handleClose={() => this.props.onClose()}
                header='Re-assign Ticket'
                content={
                    <>
                    <div style={{padding: '0.5rem 0'}}>
                        {'Re-assigning this ticket to another user will allow him/her to manage this ticket instead.'}
                    </div>
                    <div style={{padding: '0.5rem 0'}}>
                        <Dropdown 
                            handleBlur={true}
                            placeholder='Enter New Assignee'
                            choices={this.state.assignees}
                            value={this.state.assignee}
                            showAllResult
                            onChange={(value: DropdownChoice) => {
                                this.setState({assignee: value});
                            }} />
                    </div>
                    <div style={{padding: '0.5rem 0'}}>
                        <InputArea 
                            placeholder='Notes'
                            defaultRows={4}
                            error={this.state.isError && this.state.notes === ''}
                            value={this.state.notes}
                            onChange={(value: string) => {
                                this.setState({notes: value});
                            }} />
                    </div>
                    </>
                }
                footer={
                    <>
                    <CustomButton
                            text='Cancel'
                            variant='outlined'
                            onClick={() => {
                                this.props.onClose()
                            }} />
                        <span style={{padding: '1rem'}} />
                    <CustomButton 
                        text='Re-assign'
                        variant='contained'
                        onClick={() => {
                            this.onReassign();
                        }} />
                    </>
                } />
        );
    }
}