import * as React from 'react';
import { CustomModal } from 'Pages/CustomerCarePage/Components/CustomModal';
import { InputArea } from 'Pages/CustomerCarePage/Components/InputArea';
import { CustomButton } from 'Pages/CustomerCarePage/Components/CustomButton';
import { Processmaker } from 'Pages/CustomerCarePage/Processmaker';
import { API } from 'Pages/CustomerCarePage/API';
import { TicketData, LoginData, CaseData } from 'Pages/CustomerCarePage/Models';
import { get } from 'lodash';
import { Global } from 'Pages/CustomerCarePage/Global';

interface IProps {
    caseId: string;
    caseData?: CaseData;
    ticket: TicketData;
    login: LoginData;
    isEscalateModalOpen: boolean;
    isForCommentModalOpen: boolean;
    isForApprovalModalOpen: boolean;
    isForAssessmentModalOpen: boolean;
    amountLimit: number;
    currentPage: 'Update Ticket' | 'Resolve Ticket';
    onClose: () => void;
    onSuccess: (onAfterSuccess: (onSuccess: Function) => void) => void;
    onBeforeMainAction: () => void;
}

interface IStates {
    notes: string;
    isError: boolean;
}

export class EscalateTicketModal extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            notes: '',
            isError: false
        };
    }










    isValid = () => {
        if (this.state.notes === '') {
            this.setState({isError : true});
            return false;
        }
        return true;
    }










    onEscalate = () => {
        if (!this.isValid()) return;

        const ticket = this.props.ticket;
        const login = this.props.login;
        const department = login.group ? login.group.department : '';

        const mainAction = (nextUser?: any, isReassign?: boolean) => {
            this.props.onBeforeMainAction();

            const supervisorPmakerUID = login.ccData ? login.ccData.supervising_department_pmaker_id : '';
            const supervisorDept = login.ccData ? login.ccData.supervising_department : '';
            const body = {
                button_pressed: (isReassign ? 'ASSIGN' : 'ESCALATE'),
                assign_to_group: (isReassign ? true : undefined),
                next_user: nextUser && nextUser.group_pmaker_ids && nextUser.group_pmaker_ids.length > 0 ? nextUser.group_pmaker_ids : supervisorPmakerUID
            };
            const patchBody = {
                assign_to: nextUser && nextUser.group_names && nextUser.group_names.length > 0 ? nextUser.group_names.join(', ') : supervisorDept,
                assigned_by: `${this.props.login.firstName} ${this.props.login.lastName}`,
                assign_remarks: this.state.notes,
                ticket_history_info: this.props.caseData ? {
                    assignee_id: login.userId,
                    assignee_name: `${login.firstName} ${login.lastName}`,
                    pmaker_task: this.props.caseData.task,
                    action: 'Escalated Ticket'
                } : undefined
            };
            Global.NEW_ASSIGNEE = patchBody.assign_to;

            Processmaker.put(`cases/${this.props.caseId}/variable`, body, () => {
                Processmaker.put(`cases/${this.props.caseId}/route-case`, {}, () => {
                    this.props.onSuccess((onSuccess) => {
                        this.onPatch(`customer-care/tickets/${ticket.id}`, patchBody, onSuccess);
                    });
                });
            });  
        }

        if (this.props.caseData) {
            if (this.props.caseData.task === 'Complaint - Close Ticket' ||
                this.props.caseData.task === 'Complaint - Claim Close Task' ||
                this.props.caseData.task === 'Complaint - Escalate Ticket 3' ||
                this.props.caseData.task === 'Complaint - Resolve Ticket' ||
                this.props.caseData.task === 'Complaint - Escalate Ticket 2') {
                const role = (
                    (this.props.caseData.task === 'Complaint - Resolve Ticket' ||
                    this.props.caseData.task === 'Complaint - Escalate Ticket 2') ? 
                    'Ticket Approver' : 
                    'Ticket Reviewer'
                );
                    
                if (role === 'Ticket Reviewer') { 
                    API.get(
                        `group/pmaker_uids?` +
                        `department=${department}&` + 
                        `role=${role}&` +
                        `with_names=true`, (response) => {
                           
                        mainAction(response);
                    });
                } else {
                    API.get(
                        `group/pmaker_uids?` +
                        `department=${department}&` + 
                        `requesting_group=${get(login, 'group.name', '')}&` +
                        `role=${role}&` +
                        `with_names=true`, (response) => {
                        mainAction(response);
                    });
                }
                
            } else if (
                this.props.caseData.task === 'Complaint - Comment 2' ||
                this.props.caseData.task === 'Complaint - Comment 3') {
                    mainAction([], true);
            } else {
                mainAction();
            }
        } else {
            
            mainAction();
        }
    }










    onForComment = () => {
        if (!this.isValid() || !this.props.caseData) return;

        this.props.onBeforeMainAction();

        const ticket = this.props.ticket;
        const login = this.props.login;

        const supervisorPmakerUID = login.ccData ? login.ccData.supervising_department_pmaker_id : '';
        const supervisorDept = login.ccData ? login.ccData.supervising_department : '';

        const variableBody = {
            button_pressed: 'ASSIGN',
            next_user: supervisorPmakerUID,
            assign_to_group: true
        };
        const patchBody = {
            assign_to: supervisorDept,
            assigned_by: `${this.props.login.firstName} ${this.props.login.lastName}`,
            assign_remarks: this.state.notes,
            ticket_history_info: {
                assignee_id: login.userId,
                assignee_name: `${login.firstName} ${login.lastName}`,
                pmaker_task: this.props.caseData.task,
                action: 'Escalated Ticket for Comment'
            }
        };
        Global.NEW_ASSIGNEE = patchBody.assign_to;

        Processmaker.put(`cases/${this.props.caseId}/variable`, variableBody, () => {
            Processmaker.put(`cases/${this.props.caseId}/route-case`, {}, () => {
                if (this.props.caseData && this.props.caseData.task === 'Approval - View Ticket Details 2') {
                    Processmaker.put(`cases/${this.props.caseId}/route-case`, {}, () => {
                        this.props.onSuccess((onSuccess) => {
                            this.onPatch(`customer-care/tickets/${ticket.id}`, patchBody, onSuccess);
                        });
                    });
                } else {
                    this.props.onSuccess((onSuccess) => {
                        this.onPatch(`customer-care/tickets/${ticket.id}`, patchBody, onSuccess);
                    });
                }
            });
        }); 
    }










    onForApproval = () => {
        if (!this.isValid() || !this.props.caseData) return;

        this.props.onBeforeMainAction();

        const ticket = this.props.ticket;
        const login = this.props.login;

        const supervisorPmakerUID = login.ccData ? login.ccData.supervising_department_pmaker_id : '';
        const supervisorDept = login.ccData ? login.ccData.supervising_department : '';

        const variableBody = {
            button_pressed: 'ESCALATE',
            next_user: supervisorPmakerUID
        };
        const patchBody = {
            assign_to: supervisorDept,
            assigned_by: `${this.props.login.firstName} ${this.props.login.lastName}`,
            assign_remarks: this.state.notes,
            ticket_history_info: {
                assignee_id: login.userId,
                assignee_name: `${login.firstName} ${login.lastName}`,
                pmaker_task: this.props.caseData.task,
                action: 'Escalated Ticket for Approval'
            }
        };
        Global.NEW_ASSIGNEE = patchBody.assign_to;

        Processmaker.put(`cases/${this.props.caseId}/variable`, variableBody, () => {
            Processmaker.put(`cases/${this.props.caseId}/route-case`, {}, () => {
                this.props.onSuccess((onSuccess) => {
                    this.onPatch(`customer-care/tickets/${ticket.id}`, patchBody, onSuccess);
                });
            });
        }); 
    }










    onForAssessment = () => {
        if (!this.isValid() || !this.props.caseData) return;

        this.props.onBeforeMainAction();

        const ticket = this.props.ticket;
        const login = this.props.login;

        const supervisorPmakerUID = login.ccData ? login.ccData.supervising_department_pmaker_id : '';
        const supervisorDept = login.ccData ? login.ccData.supervising_department : '';

        const variableBody = {
            button_pressed: 'PROCEED WITH FINAL ASSESSMENT',
            next_user: supervisorPmakerUID
        };
        const patchBody = {
            assign_to: supervisorDept,
            assigned_by: `${this.props.login.firstName} ${this.props.login.lastName}`,
            assign_remarks: this.state.notes,
            ticket_history_info: {
                assignee_id: login.userId,
                assignee_name: `${login.firstName} ${login.lastName}`,
                pmaker_task: this.props.caseData.task,
                action: 'Escalated Ticket for Approval'
            }
        };
        Global.NEW_ASSIGNEE = patchBody.assign_to;

        Processmaker.put(`cases/${this.props.caseId}/variable`, variableBody, () => {
            Processmaker.put(`cases/${this.props.caseId}/route-case`, {}, () => {
                this.props.onSuccess((onSuccess) => {
                    this.onPatch(`customer-care/tickets/${ticket.id}`, patchBody, onSuccess);
                });
            });
        }); 
    }










    onPatch = (url: string, body: any, onSuccess: Function) => {
        API.patch(url, body, () => {
            onSuccess();
        });
    }










    render(): JSX.Element {
        return (
            <CustomModal
                className='modal'
                isOpen={
                    this.props.isEscalateModalOpen || 
                    this.props.isForApprovalModalOpen ||
                    this.props.isForCommentModalOpen ||
                    this.props.isForAssessmentModalOpen}
                handleClose={() => this.props.onClose()}
                header={
                    this.props.isEscalateModalOpen ? 'Escalate Ticket' : 
                    this.props.isForApprovalModalOpen ? 'For Approval' : 
                    this.props.isForCommentModalOpen ? 'For Comment' : 
                    this.props.isForAssessmentModalOpen ? 'Proceed with Final Assessment' : ''}
                content={
                    <>
                    <div style={{padding: '0.5rem 0'}}> 
                        This ticket will be automatically assigned to your supervisor for ticket updates.
                    </div>
                    <div style={{padding: '0.5rem 0'}}>
                        <InputArea
                            placeholder='Notes'
                            defaultRows={4}
                            value={this.state.notes}
                            error={this.state.isError && this.state.notes === ''}
                            onChange={(value: string) => {
                                this.setState({notes: value});
                            }} />
                    </div>
                    </>
                }
                footer={
                    <>
                    <CustomButton
                        text='Cancel'
                        variant='outlined'
                        onClick={() => {
                            this.props.onClose();
                        }} />
                    <span style={{padding: '1rem'}} />
                    {this.props.isEscalateModalOpen ? (
                        <CustomButton
                            text='Escalate'
                            onClick={() => {
                                this.onEscalate();
                            }} />
                    ) : this.props.isForCommentModalOpen ? (
                        <CustomButton
                            text='Escalate For Comment'
                            onClick={() => {
                                this.onForComment();
                            }} />
                    ) : this.props.isForApprovalModalOpen ? (
                        <CustomButton
                            text='Escalate For Approval'
                            onClick={() => {
                                if (this.props.currentPage === 'Update Ticket') {
                                    this.onForAssessment();
                                } else {
                                    this.onForApproval();
                                }
                            }} />
                    ) : this.props.isForAssessmentModalOpen ? (
                        <CustomButton
                            className='button-special'
                            text='Proceed with Final Assessment'
                            onClick={() => {
                                this.onForAssessment();
                            }} />
                    ) : null}
                    </>
                } />
        );
    }
}
