import { LoginData } from "./Models";
import { Utils } from "@hims/core";
import { Payloads } from "./Payloads";

let backendUrl = 
    localStorage.getItem('PMAKER_BASE_URL') ?
    localStorage.getItem('PMAKER_BASE_URL') :
    localStorage.getItem('XDEV_PMAKER_BASE_URL');

export class Processmaker {

    static loginData?: LoginData;

    static async get(url: string, success: Function, fail?: Function) {
        const mainAction = (loginData: LoginData) => {
            fetch(backendUrl + url, {
                method: 'GET',
                headers: { 
                    'Content-Type': 'application/json',
                    'Authorization' : 'Bearer ' + loginData.pmToken
                },
            })
            .then(response => response.json())
            .then(data => success(data))
            .catch(error => fail ? fail(error) : {});
        }

        if (Processmaker.loginData) {
            mainAction(Processmaker.loginData);
        } else {
            Utils.StorageService('user_data').then((result) => {
                Processmaker.loginData = Payloads.getLoginData(result);
                if (Processmaker.loginData) {
                    mainAction(Processmaker.loginData);
                }
            });
        }
    }

    static async post(url:string, body: any, success: Function, fail?: Function) {
        const mainAction = (loginData: LoginData) => {
            fetch(backendUrl + url, {
                method: 'POST',
                headers: { 
                    'Content-Type': 'application/json',
                    'Authorization' : 'Bearer ' + loginData.pmToken
                },
                body: JSON.stringify(body)
            })
            .then(response => response.json())
            .then(data => success(data))
            .catch(error => fail ? fail(error) : {});
        }

        if (Processmaker.loginData) {
            mainAction(Processmaker.loginData);
        } else {
            Utils.StorageService('user_data').then((result) => {
                Processmaker.loginData = Payloads.getLoginData(result);
                if (Processmaker.loginData) {
                    mainAction(Processmaker.loginData);
                }
            });
        }
    }

    static async put(url: string, body: any, success: Function, fail?: Function) {
        const mainAction = (loginData: LoginData) => {
            fetch(backendUrl + url, {
                method: 'PUT',
                headers: { 
                    'Content-Type': 'application/json',
                    'Authorization' : 'Bearer ' + loginData.pmToken
                },
                body: JSON.stringify(body)
            })
            .then(response => response)
            .then(data => success(data))
            .catch(error => fail ? fail(error) : {});
        }

        if (Processmaker.loginData) {
            mainAction(Processmaker.loginData);
        } else {
            Utils.StorageService('user_data').then((result) => {
                Processmaker.loginData = Payloads.getLoginData(result);
                if (Processmaker.loginData) {
                    mainAction(Processmaker.loginData);
                }
            });
        }
    }

    /* Use this shortcut call if you just want the success response */
    static shortcutCall = (method: 'POST' | 'PUT' | 'GET', url: string, body: any, onSuccess: Function, onError?: Function) => {
        if (method === 'POST') {
            Processmaker.post(url, body,
                (response) => {
                    onSuccess(response);
                },
                (error) => {
                    if (onError) {
                        onError(error);
                    }
                }
            );
        }

        if (method === 'PUT') {
            Processmaker.put(url, body,
                (response) => {
                    onSuccess(response);
                },
                (error) => {
                    if (onError) {
                        onError(error);
                    }
                }
            );
        }

        if (method === 'GET') {
            Processmaker.get(url,
                (response) => {
                    onSuccess(response);
                },
                (error) => {
                    if (onError) {
                        onError(error);
                    }
                }
            );
        }
    }

    static async postclaim(url: string, success: Function, fail: Function) {
        const mainAction = (loginData: LoginData) => {
            fetch(backendUrl + url, {
                method: 'POST',
                headers: { 
                    'Content-Type': 'application/json',
                    'Authorization' : 'Bearer ' + loginData.pmToken
                }
            })
            .then(response => response)
            .then(data => success(data))
            .catch(error => fail(error));
        }

        if (Processmaker.loginData) {
            mainAction(Processmaker.loginData);
        } else {
            Utils.StorageService('user_data').then((result) => {
                Processmaker.loginData = Payloads.getLoginData(result);
                if (Processmaker.loginData) {
                    mainAction(Processmaker.loginData);
                }
            });
        }
    }
}