import * as React from 'react';
import { CustomTable, HeaderData } from 'Pages/CustomerCarePage/Components/CustomTable';
import { TableRow, TableCell, Grid, Paper } from '@material-ui/core';
import { faDownload } from '@fortawesome/free-solid-svg-icons';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';
import { API } from 'Pages/CustomerCarePage/API';
import { MemberData, MemberUtilData, ClientData, ClientUtilData } from 'Pages/CustomerCarePage/Models';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';

interface IProps {
    member?: MemberData;
    client?: ClientData;
}

interface IStates {
    searchText: string;
    isLoaded: boolean;

    /* Data coming from backend */
    limit?: {
        type: string;
        limit: number;
        limitUnit: string;
    };
    ibnrAmount: number;
    utilAmount: number;
    paidPremium?: number;
    issuedRefund?: number;
    recommendedRenewalRate?: number;
    memberUtils?: MemberUtilData[];
    clientUtils?: ClientUtilData[];
}


export class CheckUtilizationSection extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            searchText: '',
            isLoaded: false,

            /* Data coming from backend */
            ibnrAmount: 0,
            utilAmount: 0
        };
    }










    componentDidMount() {
        if (this.props.member) {
            const memberId = this.props.member.memberId;
            const memberMongoId = this.props.member.id;
            const contractId = this.props.member.contract ? this.props.member.contract.id : '';

            /* Get ABL or MBL Limits */
            API.get(`members/${memberMongoId}/contracts/${contractId}/benefits`, (response) => {
                const roomAndBoard = response.find(benefit => {
                    return benefit.level === 4 && benefit.name.toLowerCase() === 'room and board';
                });
                if (roomAndBoard) {
                    const ablLimit = roomAndBoard.limits.find(limit => limit.type === 'ABL');
                    const mblLimit = roomAndBoard.limits.find(limit => limit.type === 'MBL');
                    if (ablLimit) {
                        this.setState({limit: {
                            type: 'ANNUAL BENEFIT LIMIT',
                            limit: typeof ablLimit.limit === 'number' ? ablLimit.limit : 0,
                            limitUnit: ablLimit.limit_unit
                        }});
                    }
                    if (mblLimit) {
                        this.setState({limit: {
                            type: 'MAXIMUM BENEFIT LIMIT',
                            limit:  typeof mblLimit.limit === 'number' ? mblLimit.limit : 0,
                            limitUnit: mblLimit.limit_unit
                        }});
                    }

                    /* Get IBNR Amount */
                    API.get(`customer-care/member-utilization-loa/member/${memberId}/contract/${contractId}`, (response) => {
                        this.setState({
                            ibnrAmount: response.total_amount ? response.total_amount : 0,
                            utilAmount: 0
                        });

                        /* Get loa table data */
                        API.get(`customer-care/member-utilization-loa/contract/${contractId}?type=member&id=${memberMongoId}`, (response) => {
                            this.setState({memberUtils: response.loas.map(loa => {
                                return {
                                    ...loa,
                                    type: 'IBNR',
                                    session: 1,
                                    date_issued_date: new Date(loa.date_issued_date)
                                }
                            })});
                        });
                    });
                    
                    this.setState({isLoaded: true});
                }
            });

        } else if (this.props.client) {
            const clientId = this.props.client.corporateAccountNo;
            const contractId = this.props.client.contract ? this.props.client.contract.id : '';
            API.get(`customer-care/member-utilization-loa/contract/${contractId}?type=client&id=${clientId}`, (response) => {
                const loaLayer1: ClientUtilData[] = response.loas.map(loa => {
                    return {
                        ticketType: loa._id.ticket_type,
                        totalAmount: loa.total_amount,
                        totalCount: 0,
                        principalsCount: loa._id.member_type === 'Principal' ? loa.count : 0,
                        dependentsCount: loa._id.member_type === 'Dependent' ? loa.count : 0,
                        unspecifiedCount: loa._id.member_type === 'Not Specified' ? loa.count : 0
                    }
                });

                const loaLayer2: ClientUtilData[] = [];
                loaLayer1.forEach(loa => {
                    let foundIndex = loaLayer2.findIndex(loa2 => loa2.ticketType === loa.ticketType);
                    if (foundIndex != -1) {
                        loaLayer2[foundIndex] = {
                            ...loaLayer2[foundIndex],
                            totalAmount: loaLayer2[foundIndex].totalAmount + loa.totalAmount,
                            principalsCount: loaLayer2[foundIndex].principalsCount + loa.principalsCount,
                            dependentsCount: loaLayer2[foundIndex].dependentsCount + loa.dependentsCount,
                            unspecifiedCount: loaLayer2[foundIndex].unspecifiedCount + loa.unspecifiedCount
                        };
                    } else {
                        loaLayer2.push(loa);
                    }
                });

                this.setState({clientUtils: loaLayer2.map(loa => {
                    return {
                        ...loa,
                        totalCount: loa.principalsCount + loa.dependentsCount + loa.unspecifiedCount
                    }
                })});

                /* Get IBNR Amount */
                if (this.state.clientUtils) {
                    let totalAmount = 0;
                    this.state.clientUtils.forEach(util => {
                        totalAmount += util.totalAmount;
                    });
                    this.setState({ibnrAmount: totalAmount});
                }

                /* Dummy Data */
                this.setState({
                    limit: {
                        type: 'ANNUAL BENEIT LIMIT',
                        limit: 0,
                        limitUnit: 'PHP',
                    },
                    utilAmount: 0,
                    // paidPremium: 999999,
                    // issuedRefund: 99999,
                    // recommendedRenewalRate: 1500000
                });

                this.setState({isLoaded: true});
            });
        }
    }










    getFilteredMemberLOAs = (loas: MemberUtilData[]) => {
        if (this.state.searchText !== '') {
            return loas.filter(loa => {
                return (
                    loa.approval_code.toLowerCase().includes(this.state.searchText.toLowerCase()) ||
                    loa.loa_no.toLowerCase().includes(this.state.searchText.toLowerCase())
                );
            });
        }
        return loas;
    }
    









    render(): JSX.Element {
        return (
            <div className='section'>
                {!this.state.isLoaded ? (
                    <Loading height='10rem'/>
                ) : (
                    this.renderMainContent()
                )}
            </div>
        );
    }










    renderMainContent(): JSX.Element {
        const limit = this.state.limit;
        const limitAmount = this.state.limit ? this.state.limit.limit : 0;
        const utilAmount = this.state.utilAmount;
        const ibnrAmount = this.state.ibnrAmount;
        const availableAmount = limitAmount - utilAmount - ibnrAmount;
        const utilPerc = utilAmount / limitAmount * 100;
        const ibnrPerc = ibnrAmount / limitAmount * 100;
        const availablePerc = availableAmount / limitAmount * 100;

        const paidPremiumText = this.state.paidPremium ? 
            GlobalFunction.formatNumber(this.state.paidPremium, true) : 
            '---';

        const issuedRefundText = this.state.issuedRefund ?
            GlobalFunction.formatNumber(this.state.issuedRefund, true) : 
            '---';

        const recommendedRenewalRateText = this.state.recommendedRenewalRate ?
            GlobalFunction.formatNumber(this.state.recommendedRenewalRate, true) : 
            '---';

        const renderItem = (header: string, value: React.ReactNode, xs?: any): JSX.Element => {
            return (
                <Grid item xs={!xs ? 3 : xs}>
                    <div className='light-text'>{header}</div>
                    <div className='header-2'>{value}</div>
                </Grid>
            );
        }

        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1'>Check Utilization</div>
                <Grid container className='sub-section' spacing={5}>
                    {limit ? (
                        <>
                        {this.state.memberUtils ? (
                            renderItem(limit.type, `${limit.limitUnit} ${GlobalFunction.formatNumber(limitAmount, true)}`)
                        ) : this.state.clientUtils ? (
                            renderItem('RECOMMENDED RENEWAL RATE', `${limit.limitUnit} ${recommendedRenewalRateText}`)
                        ) : null}
                        {/* {renderItem(limit.type, `${limit.limitUnit} ${GlobalFunction.formatNumber(limitAmount, true)}`)} */}
                        <Grid item xs={6}>
                            <Line 
                                // @ts-ignore
                                items={[
                                    { 
                                        value: Math.min(100, Math.max(utilPerc, 0)), 
                                        color: '#3FBB7F', 
                                        label1: 'UTILIZED', 
                                        label2: `${limit.limitUnit} ${GlobalFunction.formatNumber(utilAmount, true)}`
                                    },
                                    { 
                                        value: Math.min(100, Math.max(ibnrPerc, 0)), 
                                        color: '#1D236F', 
                                        label1: 'IBNR', 
                                        label2: `${limit.limitUnit} ${GlobalFunction.formatNumber(ibnrAmount, true)}`
                                    },
                                    this.state.memberUtils ? { 
                                        value: Math.min(100, Math.max(isNaN(availablePerc) ? 100 : availablePerc, 0)), 
                                        color: '#FCB53F', 
                                        label1: 'AVAILABLE', 
                                        label2: `${limit.limitUnit} ${GlobalFunction.formatNumber(availableAmount, true)}`
                                    } : undefined
                                ].filter(item => item)}
                                maxValue={100} />
                        </Grid>
                        <Grid item xs={3} />
                        {this.props.client ? (
                            <>
                            {renderItem('PAID PREMIUM', `${limit.limitUnit} ${paidPremiumText}`)}
                            {renderItem('ISSUED REFUND', `${limit.limitUnit} ${issuedRefundText}`)}
                            {/* {renderItem('RECOMMENDED RENEWAL RATE', `${limit.limitUnit} ${recommendedRenewalRateText}`, 6)} */}
                            </>
                        ): null}
                        </>
                    ) : null}
                </Grid>
                
                {this.state.memberUtils ? (
                    this.renderMemberUtilTable(this.state.memberUtils)
                ) : this.state.clientUtils ? (
                    this.renderClientUtilTable(this.state.clientUtils)
                ) : null}
                
                <div className='section' />
            </div>
        );
    }










    renderMemberUtilTable(utils: MemberUtilData[]): JSX.Element {

        const getStickyStyle = (width: string): React.CSSProperties => {
            return {
                left: 0,
                zIndex: 2,
                borderRight: '#9EA3AC 1px solid',
                position: 'sticky',
                minWidth: width, 
                maxWidth: width
            }
        }

        const getNormalStyle = (width: string): React.CSSProperties => {
            return {
                minWidth: width, 
                maxWidth: width
            }
        }

        const diagnosisFilter = (filterValue: any, cellValue: any) => {
            const foundData = cellValue ? cellValue.find(diagnosis => {
                return (
                    filterValue &&
                    `${diagnosis.icd10_code} - ${diagnosis.diagnosis}`.toLowerCase().includes(filterValue.toLowerCase())
                )
            }) : undefined;
            return foundData ? true : false;
        }

        const procedureFilter = (filterValue: any, cellValue: any) => {
            const foundData = cellValue ? cellValue.find(procedure => {
                return (
                    filterValue &&
                    `${procedure.cpt_code} - ${procedure.procedure_name}`.toLowerCase().includes(filterValue.toLowerCase())
                )
            }) : undefined;
            return foundData ? true : false;
        }

        const memberDataColumn: HeaderData[] = [
            { name: 'LOA ID',        key: 'loa_no',           style: getStickyStyle('100px'), filterType: 'string', sortType: 'string' },
            { name: 'Approval Code', key: 'approval_code',    style: getNormalStyle('120px'), filterType: 'string', sortType: 'string' },
            { name: 'Diagnosis',     key: 'diagnoses',        style: getNormalStyle('200px'), filterType: 'string', filterFunc: diagnosisFilter },
            { name: 'Procedure',     key: 'procedures',       style: getNormalStyle('200px'), filterType: 'string', filterFunc: procedureFilter },
            { name: 'Provider',      key: 'partner_name',     style: getNormalStyle('175px'), filterType: 'string', sortType: 'string' },
            { name: 'Amount',        key: 'total_amount',     style: getNormalStyle('125px'), filterType: 'number', sortType: 'number' },
            { name: 'Sessions',      key: 'session',          style: getNormalStyle('80px'),  filterType: 'number', sortType: 'number' },
            { name: 'Date Availed',  key: 'date_issued_date', style: getNormalStyle('150px'), filterType: 'date',   sortType: 'date' },
            { name: 'Type',          key: 'type',             style: getNormalStyle('75px'),  filterType: 'string', sortType: 'string' },
            { name: 'Status',        key: 'status',           style: getNormalStyle('75px'),  filterType: 'string', sortType: 'string' }
        ];

        const renderTableRow = (util: MemberUtilData, index: number, filter?: any): JSX.Element => {
            const style: React.CSSProperties = {
                backgroundColor: (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA')
            };
            const cellStyle: React.CSSProperties = {
                textAlign: 'center'
            };
            const stickyStyle: React.CSSProperties = {
                backgroundColor: (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA'),
                left: 0,
                zIndex: 2,
                borderRight: '#9EA3AC 1px solid',
                position: 'sticky'
            };
    
            return (
                <TableRow style={style}>
                    <TableCell style={stickyStyle}>
                        {util.loa_no}
                        <div className='sub-section-2' />
                        <div className='light-text'>SWIPE</div>
                        <div className='small-text'>{util.swipe ? 'Yes' : 'No'}</div>
                    </TableCell>
                    <TableCell>
                        {util.approval_code}
                    </TableCell>
                    <TableCell>
                        {util.primary_diagnosis && !util.diagnoses ? (
                            <>
                            {util.primary_icd10_code ? util.primary_icd10_code + ' - ' : ''}
                            {util.primary_diagnosis}
                            </>
                        ) : null}
                        {util.diagnoses ? (
                            util.diagnoses
                                .filter(diagnosis => {
                                    if (filter && filter.diagnoses && 
                                        !diagnosis.icd10_code.toLowerCase().includes(filter.diagnoses.toLowerCase()) &&
                                        !diagnosis.diagnosis.toLowerCase().includes(filter.diagnoses.toLowerCase())) {
                                        return false;
                                    }
                                    return true;
                                })
                                .map(diagnosis => {
                                    return (
                                        <div className='sub-section-2'>
                                            {`${diagnosis.icd10_code} - ${diagnosis.diagnosis}`}
                                        </div>
                                    );
                                })
                        ) : null}
                    </TableCell>
                    <TableCell>
                        {util.procedures && util.procedures.length > 0 ? (
                            util.procedures
                                .filter(procedure => {
                                    if (filter && filter.procedures && 
                                        !procedure.cpt_code.toLowerCase().includes(filter.procedures.toLowerCase()) &&
                                        !procedure.procedure_name.toLowerCase().includes(filter.procedures.toLowerCase())) {
                                        return false;
                                    }
                                    return true;
                                })
                                .map((procedure, index) => {
                                    return (
                                        <>
                                        {index > 0 ? <div className='sub-section-2' /> : null}
                                        {`${procedure.cpt_code} - ${procedure.procedure_name}`}
                                        </>
                                    );
                                })
                        ) : '---'}
                    </TableCell>
                    <TableCell>
                        {util.partner_name ? util.partner_name : '---'}
                        {util.partner_doctor_name ? (
                            <>
                            <div className='sub-section-2' />
                            <div className='light-text'>MEDICAL DOCTOR</div>
                            {util.partner_doctor_name}
                            </>
                        ) : null}
                    </TableCell>
                    <TableCell style={{textAlign: 'right'}}>
                        {`PHP ${GlobalFunction.formatNumber(util.total_amount, true)}`}
                    </TableCell>
                    <TableCell style={cellStyle}>{util.session}</TableCell>
                    <TableCell style={cellStyle}>{GlobalFunction.toShortDateString(util.date_issued_date)}</TableCell>
                    <TableCell style={cellStyle}>{util.type}</TableCell>
                    <TableCell style={cellStyle}>{util.status}</TableCell>
                </TableRow>
            );
        }

        return (
            <>
            <Grid className='sub-section' container spacing={3}>
                <Grid item xs={9}>
                    <InputField
                        innerPlaceholder='Search an availment'
                        value={this.state.searchText}
                        onChange={(value: string) => {
                            this.setState({searchText: value});
                        }} />
                </Grid>
                <Grid item xs={3} style={{lineHeight: '32px', color: '#AAAAAA'}}>
                    <TextButton
                        icon={faDownload}
                        color='inherit'
                        label='Generate Report'
                        onClick={() => {

                        }} />
                </Grid>
            </Grid>
            <CustomTable
                className='custom-table-scrollable-small'
                canOverflow
                stickyHeader
                columns={memberDataColumn}
                rows={this.getFilteredMemberLOAs(utils)}
                rowsPerPageOptions={[5, 25, 50]}
                renderRow={(rowData: any, index: number, filter?: any) => {
                    return renderTableRow(rowData, index, filter);
                }}
                // serverSideOption={{
                //     getUrl: (offset: number, limit: number) => {
                //         const memberMongoId = this.props.member ? this.props.member.id : '';
                //         const contractId = this.props.member && this.props.member.contract ? this.props.member.contract.id : '';
                //         return (
                //             `customer-care/member-utilization-loa/contract/${contractId}?` +
                //             `type=member&` +
                //             `id=${memberMongoId}&` +
                //             `filter[skip]=${offset}&filter[limit]=${limit}`
                //         );
                //     },
                //     onSuccess: (response: any) => {
                //         this.setState({loas: response.loas});
                //     },
                //     getCount: (response: any) => {
                //         return response.count;
                //     }
                // }} 
                />
            </>
        );
    }










    renderClientUtilTable(utils: ClientUtilData[]): JSX.Element {

        const clientDataColumn: HeaderData[] = [
            { name: 'Type',                  key: 'ticketType',      filterType: 'string', sortType: 'string', style: { minWidth: '150px' } },
            { name: 'Total Amount',          key: 'totalAmount',     filterType: 'number', sortType: 'number', style: { width: '150px' } },
            { name: 'Number of Cases',       key: 'totalCount',      filterType: 'number', sortType: 'number', style: { width: '130px' } },
            { name: 'Availed by Principals', key: 'principalsCount', filterType: 'number', sortType: 'number', style: { width: '150px' } },
            { name: 'Availed by Dependents', key: 'dependentsCount', filterType: 'number', sortType: 'number', style: { width: '175px' } }
        ];

        const renderTableRow = (util: ClientUtilData, index: number): JSX.Element => {
            const style: React.CSSProperties = {
                backgroundColor: (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA')
            };
            const cellStyle: React.CSSProperties = {
                textAlign: 'right'
            };
    
            return (
                <TableRow style={style}>
                    <TableCell>{util.ticketType}</TableCell>
                    <TableCell style={cellStyle}>{`PHP ${GlobalFunction.formatNumber(util.totalAmount, true)}`}</TableCell>
                    <TableCell style={cellStyle}>{util.totalCount}</TableCell>
                    <TableCell style={cellStyle}>{util.principalsCount}</TableCell>
                    <TableCell style={cellStyle}>{util.dependentsCount}</TableCell>
                </TableRow>
            )
        }

        return (
            <Paper>
                <CustomTable
                    className='custom-table-scrollable-small'
                    columns={clientDataColumn}
                    rows={utils}
                    renderRow={renderTableRow} />
                <Grid className='small-section' container spacing={1}>
                    <Grid className='light-text' item xs={3}>TOTAL</Grid>
                    <Grid className='header-2' item xs={3}>
                        {`PHP ${GlobalFunction.formatNumber(this.state.ibnrAmount, true)}`}
                    </Grid>
                </Grid>
            </Paper>
            
        );
    }
}










interface LineProps {
    items: {
        value: number;
        color: string;
        label1: string;
        label2: string;
    }[]
    maxValue: number;
}

export class Line extends React.Component<LineProps> {

    render(): JSX.Element {
        return (
            <div style={{width: '100%', height: 0}}>
                {this.props.items.map(item => {
                    return (
                        <div 
                            style={{
                                width: (item.value / this.props.maxValue * 100) + '%', 
                                height: '6px', 
                                display: 'inline-block',
                                backgroundColor: item.color
                            }} />
                    );
                })}
                <div style={{padding: '0.5rem 0'}}>
                {this.props.items.map(item => {
                    return (
                        <div 
                            style={{
                                width: (1 / this.props.items.length * 100) + '%', 
                                height: '6px', 
                                display: 'inline-block'
                            }}>
                            <div>
                                <div 
                                    style={{
                                        width: '10px', 
                                        height: '10px', 
                                        backgroundColor: item.color,
                                        display: 'inline-block',
                                        marginRight: '1rem'
                                    }}/>
                                <div 
                                    className='light-text' 
                                    style={{
                                        display: 'inline-block'
                                    }}>
                                    {item.label1}
                                </div>
                                <div 
                                    className='header-2' 
                                    style={{
                                        color: item.color,
                                    }}>
                                    {item.label2}
                                </div>
                            </div>
                         </div>
                    );
                })}
                </div>
            </div>
        );
    }
}