import React from 'react';
import {
    Grid,
    TableCell,
    TableRow,
    Snackbar
} from '@material-ui/core';
import { CustomDropzone } from 'Pages/CustomerCarePage/Components/CustomDropzone';
import { CustomChip } from 'Pages/CustomerCarePage/Components/CustomChip';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { CustomTable } from 'Pages/CustomerCarePage/Components/CustomTable';
import { ComplaintDetails, PermissionData, LoginData } from '../../../Models';
import { TextButton } from 'Pages/CustomerCarePage/Components/TextButton';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import { Payloads } from 'Pages/CustomerCarePage/Payloads';
import { API } from 'Pages/CustomerCarePage/API';
import { CommentData } from 'Pages/CustomerCarePage/Models';
import { GlobalFunction, TimeOption } from 'Pages/CustomerCarePage/GlobalFunction';

interface IProps {
    complaint: ComplaintDetails;
    updates: CommentData[];
    permission: PermissionData;
    ticketId: string;
    currentPage: 'Update Ticket' | 'Resolve Ticket';
    isShowRequiredField?: boolean;
    login: LoginData;
    callSnackbar?: (message: string) => void;
    refreshComplaintState: () => void;
    refreshUpdateState: () => void;
}

interface IStates {
    update: string;
    timeNow: number;

    snackbarMessage: string | null;
    isSending: boolean;
}


export class UpdateComplaintSection extends React.Component<IProps, IStates> {

    interval?: NodeJS.Timeout;

    constructor(props: IProps) {
        super(props);

        this.state = {
            update: '',
            timeNow: 0,

            snackbarMessage: null,
            isSending: false
        };
    }

    componentDidMount() {
        this.interval = setInterval(() => this.setState({ timeNow: Date.now() }), 5000);
    }

    onAddUpdate = () => {
        if (this.props.updates) {
            this.props.updates.unshift({
                sender: `${this.props.login.firstName} ${this.props.login.lastName}`,
                senderId: this.props.login.userId,
                message: this.state.update,
                date: new Date()
            });
            this.props.refreshUpdateState();
            this.setState({isSending: false});

            const payload = Payloads.getPatchUpdatePayload(this.props.updates);

            API.patch(`customer-care/tickets/${this.props.ticketId}`, payload,
                (patchTicketResponse) => {
                    console.log('Patch Response: ', patchTicketResponse);
                    if (patchTicketResponse.status < 400) {
                        this.setState({update: '', snackbarMessage: 'You added new update.'});
                    } else {
                        this.setState({update: '', snackbarMessage: 'Failed to add update.'});
                        if (this.props.updates) {
                            this.props.updates.shift();
                            this.props.refreshUpdateState();
                        }
                    }
                    this.setState({isSending: false});
                },
                (error) => {
                    console.log(error);
                    this.setState({
                        update: '', 
                        isSending: false,
                        snackbarMessage: 'Failed to add update.'
                    });
                    if (this.props.updates) {
                        this.props.updates.shift();
                        this.props.refreshUpdateState();
                    }
                }
            );
        }
    }

    render(): JSX.Element {
        return (
            <div className='section'>

                <Snackbar
			        anchorOrigin={{vertical: 'top', horizontal: 'right'}}
			        open={this.state.snackbarMessage ? true : false}
			        autoHideDuration={6000}
                    message={this.state.snackbarMessage}
                    onClose={() => {
                        this.setState({snackbarMessage: null});
                    }} />

                <div style={{ paddingTop: '2.5rem' }} />
                <div className='header-1'>Update Complaint/Concern</div>
                <div className='sub-section'>
                    <Grid container spacing={2}>
                        <Grid item xs={6}>
                            {this.renderInfoItem('SUMMARY OF COMPLAINT (TITLE)', this.props.complaint.summary)}
                            {this.renderInfoItem('COMPLAINT TYPE', this.props.complaint.type.label)}
                            {this.renderInfoItem('SPECIFICS', this.props.complaint.specifics)}
                        </Grid>
                        <Grid item xs={6}>
                            {this.renderInfoItem('BACKGROUND DETAILS', this.props.complaint.details)}
                            {this.renderInfoItem('NOTES', this.props.complaint.notes)}
                            <div className='sub-section'>
                                <div className='light-text'>FILES</div>
                                {this.props.permission.canUploadFile ? (
                                    <CustomDropzone
                                        accept='image/*, .pdf, .doc, .docx, .csv, .odt, .ods, .xls, .xlsx, .txt'
                                        size='small'
                                        onDrop={(files: File[]) => {
                                            this.props.complaint.files.push(files[0]);
                                            this.props.refreshComplaintState();
                                        }} />
                                ) : null}
                                {this.props.complaint.files.map((file, index) => {
                                    // @ts-ignore
                                    const downloadLink = file.url ? file.url.replace('public/', API.getUrl()) : undefined;

                                    if (!file) {
                                        if (this.props.callSnackbar) {
                                            this.props.complaint.files.splice(index, 1);
                                            this.props.callSnackbar('Unsupported file type');
                                        }
                                        return null;
                                    }

                                    return (
                                        this.props.permission.canUploadFile ? (
                                            <CustomChip
                                                label={file.name}
                                                value={index.toString()}
                                                downloadLink={downloadLink}
                                                onDelete={(value: string) => {
                                                    this.props.complaint.files.splice(parseInt(value), 1);
                                                    this.props.refreshComplaintState();
                                                }} />
                                        ) : (
                                            <div className='small-text'>
                                                <TextButton label={file.name} downloadLink={downloadLink} />
                                            </div>
                                        )
                                    );
                                })}
                            </div>
                        </Grid>
                    </Grid>
                </div>
                {this.props.currentPage === 'Resolve Ticket' ? (
                    <div className="sub-section">
                        {this.props.permission.canResolve ? (
                            <Grid container spacing={2}>
                                <Grid item xs={6}>
                                    <InputField 
                                        placeholder='Root Cause*' 
                                        value={this.props.complaint.rootCause}
                                        error={
                                            this.props.isShowRequiredField && 
                                            this.props.complaint.rootCause === ''}
                                        onChange={(value: string) => {
                                            this.props.complaint.rootCause = value;
                                            this.props.refreshComplaintState();
                                        }} />
                                    <InputField 
                                        placeholder='Resolution Acknowledged By*' 
                                        value={this.props.complaint.acknowledgedBy}
                                        charLimit={50}
                                        error={
                                            this.props.isShowRequiredField && 
                                            this.props.complaint.acknowledgedBy === ''}
                                        onChange={(value: string) => {
                                            this.props.complaint.acknowledgedBy = value;
                                            this.props.refreshComplaintState();
                                        }} />
                                </Grid>
                                <Grid item xs={6}>
                                    <InputField 
                                        placeholder='Resolution*' 
                                        value={this.props.complaint.resolution}
                                        error={
                                            this.props.isShowRequiredField && 
                                            this.props.complaint.resolution === ''}
                                        onChange={(value: string) => {
                                            this.props.complaint.resolution = value;
                                            this.props.refreshComplaintState();
                                        }} />
                                    <div className='light-text'>Supporting Documents</div>
                                    <CustomDropzone
                                        accept='image/*, .pdf, .doc, .docx, .csv, .odt, .ods, .xls, .xlsx, .txt'
                                        size='small'
                                        onDrop={(files: File[]) => {
                                            this.props.complaint.files.push(files[0]);
                                            this.props.refreshComplaintState();
                                        }} />
                                    {this.props.complaint.files.map((file, index) => {
                                        // @ts-ignore
                                        const downloadLink = file.url ? file.url.replace('public/', API.getUrl()) : undefined;

                                        if (!file) {
                                            if (this.props.callSnackbar) {
                                                this.props.complaint.files.splice(index, 1);
                                                this.props.callSnackbar('Unsupported file type');
                                            }
                                            return null;
                                        }
                                        return (
                                            <CustomChip
                                                key={index}
                                                label={file.name}
                                                value={index.toString()}
                                                downloadLink={downloadLink}
                                                onDelete={(value: string) => {
                                                    this.props.complaint.files.splice(parseInt(value), 1);
                                                    this.props.refreshComplaintState();
                                                }} />
                                        );
                                    })}
                                </Grid>
                            </Grid>
                        ) : (
                            <Grid container spacing={2}>
                                <Grid item xs={6}>
                                    {this.renderInfoItem('ROOT CAUSE', this.props.complaint.rootCause)}
                                    {this.renderInfoItem('RESOLUTION ACKNOWLEDGED BY', this.props.complaint.acknowledgedBy)}
                                </Grid>
                                <Grid item xs={6}>
                                    {this.renderInfoItem('RESOLUTION', this.props.complaint.resolution)}
                                    
                                    {this.renderInfoItem('SUPPORTING DOCUMENTS', 
                                        <>
                                        {this.props.complaint.files.map((file) => {
                                            return (
                                                <div className='small-text'>
                                                    <TextButton label={file.name} onClick={() => {}}/>
                                                </div>
                                            );
                                        })}
                                        </>
                                    )}
                                </Grid>
                            </Grid>
                        )}
                    </div>
                ) : null}
                <div className="sub-section">
                    <div className="header-2" style={{ marginBottom: '10px'}}>UPDATES</div>
                    <CustomTable
                        className={!this.props.permission.canUpdate ? 'custom-table' : undefined}
                        rows={this.props.updates}
                        renderHeader={() => this.renderTableHeader()}
                        renderRow={(rowData: any, index: number) =>
                            this.renderTableRow(rowData, index)} 
                    />
                </div>
            </div>
        );
    }

    renderTableHeader(): JSX.Element {

        return (
            this.props.permission.canUpdate ? (
                <>
                <TableCell>
                    <InputField
                        value={this.state.update}
                        innerPlaceholder='Add update'
                        onChange={(value: string) => {
                            this.setState({update: value});
                        }}
                        onKeyDown={(key: string) => {
                            if (key === 'Enter' && !this.state.isSending && this.state.update !== '') {
                                this.onAddUpdate();
                            }
                        }} />
                </TableCell>
                {this.state.update && !this.state.isSending ? (
                    <TableCell style={{width: '150px', textAlign: 'center'}}>
                        <TextButton 
                            icon={faPlus} 
                            label='Add update'
                            onClick={() => {
                                this.onAddUpdate();
                            }} />
                    </TableCell>
                ) : this.state.isSending ? (
                    <TableCell style={{width: '150px', textAlign: 'center'}}>
                        <Loading height='none' size={16} />
                    </TableCell>
                ) : null}
                </> 
            ) : (
                <TableCell>Updates</TableCell>
            )
        )
    }
    
    renderTableRow(rowData: any, index: number): JSX.Element {
        const backgroundColor = (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA');
    
        const timeOption: TimeOption = {
            dateTimeDivider: ' at ',
            showDayFromWeek: true
        };
        
        const timeDiff = GlobalFunction.getTimeDeffirence(new Date(rowData.date), new Date());

        return (
            <TableRow style={{ backgroundColor: backgroundColor }}>
                <TableCell colSpan={2}>
                    <Grid container spacing={1}>
                        <Grid item xs={7} className='header-2'>
                            {rowData.sender}
                        </Grid>
                        <Grid item xs={5} className='light-text' style={{ textAlign: 'right' }}>
                            {(timeDiff.length > 0 ? timeDiff + ' ago ' : ' ') + 
                                `(${GlobalFunction.toDateAndTime(rowData.date, timeOption)})`}
                        </Grid>
                        <Grid item xs={12} className='text'>
                            {rowData.message}
                        </Grid>
                    </Grid>
                </TableCell>
            </TableRow>
        );
    }

    renderInfoItem(label: string, content: React.ReactNode): JSX.Element {
        return (
            <div className='sub-section-2'>
                <div className='light-text'>{label}</div>
                <div className='small-text'>{content}</div>
            </div>
        );
    }
}

