export const formatNumber = (numvalue:string) => {
	numvalue = numvalue && numvalue.trim() ? numvalue.trim().replace(/,/g,'') : '';

	if(numvalue === ''){
		return numvalue;
	}

	let newFormat = '';
	let basenum = numvalue;
	let decimal = ''
	let chkdec = numvalue.indexOf('.');
	if(chkdec>-1)
	{
		decimal = numvalue.substring(chkdec)
		basenum = numvalue.substring(0,chkdec)
	}
	if(basenum.length>3)
	{
		let tempnum = basenum;
		while(tempnum.length>3){
			let sliced = ','+tempnum.slice(-3);
			newFormat = sliced + newFormat;
			tempnum = tempnum.substring(0,tempnum.length-3)
		}
		newFormat = tempnum + newFormat;
	}else{
		newFormat = basenum;
	}

	return newFormat+decimal;
}

const Select = ({ items, disabled, value, ...restProps  }: any) => {
	const cellSelectClasses = cellSelectStyles();
	if (disabled) {
		if (value !== null) {
			const newValue: any = items.find((item: any) => {
				return item.value === value;
			});
			if (newValue) {
				return newValue.label;
			} else {
				return '';
			}
		}
		return '';
	} else {
		return (
			<MaterialUI.Select
				classes={cellSelectClasses}
				style={{ width: '100%', backgroundColor: 'transparent' }}
				value={value}
				renderValue={(selected: any) => {
					if (selected !== null) {
						const value: any = items.find((item: any) => {
							return item.value === selected;
						});
						if (value) {
							return value.label;
						} else {
							return '';
						}
					}
					return '';
				}}
				{...restProps}
			>
				{items.map((item: any, i: number) => (
					<MaterialUI.MenuItem key={i} value={item.value}>
						{item.label}
					</MaterialUI.MenuItem>
				))}
			</MaterialUI.Select>
		);
	}
};

const MultipleSelect = ({ id, text, value, items, disabled, isMaternity = false, ...restProps }: any) => {
	let cellSelectClasses = cellSelectStyles();

	if (isMaternity) {
		cellSelectClasses = maternitySelectStyles();
	}

	if (disabled) {
		if (text !== undefined) {
			const newValue: any = items.find((item: any) => {
				return item.value === text;
			});
			if (newValue) {
				return newValue.label;
			} else {
				return '';
			}
		}

		const selectedNames: string[] = [];

		value.map((item: any) => {
			const newValue: any = items.find((item2: any) => {
				return item2.value === item;
			});
			selectedNames.push(newValue.label);
		});
		return selectedNames.join(', ');
	} else {
		return (
			<MaterialUI.Select
				multiple
				input={<MaterialUI.Input id={id} />}
				value={value}
				renderValue={(selected: any) => {
					if (text !== undefined) {
						const value: any = items.find((item: any) => {
							return item.value === text;
						});
						if (value) {
							return value.label;
						} else {
							return '';
						}
					}

					const selectedNames: string[] = [];

					selected.map((item: any) => {
						const value: any = items.find((item2: any) => {
							return item2.value === item;
						});
						selectedNames.push(value.label);
					});
					return selectedNames.join(', ');
				}}
				style={{ width: '100%', backgroundColor: 'transparent' }}
				classes={cellSelectClasses}
				{...restProps}
			>
				{items.map((item: any, i: number) => (
					<MaterialUI.MenuItem key={i} value={item.value}>
						<MaterialUI.Checkbox checked={value.indexOf(item.value) > -1} />
						<MaterialUI.ListItemText primary={item.label} />
					</MaterialUI.MenuItem>
				))}
			</MaterialUI.Select>
		);

	}

};

