const BenefitsCheckBox = ({ row, style, selected, onToggle, classes, ...restProps }: any) => {
	const disabled = row.disabled !== undefined ? row.disabled : false;

	return (
		<MaterialUI.TableCell padding="checkbox" style={style} classes={classes} {...restProps}>
			<MaterialUI.Checkbox
				icon={<FontAwesomeIcon icon={faSquare} />}
				checkedIcon={<FontAwesomeIcon icon={faCheckSquare} />}
				classes={classes}
				checked={disabled ? true : selected}
				onClick={e => {
					e.stopPropagation();
					onToggle();
				}}
				disabled={disabled}
			/>
		</MaterialUI.TableCell>
	);
};


const BenefitsHeaderCheckBox = ({ style, someSelected, allSelected, onToggle, classes, ...restProps }: any) => {
	return (
		<MaterialUI.TableCell padding="checkbox" classes={classes} {...restProps}>
			<MaterialUI.Checkbox
				icon={<FontAwesomeIcon icon={faSquare} />}
				checkedIcon={allSelected ? <FontAwesomeIcon icon={faCheckSquare} /> : <FontAwesomeIcon icon={faMinusSquare} /> }
				classes={classes}
				checked={someSelected || allSelected}
				onClick={e => {
					e.stopPropagation();
					onToggle();
				}}
			/>
		</MaterialUI.TableCell>
	);
};

