	const NumberDataTypeProvider = (props: any) => (
		<DataTypeProvider formatterComponent={NumberFormatter} {...props} />
	);

	const NumberFormatter = ({ value, column, row }: any) => {
		const [editMode, setEditMode] = useState(false);
		const [tempValue, setTempValue] = useState('');
		let numallowedpattern;
		if(column['name']==='payment')
		{
			numallowedpattern = new RegExp(/^\d+(?:\.\d{0,2})?$/);
		}
		else
		{
			numallowedpattern = new RegExp(/[\D,]/); 
		}
		const textFieldClasses = textFieldStyles();

		if (viewOnly) {
			return (
				<span>
					{value}
				</span>
			);
		} else {
			return <MaterialUI.TextField 
						onChange={(e) => 
							{
								console.log('check')
								if(e.target.value.length===0)
								{
									e.target.value = '0';
								}
								if(column['name']==='payment'){
									if(numallowedpattern.test(e.target.value)){
										if(e.target.value.indexOf('0')===0 && e.target.value.length===1)
										{
											e.target.value = '0';
										}
										else if(e.target.value.indexOf('0')===0 && e.target.value.substring(1,2)!=='.')
										{
											e.target.value = e.target.value.substring(1);
										}
										setTempValue(e.target.value)
									} 
								}
								else{
									if(!numallowedpattern.test(e.target.value)){
										if(e.target.value.indexOf('0')===0 && e.target.value.length===1)
										{
											e.target.value = '0';
										}
										else if(e.target.value.indexOf('0')===0)
										{
											e.target.value = e.target.value.substring(1);
										}
										setTempValue(e.target.value)
									} 
								}
							}
						}
						onFocus={() => {
							setTempValue(value); 
							setEditMode(true)
						}}
						onBlur={(e) => {
							setRowMetadata(e, row.id)
							setEditMode(false);
						}}
						name={column['name']}
						value={editMode ? tempValue : value}
						type={'tel'}
						classes={textFieldClasses}
						disabled={viewOnly}
						inputProps={{min:'0'}}
					/>;
		}

			
	};

	const EligibleAvaileesDataTypeProvider = (props: any) => (
		<DataTypeProvider formatterComponent={EligibleAvaileesFormatter} {...props} />
	);

