	const PHPFormatter = ({ column, row , value }: any) => {
		const inputClasses = inputStyles();
		const [editMode, setEditMode] = useState(false);
		const [tempValue, setTempValue] = useState('');
		const numallowedpattern = new RegExp(/^\d+(?:\.\d{0,2})?$/);
		const handleChangeTemp = (e: any) => {
			if (e.target.min) {
				const min = e.target.min;
				e.target.value = e.target.value.trim().replace(/,/g,'')
				if(e.target.value.length===0)
				{
					e.target.value = '0';
				}
				if(e.target.value.indexOf('0')===0 && e.target.value.length===1)
				{
					e.target.value = '0';
				}
				else if(e.target.value.indexOf('0')===0 && e.target.value.substring(1,2)!=='.')
				{
					e.target.value = e.target.value.substring(1);
				}
				if (e.target.value >= min) {
					if(numallowedpattern.test(e.target.value)){
						setTempValue(formatNumber(e.target.value ? (e.target.value).toString() : ''));
					}
				}
			} 
		}

		if (viewOnly) {
			return (
				<span>
					{value} {value ? 'PHP' : null}
				</span>
			);
		} else { 
			return (
				<MaterialUI.Input
					onChange={handleChangeTemp}
					onFocus={(e) => {
						setTempValue(e.target.value); 
						setEditMode(true)
					}}
					onBlur={(e) => {
						e.target.value = e.target.value.trim().replace(/,/g,'')
						setRowMetadata(e, row.id);
						setEditMode(false);
					}}
					onKeyPress={(e)=>{
						const chk = new RegExp(/^[0-9.]*$/)
						if(!chk.test(e.key))
						{
							e.preventDefault();
						}
					}}
					disabled={viewOnly}
					value={editMode ? formatNumber(tempValue) : formatNumber(value ? value.toString() : '')}
					type={'text'}
					name={column['name']}
					classes={inputClasses}
					inputProps={{
						style: { textAlign: 'right' },
						min: 0,
					}}
					endAdornment = {(
						<MaterialUI.InputAdornment disableTypography={true} position="end">
							<MaterialUI.Typography noWrap={true}>PHP</MaterialUI.Typography>
						</MaterialUI.InputAdornment>
					)}
					
				/>
			);
		}
	};

