	const EligibilityAgeLimitFormatter = ({ column, row }: any) => {
		const items = EligibilityAgeLimitItems;
		const inputClasses = inputStyles();
		const [editMode, setEditMode] = useState(false);
		const [editMode2, setEditMode2] = useState(false);
		const [tempValue, setTempValue] = useState('');

		const handleChangeTemp = (e: any) => {
			if (e.target.type === 'number' && e.target.min) {
				const min = e.target.min;

				if (e.target.value !== '' || e.target.value >= min) {
					if (e.target.name === column['name']+ "_min") {
						setTempValue(e.target.value);
					}
					if (e.target.name === column['name']+ "_max") {
						setTempValue(e.target.value);
					}
				}
			} 
		}

		if (viewOnly) {
			if (!row['custom_metadata'][column['name']+ "_min"] && !row['custom_metadata'][column['name']+ "_min_unit"] &&
				!row['custom_metadata'][column['name']+ "_max"] && !row['custom_metadata'][column['name']+ "_max_unit"]) {
				return <span></span>
			} else {
				const fields = ["min", 'to', "max"].map((step: string) => {
					const columnName = column['name']+ "_" + step;
					if (step === 'to') {
						if (row['custom_metadata'][column['name']+ "_max"]) {
							return <span style={{width: '30px'}}>to</span>;
						}
						return null;
					} else {
						let inputValue = '';
						let unit = '';

						if (row['custom_metadata'][columnName]) {
							inputValue = row['custom_metadata'][columnName];
						}

						if (row['custom_metadata'][columnName+'_unit']) {
							const found = items.find((item) => item.value === row['custom_metadata'][columnName+'_unit']);
							if (found) {
								unit = found['label'];
							}
						}

						return (
							<span>
								{inputValue} {unit}
							</span>
						);
					}
				});
				return (
			       <div style={{ display: 'inline-flex' }}>
			         { fields }
			       </div>
				);
			}

		} else { 

			const fields = ["min", 'to', "max"].map((step: string) => {
				const columnName = column['name']+ "_" + step;
				
				if (step === 'to') {
					return <span style={{width: '30px'}}>to</span>;
				} else {
					let inputValue = '';

					if (editMode) {
						if (columnName === column['name']+ "_min") {
							inputValue = tempValue;
						} else {
							inputValue = row['custom_metadata'][columnName] ? row['custom_metadata'][columnName] : ''
						}
					} else if (editMode2) {
						if (columnName === column['name']+ "_max") {
							inputValue = tempValue;
						} else {
							inputValue = row['custom_metadata'][columnName] ? row['custom_metadata'][columnName] : ''
						}
					}else {
						inputValue = row['custom_metadata'][columnName] ? row['custom_metadata'][columnName] : '';
					}

					return (
						<Select 
							onChange={(e) => setRowMetadata(e, row.id)}
							name={columnName + "_unit"}
							disabled={viewOnly}
							items={items}
							id={columnName + "_unit"}
							value={row['custom_metadata'][columnName + "_unit"] ? row['custom_metadata'][columnName + "_unit"] : ''}
							startAdornment = {(
								<MaterialUI.InputAdornment disableTypography={true} position="start">
									<MaterialUI.Input
										onChange={handleChangeTemp}
										onFocus={(e) => {
											if (columnName === column['name']+ "_min") {
												setTempValue(e.target.value); 
												setEditMode(true);
											} else if (columnName === column['name']+ "_max") {
												setTempValue(e.target.value); 
												setEditMode2(true);
											}
										}}
										onBlur={(e) => {
											setRowMetadata(e, row.id);
											setEditMode(false);
											setEditMode2(false);
										}}
										id={'enrollment_period'}
										disabled={viewOnly}
										value={inputValue}
										type={'number'}
										name={columnName}
										classes={inputClasses}
										inputProps={{
											style: { textAlign: 'right', width: '50px' },
											min: 0,
										}}
									/>
								</MaterialUI.InputAdornment>
							)}
						/>
					);
				}
			});

			return (
		       <div style={{ display: 'inline-flex' }}>
		         { fields }
		       </div>
			);
		}

		return null;
	};

