//#region Global Imports
import * as React from 'react';
import { useState } from 'react';
import { BenefitsTable } from 'Components/UI/TableComponent/BenefitsTable';
import Grid from '@material-ui/core/Grid';
// import Hidden from '@material-ui/core/Hidden';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Typography from '@material-ui/core/Typography';

import {
	CurrencyField,
	WaitingPeriodField,
} from '../../ComboField';
import { ViewField } from '../../ViewField';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
//import { Components } from '@hims/core';
//#endregion Global Imports
import { defaultProps, radioStyles, displayValue } from './Constants';
import { ICategorySubForm } from './ICategorySubForm';

import { PHPPercField } from '../../PHPPercField';
import { GlobalFunction } from 'Components/UI/GlobalFunction';


export const MaternityForm: React.FC<ICategorySubForm.IProps> = (
	props: ICategorySubForm.IProps,
): JSX.Element | null => {
	const { formHeader, viewOnly, rows, setSubForm, setSubForm2, subFormNode } = props;
	const radioClasses = radioStyles();
  	const [blankValue, setBlankValue] = useState(false);
	
	let metadata:any = {};
	if (subFormNode) {
		if (subFormNode['custom_metadata']) {
			metadata = subFormNode['custom_metadata'];
		}
	}
 
	// console.log(rows);

	// const fakeRows: any = [
	// 	{
	// 		provision: 'Pre-natal checkup',
	// 		eligible_availees: ['principals'],
	// 		limit_type: ['max_session'],
	// 		limit: [3],
	// 		availment: 'outright',
	// 	},
	// 	{
	// 		provision: 'Normal delivery',
	// 		eligible_availees: ['principals'],
	// 		limit_type: ['amt_limit'],
	// 		limit: [1000],
	// 		availment: 'reimbursement',
	// 	},
	// 	{
	// 		provision: 'Casaerean section',
	// 		eligible_availees: ['mothers', 'spouses'],
	// 		limit_type: ['amt_limit'],
	// 		limit: [1000],
	// 		availment: 'reimbursement',
	// 	},
	// 	{
	// 		provision: 'Ultrasound',
	// 		eligible_availees: ['all_dependents'],
	// 		limit_type: ['max_session'],
	// 		limit: [2],
	// 		availment: 'reimbursement',
	// 	},
	// 	{
	// 		provision: 'Consultation',
	// 		eligible_availees: ['principals'],
	// 		limit_type: ['amt_limit'],
	// 		limit: [2000],
	// 		availment: 'reimbursement',
	// 	},
	// ];

	const columns: any = [
		{ name: 'name', title: 'Provision' },
		{ 
			name: 'eligible_availees',
			title: 'Eligible Availees',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.eligible_availees 
								 	? row.custom_metadata.eligible_availees : []),
		},
		{ 
			name: 'limit_type', 
			title: 'Limit Type',
			getCellValue: row => (row.limits
									? row.limits : [])
		},
		{
			name: 'limit', 
			title: 'Limit',
			getCellValue: row => (row.limits
									? row.limits : [])
		},
		{ 
			name: 'availment',
			title: 'Availment',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.availment 
								 	? row.custom_metadata.availment : []),
		},
	];

	const formattedColumns = {
		maternity_eligible_availees: ['eligible_availees'],
		limit_type: ['limit_type'],
		limit: ['limit'],
		availment: ['availment'],
	};

	const columnExtensions = [
		{ columnName: 'eligible_availees', wordWrapEnabled: true },
		{ columnName: 'limit_type', width: 150, wordWrapEnabled: true },
		{ columnName: 'limit', width: 150, wordWrapEnabled: true },
	];


	const billingArrangement = {
		premium: "Imputed in premiums",
		billback: "Bill back with admin fee",
		rider_fee: "1-time rider fee to be upon enrollment"
	}

	let finalRows = rows;
	

	
	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2}>
					{!viewOnly ? formHeader : null}
				</Grid>
			</Grid>
			<Grid item sm={12}>
				<BenefitsTable
					viewOnly={viewOnly}
					columns={columns}
					formattedColumns={formattedColumns}
					columnExtensions={columnExtensions}
					{...props}
					rows={finalRows}
				/>
			</Grid>
			<Grid item sm={12}>
				<Grid item sm={12}>
				{!viewOnly ? (
					<React.Fragment>
						<InputLabel htmlFor="availmentPerYear">Availment Per Year</InputLabel>
						<FormControl margin="normal" style={{marginBottom: '20px'}}>
							<OutlinedInput
								onChange={(e) => {
						        	setBlankValue(false);
									setSubForm(e);
								}}
								value={blankValue ? '' : (metadata['availment_per_year'] ? metadata['availment_per_year'] : 0)}
								type="tel"
								labelWidth={0}
								name="availment_per_year"
								id="availmentPerYear"
						        onFocus={() => {
						          if (!metadata['availment_per_year'] || metadata['availment_per_year'] === 0 || metadata['availment_per_year'] === '0') {
						            setBlankValue(true);
						          }
						        }}
						        onBlur={() => {
						        	setBlankValue(false);
						        }}
								inputProps={{ min: 0 }}
							/>
						</FormControl>
					</React.Fragment> 
				) : (
					metadata['availment_per_year'] ? (
						<ViewField 
							label="Availment Per Year"
							value={
								metadata['availment_per_year'] && metadata['availment_per_year'] !== undefined ?
									GlobalFunction.formatNumber(metadata['availment_per_year']) : '--'
							} />
					) : null	
				)}
				</Grid>

				<Grid item sm={12}>
					{!viewOnly ? (
						<React.Fragment>
							<Typography variant="overline" display="block" gutterBottom>
								Activation
							</Typography>
							<FormControl component="fieldset">
								<RadioGroup name="waiting_period">
									<FormControlLabel
										classes={radioClasses}
										value="waiting_period_effectivity"
										control={
											<Radio 
												checked={metadata['waiting_period_type'] === 'effectivity'} 
												name="waiting_period_type"
												onClick={(e) => {
													e.stopPropagation();
													if(metadata['waiting_period_type'] !== 'effectivity'){
														setSubForm2("waiting_period", "");
														setSubForm2("waiting_period_unit", "");
														setSubForm2("waiting_period_type", "effectivity");
													}
												}} />
										}
										label={
											<React.Fragment>
												<Typography variant="body1">
													Waiting period after effectivity of member
												</Typography>
												<FormControl>
													<WaitingPeriodField 
														disableSelect={metadata['waiting_period_type'] !== 'effectivity'}
														disableInput={metadata['waiting_period_type'] !== 'effectivity'}
														id="waiting_period_effectivity" 
														fieldName="waiting_period"
														selectName="waiting_period_unit"
														fieldValue={
															metadata['waiting_period_type'] === 'effectivity' ? 
															metadata['waiting_period'] : 0
														}
														selectValue={
															metadata['waiting_period_type'] === 'effectivity' ?
															metadata['waiting_period_unit'] : ''
														}
														fieldOnChange={setSubForm}
														selectOnChange={setSubForm}
														inputProps={{ min: 0 }}
														noDefault
														allowNegative={false}
													/>
												</FormControl>
											</React.Fragment>
										} />
									<FormControlLabel
										classes={radioClasses}
										value="waiting_period_hire"
										control={
											<Radio 
												checked={metadata['waiting_period_type'] === 'date_of_hire'} 
												name="waiting_period_type"
												onClick={(e) => {
													e.stopPropagation();
													if(metadata['waiting_period_type'] !== 'date_of_hire'){
														setSubForm2("waiting_period", "");
														setSubForm2("waiting_period_unit", "");
														setSubForm2("waiting_period_type", "date_of_hire");
													}
												}} />
										}
										label={
											<React.Fragment>
												<Typography variant="body1">
													Waiting period after date of hire
												</Typography>
												<FormControl>
													<WaitingPeriodField 
														disableSelect={metadata['waiting_period_type'] !== 'date_of_hire'}
														disableInput={metadata['waiting_period_type'] !== 'date_of_hire'}
														id="waiting_period_hire" 
														fieldName="waiting_period"
														selectName="waiting_period_unit"
														fieldValue={
															metadata['waiting_period_type'] === 'date_of_hire' ? 
															metadata['waiting_period'] : 0
														}
														selectValue={
															metadata['waiting_period_type'] === 'date_of_hire' ?
															metadata['waiting_period_unit'] : ''
														}
														fieldOnChange={setSubForm}
														selectOnChange={setSubForm}
														inputProps={{ min: 0 }}
														noDefault
													/>
												</FormControl>
											</React.Fragment>
										} />
								</RadioGroup>
							</FormControl>
						</React.Fragment> 
					) : (
						metadata['waiting_period_type'] && metadata['waiting_period_type'] !== undefined ? (
							<ViewField 
								label="Activation"
								value={
									<div>
										<div>
											{ metadata['waiting_period_type'] === "effectivity" ?
												"Waiting period after effectivity of member": 
												metadata['waiting_period_type'] === "date_of_hire" ?
												"Waiting period after date of hire": null
											}
										</div>
										{ metadata['waiting_period'] ? (
											<div>
												{ metadata['waiting_period_unit'] ? 
													displayValue(metadata['waiting_period'], metadata['waiting_period_unit']) :
													GlobalFunction.formatNumber(metadata['waiting_period'])
												}
											</div>
										) : null}
									</div>
								} />
						) : null
					)}
				</Grid>

				<Grid item sm={12}>
					{!viewOnly ? (
						<React.Fragment>
							<Typography variant="body1" style={{ marginTop: '20px' }} gutterBottom>
								Billing arrangement
							</Typography>
							<RadioGroup name="billing_arangement">
								<FormControl component="fieldset">
										<FormControlLabel
											value="premium"
											control={
												<Radio 
													checked={metadata['billing_arangement'] === 'premium'} 
													name="premium"
													onClick={(e) => {
														e.stopPropagation();
														if (metadata['billing_arangement'] !== 'premium') {
															setSubForm2("billback_fee", null);
															setSubForm2("billback_type", null);
															setSubForm2("rider_fee", null);
															setSubForm2("rider_fee_unit", null);
															setSubForm2("billing_arangement", "premium");
														}
													}} />
											}
											label={billingArrangement['premium']}
										/>
										</FormControl>
										<FormControl component="fieldset">
										<FormControlLabel
											classes={radioClasses}
											value="billback"
											control={
												<Radio 
													checked={metadata['billing_arangement'] === 'billback'} 
													name="premium"
													onClick={(e) => {
														e.stopPropagation();
														if (metadata['billing_arangement'] !== 'billback') {
															setSubForm2("billback_fee", 15);
															setSubForm2("billback_type", 'percent');
															setSubForm2("rider_fee", null);
															setSubForm2("rider_fee_unit", null);
															setSubForm2("billing_arangement", "billback");
														}
													}} />
											}
											label={
												<React.Fragment>
													<Typography variant="body1" gutterBottom>
														{billingArrangement['billback']}
													</Typography>
													<div style={{display:metadata['billing_arangement'] === 'billback'?'block':'none'}}>
														<PHPPercField
															id="billback_fee"
															variant="outlined"
															fieldName="billback_fee"
															selectName="billback_type"
															fieldValue={
																metadata['billing_arangement'] === 'billback' ? 
																metadata['billback_fee'] : 0
															}
															selectValue={
																metadata['billing_arangement'] === 'billback' ?
																metadata['billback_type'] : 'percent'
															}
															fieldOnChange={setSubForm}
															selectOnChange={setSubForm}
															allowNegative={false}
														/>
													</div>
												</React.Fragment>
											}
										/>
										</FormControl>
										<FormControl component="fieldset">
										<FormControlLabel
											classes={radioClasses}
											value="rider_fee"
											control={
												<Radio 
													checked={metadata['billing_arangement'] === 'rider_fee'} 
													name="rider_fee"
													onClick={(e) => {
														e.stopPropagation();
														if (metadata['billing_arangement'] !== 'rider_fee') {
															setSubForm2("billback_fee", null);
															setSubForm2("billback_type", null);
															setSubForm2("rider_fee", '');
															setSubForm2("rider_fee_unit", 'PHP');
															setSubForm2("billing_arangement", "rider_fee");
														}
													}} />
											}
											label={
												<React.Fragment>
													<Typography variant="body1" gutterBottom>
														{billingArrangement['rider_fee']}
													</Typography>
													
													<div style={{display:metadata['billing_arangement'] === 'rider_fee'?'block':'none'}}>
														<Typography variant="body1">Fee</Typography>
														<CurrencyField 
															id="fee_field"
															disabled={metadata['billing_arangement'] !== 'rider_fee'}
															fieldName="rider_fee"
															selectName="rider_fee_unit"
															fieldValue={
																metadata['billing_arangement'] === 'rider_fee' ? 
																metadata['rider_fee'] : 0
															}
															selectValue={
																metadata['billing_arangement'] === 'rider_fee' ?
																metadata['rider_fee_unit'] : ''
															}
															fieldOnChange={setSubForm}
															selectOnChange={setSubForm}
															inputProps={{ min: 0 }}
															allowNegative={false}
														/>
													</div>
													
												</React.Fragment>
											}
										/>
								</FormControl>
							</RadioGroup>
						</React.Fragment> 
					) : (
						billingArrangement[metadata['billing_arangement']] ? (
							<ViewField 
								label="Billing arrangement"
								value={
									<div>
										<div>{billingArrangement[metadata['billing_arangement']]}</div>
										{metadata['billback_fee'] ? (
											<div>{`${metadata['billback_fee']} ${metadata['billback_type'] === 'percent' ? '%' : metadata['billback_type']}`}</div>
										) : null}
										{metadata['rider_fee'] ? (
										<div>{`${metadata['rider_fee']} ${metadata['rider_fee_unit']}`}</div>
									) : null}
									</div>
								} />
						) : null
					)}
				</Grid>
			</Grid>
		</React.Fragment>
	);
};

MaternityForm.defaultProps = defaultProps;