import React from 'react';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { ComplaintDetails, DropdownChoice } from '../../../Models';

interface IProps {
    complaint: ComplaintDetails;
    refreshComplaintState: () => void;
    createTicketAttemp: boolean;
}

export class ComplaintSection extends React.Component<IProps> {

    complaintTypes: DropdownChoice[] = [
        { value: '1', label: 'Complaint - Member Coverage and Benefits' },
        { value: '2', label: 'Complaint - Member Network Access' },
        { value: '3', label: 'Complaint - Network-Related' },
        { value: '4', label: 'Complaint - Availment-Related' },
        { value: '5', label: 'Complaint - HMO Servicing' },
        { value: '6', label: 'Complaint - HMO Personnel' },
        { value: '7', label: 'Complaint - SLA-Related / Delays' },
        { value: '8', label: 'Complaint - Member Status' },
        { value: '9', label: 'Complaint - Others' },
        { value: '10', label: 'Concern - Request for Investigation' },
        { value: '11', label: 'Concern - Billing Adjustment / Dispute' },
        { value: '12', label: 'Concern - Others' },
    ];

    render(): JSX.Element {
        return (
            <div className='availment-section'>
                <div style={{paddingTop: '5rem'}} />
                <div className='sub-section'>
                    <div className='header-2'>Complaint Type</div>
                    <Dropdown 
                        choices={this.complaintTypes}
                        placeholder='Request Type*'
                        value={this.props.complaint.type}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.complaint.type.value === ''}
                        onChange={(value: DropdownChoice) => {
                            this.props.complaint.type = value;
                            this.props.refreshComplaintState();
                        }}
                        />
                    <InputField
                        placeholder='Summary of Complaint (Title)*'
                        value={this.props.complaint.summary}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.complaint.summary === ''}
                        onChange={(value: string) => {
                            this.props.complaint.summary = value;
                            this.props.refreshComplaintState();
                        }}
                        charLimit={60} />
                    <InputField
                        placeholder='Specifics'
                        value={this.props.complaint.specifics}
                        onChange={(value: string) => {
                            this.props.complaint.specifics = value;
                            this.props.refreshComplaintState();
                        }}
                        charLimit={50} />
                    <InputField
                        placeholder='Background / Details of Complaint*'
                        value={this.props.complaint.details}
                        error={
                            this.props.createTicketAttemp && 
                            this.props.complaint.details === ''}
                        onChange={(value: string) => {
                            this.props.complaint.details = value;
                            this.props.refreshComplaintState();
                        }} />
                    <InputField
                        placeholder='Notes'
                        value={this.props.complaint.notes}
                        onChange={(value: string) => {
                            this.props.complaint.notes = value;
                            this.props.refreshComplaintState();
                        }} />
                </div>
                <div className='section' />
            </div>
        );
    }

}