	const LimitType = ({ column, row, value, items }: any) => {
		const limits = value.reduce ((result, limit) => {
		   const found = items.find ((item) => 
		   		item['type'] === limit['type']
		   );

		   if (found) {
		   	result.push(found.value);
		   }

		   return result;
		}, new Array());

		return ( 
			<React.Fragment>
				{
					limits.length > 0 ?
						limits.map((lim: any, i: number) => (
							<React.Fragment>
								<MultipleSelect
									key={i}
									onChange={(e) => setRowLimits(e, row.id)}
									value={limits}
									name={column['name']}
									items={items}
									id={'limit_type'}
									text={lim}
									disabled={viewOnly}
								/> <br /> 
							</React.Fragment>
						)) :
						<MultipleSelect
							onChange={(e) => setRowLimits(e, row.id)}
							value={[]}
							name={column['name']}
							items={items}
							id={'limit_type'}
							text={''}
							disabled={viewOnly}
						/>
				}
			</React.Fragment>
		);
	};

