import * as React from 'react';
import { CustomModal } from 'Pages/CustomerCarePage/Components/CustomModal';
import { CustomButton } from 'Pages/CustomerCarePage/Components/CustomButton';
import { TicketData, LoginData, CaseData } from 'Pages/CustomerCarePage/Models';
import { API } from 'Pages/CustomerCarePage/API';
import { Processmaker } from 'Pages/CustomerCarePage/Processmaker';
import { get } from 'lodash';

interface IProps {
    caseId: string;
    caseData: CaseData,
    ticket: TicketData;
    login: LoginData;
    isSkipGenerateApprovalCode?: boolean;
    isCloseTicketModalOpen: boolean;
    isResolveTicketModalOpen: boolean;
    isResolveAndCloseModalOpen: boolean;
    action: 'Resolve' | 'Close' | 'Resolve and Close';
    onClose: () => void;
    onSuccess: (onAfterSuccess?: (onSuccess: Function) => void) => void;
    onBeforeMainAction: () => void;
}

export class ResolveAndCloseModal extends React.Component<IProps> {

    onResolveAndSkipGenerateApprovalCode = () => {
        this.props.onBeforeMainAction();

        const caseId = this.props.caseId;
        const login = this.props.login;
        const caseData = this.props.caseData;

        const variableBody1 = {
            button_pressed: 'GENERATE APPROVAL CODE'
        };
        Processmaker.put(`cases/${caseId}/variable`, variableBody1, () => {
            Processmaker.put(`cases/${caseId}/route-case`, {}, () => {
                API.get(
                    'group/pmaker_uids?department='+get(login, 'group.department', '')+'&role=Ticket Reviewer&with_names=true', 
                    response => {
                        let nextUser : any[] = []
                        if(response !== undefined && response.group_pmaker_ids !== undefined){
                            nextUser = response.group_pmaker_ids
                        }
                        const variableBody2 = {
                            button_pressed: 'RESOLVE',
                            next_user: nextUser && nextUser.length > 0 ? nextUser : get(login, 'ccData.supervising_department_pmaker_id', '')
                        };

                        const apiBody = {
                            status: 'Closed',
                            ticket_history_info: {
                                assignee_id: login.userId,
                                assignee_name: `${login.firstName} ${login.lastName}`,
                                pmaker_task: caseData.task,
                                action: 'Resolved Ticket'
                            }
                        };

                        Processmaker.put(`cases/${caseId}/variable`, variableBody2, () => {
                            Processmaker.put(`cases/${caseId}/route-case`, {}, () => {
                                API.patch(`customer-care/tickets/${this.props.ticket.id}`, apiBody, () => {
                                    this.props.onSuccess();
                                });
                            });
                        });
                    }
                );
            });
        });
    }

    onResolve = () => {
        const login = this.props.login;
        const caseData = this.props.caseData;
        const ticket = this.props.ticket;

        this.props.onBeforeMainAction();
        
        API.get(
            `group/pmaker_uids?` +
            `department=${get(login, 'group.department', '')}&` +
            `role=Ticket Reviewer&` +
            `with_names=true`, (response) => {
            let nextUser: any[] = [];
            if (response !== undefined && response.group_pmaker_ids !== undefined) {
                nextUser = response.group_pmaker_ids
            }
            const patchBody = {
                assign_to: response && response.group_names && response.group_names.length > 0 ? response.group_names.join(', ') : '',
                assigned_by: `${this.props.login.firstName} ${this.props.login.lastName}`,
            };
            const variableBody = {
                button_pressed: 'RESOLVE',
                next_user: nextUser && nextUser.length > 0 ? nextUser : get(login, 'ccData.supervising_department_pmaker_id', ''),
            };

            const apiBody = { 
                status: 'Resolved', 
                assign_remarks : '',
                ticket_history_info: {
                    assignee_id: login.userId,
                    assignee_name: `${login.firstName} ${login.lastName}`,
                    pmaker_task: caseData.task,
                    action: 'Resolved Ticket'
                }
            };

            Processmaker.put(`cases/${this.props.caseId}/variable`, variableBody, () => {
                Processmaker.put(`cases/${this.props.caseId}/route-case`, {}, () => {
                    API.patch(`customer-care/tickets/${ticket.id}`, apiBody, () => {
                        this.props.onSuccess((onSuccess) => {
                            API.patch(`customer-care/tickets/${ticket.id}`, patchBody, onSuccess);
                        });
                    });
                });
            });
        });
    }

    onResolveAndClose = () => {
        const login = this.props.login;
        const caseData = this.props.caseData;

        this.props.onBeforeMainAction();

        API.get(
            'group/pmaker_uids?department='+get(login, 'group.department', '')+'&role=Ticket Reviewer&with_names=true', 
            response => {
                let nextUser : any[] = []
                if(response !== undefined && response.group_pmaker_ids !== undefined){
                    nextUser = response.group_pmaker_ids
                }
                const variableBody = {
                    button_pressed: 'RESOLVE',
                    next_user: nextUser && nextUser.length > 0 ? nextUser : get(login, 'ccData.supervising_department_pmaker_id', '')
                };
                const apiBody = {
                    status: 'Closed',
                    ticket_history_info: {
                        assignee_id: login.userId,
                        assignee_name: `${login.firstName} ${login.lastName}`,
                        pmaker_task: caseData.task,
                        action: 'Resolve and Closed Ticket'
                    }
                };
                Processmaker.put(`cases/${this.props.caseId}/variable`, variableBody, () => {
                    Processmaker.put(`cases/${this.props.caseId}/route-case`, {}, () => {
                        API.patch(`customer-care/tickets/${this.props.ticket.id}`, apiBody, () => {
                            this.props.onSuccess();
                        });
                    });
                });
            }
        );
    }

    onClose = () => {
        this.props.onBeforeMainAction();
        const login = this.props.login;
        const caseData = this.props.caseData;
        
        const variableBody = {
            button_pressed: 'CLOSE',
        };

        const apiBody = {
            status: 'Closed',
            ticket_history_info: {
                assignee_id: login.userId,
                assignee_name: `${login.firstName} ${login.lastName}`,
                pmaker_task: caseData.task,
                action: 'Closed Ticket'
            }
        };

        Processmaker.put(`cases/${this.props.caseId}/variable`, variableBody, () => {
            Processmaker.put(`cases/${this.props.caseId}/route-case`, {}, () => {
                API.patch(`customer-care/tickets/${this.props.ticket.id}`, apiBody, () => {
                    this.props.onSuccess();
                });
            });
        });
    }

    render(): JSX.Element {
        return (
            <CustomModal
                className='modal'
                isOpen={
                    this.props.isCloseTicketModalOpen || 
                    this.props.isResolveTicketModalOpen ||
                    this.props.isResolveAndCloseModalOpen}
                handleClose={() => {
                    this.props.onClose();
                }}
                header={
                    `${
                        this.props.action === 'Resolve' ? 'Resolve' : 
                        this.props.action === 'Close' ? 'Close' : 'Resolve and Close'
                    } Ticket`}
                content={
                    `${
                        this.props.action === 'Resolve' ? 'Resolving' :
                        this.props.action === 'Close' ? 'Closing' : 'Resolving and closing'
                    } ` +
                    `will no longer allow you to modify ` + 
                    `your approval/disapproval to this ticket. Are you sure ` +
                    `you want to ${
                        this.props.action === 'Resolve' ? 'resolve' : 
                        this.props.action === 'Close' ? 'close' : 'resolve and close'}?`}
                footer={
                    <>
                    <CustomButton
                        text='Cancel'
                        variant='outlined'
                        onClick={this.props.onClose} />
                    <span style={{padding: '1rem'}} />
                    {this.props.isResolveTicketModalOpen ? (
                        <CustomButton
                            text='Resolve'
                            onClick={() => {
                                
                                if (this.props.isSkipGenerateApprovalCode) {
                                    this.onResolveAndSkipGenerateApprovalCode();
                                } else {
                                    this.onResolve();
                                }
                            }} />
                    ) : this.props.isCloseTicketModalOpen ? (
                        <CustomButton
                            text='Close'
                            onClick={() => {
                                if (this.props.isSkipGenerateApprovalCode) {
                                    this.onResolveAndSkipGenerateApprovalCode();
                                } else {
                                    this.onClose();
                                }
                            }} />
                    ) : this.props.isResolveAndCloseModalOpen ? (
                        <CustomButton
                            text='Resolve and Close'
                            onClick={() => {
                                if (this.props.isSkipGenerateApprovalCode) {
                                    this.onResolveAndSkipGenerateApprovalCode();
                                } else {
                                    this.onResolveAndClose();
                                }
                            }} />
                    ) : null}
                    </>
                } />
        );
    }
}