import * as React from 'react';
import { API } from 'Pages/CustomerCarePage/API';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { IconButton } from 'Pages/CustomerCarePage/Components/IconButton';
import { Grid } from '@material-ui/core';
import { TicketData, AvailmentDetails } from 'Pages/CustomerCarePage/Models';
import { GlobalFunction } from 'Pages/CustomerCarePage/GlobalFunction';
import { Payloads } from 'Pages/CustomerCarePage/Payloads';
import { get, forEach, map } from 'lodash';

interface IProps {
    ticketId: string;
    onCloseButtonClicked: () => void;
}

interface IStates {
    ticket?: TicketData;
    totalAmount: number;
    availment?: AvailmentDetails;
    inquiries?: string[];
}

export class TicketInfoModal extends React.Component<IProps, IStates> {
    constructor(props: IProps) {
        super(props);

        this.state = {
            totalAmount: 0,
        };
    }

    componentDidMount() {
        API.get(
            'customer-care/tickets/' + this.props.ticketId,
            response => {
                this.setState(
                    {
                        ticket: Payloads.getTicketData(response),
                    },
                    () => {
                        const ticketCategory = response.ticket_category;
                        const { ticket_type, specific_type } = ticketCategory;
                        if (ticket_type === 'Availment') {
                            let providerData: any[] = [];
                            switch (specific_type) {
                                case 'Outpatient Procedure':
                                    const availmentDetails = ticketCategory.availment_details;
                                    providerData = availmentDetails.provider.split(' - ');
                                    this.setState({
                                        availment: {
                                            provider: {
                                                value: providerData[0],
                                                label: providerData[1],
                                            },
                                            diagnoses: this.getAvailmentsDiagnoses(availmentDetails),
                                        },
                                    });
                                    break;

                                case 'Inpatient':
                                    const confinementDetails = ticketCategory.confinement_details;
                                    providerData = confinementDetails.provider.split(' - ');
                                    this.setState({
                                        availment: {
                                            provider: {
                                                value: providerData[0],
                                                label: providerData[1],
                                            },
                                            diagnoses: this.getConfinementDiagnoses(confinementDetails),
                                        },
                                    });
                                    break;

                                case 'Emergency Room':
                                    const erDetails = ticketCategory.er_details;
                                    providerData = erDetails.provider.split(' - ');
                                    this.setState({
                                        availment: {
                                            provider: {
                                                value: providerData[0],
                                                label: providerData[1],
                                            },
                                            diagnoses: this.getErDiagnoses(erDetails),
                                        },
                                    });
                                    break;

                                case 'Consultation':
                                    const consultationDetails = ticketCategory.consultation_details;
                                    providerData = consultationDetails.provider.split(' - ');
                                    this.setState({
                                        availment: {
                                            provider: {
                                                value: providerData[0],
                                                label: providerData[1],
                                            },
                                            diagnoses: this.getConsultationDiagnoses(consultationDetails),
                                        },
                                    });
                                    break;

                                default:
                                    break;
                            }
                        } else {
                            const inquiryDetails = ticketCategory.inquiry_details;
                            this.setState({
                                inquiries: this.getInquiries(inquiryDetails),
                            });
                        }
                    },
                );
            },
            error => {
                console.log(error);
            },
        );
    }

    getAvailmentsDiagnoses(availment: any): any[] {
        let totalAmount = 0;

        const diagnoses: any[] = [];
        forEach(availment.availments_list, availmentItem => {
            forEach(availmentItem.diagnosis, diagnosis => {
                diagnoses.push({
                    icd10Code: {
                        value: diagnosis.icd10_code,
                        label: diagnosis.diagnosis,
                    },
                    remarks: diagnosis.remarks,
                    isPrimary: diagnosis.is_primary,
                    isFinal: diagnosis.is_final,
                    doctors: diagnosis.doctors.map(doctor => {
                        return {
                            id: doctor.id,
                            name: doctor.name,
                            specialization: doctor.specialization,
                            fee: doctor.fee,
                            noOfVisits: doctor.no_of_visits,
                            remarks: doctor.remarks,
                        };
                    }),
                    procedures: diagnosis.procedures.map(procedure => {
                        let procedureAmount = get(procedure, 'amount', get(procedure, 'approved_amount', get(procedure, 'ruv_amount', 0)));
                        totalAmount += parseFloat(procedureAmount);

                        return {
                            cptCode: {
                                value: procedure.cpt_code,
                                label: procedure.procedure_name,
                            },
                            isPHIC: procedure.phic,
                            amount: procedureAmount,
                            notes: procedure.notes,
                            assessmentType: procedure.assessment_type,
                            assessmentRemarks: procedure.assessment_remarks,
                        };
                    }),
                });
            });
        });

        this.setState({
            totalAmount: totalAmount,
        });

        return diagnoses;
    }

    getConfinementDiagnoses(confinement: any): any[] {
        let totalAmount = 0;
        const diagnoses: any[] = [];
        forEach(confinement.diagnoses, diagnosis => {
            diagnoses.push({
                icd10Code: {
                    value: diagnosis.icd10_code,
                    label: diagnosis.diagnosis,
                },
                remarks: diagnosis.remarks,
                isPrimary: diagnosis.is_primary,
                isFinal: diagnosis.is_final,
                doctors: diagnosis.doctors.map(doctor => {
                    return {
                        id: doctor.id,
                        name: doctor.name,
                        specialization: doctor.specialization,
                        fee: doctor.fee,
                        noOfVisits: doctor.no_of_visits,
                        remarks: doctor.remarks,
                    };
                }),
                procedures: diagnosis.procedures.map(procedure => {
                    let procedureAmount = get(procedure, 'amount', get(procedure, 'approved_amount', get(procedure, 'ruv_amount', 0)));
                    totalAmount += parseFloat(procedureAmount);

                    return {
                        cptCode: {
                            value: procedure.cpt_code,
                            label: procedure.procedure_name,
                        },
                        isPHIC: procedure.phic,
                        amount: parseInt(procedure.amount),
                        notes: procedure.notes,
                        assessmentType: procedure.assessment_type,
                        assessmentRemarks: procedure.assessment_remarks,
                    };
                }),
            });
        });

        this.setState({
            totalAmount: totalAmount,
        });

        return diagnoses;
    }

    getErDiagnoses(erDetails: any): any[] {
        let totalAmount = 0;
        const diagnoses: any[] = [];
        forEach(erDetails.availments_list, availmentItem => {
            forEach(availmentItem.diagnosis, diagnosis => {
                diagnoses.push({
                    icd10Code: {
                        value: diagnosis.icd10_code,
                        label: diagnosis.diagnosis,
                    },
                    remarks: diagnosis.remarks,
                    isPrimary: diagnosis.is_primary,
                    isFinal: diagnosis.is_final,
                    doctors: diagnosis.doctors.map(doctor => {
                        return {
                            id: doctor.id,
                            name: doctor.name,
                            specialization: doctor.specialization,
                            fee: doctor.fee,
                            noOfVisits: doctor.no_of_visits,
                            remarks: doctor.remarks,
                        };
                    }),
                    procedures: diagnosis.procedures.map(procedure => {
                        let procedureAmount = get(procedure, 'amount', get(procedure, 'approved_amount', get(procedure, 'ruv_amount', 0)));
                        totalAmount += parseFloat(procedureAmount);
                        return {
                            cptCode: {
                                value: procedure.cpt_code,
                                label: procedure.procedure_name,
                            },
                            isPHIC: procedure.phic,
                            amount: parseInt(procedure.amount),
                            notes: procedure.notes,
                            assessmentType: procedure.assessment_type,
                            assessmentRemarks: procedure.assessment_remarks,
                        };
                    }),
                });
            });
        });

        this.setState({
            totalAmount: totalAmount,
        });

        return diagnoses;
    }

    getConsultationDiagnoses(consultationDetails: any): any[] {
        const diagnoses: any[] = [];
        const icd10Code = get(consultationDetails, 'diagnosis', ' - ').split(' - ');
        const doctorData = get(consultationDetails, 'doctor', ' - ').split(' - ');
        const totalAmount = parseFloat(get(consultationDetails, 'amount', get(consultationDetails, 'approved_amount', get(consultationDetails, 'ruv_amount', 0))));

        diagnoses.push({
            icd10Code: {
                value: icd10Code[0],
                label: icd10Code[1],
            },
            remarks: consultationDetails.remarks,
            isPrimary: consultationDetails.is_primary,
            isFinal: consultationDetails.is_final,
            doctors: [
                {
                    id: doctorData[0],
                    name: doctorData[1],
                    specialization: null,
                    fee: 0,
                    noOfVisits: 0,
                    remarks: consultationDetails.assessment_remarks,
                },
            ],
            procedures: [],
        });

        this.setState({
            totalAmount: totalAmount,
        });

        return diagnoses;
    }

    getInquiries(inquiriesDetails: any): any[] {
        const inquiries: any[] = [];
        forEach(get(inquiriesDetails, 'inquiry_list', []), inquiry => {
            inquiries.push({
                inquiry: inquiry.inquiry,
                value: inquiry.value,
            });
        });

        return inquiries;
    }

    getTotalAailmentProcedureCost(): number {
        let total = 0;

        if (this.state.availment) {
            forEach(this.state.availment.diagnoses, diagnosis => {
                forEach(diagnosis.procedures, procedure => {
                    total += procedure.amount;
                });
            });
        }

        return total;
    }

    render(): JSX.Element {
        return (
            <div className="ticket-modal section">
                <div
                    style={{
                        float: 'right',
                        marginTop: '-1rem',
                        fontSize: '1rem',
                    }}
                >
                    <IconButton
                        icon={faTimes}
                        onClick={() => {
                            this.props.onCloseButtonClicked();
                        }}
                    />
                </div>
                {this.state.ticket ? this.renderTicketInfo(this.state.ticket) : null}
            </div>
        );
    }

    renderTicketInfo(ticket: TicketData): JSX.Element {
        return (
            <>
                <div className="sub-sections">
                    <div className="header-1">{`Ticket ${ticket.ticketNo}`}</div>
                    <div className="text">{ticket.status + ' - ' + ticket.urgency + ', Due on May 23, 2019*'}</div>
                </div>
                <div className="sub-section">
                    <Grid container>
                        <Grid item className="sub-section" xs={6}>
                            <div className="light-text">TICKET TYPE</div>
                            <div className="small-text">{ticket.ticketType}</div>
                        </Grid>
                        <Grid item className="sub-section" xs={6}>
                            <div className="light-text">SPECIFIC TYPE</div>
                            <div className="small-text">{ticket.specificType}</div>
                        </Grid>
                        <Grid item className="sub-section" xs={6}>
                            <div className="light-text">RECEIVED VIA</div>
                            <div className="small-text">{ticket.ticketChannel}</div>
                        </Grid>
                        <Grid item className="sub-section" xs={6}>
                            <div className="light-text">CALL TRANSACTION NO.</div>
                            <div className="small-text">{ticket.transactionNo}</div>
                        </Grid>
                        <Grid item className="sub-section" xs={6}>
                            <div className="light-text">RECEIVED FROM</div>
                            <div className="small-text">{get(ticket, 'ticketSource', '-')}</div>
                        </Grid>
                        <Grid item className="sub-section" xs={6}>
                            <div className="light-text">NUMBER / EMAIL</div>
                            <div className="small-text">{ticket.contactNo}</div>
                        </Grid>
                    </Grid>
                    <div className="sub-section">
                        <div className="light-text">ASSIGNED TO</div>
                        <div className="small-text">{ticket.assignee.name}</div>
                    </div>
                    <div className="sub-section">
                        {get(this.state, 'ticket.ticketType', '') === 'Availment' ? (
                            <>
                                <div className="light-text">DIAGNOSIS  PROCEDURES</div>
                                {this.state.availment ? (
                                    <Grid container spacing={2} className="sub-section">
                                        {this.state.availment.diagnoses.map((diagnosis, index) => {
                                            return (
                                                <>
                                                    <Grid item xs={12} className="header-2">
                                                        {`${index + 1}. ${diagnosis.icd10Code.value} - ${diagnosis.icd10Code.label}`}
                                                    </Grid>
                                                    {diagnosis.procedures.map((procedure, index2) => {
                                                        return (
                                                            <>
                                                                <Grid item xs={1} />
                                                                <Grid item xs={11} className="small-text">
                                                                    {`${(index2 + 10).toString(36)}. ${procedure.cptCode.label} (Php ${GlobalFunction.formatNumber(procedure.amount, true)})`}
                                                                </Grid>
                                                            </>
                                                        );
                                                    })}
                                                </>
                                            );
                                        })}
                                    </Grid>
                                ) : null}
                                <div style={{ paddingTop: '0.5rem' }} />
                                <div className="header-2">{'TOTAL: PHP ' + GlobalFunction.formatNumber(this.state.totalAmount, true)}</div>
                            </>
                        ) : get(this.state, 'ticket.ticketType', '') === 'Inquiry' ? (
                            <>
                                <div className="light-text">Inquiries</div>
                                {this.state.inquiries ? (
                                    <Grid container spacing={2} className="sub-section">
                                        {map(get(this.state, 'inquiries', []), (inquiry: any, index) => {
                                            return (
                                                <>
                                                    <Grid item xs={12} className="header-2">
                                                        {`${index + 1}. ${inquiry.inquiry}`}
                                                    </Grid>
                                                    <Grid item xs={1} />
                                                    <Grid item xs={11} className="small-text">
                                                        {`${(index + 10).toString(36)}. ${inquiry.value})`}
                                                    </Grid>
                                                </>
                                            );
                                        })}
                                    </Grid>
                                ) : (
                                    'No inquiries.'
                                )}
                            </>
                        ) : null}
                    </div>

                    <div className="sub-section">
                        <div className="light-text">CREATED BY</div>
                        <div className="small-text">{ticket.createdBy.name}</div>
                    </div>
                    <div className="sub-section">
                        <div className="light-text">DATE AND TIME CREATED</div>
                        <div className="small-text">{GlobalFunction.toDateAndTime(ticket.dateCreated)}</div>
                    </div>
                    <div className="sub-section">
                        <div className="light-text">DATE AND TIME LAST UPDATED</div>
                        <div className="small-text">{GlobalFunction.toDateAndTime(ticket.dateUpdated)}</div>
                    </div>
                    <div className="sub-section">
                        <div className="light-text">DATE AND TIME LAST ESCALATED</div>
                        <div className="small-text">{'May 14, 2019  09:33:00 AM*'}</div>
                    </div>
                </div>
            </>
        );
    }
}
