import * as React from 'react';
import htmlDocx from 'html-docx-js/dist/html-docx';
import { saveAs } from 'file-saver';
import moment from 'moment';
var htmlToPdfmake = require("html-to-pdfmake");
var pdfFonts = require("pdfmake/build/vfs_fonts");
var pdfMake = require("pdfmake/build/pdfmake");
pdfMake.vfs = pdfFonts.pdfMake.vfs;
// var minify = require('html-minifier').minify;

export interface HtmlToPDFProps {
    html?: any,
    renderType: string,
    resetFlag: any,
    fileType?: string | null | undefined,
    clientID: string,
    clientName: string,
    renderAction: string,
    viewOnly?: (params) => void,
    onStartDownload: () => void,
    onError: () => void
}

export class HtmlToPDFRenderer extends React.Component<HtmlToPDFProps>  {
    constructor(props: HtmlToPDFProps) {
        super(props)
    }

    componentWillReceiveProps() {
        this.printRender();
    }

    componentDidMount() {
        this.printRender();
    }

    printRender = () => {
        var htmlData = this.props.html["html"].replace(/\s\s+/g, ' ');
        //var htmlData = this.props.html
        console.log(this.props.html.params['default_style']);
        if (typeof this.props.fileType == "string" && this.props.fileType.toLowerCase() == "docx") {
            try {
                this.printDocx(htmlData);
            } catch {
                this.props.onError();
            }
        } else {
            try {
                this.printPdf(htmlData);
            } catch {
                this.props.onError();
            }
        }
    }

    printPdf = (htmlData) => {
        let preFileName = '';
        if (this.props.renderType == 'renderProposal') {
            preFileName = 'Proposal';
            var html = htmlToPdfmake(htmlData, {
                defaultStyles: this.props.html.params["default_style"] !== undefined ? JSON.parse(this.props.html.params["default_style"]) : {}
            });
            if (html[0].stack[0].table !== undefined) {
                html[0].stack[0].table.body[0].map(d => {
                    d['border'] = [false, false, false, false];
                });
            }
            var docDefinition = {
                // header: 'simple text',
                content: [
                    html
                ],
                styles: this.props.html.params["pdfmake_style"] !== undefined ? JSON.parse(this.props.html.params["pdfmake_style"]) : {},
                pageBreakBefore: function (currentNode) {
                    return currentNode.style && currentNode.style.indexOf('pdf-pagebreak-before') > -1;
                }
            };
            var pdfDocGenerator = pdfMake.createPdf(docDefinition);
        } else if (this.props.renderType == 'renderContract') {
            preFileName = 'Contract';
            const c_default_style = { 
                // td: {
                //     margin: [0, 0, 0, 0],
                // },
                // th: {
                //     backgroundColor: 'red'
                // },
                // h4: {
                //     margin: [0, 10, 0, 10],
                //     fontSize: 14
                // },
                // p: {
                //     margin: [5, 2, 5, 2],
                // }
            };
            var html = htmlToPdfmake(htmlData, {
                defaultStyles: this.props.html.params["default_style"] !== undefined ? JSON.parse(this.props.html.params["default_style"]) : c_default_style
            });
            var _docDefinition = {
                // header: 'simple text',
                footer: this.props.html.params["default_footer"] !== undefined ? JSON.parse(this.props.html.params["default_footer"]) : {},
                content: [html],
                styles: this.props.html.params["pdfmake_style"] !== undefined ? JSON.parse(this.props.html.params["pdfmake_style"]) : {},
                pageBreakBefore: function (currentNode) {
                    return currentNode.style && currentNode.style.indexOf('pdf-pagebreak-before') > -1;
                }
            };
            var pdfDocGenerator = pdfMake.createPdf(_docDefinition);
        }
      
        let fileName =  preFileName + '-' + this.props.clientName + '-' + moment().format('MMDDYYYY-hmssa');
        if (this.props.clientName === "" || this.props.clientName === undefined || this.props.clientName === null) {
            fileName = preFileName + '-'  + this.props.clientID + '-' + moment().format('MMDDYYYY-hmssa');
        }
        if (this.props.viewOnly !== undefined && this.props.renderAction === 'view') {
            pdfDocGenerator.getBlob((blob) => {
                this.props.viewOnly !== undefined && this.props.viewOnly(blob);
            });
        } else {
            pdfDocGenerator.download(fileName+'.pdf');
        }
        //pdfDocGenerator.download(fileName+'.pdf');
        this.props.onStartDownload();

        this.props.onStartDownload();

        if (this.props.resetFlag) {
            this.props.resetFlag();
        }
    }

    printDocx = (htmlData) => {
        let preFileName = '';
        if (this.props.renderType === 'renderProposal') {
            var converted = htmlDocx.asBlob(htmlData, { orientation: 'portrait', margins: { top: 720 } });
            preFileName = 'Proposal';
        } else if (this.props.renderType === 'renderContract') {
            var converted = htmlDocx.asBlob(htmlData, { orientation: 'portrait', margins: { top: 720, left: 720, right: 720, bottom: 720 } });
            preFileName = 'Contract';
        }
        let fileName = preFileName + '-' + this.props.clientName + '-' + moment().format('MMDDYYYY-hmssa');
        if (this.props.clientName === "" || this.props.clientName === undefined || this.props.clientName === null) {
            fileName = preFileName + '-' + this.props.clientID + '-' + moment().format('MMDDYYYY-hmssa');
        }

        saveAs(converted, fileName + '.docx');

        if (this.props.resetFlag) {
            this.props.resetFlag();
        }

        this.props.onStartDownload();
    }

    render() {
        return (
            <>
            </>
        )
    }
}