import * as React from 'react';
import { useState, useEffect } from 'react';
import { Grid } from '@material-ui/core';
import { API } from 'Pages/CustomerCarePage/API';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';
import { BenefitsTable } from './BenefitsTable';

interface MemberBenefitProps {
    memberId: string;
}

export const MemberBenefitSection: React.FC<MemberBenefitProps> = (props: MemberBenefitProps): JSX.Element => {

    const [isLoading, SetIsLoading] = useState<boolean>(false);
    const [memberBenefits, SetMemberBenefits] = useState<any[]>([]);
    
    const loadBenefitsData = () : void => {
        console.log('memberId: ', props.memberId);
        SetIsLoading(true);
        API.get(
            `customer-care/members/${props.memberId}`, 
            (memberResponse) => {
                if(memberResponse && memberResponse.error === undefined){
                    API.get(
                        `members/${memberResponse._id}/contracts/${memberResponse.latest_contract._id}/benefits`,
                        (benefitResponse) => {
                            if(benefitResponse && benefitResponse.error === undefined){

                                const nodes = benefitResponse.sort ((node1: any, node2: any) => {
                                  if (node1.plan_type_rank && node2.plan_type_rank) {
                                    if (node1.plan_type_rank < node2.plan_type_rank) return -1;
                                    if (node1.plan_type_rank > node2.plan_type_rank) return 1;
                                  }

                                  if (node1.plan_type_rank) return -1;
                                  if (node2.plan_type_rank) return 1;

                                  if (node1.sort_index > node2.sort_index) return -1;
                                  if (node1.sort_index < node2.sort_index) return 1;

                                  if (node1.name > node2.name) return 1;
                                  if (node1.name < node2.name) return -1;

                                  return 0;
                                });

                                SetIsLoading(false);
                                console.log('benefits: ', nodes);
                                SetMemberBenefits(benefitResponse);
                            }
                            else{
                                SetIsLoading(false);
                                console.log('get customer care contract error ', benefitResponse.error);
                            }
                        },
                        (benefitError) => {
                            SetIsLoading(false);
                            console.log('get customer care contract error ', benefitError);
                        }
                    );
                }
                else{
                    SetIsLoading(false);
                    console.log('get customer care data error ', memberResponse.error);
                }
            },
            (error) => {
                SetIsLoading(false);
                console.log('get customer care data error ', error);
            }
        );
    }

    useEffect(() => {
        loadBenefitsData();
    }, []);
    
    return (
        <div className='section'>
            {
                isLoading ? 
                <Loading height='10rem'/> 
                : (
                    <div>
                        <div style={{paddingTop: '2.5rem'}} />
                        <div className='header-1'>Member Benefits</div>
                        <Grid container className='sub-section' spacing={5} style={{paddingRight: 24, paddingLeft: 24, marginTop: 24}}>
                            <BenefitsTable 
                                contractNum={'-'}
                                memberBenefits={memberBenefits} />
                        </Grid>
                        <div className='section' />
                    </div>
                )
            }
        </div>
    );
}