    static getConfinementData(confinement: any, response: any): UpdateInpatientDetails {
        const providerData = confinement.provider.split(' - ');
        return {
            provider: { value: providerData[0], label: providerData[1] },
            dateAdmitted: new Date(confinement.date_admitted),
            confinementKind: confinement.confinement_kind,
            giftGiven: confinement.gift_given,
            coverage: { value: '', label: confinement.coverage },
            ticketNo: confinement.ticket_no,
            paidBy: confinement.paid_by,
            philhealthAmount: confinement.philhealth_amount,
            finalBill: confinement.final_bill,
            dischargeDate: confinement.discharge_date ? new Date(confinement.discharge_date) : undefined,

            primaryDiagnosisIndex: confinement.diagnoses
                .map((value, index) => {
                    if (value.is_primary) {
                        return index;
                    }
                    return -1;
                })
                .filter(value => value != -1)[0],

            updates: confinement.updates.map(value => {
                return {
                    remarks: value.remarks,
                    date: new Date(value.date),
                    hospitalBill: parseFloat(value.hospital_bill)
                };
            }),
            accommodations: confinement.room_accommodation.map(item => {
                return {
                    roomType: { value: item.room_type, label: item.room_type },
                    roomNo: item.room_no,
                    days: item.no_of_days,
                    costPerDay: item.amount
                };
            }),
            diagnoses: confinement.diagnoses.map(value => {
                return {
                    diagnosis: { value: value.icd10_code, label: value.diagnosis },
                    remarks: value.remarks,
                    isFinal: value.is_final,
                    isPrimary: value.is_primary
                };
            }),
            physicians: [].concat(...confinement.diagnoses.map(diagnosis => {
                return diagnosis.doctors.map(doctor => {
                    return {
                        physician: {
                            value: doctor.doctor_id,
                            label: doctor.name,
                            params: [doctor.specialization, doctor.type]
                        },
                        diagnosis: { value: diagnosis.icd10_code, label: diagnosis.diagnosis },
                        remarks: doctor.remarks,
                        visits: doctor.no_of_visits,
                        amount: doctor.fee
                    };
                });
            })),
            procedures: [].concat(...confinement.diagnoses.map(diagnosis => {
                return diagnosis.procedures.map(procedure => {
                    return {
                        procedure: { value: procedure.cpt_code, label: procedure.procedure_name },
                        diagnosis: { value: diagnosis.icd10_code, label: diagnosis.diagnosis },
                        physician: { value: '', label: procedure.doctor },
                        date: new Date(procedure.date_added),
                        remarks: procedure.notes,
                        amount: parseInt(procedure.amount)
                    };
                });
            })),
            items: confinement.others.map(value => {
                return {
                    name: value.item_name,
                    type: { value: '', label: value.type },
                    amount: value.amount
                };
            }),
            files: response.supporting_documents.map(value => {
                return {
                    docName: value.doc_name,
                    file: value
                };
            })
        }
    }










    static getResolveConfinementData(confinement: any): ResolveInpatientDetails {
        const providerData = confinement.provider.split(' - ');
        return {
            provider: { value: providerData[0], label: providerData[1] },
            dateAdmitted: new Date(confinement.date_admitted),
            confinementKind: confinement.confinement_kind,
            giftGiven: confinement.gift_given,
            coverage: { value: '', label: confinement.coverage },
            philhealthAmount: confinement.philhealth_amount,
            finalBill: confinement.final_bill,
            dischargeDate: confinement.discharge_date ? new Date(confinement.discharge_date) : undefined,
            diagnoses: confinement.diagnoses.map(diagnosis => {
                const doctorWithApprovedAmount = diagnosis.doctors.find(doctor => doctor.approved_amount);
                return {
                    icd10Code: { value: diagnosis.icd10_code, label: diagnosis.diagnosis },
                    remarks: diagnosis.remarks,
                    isPrimary: diagnosis.is_primary,
                    isFinal: diagnosis.is_final,
                    assessmentType: diagnosis.assessment_type,
                    assessmentRemarks: diagnosis.assessment_remarks,
                    approvedAmount: doctorWithApprovedAmount ? doctorWithApprovedAmount.approved_amount : 0,
                    loaNo: diagnosis.loa_no,
                    approvalCode: diagnosis.approval_code,
                    doctors: diagnosis.doctors.map(doctor => {
                        return {
                            id: doctor.doctor_id,
                            name: doctor.name,
                            specialization: doctor.specialization,
                            type: doctor.type,
                            fee: doctor.fee,
                            noOfVisits: doctor.no_of_visits,
                            remarks: doctor.remarks
                        }
                    }),
                    procedures: diagnosis.procedures.map(procedure => {
                        return {
                            cptCode: { value: procedure.cpt_code, label: procedure.procedure_name },
                            isPHIC: procedure.phic,
                            amount: parseInt(procedure.amount),
                            notes: procedure.notes,
                            doctor: procedure.doctor,
                            dateAdded: new Date(procedure.date_added),
                            assessmentType: procedure.assessment_type,
                            assessmentRemarks: procedure.assessment_remarks,
                            approvedAmount: procedure.approved_amount,
                            loaNo: procedure.loa_no,
                            approvalCode: procedure.approval_code
                        }
                    })
                }
            }),
            accommodations: confinement.room_accommodation.map(accommodation => {
                return {
                    roomType: accommodation.room_type,
                    roomNo: accommodation.room_no,
                    days: parseInt(accommodation.no_of_days),
                    costPerDay: accommodation.amount
                }
            }),
            updates: confinement.updates,
            others: confinement.others
        }
    }










