//#region Global Imports
import * as React from 'react';

import { BenefitsTable } from '../../TableComponent/BenefitsTable';

import Grid from '@material-ui/core/Grid';
// import Hidden from '@material-ui/core/Hidden';

//import { Components } from '@hims/core';
//#endregion Global Imports


import { defaultProps } from './Constants';
import { ICategorySubForm } from './ICategorySubForm';

export const ExclusionsForm: React.FC<ICategorySubForm.IProps> = (
	props: ICategorySubForm.IProps,
): JSX.Element | null => {
	const { formHeader, rows, viewOnly } = props;

	console.log(rows);

	let columns: any = [	{ name: 'parent_name', title: 'Type' }, 
							{ name: 'name', title: 'Item' }
							
						];
	let formattedColumns = {};

	if (!viewOnly) {
		columns.push({	
			name: 'included', 
			title: 'Include item?'
		});

		formattedColumns = {
			exclusions_checkbox: ['included'],
		};
	}

	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2} style={{ marginTop: '27.5px' }}>
					{!viewOnly ? formHeader : null}
				</Grid>
			</Grid>
			<Grid item sm={12}>
				<BenefitsTable {...props} viewOnly={viewOnly} disableSelect columns={columns} formattedColumns={formattedColumns} />
			</Grid>
		</React.Fragment>
	);
};

ExclusionsForm.defaultProps = defaultProps;