import * as React from 'react';
import Grid from '@material-ui/core/Grid';
import { Dropdown } from 'Pages/CustomerCarePage/Components/Dropdown';
import { DetailedDropdown } from 'Pages/CustomerCarePage/Components/DetailedDropdown';
import { DetailedNumberField } from 'Pages/CustomerCarePage/Components/DetailedNumberField';
import { DatePicker } from 'Pages/CustomerCarePage/Components/DatePicker';
import { DropdownChoice, ConfinementDetails } from 'Pages/CustomerCarePage/Models';
import { Global } from 'Pages/CustomerCarePage/Global';
import { TimePicker } from 'Pages/CustomerCarePage/Components/TimePicker';
import { InputField } from 'Pages/CustomerCarePage/Components/InputField';
import { API } from 'Pages/CustomerCarePage/API';

interface IProps {
    confinement: ConfinementDetails;
    facilities: DropdownChoice[];
    doctors: DropdownChoice[];
    createTicketAttemp: boolean;
    refreshConfinementState: () => void;
    loadDoctors: (facilityCode: string) => void;
    loadFacilities: () => void;
}

interface IStates {
    roomTypes: DropdownChoice[];
}

export class ConfinementSection extends React.Component<IProps, IStates> {

    confinementTypes: DropdownChoice[];

    constructor(props: IProps) {
        super(props);

        this.state = {
            roomTypes: []
        };

        this.confinementTypes = [
            { value: '1', label: 'Elective / Scheduled' },
            { value: '2', label: 'ER leading to confinement' }
        ];
    }

    loadRoomTypes = () => {
        if (this.state.roomTypes.length === 0) { // Optimization
            API.get('customer-care/room-categories', (response) => {
                const rtype: DropdownChoice[] = [];
                response.forEach((item) => {
                    rtype.push({
                        value: item.category,
                        label: item.category
                    });
                });
                this.setState({roomTypes: rtype});
            });
        }
    }

    render(): JSX.Element {
        const confinement = this.props.confinement;
        return (
            <div className='confinement-details'>
                <div style={{paddingTop: '5rem'}} />
                <div className='sub-section'>
                    <div className='header-2'>CONFINEMENT DETAILS</div>
                    <DetailedDropdown 
                        choices={this.props.facilities}
                        placeholder='Provider*'
                        value={confinement.provider}
                        error={
                            this.props.createTicketAttemp && 
                            confinement.provider.value === ''}
                        onFocus={() => {
                            this.props.loadFacilities();
                        }}
                        onChange={(value: DropdownChoice) => {
                            confinement.provider = value;
                            confinement.doctor = { value: '', label: '' };
                            this.props.refreshConfinementState();
                        }} />
                    <Grid container spacing={1}>
                        <Grid item xs={4}>
                            <DatePicker 
                                placeholder='Date Admitted' 
                                value={confinement.dateAdmitted}
                                onChange={(value: Date) => {
                                    confinement.dateAdmitted = value;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                        <Grid item xs={3}>
                            <TimePicker 
                                placeholder='Time Admitted' 
                                value={confinement.dateAdmitted}
                                onChange={(value: Date) => {
                                    confinement.dateAdmitted = value;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                        <Grid item xs={5}>
                            <Dropdown 
                                placeholder='Kind of Confinement*' 
                                choices={this.confinementTypes}
                                value={{ value: '', label: confinement.confinementKind}}
                                error={
                                    this.props.createTicketAttemp && 
                                    confinement.confinementKind === ''}
                                onChange={(value: DropdownChoice) => {
                                    confinement.confinementKind = value.label;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                        <Grid item xs={2}>
                            <Dropdown  
                                placeholder='Room Type' 
                                choices={this.state.roomTypes}
                                value={confinement.roomType}
                                onFocus={() => {
                                    this.loadRoomTypes();
                                }}
                                onChange={(value: DropdownChoice) => {
                                    confinement.roomType = value;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                        <Grid item xs={2}>
                            <InputField 
                                placeholder='Room No.' 
                                value={confinement.roomNo}
                                onChange={(value: string) => {
                                    confinement.roomNo = value;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                        <Grid item xs={3}>
                            <DetailedNumberField
                                disabled={confinement.roomType.value.trim() === ''}
                                placeholder='Cost Per Day' 
                                value={confinement.costPerDay}
                                detail={'PHP'}
                                isFloat={true}
                                onChange={(value: number) => {
                                    confinement.costPerDay = value;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                        <Grid item xs={5}>
                            <Dropdown  
                                placeholder='Admitting Physician' 
                                choices={this.props.doctors}
                                value={confinement.doctor}
                                onFocus={() => {
                                    this.props.loadDoctors(confinement.provider.label);
                                }}
                                onChange={(value: DropdownChoice) => {
                                    confinement.doctor = value;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                        <Grid item xs={7}>
                            <DetailedDropdown 
                                placeholder='Initial Diagnosis*' 
                                choices={Global.ICD10_CODES ? Global.ICD10_CODES : []}
                                value={confinement.initialDiagnosis}
                                error={
                                    this.props.createTicketAttemp && 
                                    confinement.initialDiagnosis.value === ''}
                                onChange={(value: DropdownChoice) => {
                                    confinement.initialDiagnosis = value;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                        <Grid item xs={3}>
                            <DetailedNumberField
                                disabled={confinement.doctor.value.trim()===''}
                                placeholder='Amount' 
                                value={confinement.amount}
                                detail={'PHP'}
                                isFloat={true}
                                onChange={(value: number) => {
                                    this.props.confinement.amount = value;
                                    this.props.refreshConfinementState();
                                }} />
                        </Grid>
                    </Grid>
                </div>
                <div className='section' />
            </div>
        );
    }
}