import * as React from 'react';
import { CustomModal } from 'Pages/CustomerCarePage/Components/CustomModal';
import { InputArea } from 'Pages/CustomerCarePage/Components/InputArea';
import { CustomButton } from 'Pages/CustomerCarePage/Components/CustomButton';
import { Processmaker } from 'Pages/CustomerCarePage/Processmaker';
import { TicketData, LoginData, CaseData } from 'Pages/CustomerCarePage/Models';
import { API } from 'Pages/CustomerCarePage/API';
import { get } from 'lodash';
import { Utils } from '@hims/core';

interface IProps {
    caseData?: CaseData;
    caseId?: string;
    ticket: TicketData;
    login: LoginData;
    isOpen: boolean;
    assigneeIsMe: boolean;
    amountLimit: number;
    onClose: () => void;
    onSuccess?: (amountLimit: number, outsideLimitNotes?: string, groupNames?: string[]) => void;
    onSuccessWithCallback?: (onAfterSuccess?: (onSuccess: Function) => void) => void;
    onBeforeMainAction: () => void;
}

interface IStates {
    supervisingDepartmentPMakerId: string;
    notes: string;
    supervisorDept: string;
    isError: boolean;
    hasPendingRequest: boolean;
}

export class OutsideTheLimitModal extends React.Component<IProps, IStates> {

    constructor(props: IProps) {
        super(props);

        this.state = {
            supervisingDepartmentPMakerId: '',
            notes: '',
            supervisorDept: '',
            isError: false,
            hasPendingRequest: false
        };
    }

    componentDidMount() {
        this.getSupervisingDepartmentPMakerId()
    }

    handleGroupNamesCommaDelimited = (str_arr : string[]) =>{
        if(str_arr === undefined || str_arr === null || str_arr.length === 0){
            return this.state.supervisorDept
        }
        let result = ''
        for(let i = 0; i < str_arr.length; i++){
            if(i === str_arr.length - 1){
                result += str_arr[i]
            }else{
                result += str_arr[i]+', '
            }
            
        }
        return result
    }


    async getSupervisingDepartmentPMakerId() {
        await Utils.StorageService('user_data', 'cc_data')
            .then(res => {
            console.log(res);
            let cc_data = res.result;
            this.setState({
                supervisorDept : cc_data.supervising_department,
                supervisingDepartmentPMakerId : cc_data.supervising_department_pmaker_id
            })
      })
      .catch(err => console.log(err));
    }

    isValid = () => {
        if (this.state.notes === '') {
            this.setState({isError: true});
            return false;
        }
        return true;
    }

    onFinalAssessment = () => {
        if (!this.isValid() || this.state.hasPendingRequest) return;

        this.props.onBeforeMainAction();
        this.setState({hasPendingRequest: true});

        const login = this.props.login;
        const limit = this.props.amountLimit;
        const department = login.group ? login.group.department : '';
        const isInpatient = this.props.ticket.specificType === 'Inpatient';

        let requesting_group_str = '';
        if (this.props.caseData && this.props.caseData.task && this.props.caseData.task === 'Outpatient - Comment') {
        } else {
            requesting_group_str = `&requesting_group=${get(login, 'group.name', '')}`
        }

        API.get(`group/pmaker_uids?amount_limit=${limit}&department=${department}`+`${requesting_group_str}&role=Ticket Approver&with_names=true`, response => {
            let response2 : any[] = [];
            if (Array.isArray(response.group_pmaker_ids) && response.group_pmaker_ids.length === 0) {
                response2.push(this.state.supervisingDepartmentPMakerId)
            } else {
                response2 = response.group_pmaker_ids;
            }

            if (this.props.caseId) {
                
                let body = {
                    button_pressed: 'PROCEED WITH FINAL ASSESSMENT',
                    within_limit: !isInpatient,
                    next_user: response2,
                };
                if (this.props.caseData && this.props.caseData.task && this.props.caseData.task === 'Outpatient - Comment') {
                    body = {
                        button_pressed: 'SUBMIT FOR ASSESSMENT',
                        within_limit: false,
                        next_user: response2,
                    };
                }
                const patchBody = {
                    assign_to: 
                        response && 
                        response.group_names && 
                        response.group_names.length > 0 ? 
                        this.handleGroupNamesCommaDelimited(response.group_names) : 
                        this.state.supervisorDept,
                    assigned_by: `${this.props.login.firstName} ${this.props.login.lastName}`,
                    assign_remarks: this.state.notes,
                };
                // API.patch(`customer-care/tickets/${this.props.ticket.id}`, patchBody, () => {
                //     Processmaker.put(`cases/${this.props.caseId}/variable`, body, () => {
                //         Processmaker.put(`cases/${this.props.caseId}/route-case`, {}, () => {
                //             this.props.onSuccess(limit);
                //         });
                //     });
                // });
                Processmaker.put(`cases/${this.props.caseId}/variable`, body, () => {
                    Processmaker.put(`cases/${this.props.caseId}/route-case`, {}, () => {
                        if (this.props.onSuccessWithCallback) {
                            this.props.onSuccessWithCallback((onSuccess) => {
                                API.patch(`customer-care/tickets/${this.props.ticket.id}`, patchBody, onSuccess);
                            });
                        }
                    });
                });
                
            } else if (this.props.onSuccess) {
                this.props.onSuccess(this.props.amountLimit, this.state.notes, response.group_names);
            }
        });
    }

    render(): JSX.Element {
        const isInpatient = this.props.ticket.specificType === 'Inpatient';

        return (
            <CustomModal
                className='modal'
                isOpen={this.props.isOpen}
                handleClose={() => this.props.onClose()}
                header={`Amount is outside ${this.props.assigneeIsMe ? 'your' : 'assignee\'s'} limit`}
                content={
                    <>
                    <div style={{padding: '0.5rem 0'}}>
                        This ticket will be assigned to an approver or group of approvers
                        who are authorized to assess availment tickets with this amount.
                        You can add a few notes for additional details or instruction.
                    </div>
                    <div style={{padding: '0.5rem 0'}}>
                        <InputArea
                            error={this.state.isError && this.state.notes === ''}
                            placeholder='Notes'
                            defaultRows={4}
                            value={this.state.notes}
                            onChange={(value: string) => {
                                this.setState({notes: value});
                            }} />
                    </div>
                    </>
                }
                footer={
                    <>
                    <CustomButton
                        text='Cancel'
                        variant='outlined'
                        onClick={() => {
                            this.props.onClose()
                        }} />
                    <span style={{padding: '1rem'}} />
                    <CustomButton
                        className={`button${isInpatient ? '-special' : ''}`}
                        text={isInpatient ? 'Proceed with Final Assessment' : 'Assign to Authorized Approver'}
                        onClick={() => {
                            this.onFinalAssessment();
                        }} />
                    </>
                } />
        );
    }
}
