import * as React from 'react';
import { API } from 'Pages/CustomerCarePage/API';
import { RendererClass } from '../../Components/RendererClass';
import { Loading } from 'Pages/CustomerCarePage/Components/Loading';

interface IProps {
    memberId?: string;
    clientId?: string;
    contractId: string;
}

interface IStates {
    clientId: string;
    registeredName: string;
    downloadProposal: boolean;
    proposalToDownload: string;
    isLoading: boolean;
    downloadurl: string;
}

export class CheckContractSection extends React.Component<IProps, IStates> {

    contractUrls: string[];

    constructor(props: IProps) {
        super(props);

        this.state = {
            clientId: '',
            registeredName: '',
            downloadProposal: false,
            proposalToDownload: '',
            isLoading: true,
            downloadurl: ''
        }

        this.contractUrls = [];
    }

    componentDidMount() {
        
        if (this.props.memberId) {
            API.get(`customer-care/members/${this.props.memberId}`, (response) => {
                API.get(`customer-care/members/${response._id}/contracts/${response.latest_contract._id}`, (benefitresponse) => {
                    const MCTNode = benefitresponse.benefits.filter(item => item.code === 'MCT');
                    this.setState({
                        clientId: response.client._id,
                        registeredName: response.client.registered_name,
                        proposalToDownload: MCTNode[0]._id,
                        downloadProposal: true,
                        isLoading: false
                    });
                });
            });
        } else if (this.props.clientId) {
            API.get(`customer-care/clients/${this.props.clientId}`, (response) => {
                API.get(`benefit-plans/tree/${response.latest_contract._id}`, (benefitresponse) => {
                    const MCTNode = benefitresponse.filter(item => item.code === 'MCT');
                    this.setState({
                        clientId: response._id,
                        registeredName: response.registered_name,
                        proposalToDownload: MCTNode[0]._id,
                        downloadProposal: true,
                        isLoading: false
                    });
                });
            });
        }
    }
    
    setDownloadProposal = () => {
        this.setState({downloadProposal: false});
    }

    displayPDF = (blob) => {
        const url = URL.createObjectURL(blob);
        this.setState({
            downloadurl: `${process.execPath}/../pdfjs-2.2.228-dist/web/viewer.html?file=${url}`
        });
    }

    render(): JSX.Element {
        //const pdfUrl = `${process.execPath}/../../../sample.pdf`;
        // const viewerUrl = `https://mozilla.github.io/pdf.js/web/viewer.html?file=${pdfUrl}`;
        //const viewerUrl = `${process.execPath}/../pdfjs-2.2.228-dist/web/viewer.html?file=${pdfUrl}`;

        return (
            <div className='section'>
                {this.state.isLoading ? (
                    <Loading height='10rem'/>
                ) : (
                    <>
                    <div style={{paddingTop: '2.5rem'}} />
                    <div className='header-1'>Check Contract</div>
                    <div className='sub-section'>
                        <iframe 
                            style={{width: '100%', height: '1280px'}}
                            src={this.state.downloadurl}  />
                    </div>
                    <RendererClass 
                        clientID={this.state.clientId} 
                        clientName={this.state.registeredName}
                        fileType={'pdf'} 
                        download={this.state.downloadProposal} 
                        setDownload={this.setDownloadProposal} 
                        hideButton 
                        label="Download Proposal" 
                        renderType={'renderContract'} 
                        objId={this.state.proposalToDownload}
                        viewOnly={this.displayPDF}
                        renderAction={'view'}
                        onError={() => {
                            this.setState({isLoading: false});
                        }}
                        onStartDownload={() => {
                            this.setState({isLoading: false});
                        }} />
                    </>
                )}
                
            </div>
        );
    }
}