//#region Global Imports
import * as React from 'react';
import { useState } from 'react';
import { BenefitsTable } from '../../TableComponent/BenefitsTable';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
// import Hidden from '@material-ui/core/Hidden';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import FormControl from '@material-ui/core/FormControl';
import Popover from '@material-ui/core/Popover';
import InputLabel from '@material-ui/core/InputLabel';
import Typography from '@material-ui/core/Typography';
import Toolbar from '@material-ui/core/Toolbar';
import { 
	TypeItems,
	HierarchyItems,
	EnrollmentPeriodItems,
	EffectivityCoverageItems,
	RelationshipToPrincipalItems,
	EligibilityAgeLimitItems
} from '../../TableComponent/DropdownConstants';
import { ViewField } from '../../ViewField';
import { RangeField } from '../../RangeField';
import Button from '@material-ui/core/Button';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMinusCircle } from '@fortawesome/free-solid-svg-icons';
import { faPlus } from '@fortawesome/free-solid-svg-icons';

import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogActions from '@material-ui/core/DialogActions';

import { Components } from '@hims/core';
//#endregion Global Imports


import { defaultProps } from './Constants';
import { ICategorySubForm } from './ICategorySubForm';

const InputTextField = Components.UI.InputTextField.InputTextField;
const InputSelect = Components.UI.InputSelect.InputSelect;
type ISelectItem = Components.UI.InputSelect.ISelectItem;

export const EligibilityForm: React.FC<ICategorySubForm.IProps> = (
	props: ICategorySubForm.IProps,
): JSX.Element | null => {
	const [anchorEl, setAnchorEl] = useState<HTMLAnchorElement | null>(null);
	const [type, setType] = useState('civil');
	const popoverOpen = Boolean(anchorEl);
	const { formHeader, rows, viewOnly, setSubForm, 
		subFormNode, createRelationship, createCivilStatus, deleteCivilStatus } = props;

	let metadata:any = {};
	if (subFormNode) {
		if (subFormNode['custom_metadata']) {
			metadata = subFormNode['custom_metadata'];
		}
	}
	// console.log("Eligibility: "+JSON.stringify(props.rows,null,2));
	const handleSubmitCivilStatus = (name: string = '', description: string = '') => {
		
		createCivilStatus(name, description);
	}

	const handleSubmitRelationship = (name: string = '', code: string = '', description: string = '', data: object, parent: any) => {
		// console.log(name, description, data, parent);
		createRelationship(name, code, description, data, parent);
	}

	const handlePopoverOpen = (
		e: React.MouseEvent<HTMLAnchorElement, MouseEvent>,
		type: string
	) => {
		const currentTarget = e.currentTarget;
		setType(type);

		setAnchorEl(currentTarget);
	}

	const handlePopoverClose = () => {
		setAnchorEl(null);
	}

	const columns: any = [
		{ name: 'name', title: 'Relationship' },
		{ 
			name: 'type', 
			title: 'Type',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.type 
								 	? row.custom_metadata.type : []),
		},
		{ 
			name: 'hierarchy', 
			title: 'Hierarchy',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.hierarchy 
								 	? row.custom_metadata.hierarchy : []),
		},
		{ 
			name: 'enrollment_period', 
			title: 'Enrollment Period \n(x days from)',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.enrollment_period 
								 	? row.custom_metadata.enrollment_period : []),
			
		 },
		{ 
			name: 'effectivity_coverage',
			title: 'Effectivity Coverage \n(x days from)',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.effectivity_coverage 
								 	? row.custom_metadata.effectivity_coverage : []),
			
		},
		{ 
			name: 'age_limit',
			title: 'Age Limit \n(min-max)',
			getCellValue: row => (row.custom_metadata && row.custom_metadata.age_limit 
								 	? row.custom_metadata.age_limit : []),

		},
	];

	const formattedColumns = {
		type: ['type'],
		hierarchy: ['hierarchy'],
		enrollment_period: ['enrollment_period'],
		effectivity_coverage: ['effectivity_coverage'],
		eligibility_age_limit: ['age_limit'],
	};

	const columnExtensions = [
		{ columnName: 'name', width: 125, wordWrapEnabled: true},
		{ columnName: 'hierarchy', width: 100, wordWrapEnabled: true },
		{ columnName: 'type', width: 120, wordWrapEnabled: true },
		{ columnName: 'enrollment_period', width: 250, wordWrapEnabled: true, align: 'center' },
		{ columnName: 'effectivity_coverage', width: 250, wordWrapEnabled: true, align: 'center' },
		{ columnName: 'age_limit', width: 280, wordWrapEnabled: true, align: 'center'  },
	];
	// console.log('minimum: '+metadata['principal_age_min'])

	let finalRows = rows;
	
	if (viewOnly) {
		finalRows = rows.reduce((result, row) => {
			let newChildren = [...row['child_nodes']];
			newChildren = newChildren.filter((child) => child.included ? child.included : false);

			row['child_nodes'] = newChildren;

			result.push(row);

			return result;
		}, new Array());   
	}

	return (
		<React.Fragment>
			<Grid item sm={12}>
				<Grid container spacing={2} style={{ marginTop: '27.5px' }}>
					{!viewOnly ? formHeader : null}

					<Grid item sm={12} style={{marginBottom: '20px'}}>
						{
							!viewOnly ? 
								<React.Fragment>
									<InputLabel htmlFor="principal_age">Principal Age</InputLabel>
									<RangeField 
										minValue={metadata['principal_age_min'] ? metadata['principal_age_min'] : 0}
										maxValue={metadata['principal_age_max'] ? metadata['principal_age_max'] : 0}
										minOnChange={setSubForm}
										maxOnChange={setSubForm}
										minName={'principal_age_min'}
										maxName={'principal_age_max'}
									/>
								</React.Fragment> :
								metadata['principal_age_min'] || metadata['principal_age_max'] ? (
									<ViewField 
										label="Principal Age"
										value={''.concat(metadata['principal_age_min'] ? metadata['principal_age_min'] : '0', '-' , metadata['principal_age_max'] ? metadata['principal_age_max'] : '0')}
									/>
								) : null
						}
					</Grid>
				</Grid>

				<Grid item sm={12} style={{marginBottom: '20px'}}>

					{
						!viewOnly ? 
							<React.Fragment>
								<Typography variant="overline" display="block" gutterBottom>
									Dependents
								</Typography>
								<InputLabel htmlFor="noDependents">No. of Dependents</InputLabel>
								<FormControl margin="normal">
									<OutlinedInput
										inputProps={{min:'0'}}
										onChange={setSubForm} 
										value={metadata['no_of_dependents'] ? metadata['no_of_dependents'] : 0}
										type="number" 
										labelWidth={0} 
										name="no_of_dependents"
										id="no_of_dependents"
									/>
								</FormControl>
							</React.Fragment> :
							metadata['no_of_dependents'] ? (
								<ViewField 
									label="No. of Dependents"
									value={metadata['no_of_dependents']}
								/>
							) : null
					}
				</Grid>

				{
					!viewOnly ? 
						<Grid item sm={12}>
							<Link
								style={{ color: '#3AB77D', marginTop: '20px'}}
								onClick={(e: React.MouseEvent<HTMLAnchorElement, MouseEvent>) => handlePopoverOpen(e, "civil")}
							>
								<FontAwesomeIcon icon={faPlus} /> Add a Civil Status
							</Link>
						</Grid> :
						null
				}		

				{finalRows.map((row: any, i: number) => (
					!row.included || (viewOnly && row.child_nodes.length === 0) ? null :
						<Grid item sm={12} key={i}>
							<Toolbar disableGutters={true} style={{ justifyContent: 'space-between' }}>
								<span>
									{row.name}&nbsp;

									{
										!viewOnly ? 
											<span onClick={() => deleteCivilStatus(row.id)}>
												<FontAwesomeIcon
													className="fa-sm"
													icon={faMinusCircle}
													style={{ color: '#FD5451', fontSize: '15px', marginLeft:'5px'}}
												/> 
											</span> : null
									}
								</span>
								{
									!viewOnly ? 
										<Link
											style={{ color: '#3AB77D', width: '150px', marginLeft: '10px', textAlign: 'right' }}
											onClick={(e: React.MouseEvent<HTMLAnchorElement, MouseEvent>) => handlePopoverOpen(e, "relationship")}
										>
											<FontAwesomeIcon icon={faPlus} /> Add a Relationship
										</Link> :
										null
								}
							</Toolbar>
							<BenefitsTable
								viewOnly={viewOnly}
								formattedColumns={formattedColumns}
								disableSearch
								disableFilter
								disablePaging
								disableMargin
								columnExtensions={columnExtensions}
								columns={columns}
								{...props}
								rows={row.child_nodes}
							/>
						</Grid>
					
				))}

				<Popover
					open={popoverOpen}
					anchorEl={anchorEl}
					onClose={handlePopoverClose}
					anchorOrigin={{
						vertical: 'bottom',
						horizontal: 'left',
					}}
					transformOrigin={{
						vertical: 'top',
						horizontal: 'left',
					}}
				>
					{ type === 'civil' ? 
						<CivilStatus
							onSubmit={handleSubmitCivilStatus}
							onClose={handlePopoverClose}
							rows={rows}
					 	/> : null }
					{ type === 'relationship' ? 
						<Relationship 
							onSubmit={handleSubmitRelationship}
							onClose={handlePopoverClose}
							rows={rows}
					 	/> : null }
				</Popover>

			</Grid>
		</React.Fragment>
	);
};



interface EligibilityPopoverProps {
	onClose: any;
	onSubmit: any;
	rows: any[];
}

const CivilStatus: React.FC<EligibilityPopoverProps> = (props: EligibilityPopoverProps): JSX.Element | null => {
	const {onClose, onSubmit} = props;

	const [name, setName] = useState<string>('');
	const [nameError, setNameError] = useState<string>('');
	const [description, setDescription] = useState<string>('');
	const [descriptionError, setDescriptionError] = useState<boolean>(false);
	const reset = () => {
		setName('');
		setNameError('');
		setDescription('');
		setDescriptionError(false);
	};

	const handleClose = () => {
		reset();
		onClose();
	};
	//console.log("add civil status: "+JSON.stringify(props.rows,null,2))
	const handleSubmit = () => {
		let chk = 0;
		for(let x = 0;x<props.rows.length;x++)
		{
			if(props.rows[x]['name'].toUpperCase().trim()===name.toUpperCase().trim())
			{chk=1;}
		}
		if (name.trim().length === 0 || chk === 1) {
			if(chk === 1)
			{setNameError('Name is Already Used');}
			else
			{setNameError('Input Name')}
		}

		if (description.length === 0) {
			setDescriptionError(true);
		}

		if (name.trim().length > 0 && description.length > 0 && chk === 0) {
			reset();
			onSubmit(name, description);
			onClose();
		}
	};

	return (
		<Grid item sm={12} style={{padding: '10px'}}>
			<DialogTitle id="form-dialog-title">Add a Civil Status</DialogTitle>
			<DialogContent>
				<Grid container spacing={2} justify="space-between" alignItems="flex-start">
					<Grid item xs={12}>
						<InputTextField
							label="Name"
							value={name}
							error={nameError.length>0}
							onChange={(value: string) => {
								setName(value);
								setNameError('');
							}}
							errorMessage={nameError}
						/>
					</Grid>

					<Grid item xs={12}>
						<InputTextField
							label="Description"
							value={description}
							error={descriptionError}
							onChange={(value: string) => {
								setDescription(value);
								setDescriptionError(value.length === 0);
							}}
							errorMessage="Input Description"
							multiline
						/>
					</Grid>
				</Grid>
			</DialogContent>
			<DialogActions>
				<Grid container spacing={2} justify="center" alignItems="flex-start">
					<Grid item xs={5}>
						<Button 
							variant="outlined" 
							color="default" 
							fullWidth
							onClick={handleClose}
						>
							Cancel
						</Button>
					</Grid>
					<Grid item xs={5}>
						<Button
							variant="contained"
							color="primary"
							fullWidth
							onClick={handleSubmit}
						>
							Add
						</Button>
					</Grid>
				</Grid>
			</DialogActions>
		</Grid>
	);
}

const Relationship: React.FC<EligibilityPopoverProps> = (props: EligibilityPopoverProps): JSX.Element | null => {
	const {onClose, onSubmit, rows} = props;

	const [civilStatus, setCivilStatus] = useState<string>('');
	const [civilStatusError, setCivilStatusError] = useState<boolean>(false);
	const [relationship, setRelationship] = useState<string>('');
	const [relationshipCode, setRelationshipCode] = useState<string>('');
	const [relationshipError, setRelationshipError] = useState<string>('');
	const [type, setType] = useState<string>('');
	const [typeError, setTypeError] = useState<boolean>(false);
	const [hierarchy, setHierarchy] = useState<string>('');
	const [hierarchyError, setHierarchyError] = useState<boolean>(false);
	const [enrollmentPeriod, setEnrollmentPeriod] = useState<string>('');
	const [enrollmentPeriodError, setEnrollmentPeriodError] = useState<boolean>(false);
	const [enrollmentPeriodUnit, setEnrollmentPeriodUnit] = useState<string>('');
	const [enrollmentPeriodUnitError, setEnrollmentPeriodUnitError] = useState<boolean>(false);
	const [effectivityCoverage, setEffectivityCoverage] = useState<string>('');
	const [effectivityCoverageError, setEffectivityCoverageError] = useState<boolean>(false);
	const [effectivityCoverageUnit, setEffectivityCoverageUnit] = useState<string>('');
	const [effectivityCoverageUnitError, setEffectivityCoverageUnitError] = useState<boolean>(false);
	const [ageLimitMin, setAgeLimitMin] = useState<string>('');
	const [ageLimitMinError, setAgeLimitMinError] = useState<boolean>(false);
	const [ageLimitMinUnit, setAgeLimitMinUnit] = useState<string>('');
	const [ageLimitMinUnitError, setAgeLimitMinUnitError] = useState<boolean>(false);
	const [ageLimitMax, setAgeLimitMax] = useState<string>('');
	const [ageLimitMaxError, setAgeLimitMaxError] = useState<boolean>(false);
	const [ageLimitMaxUnit, setAgeLimitMaxUnit] = useState<string>('');
	const [ageLimitMaxUnitError, setAgeLimitMaxUnitError] = useState<boolean>(false);
	
	
	const reset = () => {
		setCivilStatus('');
		setCivilStatusError(false);
		setRelationship('');
		setRelationshipCode('');
		setRelationshipError('');
		setType('');
		setTypeError(false);
		setHierarchy('');
		setHierarchyError(false);
		setEnrollmentPeriod('');
		setEnrollmentPeriodError(false);
		setEnrollmentPeriodUnit('');
		setEnrollmentPeriodUnitError(false);
		setEffectivityCoverage('');
		setEffectivityCoverageError(false);
		setEffectivityCoverageUnit('');
		setEffectivityCoverageUnitError(false);
	};

	const handleClose = () => {
		reset();
		onClose();
	};

	const handleSubmit = () => {
		let ind = 0;
		let chk =0;
		if (civilStatus.length === 0) {
			
			setCivilStatusError(true);
		}
		else
		{
			for(let x=0;x<props.rows.length;x++)
			{
				// console.log('ind'+ind+' '+civilStatus+' '+x+' '+props.rows.length);
				if(civilStatus===props.rows[x]['code'])
				{ind=x};
			}
			for(let x=0;x<props.rows[ind]['child_nodes'].length;x++)
			{
				// console.log(relationship)
				if(relationship.toUpperCase().trim()===props.rows[ind]['child_nodes'][x]['name'].toUpperCase().trim())
				{chk = 1}	
			}
		}
		if (type.length === 0) {
			setTypeError(true);
		}
		
		
		
		if (relationship.trim().length === 0 || chk === 1) {
			if(chk===1)
				{setRelationshipError('Relationship is Already used in Selected Civil Status');}
			else
				{setRelationshipError('Relationship is Required');}
		}
		else
		{
			setRelationshipError('')
		}
		if (enrollmentPeriod.length === 0) {
			setEnrollmentPeriodError(true);
		}

		if (enrollmentPeriodUnit.length === 0) {
			setEnrollmentPeriodUnitError(true);
		}

		if (effectivityCoverage.length === 0) {
			setEffectivityCoverageError(true);
		}

		if (effectivityCoverageUnit.length === 0) {
			setEffectivityCoverageUnitError(true);
		}

		if (ageLimitMin.length === 0) {
			setAgeLimitMinError(true);
		}

		if (ageLimitMinUnit.length === 0) {
			setAgeLimitMinUnitError(true);
		}

		if (ageLimitMax.length === 0) {
			setAgeLimitMaxError(true);
		}

		if (ageLimitMaxUnit.length === 0) {
			setAgeLimitMinUnitError(true);
		}

		if ( civilStatus.length > 0 && 
			 type.length > 0 && 
			 relationship.length > 0 && 
			 enrollmentPeriod.length > 0 && 
			 enrollmentPeriodUnit.length > 0 &&
			 effectivityCoverage.length > 0 &&
			 effectivityCoverageUnit.length > 0 &&
			 ageLimitMin.length > 0 &&
			 ageLimitMinUnit.length > 0 &&
			 ageLimitMax.length > 0 &&
			 ageLimitMaxUnit.length > 0 &&
			 chk === 0) {

			const parent = rows.find ((item) => {
				return civilStatus === item.code;
			});

			const data: object = {
				type: type,
				relationship: relationship, 
				hierarchy: hierarchy.length > 0 ? hierarchy : null,
				enrollment_period: enrollmentPeriod,
				enrollment_period_unit: enrollmentPeriodUnit,
				effectivity_coverage: effectivityCoverage,
				effectivity_coverage_unit: effectivityCoverageUnit,
				age_limit_min: ageLimitMin,
				age_limit_min_unit: ageLimitMinUnit,
				age_limit_max: ageLimitMax,
				age_limit_max_unit: ageLimitMaxUnit,
			};

			reset();
			onSubmit(relationship, relationshipCode, "description", data, parent);
			onClose();
		}
	};


	const getCivilStatus = () => {
		const inputTypes: ISelectItem[] = rows.reduce((result, status, i) => {
			if (status['included']) {
				const input = {
					id: i,
					label: status.name,
					value: status.code
				}

				result.push (input);
			}

			return result;
		}, new Array());

		return inputTypes;
	};

	const getRelationships = () => {
		const currentCivilStatus = props.rows.find((row) => row.code === civilStatus);

		if (currentCivilStatus !== undefined) {
			const inputTypes: ISelectItem[] = RelationshipToPrincipalItems.reduce((result, relationship) => {
				const found = currentCivilStatus.child_nodes.find((child) => child.code === relationship.value);

				if (!found) {
					result.push (relationship);
				}

				return result;
			}, new Array());
			return inputTypes;
		} 

		const inputTypes: ISelectItem[] = RelationshipToPrincipalItems;
		
		return inputTypes;
	};

	const getTypes = () => {
		const inputTypes: ISelectItem[] = TypeItems;

		return inputTypes;
	};

	const getHierarchy = () => {
		const inputTypes: ISelectItem[] = HierarchyItems;

		return inputTypes;
	};

	const getEnrollmentPeriod = () => {
		const inputTypes: ISelectItem[] = EnrollmentPeriodItems;

		return inputTypes;
	};

	const getEffectivityCoverage = () => {
		const inputTypes: ISelectItem[] = EffectivityCoverageItems;

		return inputTypes;
	};

	const getAgeLimit = () => {
		const inputTypes: ISelectItem[] = EligibilityAgeLimitItems;

		return inputTypes;
	}

	return (
		<Grid item sm={12} style={{padding: '10px'}}>
			<DialogTitle id="form-dialog-title">Add a Relationship</DialogTitle>
			<DialogContent>
				<Grid container spacing={2} justify="space-between" alignItems="flex-start">
					<Grid item sm={12}>
						<InputSelect
							id="civil-status-select"
							label="Civil Status"
							data={getCivilStatus()}
							value={civilStatus}
							error={civilStatusError}
							onChange={(selected: object) => {
								const value = selected['value'];
								setCivilStatus(value);
								setCivilStatusError(value.length === 0);
							}}
						/>
					</Grid>

					<Grid item sm={12}>
						<InputSelect
							id="relationship-select"
							label="Relationship to Principal"
							data={getRelationships()}
							value={relationship}
							error={relationshipError!==''}
							onChange={(selected: object) => {
								const label = selected['label'];
								const value = selected['value'];
								setRelationship(label);
								setRelationshipCode(value);
								setRelationshipError('');
							}}
						/>
					</Grid>

					<Grid item sm={8}>
						<InputSelect
							id="type-select"
							label="Type"
							data={getTypes()}
							value={type}
							error={typeError}
							onChange={(selected: object) => {
								const value = selected['value'];
								setType(value);
								setTypeError(value.length === 0);
							}}
						/>
					</Grid>

					<Grid item sm={4}>
						<InputSelect
							id="hierarchy-select"
							label="Hierarchy"
							data={getHierarchy()}
							value={hierarchy}
							error={hierarchyError}
							onChange={(selected: object) => {
								const value = selected['value'];
								setHierarchy(value);
								setHierarchyError(value.length === 0);
							}}
						/>
					</Grid>
					<Grid item sm={3}>
						<InputTextField
							id="enrollment-select"
							label="Enrollment Period (x days from)"
							value={enrollmentPeriod}
							error={enrollmentPeriodError}
							onChange={(value: string) => {
								setEnrollmentPeriod(value);
								setEnrollmentPeriodError(value.length === 0);
							}}
						/>
					</Grid>
					<Grid item sm={9}>
						<InputSelect
							id="enrollment-unit-select"
							label="&nbsp;"
							data={getEnrollmentPeriod()}
							value={enrollmentPeriodUnit}
							error={enrollmentPeriodUnitError}
							onChange={(selected: object) => {
								const value = selected['value'];
								setEnrollmentPeriodUnit(value);
								setEnrollmentPeriodUnitError(value.length === 0);
							}}
						/>
					</Grid>
					<Grid item sm={3}>
						<InputTextField
							id="effectivity-select"
							label="Effectivity Coverage (x days from)"
							value={effectivityCoverage}
							error={effectivityCoverageError}
							onChange={(value: string) => {
								setEffectivityCoverage(value);
								setEffectivityCoverageError(value.length === 0);
							}}
						/>
					</Grid>
					<Grid item sm={9}>
						<InputSelect
							id="effectivity-unit-select"
							label="&nbsp;"
							data={getEffectivityCoverage()}
							value={effectivityCoverageUnit}
							error={effectivityCoverageUnitError}
							onChange={(selected: object) => {
								const value = selected['value'];
								setEffectivityCoverageUnit(value);
								setEffectivityCoverageUnitError(value.length === 0);
							}}
						/>
					</Grid>

					<Grid item sm={3}>
						<InputTextField
							id="age-limit-min-select"
							label="Age Limit (min)"
							value={ageLimitMin}
							error={ageLimitMinError}
							onChange={(value: string) => {
								setAgeLimitMin(value);
								setAgeLimitMinError(value.length === 0);
							}}
						/>
					</Grid>
					<Grid item sm={3}>
						<InputSelect
							id="age-limit-min-unit-select"
							label="&nbsp;"
							data={getAgeLimit()}
							value={ageLimitMinUnit}
							error={ageLimitMinUnitError}
							onChange={(selected: object) => {
								const value = selected['value'];
								setAgeLimitMinUnit(value);
								setAgeLimitMinUnitError(value.length === 0);
							}}
						/>
					</Grid>

					<Grid item sm={3}>
						<InputTextField
							id="age-limit-max-select"
							label="Age Limit (max)"
							value={ageLimitMax}
							error={ageLimitMaxError}
							onChange={(value: string) => {
								setAgeLimitMax(value);
								setAgeLimitMaxError(value.length === 0);
							}}
						/>
					</Grid>
					<Grid item sm={3}>
						<InputSelect
							id="age-limit-max-unit-select"
							label="&nbsp;"
							data={getAgeLimit()}
							value={ageLimitMaxUnit}
							error={ageLimitMaxUnitError}
							onChange={(selected: object) => {
								const value = selected['value'];
								setAgeLimitMaxUnit(value);
								setAgeLimitMaxUnitError(value.length === 0);
							}}
						/>
					</Grid>

					<Grid item sm={12}>
					</Grid>
				</Grid>
			</DialogContent>
			<DialogActions>
				<Grid container spacing={2} justify="center" alignItems="flex-start">
					<Grid item xs={5}>
						<Button 
							variant="outlined" 
							color="default" 
							fullWidth
							onClick={handleClose}
						>
							Cancel
						</Button>
					</Grid>
					<Grid item xs={5}>
						<Button
							variant="contained"
							color="primary"
							fullWidth
							onClick={handleSubmit}
						>
							Add
						</Button>
					</Grid>
				</Grid>
			</DialogActions>
		</Grid>
	);
}

EligibilityForm.defaultProps = defaultProps;