import * as React from 'react';
import {  ConsultationDetails, TicketData } from 'Pages/CustomerCarePage/Models';
import { Grid } from '@material-ui/core';
import { CustomButton } from 'Pages/CustomerCarePage/Components/CustomButton';
import { NumberField } from 'Pages/CustomerCarePage/Components/NumberField';

interface IProps {
    ticket: TicketData;
    consultation: ConsultationDetails;
    canEditLOAs: boolean;
    refreshLOAStates: () => void;
    onBack?: () => void;
}

export class SummaryConsultationSection extends React.Component<IProps> {

    render(): JSX.Element {
        const ticket = this.props.ticket;
        const consultation = this.props.consultation;

        return (
            <div className='section'>
                <div style={{paddingTop: '2.5rem'}} />
                <div className='header-1' style={{display: 'inline-block'}}>
                    Resolve Consultation
                </div>
                {this.props.onBack ? (
                    <div style={{float: 'right'}}>
                        <CustomButton 
                            className='button'
                            text='Edit Assessment'
                            onClick={this.props.onBack} />
                    </div>
                ) : null}
                <div className='sub-section-2'>
                    <div className='light-text'>PROVIDER</div>
                    <div className='small-text'>
                        {`${consultation.provider.value} - ${consultation.provider.label}`}
                    </div>
                </div>
                {consultation && consultation.diagnosis && consultation.diagnosis.value && consultation.diagnosis.value !== ' - ' ? (
                        <div className='sub-section-2'>
                            <div className='light-text'>DIAGNOSIS</div>
                            <div className='small-text'>
                                {`${consultation.diagnosis.value} - ${consultation.diagnosis.label}`}
                            </div>
                        </div>
                ) : null}
                <div className='sub-section-2'>
                    <div className='light-text'>DOCTOR</div>
                    <div className='small-text'>
                        {consultation.doctor.label}
                    </div>
                </div>
                <div className='sub-section-2'>
                    <div className='light-text'>NOTES</div>
                    <div className='small-text'>{consultation.notes ? consultation.notes : 'None'}</div>
                </div>
                {ticket.approverName ? (
                    <div className='sub-section'>
                        <div className='light-text'>APPROVER NAME</div>
                        <div className='small-text'>{ticket.approverName}</div>
                    </div>
                ) : null}

                {consultation.assessmentType !== '' ? (
                    <Grid className='sub-section' container>
                        <Grid item xs={6}>
                            <div className='header-2'>ASSESSMENT</div>
                            <div className='small-text'>{consultation.assessmentType}</div>
                            <div className='sub-section-2'>
                                <div className='light-text'>REASON</div>
                                <div className='small-text'>{consultation.assessmentRemarks ? consultation.assessmentRemarks : 'None'}</div>
                            </div>
                        </Grid>
                    </Grid>
                ) : (
                    <Grid className='sub-section' container>
                        <Grid item xs={6}>
                            <div className='header-2'>ASSESSMENT</div>
                            <div className='small-text'>None</div>
                        </Grid>
                    </Grid>
                )}
                {this.props.canEditLOAs && consultation.assessmentType === 'Approved' ? (
                    <Grid className='sub-section' container>
                        <Grid item xs={6}>
                            <div className='header-2'>ADD LOA</div>
                            <Grid container spacing={1}>
                                <Grid item xs={6}>
                                    <NumberField
                                        placeholder='LOA Number*'
                                        value={consultation.loaNo}
                                        disableDelimiter={true}
                                        onChange={(value: number) => {
                                            consultation.loaNo = value;
                                            this.props.refreshLOAStates();
                                        }} />
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                ) : consultation.loaNo ? (
                    <Grid className='sub-section' container>
                        <Grid item xs={6}>
                            <div className='header-2'>LOA DETAILS</div>
                            <div className='sub-section-2'>
                                <div className='light-text'>LOA NUMBER</div>
                                <div className='small-text'>{consultation.loaNo}</div>
                            </div>
                            <div className='sub-section-2'>
                                <div className='light-text'>APPROVAL CODE</div>
                                <div className='small-text'>{consultation.approvalCode ? consultation.approvalCode : ''}</div>
                            </div>
                        </Grid>
                    </Grid>
                ) : null}
                <div className='section' />
            </div>
        );
    }
}