import clsx from 'clsx';
import React, { useState, useRef } from 'react';
import {
  Paper,
  Popper,
  Typography,
  ClickAwayListener,
} from '@material-ui/core/';
// import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faCircle,
  faExclamationCircle,
} from '@fortawesome/free-solid-svg-icons';
import moment from 'moment';

interface StatusComponentProps {
  memberData?: any;
}

const hmoinfoStyles = makeStyles(theme => ({
  popper: {
    // backgroundColor: '#363636',
    top: '4px',
    '&[x-placement*="bottom"] $arrow': {
      top: 0,
      left: 0,
      marginTop: '-0.9em',
      width: '3em',
      height: '1em',
      '&::before': {
        borderWidth: '0 1em 1em 1em',
        borderColor: `transparent transparent #363636 transparent`,
      },
    },
    '&[x-placement*="top"] $arrow': {
      bottom: 0,
      left: 0,
      marginBottom: '-0.9em',
      width: '3em',
      height: '1em',
      '&::before': {
        borderWidth: '1em 1em 0 1em',
        borderColor: `${theme.palette.background.paper} transparent transparent transparent`,
      },
    },
    '&[x-placement*="right"] $arrow': {
      left: 0,
      marginLeft: '-0.9em',
      height: '3em',
      width: '1em',
      '&::before': {
        borderWidth: '1em 1em 1em 0',
        borderColor: `transparent ${theme.palette.background.paper} transparent transparent`,
      },
    },
    '&[x-placement*="left"] $arrow': {
      right: 0,
      marginRight: '-0.9em',
      height: '3em',
      width: '1em',
      '&::before': {
        borderWidth: '1em 0 1em 1em',
        borderColor: `transparent transparent transparent ${theme.palette.background.paper}`,
      },
    },
  },
  arrow: {
    position: 'absolute',
    fontSize: 7,
    width: '3em',
    height: '3em',
    '&::before': {
      content: '""',
      margin: 'auto',
      display: 'block',
      width: 0,
      height: 0,
      borderStyle: 'solid',
    },
  },
  paper: {
    backgroundColor: '#363636',
    padding: '8px',
    width: 127,
  },
  status: {
    padding: '5px',
    cursor: 'pointer',
  },
}));

export const StatusComponent: React.FC<StatusComponentProps> = (
  props: StatusComponentProps,
): JSX.Element => {
  const classes = hmoinfoStyles(props);
  const { memberData } = props;
  const anchorRef = useRef(null);
  const [arrowRef, setArrowRef] = useState<any>(null);
  const [isPopoverOpenClick, setPopupoverOpenClick] = useState(false);
  const [isPopoverOpenHover, setPopupoverOpenHover] = useState(false);
  const [poperMsg, setPoperMsg] = useState('');

  const handleEnablePopper = () => {
    if (
      memberData &&
      memberData['member_suspension'] &&
      memberData['member_suspension']['suspension_start_date'] &&
      moment(
        memberData['member_suspension']['suspension_start_date'],
      ).isValid() &&
      moment(memberData['member_suspension']['suspension_start_date']).diff(
        new Date(),
      ) > 0
    ) {
      setPopupoverOpenHover(true);
      setPoperMsg('Marked for suspension');
    } else if (
      memberData &&
      memberData['member_termination'] &&
      memberData['member_termination']['termination_date'] &&
      moment(memberData['member_termination']['termination_date']).isValid() &&
      moment(memberData['member_termination']['termination_date']).diff(
        new Date(),
      ) > 0
    ) {
      setPopupoverOpenHover(true);
      setPoperMsg('Marked for termination');
    }
  };

  return (
    <span
      onMouseEnter={handleEnablePopper}
      onMouseLeave={() => {
        setPopupoverOpenHover(false);
      }}
    >
      <ClickAwayListener
        onClickAway={() => {
          setPopupoverOpenClick(false);
        }}
      >
        <span
          className={clsx('member-status-span')}
          ref={anchorRef}
          onClick={handleEnablePopper}
        >
          <FontAwesomeIcon
            className="fa"
            icon={
              memberData &&
              ((memberData['member_termination'] &&
                memberData['member_termination']['termination_date'] &&
                moment(
                  memberData['member_termination']['termination_date'],
                ).isValid() &&
                moment(
                  memberData['member_termination']['termination_date'],
                ).diff(new Date()) > 0) ||
                (memberData['member_suspension'] &&
                  memberData['member_suspension']['suspension_start_date'] &&
                  moment(
                    memberData['member_suspension']['suspension_start_date'],
                  ).isValid() &&
                  moment(
                    memberData['member_suspension']['suspension_start_date'],
                  ).diff(new Date()) > 0))
                ? faExclamationCircle
                : faCircle
            }
            style={{
              color:
                memberData &&
                memberData['member_termination'] &&
                memberData['member_termination']['termination_date'] &&
                moment(
                  memberData['member_termination']['termination_date'],
                ).isValid() &&
                moment(
                  memberData['member_termination']['termination_date'],
                ).diff(new Date()) > 0
                  ? '#BC2206'
                  : memberData['member_suspension'] &&
                    memberData['member_suspension']['suspension_start_date'] &&
                    moment(
                      memberData['member_suspension']['suspension_start_date'],
                    ).isValid() &&
                    moment(
                      memberData['member_suspension']['suspension_start_date'],
                    ).diff(new Date()) > 0
                  ? '#BC2206'
                  : memberData.member_status.toLowerCase() ===
                      'for validation' ||
                    memberData.member_status.toLowerCase() === 'pending'
                  ? '#FAB435'
                  : memberData.member_status.toLowerCase() === 'terminated' ||
                    memberData.member_status.toLowerCase() === 'cancelled'
                  ? '#BC2206'
                  : memberData.member_status.toLowerCase() === 'suspended'
                  ? '#FD5451'
                  : memberData.member_status.toLowerCase() === 'disapproved'
                  ? '#8034AD'
                  : memberData.member_status.toLowerCase() === 'expired'
                  ? '#B5B8C1'
                  : '#3AB77D',
              fontSize: '11px',
              marginBottom: '1px',
            }}
          />

          <Popper
            open={isPopoverOpenClick || isPopoverOpenHover}
            anchorEl={anchorRef.current}
            placement={'bottom-end'}
            className={classes.popper}
            disablePortal={false}
            style={{ marginTop: '4px' }}
            modifiers={{
              flip: {
                enabled: false,
              },
              preventOverflow: {
                enabled: true,
                boundariesElement: 'undefined',
              },
              arrow: {
                enabled: true,
                element: arrowRef,
              },
            }}
          >
            <span className={classes.arrow} ref={setArrowRef} />
            <Paper className={classes.paper}>
              <Typography style={{ fontSize: '10px', color: 'white' }}>
                {poperMsg}
              </Typography>
            </Paper>
          </Popper>
        </span>
      </ClickAwayListener>
    </span>
  );
};
