    componentDidMount() {
        this.loadFacilities();        

        Global.loadIcd10Codes(undefined, () => {
            this.setState({snackbarMessage: 'Failed to get ICD10 codes.'});
        });

        Global.loadCptCodes(undefined, () => {
            this.setState({snackbarMessage: 'Failed to get CPT codes.'});
        });

        Utils.StorageService('user_data').then((result) => {
            this.setState({login: Payloads.getLoginData(result)});

            if (!this.state.login) {
                this.setState({snackbarMessage: 'Failed to get user data. Please try to re-login.'});
                return;
            }

            API.get(`tmp?` +
                `filter[where][and][0][user_id]=${this.state.login.userId}&` +
                `filter[where][and][1][module]=Customer%20Care&` +
                `filter[where][and][2][process]=Create%20Ticket`, (response) => {
                response = response.filter(item => item.process === 'Create Ticket');
                if (response.length > 0) {
                    this.setState({
                        lastSave: new Date(response[0].timestamp),
                        ticket: response[0].data.ticket,
                        availment: response[0].data.availment,
                        confinement: response[0].data.confinement,
                        consultation: response[0].data.consultation,
                        complaint: response[0].data.complaint,
                        inquiries: response[0].data.inquiries,
                        endorsement: response[0].data.endorsement,
                        requestApproval: response[0].data.requestApproval,
                        doctors: response[0].data.doctors
                    });

                    const id = 
                        response[0].data.client_id ? response[0].data.client_id :
                        response[0].data.member_id ? response[0].data.member_id : '';

                    const type = response[0].data.client_id ? 'Client' : 'Member';

                    this.loadMemberAndClient(id, type);

                    if (this.state.ticket.ticketType !== '') {
                        this.loadAssignees(this.state.ticket.ticketType);
                    }
                }
                this.setState({isLoading: false});
            }, () => {
                this.setState({isLoading: false});
            });

            if (this.state.login && 
                this.state.login.group && 
                this.state.login.group.name === 'Customer Service Specialist GROUP') {
                this.setState({searchBarData: {
                    value: '',
                    type: { value: '2', label: 'Member ID' }
                }});
            }
        }).catch(() => {
            this.setState({snackbarMessage: 'Failed to get user data. Please try to re-login.'});
        });

        
    }










