    loadFacilities = () => {
        API.pnGet('p/f/partners/bare-facilities', (response) => {
            this.facilities = response.data.map(facility => {
                return {
                    value: facility.facility_id,
                    label: facility.facility_name
                }
            });
        }, () => {
            this.setState({snackbarMessage: 'Failed to get facilities data.'});
        });
    }










    loadAssignees = (ticketType: string, specificType?: string) => {
        const departmentArray: string[] = [];
        if (specificType) {
            if (ticketType !== 'Request Requiring Approval' && specificType !== 'Inpatient')
                departmentArray.push('Customer Service');
        }
        
        if (ticketType === 'Inquiry' || 
            ticketType === 'Request/Endorsement' ||
            ticketType === 'Complaint/Concern' ||
            specificType === 'Inpatient' ||
            specificType === 'Outpatient Procedure')
            departmentArray.push('Patient Relations');
        if (ticketType !== 'Availment')
            departmentArray.push('AMD');
        const department = encodeURIComponent(departmentArray.join());
        API.get(`user-groups/customer-care?department=${department}`, (response) => {
            if (!this.state.login) return;
            const groups = response.groups.map(group => {
                return {
                    value: group.pmaker_uid,
                    label: group.name,
                    params: [true, group]
                }
            });
            const users = response.users.map(user => {
                return {
                    value: user.pmaker_uid,
                    label: `${user.first_name} ${user.last_name}`,
                    params: [false, user]
                }
            });
            const combined = groups.concat(users);
            console.log('combined',combined)
            GlobalFunction.setMeInAssignees(combined, this.state.login.pmakerUid);
            this.setState({assignees: combined});
        }, () => {
            this.setState({snackbarMessage: 'Failed to get assignees.'});
        });
    }










    loadDoctors = (hospitalName: string) => {
        if (hospitalName !== '') {
            
            API.pnGet(`p/f/partners/facilities/${hospitalName}/doctor-procedures`, (response) => {
                const doctors = response.data.doctors.map(doctor => {
                    return {
                        value: doctor.doctor_id,
                        label: doctor.doctor_name,
                        params: doctor.type ? [doctor.type] : ['']
                    };
                });
                this.setState({doctors: doctors});
            }, () => {
                this.setState({doctors: []});
                this.setState({snackbarMessage: 'Failed to get doctors data.'});
            });
        }
    }










    loadMemberAndClient = (id: string, type: 'Member' | 'Client') => {
        if (type === 'Member') {
            API.get(`customer-care/members/${id}`, (response2) => {
                this.setState({member: Payloads.getMemberData(response2)});
                API.get(`customer-care/members/${id}/benefits`, () => {});
            }, () => {
                this.setState({snackbarMessage: 'Failed to get member data.'});
            });
        }

        if (type === 'Client') {
            API.get(`customer-care/clients/${id}`, (response) => {
                this.setState({client: Payloads.getClientData(response)});
            }, () => {
                this.setState({snackbarMessage: 'Failed to get client data.'});
            });
        }
    }










    getValidationErrorList = () => {
        let errors: ErrorMessageData[] = [];
        const client = this.state.client;
        const member = this.state.member;
        const ticket = this.state.ticket;
        const confinement = this.state.confinement;
        const inquiries = this.state.inquiries;
        const availment = this.state.availment;
        const consultation = this.state.consultation;
        const complaint = this.state.complaint;
        const requestApproval = this.state.requestApproval;
        const endorsement = this.state.endorsement;

        const ticketRequiredFields: string[] = [];
        if (ticket.ticketChannel.length === 0)
            ticketRequiredFields.push('Received via');
        if (ticket.transactionNo.length === 0)
            ticketRequiredFields.push('Transaction No.');
        if (ticket.ticketSource.length === 0)
            ticketRequiredFields.push('Received From');
        // if (ticket.callerName.length === 0)
        //     ticketRequiredFields.push('Name of Caller');
        if (ticket.contactNo.length === 0)
            ticketRequiredFields.push('Contact No.');
        if (ticket.ticketType.length === 0)
            ticketRequiredFields.push('Ticket Type');
        if (ticket.specificType.length === 0)
            ticketRequiredFields.push('Specific Type');
        if (ticket.urgency.length === 0)
            ticketRequiredFields.push('Urgency');
        if (ticket.assignee.name.length === 0)
            ticketRequiredFields.push('Assign To');
        if (ticket.assignee.name !== 'Me' && ticket.additionalNotes === '')
            ticketRequiredFields.push('Additional Notes');

        switch(ticket.ticketSource){
            case 'Member':
                if(member){
                    if(ticket.memberSourceChoice.value === ''){
                        ticketRequiredFields.push('Caller');
                    }
                    if(ticket.memberSourceChoice.value === 'Other'){
                        if(!(ticket.relationshipToMember && ticket.relationshipToMember.trim()))
                            ticketRequiredFields.push('Relationship to Member');
                        if (ticket.callerName.length === 0)
                            ticketRequiredFields.push('Name of Caller');
                    }
                }
                else if(client){
                    if (ticket.callerName.length === 0)
                        ticketRequiredFields.push('Name of Caller');
                }
                break;
            case 'Corporate Client':
                if(ticket.corporateSourceChoice.value === ''){
                    ticketRequiredFields.push('Caller');
                }
                if (ticket.callerName.length === 0)
                    ticketRequiredFields.push('Name of Caller');
                if(ticket.corporateSourceChoice.value === 'Other'){
                    if(!(ticket.companyPosition && ticket.companyPosition.trim()))
                        ticketRequiredFields.push('Company Designation/Position');
                }
                break;
            case 'Broker':
                if(ticket.brokerName.value === ''){
                    ticketRequiredFields.push('Broker Name');
                }
                if (ticket.callerName.length === 0)
                    ticketRequiredFields.push('Name of Caller');
                break;
            case 'Agent':
                if (ticket.callerName.length === 0)
                    ticketRequiredFields.push('Name of Caller');
                break;
            case 'Clinic/Hospital':
                if(ticket.facility.value === ''){
                    ticketRequiredFields.push('Name of Hospital');
                }
                if(ticket.facility.value === 'NOT AFFILIATED'){
                    if(ticket.otherFacility.trim() === ''){
                        ticketRequiredFields.push('Clinic/Hospital Name');
                    }
                }
                if(ticket.department.trim() === ''){
                    ticketRequiredFields.push('Department/Section');
                }
                if (ticket.callerName.length === 0)
                    ticketRequiredFields.push('Name of Caller');
                break;
            default:
                break;
        }

        if (!member && !client) {
            errors.push({
                message: 'Choose a member/client first before submitting the ticket.',
                targets: []
            });
        }

        if (ticketRequiredFields.length > 0) {
            errors.push({
                message: 'Please fill up the required fields before submitting the ticket:',
                targets: ticketRequiredFields
            });
        }

        if (ticket.ticketType === 'Inquiry') {
            const inquiriesRequiredFields: string[] = [];
            if (inquiries.length === 0) {
                inquiriesRequiredFields.push('Please add an inquiry details.');
            }
            inquiries.forEach(inquiry => {
                if (inquiry.detail.length === 0)
                    inquiriesRequiredFields.push(`${inquiry.inquiry.label}: Details of inquiry`);
                if(this.state.buttonClicked === 'Resolve and Close'){
                    if (inquiry.answer === '')
                        inquiriesRequiredFields.push(`${inquiry.inquiry.label}: Answer`);
                }
            });
            if (inquiriesRequiredFields.length > 0) {
                errors.push({
                    message: 'Ticket Type: Inquiry',
                    targets: inquiriesRequiredFields
                });
            }
        }

        if (ticket.specificType === 'Outpatient Procedure' ||
            ticket.specificType === 'Emergency Room') {
            const outpatientRequiredFields: string[] = [];
            if (availment.diagnoses.length === 0) {
                outpatientRequiredFields.push('Please add an availment details.');
            }
            if (availment.provider.value === '') {
                outpatientRequiredFields.push('Provider');
            }
            if (ticket.specificType === 'Emergency Room') {
                if (!availment.chiefComplaint || availment.chiefComplaint === '')
                    outpatientRequiredFields.push('Chief Complaint');
            }
            availment.diagnoses.forEach(diagnosis => {
                const procedures = diagnosis.procedures.filter(procedure => procedure.cptCode.value !== '');
                if (procedures.length === 0 && ticket.specificType === 'Outpatient Procedure') {
                    outpatientRequiredFields.push(`${diagnosis.icd10Code.value}: Please add atleast one procedure.`);
                }
                procedures.forEach(procedure => {
                    if (procedure.amount === 0) {
                        outpatientRequiredFields.push(
                            `${diagnosis.icd10Code.value}: ` +
                            `${procedure.cptCode.value}: ` +
                            `Amount should not be 0.`);
                    }
                });
            });
            if (outpatientRequiredFields.length > 0) {
                errors.push({
                    message: `Ticket Type: ${ticket.specificType}`,
                    targets: outpatientRequiredFields
                });
            }
        }

        if (ticket.specificType === 'Inpatient') {
            const inpatientRequiredFields: string[] = [];
            if (confinement.provider.value === '')
                inpatientRequiredFields.push('Provider');
            if (confinement.confinementKind === '')
                inpatientRequiredFields.push('Kind of Confinement');
            // if (confinement.roomType === '')
            //     inpatientRequiredFields.push('Room Type');
            // if (confinement.doctor.value === '')
            //     inpatientRequiredFields.push('Admitting Physician');
            if (confinement.initialDiagnosis.value === '')
                inpatientRequiredFields.push('Initial Diagnosis');
            if (!GlobalFunction.isValidDate(confinement.dateAdmitted, true))
                inpatientRequiredFields.push('Date and Time admitted (Invalid Format)');
            if (inpatientRequiredFields.length > 0) {
                errors.push({
                    message: 'Ticket Type: Inpatient',
                    targets: inpatientRequiredFields
                });
            }
        }

        if (ticket.ticketType === 'Request Requiring Approval') {
            const reqReqApprovalFields: string[] = [];
            if (requestApproval.title === '')
                reqReqApprovalFields.push('Summary of Request (Title)');
            // if (requestApproval.background === '')
            //     reqReqApprovalFields.push('Background / Details of the Request');
            // if (requestApproval.notes === '')
            //     reqReqApprovalFields.push('Notes')
            if (requestApproval.type.value === '')
                reqReqApprovalFields.push('Request Type')
            if (requestApproval.recommendation === '')
                reqReqApprovalFields.push('Recommendation')
            if (requestApproval.basisRecommendation === '')
                reqReqApprovalFields.push('Basis for Recommendation')
            if (reqReqApprovalFields.length > 0) {
                errors.push({
                    message: 'Ticket Type: Request Requiring Approval',
                    targets: reqReqApprovalFields
                })
            }
        }

        if (ticket.specificType === 'Consultation') {
            const consultationRequiredFields: string[] = [];
            if (consultation.provider.value === '')
                consultationRequiredFields.push('Provider');
            if (consultation.doctor.value === '')
                consultationRequiredFields.push('Doctor');
            if (consultationRequiredFields.length > 0) {
                errors.push({
                    message: 'Ticket Type: Consultation',
                    targets: consultationRequiredFields
                });
            }
        }

        if (ticket.ticketType === 'Complaint/Concern') {
            const complaintRequiredFields: string[] = [];
            if (complaint.type.value === '')
                complaintRequiredFields.push('Request Type');
            if (complaint.summary === '')
                complaintRequiredFields.push('Summary of Complaint (Title)');
            if (complaint.details === '')
                complaintRequiredFields.push('Background / Details of Complaint');
            if (complaintRequiredFields.length > 0) {
                errors.push({
                    message: 'Ticket Type: Complaint/Concern',
                    targets: complaintRequiredFields
                });
            }
        }

        if (ticket.ticketType === 'Request/Endorsement') {
            const endorsementRequiredFields: string[] = [];
            if (endorsement.requestType.value === '')
                endorsementRequiredFields.push('Request Type');
            if (endorsement.background === '')
                endorsementRequiredFields.push('Background / Details of Request');
            if (endorsementRequiredFields.length > 0) {
                errors.push({
                    message: 'Ticket Type: Request/Endorsement',
                    targets: endorsementRequiredFields
                });
            }
        }

        this.setState({createTicketAttemp: true});
        return errors;
    }










    onUploadFiles = (files: File[], key: string, nextAction: Function) => {
        let hasFiles = false;

        const formData = new FormData();
        files.forEach(file => {
            if (!file['field']) {
                formData.append(key, file);
                hasFiles = true;
            }
        });

        this.setState({isLoading: true});

        if (hasFiles) {
            API.postFormData('tmp/upload?path=customer-care', formData, (response) => {
                nextAction(response);
            }, () => {
                this.setState({
                    isLoading: false, 
                    snackbarMessage: 'Failed to upload files.'
                });
            });
        } else {
            nextAction([]);
        }
    }










    onCheckApprovalLimit = (buttonPressed: PMButtonPressed, availment: AvailmentDetails, assignee?: any) => {
        if (this.state.login && this.state.login.ccData) {
            const availmentCost = GlobalFunction.getAvailmentCost(availment);
            const approvalLimit = assignee ? 
                assignee.amount_limit : 
                this.state.login.ccData.amount_limit;
            if (availmentCost > approvalLimit) {
                this.setState({isOutsideTheLimitModalOpen: true});
            } else {
                this.onSubmitTicket(buttonPressed, this.state.login, availmentCost, true);
            }
        }
    }










    onSubmitTicket = (
        buttonPressed: PMButtonPressed,
        login: LoginData,
        amountLimit: number,
        isWithinLimit?: boolean) => {

        const ticket = this.state.ticket;
        const endorsement = this.state.endorsement;

        this.setState({isLoading: true});

        const postTicket = (supportingFiles: File[], inquiryFiles: File[][], endorsementFiles: File[]) => {
            const payload = Payloads.getPostTicketPayload(
                ticket,
                supportingFiles,
                inquiryFiles,
                endorsementFiles,
                login,
                this.state.member,
                this.state.client,
                this.state.confinement,
                this.state.availment,
                this.state.inquiries,
                this.state.consultation,
                this.state.complaint,
                this.state.endorsement,
                this.state.requestApproval,
                buttonPressed === 'RESOLVE');
            API.post('customer-care/tickets', JSON.stringify(payload), (response) => {
                if (response['_id']) {
                    const isInpatient = this.state.ticket.specificType === 'Inpatient';
                    this.onSubmitTicketProcessmaker(
                        buttonPressed,
                        response['_id'],
                        login,
                        amountLimit,
                        isWithinLimit,
                        isInpatient);
                } else {
                    this.setState({
                        disableButtons: false,
                        isLoading: false, 
                        snackbarMessage: 'Failed to create ticket.'
                    });
                }
            }, () => {
                this.setState({
                    disableButtons: false,
                    isLoading: false, 
                    snackbarMessage: 'Failed to create ticket.'
                });
            });
        }

        this.onUploadFiles(ticket.additionalFiles, 'supporting_document', (response) => {

            const supportingFiles = !response.error && response.length > 0 ? (
                ticket.additionalFiles.filter(file => file['field']).concat(response)
            ) : (
                ticket.additionalFiles
            );

            if (ticket.ticketType === 'Inquiry' && this.state.inquiries.length > 0) {
                const inquiryFiles: File[][] = [];
                this.state.inquiries.forEach(inquiry => {
                    this.onUploadFiles(inquiry.supportDocs, 'supporting_document', (response) => {
                        inquiryFiles.push(!response.error && response.length > 0 ? (
                            inquiry.supportDocs.filter(file => file['field']).concat(response)
                        ) : (
                            inquiry.supportDocs
                        ));
                        if (this.state.inquiries.length === inquiryFiles.length) {
                            postTicket(supportingFiles, inquiryFiles, []);
                        }
                    });
                });

            } else if (ticket.ticketType === 'Request/Endorsement') {
                this.onUploadFiles(endorsement.requestFiles, 'supporting_document', (response) => {
                    const endorsementFiles = !response.error && response.length > 0 ? (
                        endorsement.requestFiles.filter(file => file['field']).concat(response)
                    ) : (
                        endorsement.requestFiles
                    );
                    postTicket(supportingFiles, [], endorsementFiles);
                });

            } else {
                postTicket(supportingFiles, [], []);
            }
        });
    }










    onSubmitTicketProcessmaker = (
        buttonPressed: PMButtonPressed,
        ticketMongoId: string,
        login: LoginData,
        amountLimit: number,
        isWithinLimit?: boolean,
        isInpatient?: boolean) => {

        let casesBody = {
            pro_uid : '4807840465def02dbed2df0067603401',
            tas_uid: '2282187525e2cf3be040094086096323'
        };

        let pmakerUids: string[] = [];

        const pmCallShortcut = (method: 'POST' | 'PUT', url: string, body: any, onSuccess: Function) => {
            Processmaker.shortcutCall(method, url, body, onSuccess, () => {
                API.delete(`customer-care/tickets/${ticketMongoId}`, null, () => {
                    this.setState({
                        disableButtons: false,
                        isLoading: false,
                        isProcessMakerErrorModalOpen:true
                    });
                });
            })
        }

        const mainAction = () => {
            const variablePayload = Payloads.getPostTicketPayloadProcessmaker(
                buttonPressed,
                this.state.ticket,
                ticketMongoId,
                login,
                pmakerUids,
                this.state.member,
                this.state.client,
                isWithinLimit);

            pmCallShortcut('POST', 'cases', casesBody, (response) => {
                const appUID = response.app_uid;
                const appNo = response.app_number;
                // const trigger = isInpatient ? '7061593985e476d59d59896067924079' : '1110479535e2e737cb64e60095828706';
                const trigger = isInpatient ? '5880415115e61cbbe9cc794028029118' : '1110479535e2e737cb64e60095828706';
                if (appUID) {
                    pmCallShortcut('PUT', `cases/${appUID}/execute-trigger/5737759115def02dc44ad03013650160`, {}, () => {
                        pmCallShortcut('PUT', `cases/${appUID}/variable`, variablePayload, () => {
                            pmCallShortcut('PUT', `cases/${appUID}/execute-trigger/${trigger}`, {}, () => {
                                if (buttonPressed === 'RESOLVE' ||
                                    buttonPressed === 'SUBMIT FOR ASSESSMENT' ||
                                    this.state.ticket.specificType === 'Inpatient') {
                                    pmCallShortcut('PUT', `cases/${appUID}/route-case`, {}, () => {
                                        patchCaseUID(appUID, appNo);
                                    });
                                } else {
                                    pmCallShortcut('PUT', `cases/${appUID}/route-case`, {}, () => {
                                        pmCallShortcut('PUT', `cases/${appUID}/route-case`, {}, () => {
                                            patchCaseUID(appUID, appNo);
                                        });
                                    });
                                }
                            });
                        });
                    });
                }
            });
        }

        const patchCaseUID = (appUID: string, appNo: string) => {
            const ticket = this.state.ticket;

            if (!login.group || !ticket.assignee.customData) return;

            const ticketId = GlobalFunction.generateTicketNumber(ticket.ticketType, ticket.specificType, appNo);
            const payload = {
                ticket_id: ticketId,
                pmaker_case_uid: appUID,
                assigned_by: `${login.firstName} ${login.lastName}`,
                assign_remarks: ticket.additionalNotes,
                ticket_owner_department: ticket.specificType === 'Inpatient' ?
                    ticket.assignee.customData.department :
                    login.group.department
            };
            
            API.patch(`customer-care/tickets/${ticketMongoId}`, payload,() => {
                redirectToNextPage(appUID);
            }, () => {
                this.setState({
                    disableButtons: false,
                    isLoading: false,
                    snackbarMessage: 'Failed to create ticket.',
                    isOutsideTheLimitModalOpen: false
                });
            });
        }

        const redirectToNextPage = (appUID: string) => {
            const ticket = this.state.ticket;
            const url = (
                ticket.ticketType !== 'Availment' ||
                ticket.specificType === 'Inpatient') ? (
                    `#/customer-care/update-ticket/${appUID}/${ticketMongoId}`
                ) : (
                    `#/customer-care/resolve-ticket/${appUID}/${ticketMongoId}`
                );
            this.props.history.push(url);
            window.location.reload();
            this.setState({isLoading: false, disableButtons: true});
        }

        if (isWithinLimit !== undefined && !isWithinLimit) {
            const department = login.group ? login.group.department : '';
            API.get(`group/pmaker_uids?amount_limit=${amountLimit}&` +
                    `department=${department}&` +
                    `requesting_group=${get(login, 'group.name', '')}&` +
                    `role=Ticket Approver&` + 
                    `with_names=true`, (response) => {
                
                if (response.group_pmaker_ids.length === 0) {
                    const deptPMakerUID = login && login.ccData ? login.ccData.supervising_department_pmaker_id : '';
                    pmakerUids.push(deptPMakerUID);
                } else {
                    pmakerUids = response.group_pmaker_ids;
                }
                mainAction();
            });
        } else {
            mainAction();
        }
    }










    onSaveForNow = () => {
        const login = this.state.login;
        const ticket = this.state.ticket;
        const inquiries = this.state.inquiries;
        const endorsement = this.state.endorsement;
        const requestApproval = this.state.requestApproval;
        const complaint = this.state.complaint;

        const postTicket = (supportingFiles: File[], inquiryFiles: File[][], endorsementFiles: File[]) => {
            if (!login) return;

            const payload = Payloads.saveInCreateTicketPayload(
                login, 
                this.state.doctors,
                ticket,
                this.state.availment,
                this.state.confinement,
                this.state.consultation,
                inquiries,
                endorsement,
                requestApproval,
                complaint,
                supportingFiles,
                inquiryFiles,
                endorsementFiles,
                this.state.member,
                this.state.client)

            API.post('tmp', JSON.stringify(payload),(response) => {
                if (response._id) {
                    this.setState({
                        isLoading: false,
                        snackbarMessage: 'Progress saved.',
                        lastSave: new Date()
                    });
                } else {
                    this.setState({
                        isLoading: false,
                        snackbarMessage: 'Failed to save progress.'
                    });
                }
            }, () => {
                this.setState({
                    isLoading: false,
                    snackbarMessage: 'Failed to save progress.'
                });
            });
        }

        this.onUploadFiles(this.state.ticket.additionalFiles, 'supporting_document', (response) => {
            const supportingFiles = !response.error && response.length > 0 ?
                ticket.additionalFiles.filter(file => file['field']).concat(response) :
                ticket.additionalFiles;

            if (ticket.ticketType === 'Inquiry' && inquiries.length > 0) {
                const inquiryFiles: File[][] = [];
                inquiries.forEach(inquiry => {
                    this.onUploadFiles(inquiry.supportDocs, 'supporting_document', (response) => {
                        inquiryFiles.push(!response.error && response.length > 0 ? 
                            inquiry.supportDocs.filter(file => file['field']).concat(response) :
                            inquiry.supportDocs
                        );
                        if (inquiries.length === inquiryFiles.length) {
                            postTicket(supportingFiles, inquiryFiles, []);
                        }
                    });
                });

            } else if (ticket.ticketType === 'Request/Endorsement') {
                this.onUploadFiles(endorsement.requestFiles, 'supporting_document', (response) => {
                    const endorsementFiles = !response.error && response.length > 0 ?
                        endorsement.requestFiles.filter(file => file['field']).concat(response) :
                        endorsement.requestFiles;
                    postTicket(supportingFiles, [], endorsementFiles);
                });

            } else {
                postTicket(supportingFiles, [], []);
            }
        });
    }










